import { NgModule, Optional, SkipSelf } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { PanelModule } from '../../panel';
import { SharedModule } from '../../shared';

//third part modules
import { PageWizardComponent } from './page/pageWizard.component';
import { BatchExportComponent } from './batchExportImport/batchExport.component';
import { BatchImportComponent } from './batchExportImport/batchImport.component';
import { BatchProgressPopup } from './batchExportImport/batchProgressPopup.component';

const COMPONENTS = [
    BatchExportComponent, 
    BatchImportComponent, 
    BatchProgressPopup,
    PageWizardComponent,  
]

@NgModule({
    imports: [
        BrowserAnimationsModule,
        PanelModule,
        SharedModule,
        ReactiveFormsModule,
    ],
    declarations: [
        COMPONENTS,
    ],
    exports: [
        COMPONENTS,
    ],
    entryComponents: [
        COMPONENTS,
    ],
})
export class WizardModule {
    constructor( @Optional() @SkipSelf() parentModule: WizardModule) {
        if (parentModule) {
            throw new Error(
                'WizardModule is already loaded. Import it in the StudioModule only');
        }
    }
}

