import { Component, Input, OnChanges, ChangeDetectorRef, ElementRef} from '@angular/core';

import { PortalService } from '../../common/services/portal.service';
import { WCFService } from '../../common/services/wcf.service';
import { TraceItem, TraceLog } from './traceLog';

@Component({
    selector: 'csi-trace-log',
    styles: [`
        :host{
            display: flex;
            flex-direction: column;
            height: 100%;
            flex: 1 1 auto;                    
        }

        .csi-trace-log{
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;                    
        }

        .header {
            background-color: lightgray;
            font-weight: bold;
            padding: 2px 5px;
            flex: 0 0 auto;
            display: inline-flex;
            width: 100%;
        }

        .header .refresh-btn {
            padding-left: 20px;
            padding-right: 20px;
            color: brown;
        }
        .header .refresh-btn i {
            cursor: pointer;
        }
        
        .header .page-selector {
            flex-basis: 20%;
        }

        .header .page-selector select {
            width: 100%;
        }

        .log-content {
            display: flex;
            overflow: hidden;
            flex: 1 1;
        }

        .log-content .transactions {
            flex: 1 1;
            overflow: auto;
        }

        .transactions-container{
            height: 100%;
            width: 100%;
        }

        .server-traces {
            width: 30%;
            overflow: auto;
        }

        .splitter {
            width: 2px;
            background-color: #dddddd;
        }
    `],
    templateUrl: 'traceLog.component.html'
})
export class TraceLogComponent implements OnChanges {
    @Input() activePage: string;

    logItems: TraceItem[] = [];
    serverItems: TraceItem[] = [];
    pages: string[] = [];
    currentPage: string = '';

    constructor(
        private _portalService: PortalService,
        private _wcf: WCFService,
        private _ch: ChangeDetectorRef,
        _element: ElementRef) {

        this._wcf.getSelectionValues("UIVirtualPageMaint", "ObjectListInquiry", true).subscribe(p => {
            if (p.Rows) {
                this.pages = p.Rows.map(r => r.Values[0]).concat('').sort();
                this._ch.markForCheck();
            }
        });

        this.reload();
    }

    ngOnChanges(ch) {
        if (ch.activePage) {
            let pn = ch.activePage.currentValue;
            if (this.pages.find(p => p == pn)) {
                this.currentPage = pn;
                this._ch.markForCheck();
            }
        }
    }

    reload() {
        this._portalService.getTraceLog(this.currentPage).subscribe(t => {
            this.logItems = (<TraceLog>t).items;
            this.serverItems = this.getServerTraces().reverse();
            this._ch.markForCheck();
        });

    }

    private getServerTraces(items: TraceItem[] = null): TraceItem[] {

        let servs: any[] = [];
        if (items == null) {
            items = this.logItems;
        }

        items.forEach(i => {
            if (i.DebugResponse)
                servs.push(i);
            else
                servs = this.getServerTraces(i.Children).concat(servs);
        });

        return servs;
    }
}
