import { Directive, Input, HostListener, } from "@angular/core";
import { MatDialog, } from "@angular/material/dialog";
import { InputDialogComponent, InputDialogContext } from "./dialog/input.dialog.component";
import { LabelService } from "../common/services/label.service";
import { PageGroupingService } from "../common/services/pageGrouping.service";
import { AlertService } from "../alert/alert.service";

@Directive({
    selector: '[context-add-group]',
})

export class InputDialogAddGroupDirective {
    @Input('context-add-group') info: string;
    @Input('key') key: string;

    @HostListener('click', ['$event']) onMouseClick(e: MouseEvent) {

        if (!["edit","add","addtonewgrp"].includes(this.key))
            return;

         if(this._pageGroupingService.isSystemGroup(this.info)){
            this._alertService.error("Unable to edit system folder");
            return;
         }
        const okLabel = this._labelService.getLabel('PS_ACTION_OK', 'Ok');
        const cancelLabel = this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel');
        const copyTitleLabel = "Enter a group name:";
        const copyInputLabel = "";
        const inputContext: InputDialogContext = {
            title: copyTitleLabel,
            message: '',
            actions: [
                { action: 'cancel', name: cancelLabel, isPrimary: false },
                { action: 'ok', name: okLabel, isPrimary: true },],
            inputs: [{ name: 'txt', type: 'text', text: '', value: copyInputLabel }]
        };

        const dockPanel = document.querySelector(".dock-panel");
        const posDockPanel = dockPanel.getBoundingClientRect();
        let dialog = this._dialog.open(InputDialogComponent, {
            disableClose: false,
            id: 'inputDialogAddGroupContainer',
            width: `${posDockPanel.width}px`,
            backdropClass: 'inputDialogAddGroup',
        });

        dialog.updatePosition({
            top: `${posDockPanel.top - 65}px`,
            right: `${posDockPanel.right}px`,
            left: `${posDockPanel.left}px`,
            bottom: `${posDockPanel.bottom}px`
        });

        dialog.componentInstance.initContext(inputContext);
        dialog.afterClosed().subscribe(p => {
            if (p != "cancel") {
                const name = p[0]['value'];
                if (this.key == "add") {          
                    this._pageGroupingService.addGroup(name);
                }
                if (this.key == "edit") {   
                    this._pageGroupingService.editGroup(name, this.info);       
                }
                if(this.key=="addtonewgrp"){
                    this._pageGroupingService.moveToGroup(this.info, name);
                }
            }
        });
    }
    constructor(
        public _dialog: MatDialog,
        private _labelService: LabelService,
        private _pageGroupingService: PageGroupingService,
        private _alertService: AlertService
    ) { }
}
