import { Component } from '@angular/core';
import * as _ from 'lodash';

import { MatDialogRef } from '@angular/material/dialog';
import { LabelService } from '../../../common/services/label.service';

import { StudioSubjects, BatchImportExportSettings } from '../../../common/objects/common';
import { MetadataService } from '../../../common/services/metadata.service';

type WS = { code: string, checked: boolean, name: string };

@Component({
    selector: 'batch-import-wizard',
    styles: [`
        :host{
            min-width: 600px;
            min-height: 300px;
            height: 100%;
            width: 100%;

            display: flex;
            flex-direction: column;
        }

        form {
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
        }

        .section-types-to-import{
            display: flex;
            flex-direction: column;
            padding: 10px;
        }

        .section-title{
            font-weight: bold;
        }

        .import-persArea {
            border: 1px solid #aaa;
            padding: 5px;
            max-height: 115px;
            overflow-y: auto;

            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
        }

        .import-persArea > label input[type=checkbox] {
            margin: 4px;
            cursor: pointer;
        }
        
        .import-persArea label{
            display: flex;
            align-items: center;
        }

        .section-import-from-file {
            display: flex;
            flex-direction: column;
            padding: 10px;
        }
            
        .section-import-from-file .file-input-area {
            display: flex;
        }

        .section-import-from-file .file-input-area input[type="file"] {
            position: absolute;
            left: -9999px;
            display: none;
            visibility: hidden;
        }

        .section-import-from-file .file-input-area input[type="text"] {
            flex: 1 0 30px;
        }

        .section-import-from-file .file-input-area  button {
            flex: auto 0 0px;
        }

        .section-validation-message{
            display: flex;
            flex-direction: column;
            padding: 10px;            
        }

        .override-existing {
            padding-top: 10px;
        }

        .override-existing input[type=checkbox] {
            margin: 4px;
            cursor: pointer;
        }
        .override-existing label{
            display: flex;
            align-items: center;
        }

    `],
    templateUrl: 'batchImport.component.html'
})
export class BatchImportComponent {
    public subjects: StudioSubjects;
    public wsCodes: WS[];
    validationMessage: string;
    selectedFileName: string = "";
    private selectedFile: any;
    private overrideIfExists: boolean = true;

    get dialog(): MatDialogRef<BatchImportComponent> {
        return this._dialog;
    }

    constructor(
        private _labelService: LabelService,
        private _metadataService: MetadataService,
        private _dialog: MatDialogRef<BatchImportComponent>) {

        this.wsCodes = this._metadataService.workspaces.map(w => <WS>{ code: w.Code, checked: false, name: w.Name });
        }

    save(): void {
        if (this.validate())
            this._dialog.close(this.getSettings());
    }

    cancel(): void {
        this._dialog.close(null);
    }

    inputFileTextKeyDown() {
        return false; // ignore any keys on input from file textbox since it is readonly.
    }

    inputFileChanged(e) {
        let fileName = "";
        let fileElement = e.currentTarget;
        if (fileElement) {
            if (e.currentTarget.files && e.currentTarget.files.length > 0) {
                let file = e.currentTarget.files[0];
                if (file.type == "text/xml" || file.type == "application/x-zip-compressed") {
                    fileName = file.name;
                    this.selectedFile = file;
                }
            }
        }
        this.selectedFileName = fileName;
    }

    inputFileBtnClick(e) {
        let fileElement = e.currentTarget.parentElement.querySelector("input[type=file]");
        if (fileElement) {
            fileElement.focus();
            fileElement.click();
        }
    }

    overrideIfExistsClick(e) {
        this.overrideIfExists = e.currentTarget.checked;
    }

    validate(): boolean {
        let res = false;
        if (_.some(this.wsCodes, w => w.checked)) {
            if (this.selectedFileName)
                res = true;
            else
                this.validationMessage = this._labelService.getLabel("PS_BatchImport_Validation1", "Import from File was not selected.");
        }
        else
            this.validationMessage = this._labelService.getLabel("PS_BatchImport_Validation2", "Select at least one workspace to import.");
        return res;
    }

    getSettings(): BatchImportExportSettings {
        let settings = new BatchImportExportSettings();
        settings.OverrideIfExists = this.overrideIfExists;
        settings.SelectedWorkspaces = this.wsCodes.filter(ws => ws.checked).map(ws1=> ws1.code);
        settings.PostedFile = this.selectedFile;
        return settings;
    }
}