import { NgModule, Optional, SkipSelf } from '@angular/core';

//app modules
import { AlertModule } from '../alert';
import { LayoutModule } from '../layout';
import { MenuModule } from '../menu';
import { PanelModule } from '../panel';
import { PropertyConditionModule } from '../panel/properties/propertyConditions';
import { SharedModule } from '../shared';
import { WizardModule } from './wizard';

//app components
import { StudioComponent } from './studio.component';

//app services
import { PortalSettingsService } from '../common/services/portalSettings.service';
import { MetadataService } from '../common/services/metadata.service';

@NgModule({
    imports: [
        AlertModule,
        LayoutModule,
        MenuModule,
        PanelModule,
        PropertyConditionModule,
        SharedModule,
        WizardModule,
    ],
    declarations: [
        StudioComponent,
    ],
    providers: [
        MetadataService,
        PortalSettingsService
    ]
})
export class StudioModule {
    constructor( @Optional() @SkipSelf() parentModule: StudioModule) {
        if (parentModule) {
            throw new Error(
                'StudioModule is already loaded. Import it in the AppModule only');
        }
    }
}

