import { Component, OnInit, Input, Output, ViewChild, ViewContainerRef, EventEmitter, ComponentFactoryResolver, Type } from '@angular/core';
import { StudioSubjects, UpdatedInfo } from '../common/objects/common';
import { PanelSettings, PanelAction, PanelUpdatedData, IContentPanel, ReadOnlyLevel } from './panel';
import { DragService } from '../shared/drag.service';
import { PropertyListComponent } from './properties/propertyList';
import { ElementPanelComponent } from './panels/elementPanel.component';
import { ControlPanelComponent } from './panels/controlPanel.component';
import { PortalObjectPanelComponent } from './panels/portalObjectPanel.component';
import { FieldDirectoryComponent } from './panels/fieldDirectory.component';

@Component({
    selector: 'csi-panel',
    styleUrls: ['./panel.component.less'],
    templateUrl: './panel.component.html'
})
export class PanelComponent implements OnInit {
    @Input() component: string;
    @Input() dataSource: string;
    @Input() subjects: StudioSubjects;
    @Input() readOnly: ReadOnlyLevel;
    @Input() parameters: any;

    @Output() exec: EventEmitter<UpdatedInfo> = new EventEmitter<UpdatedInfo>();

    @ViewChild('panelHolder', { read: ViewContainerRef, static: true }) panelHolder: ViewContainerRef;

    settings: PanelSettings;
    get typing() : boolean {return this._dragService.startInputName;}

    constructor(
        private _compFactoryResolver : ComponentFactoryResolver,
        private _dragService: DragService) {
    }

    ngOnInit() {

        this.settings = new PanelSettings(this.component);
        this.settings.dataSource = this.dataSource;
        this.settings.readOnly = this.readOnly;
        this.settings.parameters = this.parameters;
        this.subjects.refreshElementPanel.subscribe((t: string) => {
            if (t === this.dataSource)       
            this.refreshPanel();
       });

        const comp = this.resolveComponent(this.component);
        if( comp ){
            this.loadComponent(comp);
        }
        else{
            console.error("The component " + this.component + " was not resolved");
        }
    }

    private loadComponent(comp: Type<IContentPanel>){
        const componentFactory = this._compFactoryResolver.resolveComponentFactory(comp);
        const viewContainerRef = this.panelHolder;
        viewContainerRef.clear();

        const componentRef = viewContainerRef.createComponent(componentFactory);
        const panelInstance = componentRef.instance as any;
        panelInstance.init(this.settings, this.subjects);
        if (panelInstance.destroy)
            componentRef.onDestroy(() => panelInstance.destroy(this.subjects));
        let upd = new PanelUpdatedData();
        upd.contentCreated = panelInstance;
        this.subjects.panel.next(upd);
    }

    private resolveComponent(selector: string) : Type<IContentPanel>{
        const comps = {
            "csi-property-list" : PropertyListComponent,
            "csi-element-panel" : ElementPanelComponent,
            "csi-control-panel" : ControlPanelComponent,
            "csi-portal-object-panel" : PortalObjectPanelComponent,
            "csi-field-directory" : FieldDirectoryComponent
        };
        return comps[selector];
    }

    refreshPanel() {
        this.settings.refreshContent.next();
    }

    searchChanged(txt: string) {
        this.settings.searchInput.next(txt);
    }

    actionClick(act: PanelAction) {
        let upd = new PanelUpdatedData();
        upd.createNew = act.name.toLowerCase() == "new";
        upd.action = act;
        this.subjects.panel.next(upd);
        if(!upd.createNew)
            this.settings.viewMode = act.modeClass;
    }

    changeName(ev: Event, inpControl: any) {
        if(ev.type == "focusout"){
            if(this.settings.nameInput.val !== inpControl.value ){
                this.settings.nameInput.val = inpControl.value;
                this.settings.nameInput.onValChange.next(inpControl.value);
            }
            this._dragService.startInputName = false;
        }
    }

    startInput(){
        this._dragService.startInputName = true;
    }
}
