import { NgModule, Optional, SkipSelf }       from '@angular/core';
import { SharedModule } from '../shared/shared.module';

//app components
import { ActionMenuComponent } from './action-menu.component';
import { TitleMenuComponent } from './title-menu.component';

@NgModule({
    imports: [
        SharedModule
    ],
    declarations: [
        ActionMenuComponent,
        TitleMenuComponent,
    ],
    exports: [
        ActionMenuComponent,
        TitleMenuComponent,
    ],
})
export class MenuModule {
    constructor( @Optional() @SkipSelf() parentModule: MenuModule) {
        if (parentModule) {
            throw new Error(
                'MenuModule is already loaded. Import it in the StudioModule only');
        }
    }
}
