import { Component, Input, AfterViewInit, ElementRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { TraceItem, TraceItemAttribute, DebugResponse } from './traceLog';
import { ServiceTraceViewComponent } from './serviceTraceView.component';
import { AlertService } from '../../alert/alert.service';
import { ILabel } from '../../common/objects/common';

@Component({
    selector: 'csi-trace-item',
    template: `
    <div class="trace-item">
        <div class="expand-mark" (click)="closed=!closed" *ngIf="!isService">
            <i *ngIf="trace.Children.length>0" [class]="closed ? 'fa fa-caret-right': 'fa fa-caret-down'" aria-hidden="true"></i>
        </div>
        <div class="content">
            <div class="head">
                <div class="caption">
                    <div class="name">{{trace.Name}}</div>
                    <div class="total-elapsed">{{trace.PageElapsedTime || 'Elapsed ' + pageElapsed}}
                        <button *ngIf="isService" (click)="traceButtonClick()">{{'Service Trace' | label: 'PS_SERVICE_TRACE'}}</button>
                        <button *ngIf="trace.PageElapsedTime" (click)="download()">{{ 'Save' | label: 'PS_SAVE' }}</button>
                        <a class="download-btn" *ngIf="trace.PageElapsedTime" [attr.download]="'trace.log'"></a>
                    </div>
                </div>
                <div class="parameters" *ngIf="trace?.Parameters?.length>0">
                    <div *ngFor="let p of trace.Parameters">
                        {{p.Name}} : {{p.Value}}
                    </div>
                </div>
            </div>
            <div class="children" *ngIf="!closed">
                <csi-trace-item [trace]="tc" [isService]="isService" *ngFor="let tc of trace?.Children"></csi-trace-item>
            </div>
        </div>
    </div>`,
    styles:[`
    .trace-item {
        display: flex;
        white-space: nowrap;
    }
    .trace-item .expand-mark {
        padding: 5px;
        width: 15px;
    }
    .trace-item .content {
        display: flex;
        flex-direction: column;
    }
    .trace-item .head {
        display: flex;
        padding: 1px;
    }
    .trace-item .head .caption {
        align-self: center;
    }

    .trace-item .head .caption .name {
        padding-right: 3px;
        font-weight: bold;
    }
    .total-elapsed {
        color: blueviolet;
    }
    .total-elapsed button {
        color: black;
    }
    .parameters {
        display: flex;
        flex-direction: column;
        border: solid 1px black;
    }
`]

})

export class TraceItemComponent implements AfterViewInit {

    @Input() trace: TraceItem;
    @Input() isService: boolean;

    closed: boolean = true;
    get pageElapsed(): string { return this.trace.Elapsed.substr(2, this.trace.Elapsed.length - 3); } 
    private downloadLinkBtn: HTMLAnchorElement;


    constructor(private _dialog: MatDialog, private _alertService: AlertService, private _element: ElementRef) {
    }

    ngAfterViewInit() {
        this.downloadLinkBtn = this._element.nativeElement.querySelector('.download-btn');
    }

    traceButtonClick() {
        let dialog = this._dialog.open(ServiceTraceViewComponent, { disableClose: true });
        dialog.componentInstance.trace = this.trace;
        dialog.afterClosed().subscribe(res => {
        });
    }


    download() {
        this._alertService.confirmModal(
                <ILabel>{ Name: "PS_SAVETRACELOG", DefaultValue: "Do you want to save Trace.log?" },
                <ILabel>{ Name: "PS_FILEDOWNLOAD", DefaultValue: "File Download" }).subscribe(ok => {
                    if (ok) {
                        let text = this.buildTraceToDownload(this.trace);
                        let blob = new Blob([text], { type: 'text/plain' });
                        this.downloadLinkBtn.href = URL.createObjectURL(blob)
                        this.downloadLinkBtn.click();
                    }
        });
    }

    private buildTraceToDownload(t: TraceItem): string {
        let s = t.Name;
        if (t.Parameters && t.Parameters.length) {
            let parameters = this.buildParameters(t.Parameters);
            s += ('[\n' + parameters + ']\n');
        }
        if( t.Children && t.Children.length) {
            s += '{\n';
            t.Children.forEach(c => s += this.buildTraceToDownload(c));
            s += '}\n';
        }
        if (t.DebugResponse) {
            s += ('{\n' + this.buildServiceTraceToDownload(t.DebugResponse) + '}\n');
        }
        return s;
    }

    private buildServiceTraceToDownload(dr: DebugResponse): string {
        let t = dr.Trace;
        let s = t.Name;
        if (t.Parameters && t.Parameters.length) {
            let parameters = this.buildParameters(t.Parameters);
            s += ('[\n' + parameters + ']\n');
        }

        s += `[\n${dr.Request}\]\n{${dr.Response}\n}\n`;

        return s;
    }

    private buildParameters(prms: TraceItemAttribute[]): string {
        let r = '';
        prms.forEach(p => r += (`${p.Name} : ${p.Value}\n`));
        return r;
    }
}
