import { Component } from '@angular/core';
import { IUIComponentRef, StudioSubjects, GroupDef, ContextMenuItem, ContextMenuItemTypes } from '../../common/objects/common';
import { PanelUpdatedData, PanelAction, IContentPanel, PanelSettings } from '../panel';
import { WCFService } from '../../common/services/wcf.service';
import { LabelService } from '../../common/services/label.service';
import { Observable, Subscription, Subject, of } from 'rxjs';
import { map} from "rxjs/operators";

import { PortalService } from '../../common/services/portal.service';
import { StudioStateService } from '../../common/services/studioStateService';
import { PageGroupingService } from '../../common/services/pageGrouping.service';
import { AlertService } from '../../alert/alert.service';

@Component({
    selector: 'csi-element-panel',
    styleUrls: [`./elementPanel.component.less`],
    templateUrl: './elementPanel.component.html'
})

export class ElementPanelComponent implements IContentPanel {

    public panelSettings: PanelSettings;

    icon: string;
    nameFilter: string;
    draggable = false;
    zone: string;

    elementGroups: Observable<GroupDef[]>;
    items: Observable<IUIComponentRef[]>;
    get isItemSelectable() { return this.checkedSubj !== undefined; }
    AllCheckedElements: { n: string, t: string }[] = [];
    selectedItem: {name: string, type: string};
    designerDropList: string[];
    contextMenuItems: ContextMenuItem[] = [];
    contextMenuEnabled: boolean;

    private subjects: StudioSubjects;
    private refreshSubs: Subscription;
    private searchInputSubs: Subscription;
    private dropAreaSubs: Subscription;
    private dataSourceName: string;
    private svc: string;
    private oldViewMode: string;
    private readonly dataSources = {
        'Page': { svc: 'UIVirtualPageMaint', draggable: true, icon: 'fa-list-alt', zone: 'Pages', defaultView: 'tree' },
        'Webpart': { svc: 'WebPartMaint', icon: 'fa-list-alt', draggable: true, zone: 'Webparts', defaultView: 'tree' },
        'Pageflow': { svc: 'UIPageFlowMaint', icon: "fa-cog", defaultView: 'list' }
    };
    private expandedGroup: string[] = [];
    private isNewAction: boolean = true;
    private checkedSubj: Subject<{ n: string, t: string }[]>;

    constructor(
        private _alertService:AlertService,
        private _wcf: WCFService,
        private _portalService: PortalService,
        private _studioState: StudioStateService,
        private _labelService: LabelService,
        private _pageGroupingService: PageGroupingService) {
    }

    public get viewMode(): string {
        return this.panelSettings.viewMode;
    }

    // IContentPanel
    init(s: PanelSettings, rootSubjects: StudioSubjects) {
        this._studioState.checkedElements = [];
        this.subjects = rootSubjects;
        this.dataSourceName = s.dataSource;
        if (s.parameters) {
            if (s.parameters.checkedSubj !== undefined)
                this.checkedSubj = s.parameters.checkedSubj;

            if (s.parameters.newAction !== undefined)
                this.isNewAction = s.parameters.newAction;
        }

        let ds = this.dataSources[this.dataSourceName];
        if (ds) {
            this.svc = ds.svc;
            this.icon = ds.icon;
            if (ds.draggable === true) {
                this.draggable = true;
                this.zone = ds.zone;
            }
            s.viewMode = ds.defaultView;
        }
        else {
            console.error('data source ' + s.dataSource + ' is not found');
        }

        s.actions = this.initActions();
        s.showSearch = true;
        s.iconSrc = this.icon;

        this.refreshSubs = s.refreshContent.subscribe(p => this.load());
        s.refreshContent.next();

        this.oldViewMode = s.viewMode;
        this.panelSettings = s;

        this.searchInputSubs = this.panelSettings.searchInput.subscribe(searchtext => {
            if (!this.nameFilter && searchtext) {
                // save old view mode 
                this.oldViewMode = this.panelSettings.viewMode;
                // toggle to LIST mode
                this.panelSettings.viewMode = 'list';
            }
            else if (this.nameFilter && !searchtext) {
                // empty search text - toggle to old mode
                this.panelSettings.viewMode = this.oldViewMode;
                this.oldViewMode = '';
            }
            // 
            this.nameFilter = searchtext;
        });

        this.dropAreaSubs = this.subjects.dropAreas.subscribe((d:string[])=>{
            this.designerDropList = d.filter(dp=> dp.startsWith("drop_wp_"));
        });

        this.contextMenuEnabled = this.svc == "UIVirtualPageMaint" && !this.isItemSelectable;
        return this.panelSettings;
    }

    // IContentPanel
    destroy(subjects?: StudioSubjects): void {
        this.refreshSubs.unsubscribe();
        this.searchInputSubs.unsubscribe();
        this.dropAreaSubs.unsubscribe();
    }

    private load(force = true) {
        this.items = this._wcf.getElementRefs(this.svc, this.dataSourceName, force);

        if (this.dataSourceName == "Page") {
            this.elementGroups = this._pageGroupingService.getPageGroups(this.items);
        }
        else if (this.dataSourceName == "Webpart") {
            let grps = this._pageGroupingService.getWebPartGroups();
            let webPartControls = this._portalService.getWebPartsList();
            grps.forEach((gw, i) => {
                if (gw.groupKey == "general")
                    gw.items = webPartControls.pipe(map(wa => wa.filter(w => w.name != "CrystalWebPart")));
                else if (gw.groupKey == "intelligence")
                    gw.items = webPartControls.pipe(map(wa => wa.filter(w => w.name == "CrystalWebPart")));
                else
                    gw.items = this.items;
            });

            this.elementGroups = of(grps);
        }
    }

    private initActions(): PanelAction[] {
        let listModeLabel = this._labelService.getLabel('PS_LIST_MODE', 'List mode');
        let tileModeLabel = this._labelService.getLabel('PS_TILE_MODE', 'Tiles mode');
        let detailModeLabel = this._labelService.getLabel('PS_DETAIL_MODE', 'Details mode');
        let newLabel = this._labelService.getLabel('PS_NEW', 'New');
        let treeModeLabel = this._labelService.getLabel('PS_TREE_MODE', 'Tree mode');

        let res = this.isNewAction ? [<PanelAction>{ name: newLabel, imgClass: 'fa-plus-circle', svc: this.svc }] : [];

        if (this.dataSourceName == "Page" || this.dataSourceName == "Webpart") {
            res.push(
                <PanelAction>{ name: treeModeLabel, imgClass: 'fa-outdent fa-lg', modeClass: 'tree' },
                <PanelAction>{ name: listModeLabel, imgClass: 'fa-list', modeClass: 'list' }
            );
        }
        else {
            res.push(
                <PanelAction>{ name: listModeLabel, imgClass: 'fa-list', modeClass: 'list' },
                <PanelAction>{ name: tileModeLabel, imgClass: 'fa-th', modeClass: 'tiles' },
                <PanelAction>{ name: detailModeLabel, imgClass: 'fa-list-ul', modeClass: 'detail' }
            );
        }
        return res;
    }

    select(el: IUIComponentRef) {
        // Disable select/open for non virtual elements (like custom web parts)
        if(!this.isItemSelectable){
            if (!el.isStatic) {
                let cmd = new PanelUpdatedData();
                cmd.elementRef = el;
                cmd.open = true;
                this.subjects.panel.next(cmd);
            }
        }
    }

    toggleGroup(g: GroupDef) {
        g.isOpen = !g.isOpen;

        if (g.isOpen)
            this.expandedGroup.push(g.groupKey);
        else
            this.expandedGroup.splice(this.expandedGroup.indexOf(g.groupKey), 1);
    }

    checkElement(el: IUIComponentRef): void {
        if (this.isItemSelectable) {
            if(!el.isStatic){
                this._studioState.checkElement(el, this.checkedSubj);
            }
        }
        else{
            // Select item
            this.selectedItem = {name: el.name, type: el.type};
            this._studioState.updatePropertyPanel(el);
        }
    }

    isChecked(name: string, type: string): boolean {
        return this._studioState.isChecked(name, type);
    }

    isSelected(el: IUIComponentRef) {
 
       return this.selectedItem && (el.name == this.selectedItem.name && el.type == this.selectedItem.type);
    }

    checkAll(ev, doCheck: boolean) {
        this._studioState.checkAll(this.AllCheckedElements, this.items, doCheck, this.checkedSubj);
        (ev as Event).preventDefault();
    }

    buildMenu(dataType: string, data: string) {
       this.contextMenuItems = this._pageGroupingService.getContextMenuItems(dataType, data);
    }

    contextMenuSelect(item: ContextMenuItem) {
        switch (item.key) {
            case "remove":
                if(item.nameItem=="All"||item.nameItem=="Undefined"){
                    this._alertService.error("Unable to remove system folder");
                    return;
                }
                this._pageGroupingService.removeGroup(item.nameItem);
                break;
            case "restore":
                this._pageGroupingService.restoreSettings();
                break;
        }
    }
    

    contextMenuToggle(item: ContextMenuItem){
        if (item.type != ContextMenuItemTypes.selected) {
            this._pageGroupingService.toggle(item);
        } 
        else {    
            this.contextMenuSelect(item)
        }
        this.panelSettings.refreshContent.next();
    }
}