import { Component, Input,  OnInit } from "@angular/core";

import { ModalPropertiesContext } from '../properties';
import { ValueProviderService, SelectableItem } from "../../../common/services/dataValue.service";
import { IPropertyMetadata, IPropertyEditor, IOMTypeDescriptor, FieldTypeCode } from "../../../common/objects/viewModel";
import { ValueUpdatedData, val } from "../../../common/objects/common";
import { Subject, from } from "rxjs";
import {toArray} from "rxjs/operators";

@Component({
    selector: "csi-page-action-rule-builder",
    styles: [`
        :host{
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
            min-width: 800px;
        }
        .csi-page-action-rule-builder{
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;            
            padding: 1px;
        }
        .rule-text{
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;        
            padding: 4px;    
        }
        
        .long-text{
            height: 300px;
        }

        .query-item{
            width: 100%;
            cursor: pointer;
            padding: 3px;
            flex: 0 0 auto;
        }
        .query-items-panel{
            padding: 4px;
            display: flex;
            flex-direction: column;
            flex: 0 0 200px;
            border: 1px solid #aaaaaa;
        }
        .query-item-selector{
            display: inline-flex;
            flex: 0 0 auto;
        }

        .query-item-selector select{
            height: 23px;
        }
        .item-rows{
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
            overflow: auto;
        }
        .item-row{
            display: inline-flex;
            flex: 0 0 auto;
        }
        .item-row:nth-child(even){
            background-color: #eeeeee;
        }
        .column-header{            
            display: flex;
            flex: 0 0 20px;       
            background-color: #dcdcdc;
            font-weight: bold;
            border-bottom: 1px solid #aaaaaa;                 
            border-top: 1px solid #aaaaaa;
        }

        .col-operation{            
            flex: 0 0 150px;
            align-self: center;
            text-align: center;
        }
        .col-leftValue-type{
            flex: 1 1 auto;
        }
        .col-leftValue-field{
        flex: 1 1 auto;
        }
        .col-leftValue-instance select{
            width: 200px;
        }
        .col-comparison{            
            flex: 1 1 auto;
            text-align: center;
        }
        .col-rightValue{
            flex: 1 1 auto;
        }
        .col-exp {            
            flex: 1 1 auto;
        }
        .col-exp input{
            width : 100%;
            background-color: transparent;
        }
        .query-items .item-row {
            display: flex;
            flex: 0 0 auto;
            border-bottom: 1px solid #aaaaaa;
            border-left: 1px solid #aaaaaa;
            border-right: 1px solid #aaaaaa;            
        }
        .query-items .item-row .col-name {
            text-align: left;  
            text-overflow: ellipsis;          
        }
        .add-button{
         max-height:28px;
        }
        .selected{
            background-color: #aaaaaa;
        }
    `],
    templateUrl: "pageActionRuleBuilder.component.html"
})
export class PageActionRuleBuilderComponent implements OnInit {  
    @Input() context: ModalPropertiesContext;

    get textValue(): string { return <string>(this.context.value) || ''; }
    set textValue(v: string) { this.context.value = v; }

    expItems: expressionItem[] = [];
    expProp : {[name:string] : IPropertyMetadata};

    propChanged = new Subject<ValueUpdatedData>();
    
    constructor(private _valueService: ValueProviderService) {
        this.propChanged.subscribe(c=> this.onFieldChanged(c));
    }  
      
    ngOnInit(): void {
        this.context.title = "Page Action Rule Builder";
        this.init();
    
        if(this.context.instance){
            // load initial parameters
            this.textValue = this.context.instance.expression;
        }
        this.onFieldChanged(new ValueUpdatedData("leftValueType", this.leftValueTypes.Container ) )
        this.changeText(this.textValue);
    }

    changeText(v) {
        this.textValue = v;
        this.expItems = this.parseText();        
    }

    onFieldChanged(upd: ValueUpdatedData){
        let pr = val(this.expProp, upd.propertyName) as IPropertyMetadata;
        pr.value = upd.val;

        let pLc = upd.propertyName.toLowerCase();        

        if(pLc  == "leftValueType".toLowerCase()){
            let lv = this.expProp.leftValueField;        
            lv.editor.parameters = [upd.val];
            lv.editor.selectableItems = this._valueService.fieldsDirectory(lv, null);
            lv.value = "";
        }
        else if(pLc == "leftValueField".toLowerCase() ){
            let md = (<IOMTypeDescriptor>upd.val).Metadata;
            let rv = this.expProp.rightValue;
            if(md.IsList || md.HasSelVal || upd.val.TypeName == "NamedObjectRef" || upd.val.TypeName == "RevisionedObjectRef"){
                rv.editor.directive = "selector";
                rv.editor.parameters = [md.CDOTypeName];
                rv.editor.selectableItems = this._valueService.ndoInstances(rv);
                rv.value = null;                
            }
            else{
                rv.editor.directive = "text";
                if( md.FieldTypeCode == FieldTypeCode.TimeStamp)
                    rv.originalTypeName = "DateTime";
                else if (md.FieldTypeCode == FieldTypeCode.Boolean)
                    rv.editor.directive = "switchBox";                
                else
                    rv.originalTypeName = "String";
                rv.value = "";                
            }
        }
    }

    private parseText() : expressionItem[] {
        let items : expressionItem[] = [];
        if( this.textValue ){            
            let op = '';
            let it : expressionItem = new expressionItem();

            this.textValue.split(' ').forEach(s=>{
                let p = s.trim();
                if( p.toLowerCase() == "and" || p.toLowerCase() == "or" ){
                    op = p;
                    items.push(it);
                    it = new expressionItem(op);
                }
                else
                    it.text += s;
            });

            // last item
            if( it.text )
                items.push(it);
        }
        return items;
    }

    private getLValue(){
        let v : IOMTypeDescriptor = this.expProp.leftValueField.value;
        if( v.TypeName == "NamedObjectRef" ||  v.TypeName == "RevisionedObjectRef")
            return v.Name + ".Name";
        return v.Name;
    }
    private getRValue(){
        let lf : IOMTypeDescriptor = this.expProp.leftValueField.value;
        let t = lf.Metadata.FieldTypeCode;
        if(t == FieldTypeCode.Integer) {
            return `Constants.${this.expProp.rightValue.value}`;
        }
        else if(t == FieldTypeCode.Boolean) {
            return `Constants.${(this.expProp.rightValue.value==true ? "True" : "False")}`;
        }
        else{
            return `"${this.expProp.rightValue.value}"`;
        }
    }

    addExp() : void {
        // disable adding if boolean is not set
        let lf : IOMTypeDescriptor = this.expProp.leftValueField.value;
        if(lf.Metadata.FieldTypeCode == FieldTypeCode.Boolean && this.expProp.rightValue.value == "")
            return;
            
        let s = '';
        // first exp item should not have an bitwise operator
        if(this.expItems.length > 0)
            s += ` ${this.expProp.bitwiseOp.value} `;
        
        s += `${this.expProp.leftValueType.value}.${this.getLValue()} ${this.expProp.comparisonOp.value} ${this.getRValue()}`;

        this.textValue += s;
        this.expItems = this.parseText();
    }

    private init() {
        this.expProp = {
            bitwiseOp : <IPropertyMetadata> {
                name:'bitwiseOp',  
                value: "And",
                editor: <IPropertyEditor>{directive : "selector", 
                    selectableItems : from(this.operations.map(s=> new SelectableItem(s))).pipe(toArray())}},
            leftValueType : <IPropertyMetadata> {
                name:'leftValueType', required: true,
                editor: <IPropertyEditor>{directive : "selector",  
                    selectableItems: from( Object.keys(this.leftValueTypes).map( k=> new SelectableItem(k, this.leftValueTypes[k]))).pipe(toArray())}},
            leftValueField : <IPropertyMetadata> { 
                name: "leftValueField",
                editor: <IPropertyEditor>{directive: "selector", provider: "fieldsDirectory"}},
            comparisonOp: <IPropertyMetadata> {
                name:'comparisonOp', required: true, 
                    value: "=",
                    editor: <IPropertyEditor>{directive : "selector", 
                    selectableItems : from(this.comparisons.map(s=> new SelectableItem(s))).pipe(toArray())}},
            rightValue: <IPropertyMetadata> { 
                name: "rightValue", value: "",
                editor: <IPropertyEditor>{directive: "text"} }
        };
    }

    private operations = ['And', 'Or'];
    private comparisons= ["=", "<>", "<", ">", "<=", ">="];
    private leftValueTypes = {Container : 'CurrentContainerStatus', Resource:'ResourceStatusDetails', 
                Event:'QualityObjectStatusDetail', ChangePackage:'ChangePackage'};
}

class expressionItem {
    public bitwiseOp: string;
    public leftValueType : string;
    public leftValueField : any;
    public comparisonOp: string;
    public rightValue: any;
    public text: string;

    constructor(bitOp: string = "", txt: string = ""){
        this.bitwiseOp = bitOp;
        this.text = txt;
    }
}
