import { Component, Input, OnInit, OnChanges, Type, Output, EventEmitter } from '@angular/core';

import { ValueUpdatedData, PropertyRef } from '../../common/objects/common';
import { MetadataService } from '../../common/services/metadata.service';
import { IPropertyMetadata } from '../../common/objects/viewModel';
import { RecordCommand, PropertyHistoryData } from '../../common/objects/historyLog';

import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { ModalContainerComponent } from './valueEditors/modalContainer';
import { ModalCollectionComponent } from './valueEditors/modalCollection';
import { ModalObjectComponent } from './valueEditors/modalObject';
import { ModalPropertiesContext } from './properties';
import { LabelSearchComponent } from './valueEditors/labelSearch';
import { EditConditionComponent } from './propertyConditions/editCondition';
import { PropertyConditionsComponent } from './propertyConditions/propertyCondition';
import { InitiatorComponent } from './propertyConditions/initiator';
import { Subject } from 'rxjs';
import {debounceTime} from "rxjs/operators";
import _ from 'lodash';
import { StudioStateService } from '../../common/services/studioStateService';

@Component({
    selector: 'csi-name-value',
    styles:[`
        .csi-name-value {
            display: flex;
            margin-left: 4px;
            margin-right: 4px;
        }
        .csi-name-value.disabled{
            color: #808080e8;
            pointer-events:none;
        }
        .csi-name-value.wide .csi-property-label {             
            text-overflow: clip;
            flex-basis: 10%;
        }

        .csi-name-value.wide .csi-value-editor {
            flex-basis: 90%;
        }

        .csi-property-hasValue {
            flex: 0 0 16px;
            max-width: 16px;
            min-width: 16px;
            color: #AFD7E1;
            margin-top: 4px;
        }
        .csi-label-editor{
            flex: 1 1;
            display: flex;
        }

        .csi-property-label {
            flex-basis: 50%;
            max-width: 300px;
            text-overflow: ellipsis;
            overflow-x: hidden;
            padding: 4px;
            user-select: none;
            -ms-user-select: none;
        }

        .csi-property-label.favorite {
            color: #1676a0;
        }

        .csi-value-editor {
            flex-basis: 50%;
            color: #000000;
            margin-left: 4px;
            padding-right: 5px;
            user-select: text;
        }

        .csi-value-editor[readonly] {
            color: #5A5A5A;
            user-select: text;
        }       

        .popup {
            text-align: right;    
        }

        .property-modal-btn {
            width: 25px;
            display: inline-block;
            cursor: pointer;
            border: 1px solid #dcdcdc;
            padding: 1px 1px 1px 2px;
            vertical-align: middle;
            text-align: center;
        }        

        /* Row mode */
        .row-mode .csi-label-editor{
            flex-direction: column;
        }

        .row-mode .csi-property-hasValue{
            display:none;
        }
            
        .row-mode .csi-property-label{
            flex-basis: auto;
            max-width: none;
        }            
        
        .row-mode .csi-property-label[title="Initiators"]{
            color: transparent;
        }

        .row-mode .csi-value-editor{
            flex-basis: auto;
            margin-left: 0;
        }
            
        .row-mode .csi-name-value{
            flex-direction: column;
        }

        .row-mode .property-modal-btn-initiators  {
            display: inline-block;
            cursor: pointer;
            vertical-align: middle;
            width: 100px;
            background-color: gray;
            border: solid black 1px;
            border-radius: 2px;
            color: white;
            padding: 1px 10px 1px 10px;
            white-space:nowrap;
        }        
        .hidden {
            display: none;
        }
    }
    `],
    templateUrl: 'nameValue.html'
})

export class NameValueComponent implements OnInit, OnChanges {
    @Input("property") __property: any; 
    @Input() instance: any;
    @Input() valueUpdated: Subject<ValueUpdatedData>;
    @Input() rowMode: boolean = false;

    @Output() displayDescription: EventEmitter<PropertyRef> = new EventEmitter<PropertyRef>(true); // async
    @Output() setFavorite = new EventEmitter<IPropertyMetadata>(true);

    constructor(private _metadataService: MetadataService, private _dialog: MatDialog, private _studioStateService : StudioStateService) { }

    get property(): IPropertyMetadata { return this.__property; }

    resizeClass: string;
    get editorName(): string { 
        if( this.property && this.property.editor && this.property.editor.directive !== this._oldDirective)
            this.fixEditor();
        return this._editorName;
    }

    get hasValue(): boolean {return !this.isEmpty(this.property.value)};
    get label() : string {return this.property.displayedName || this.property.name};
    get disabled(){
        return this.property.readOnly && this.property.name === "Display";
    };

    propCondInitiators = false;
    counter: number = 0;

    valueChanged = new Subject<ValueUpdatedData>();
    openWizard = new Subject<any>();
    inputResized = new Subject<boolean>();

    modalButtonCaption: string = " . . . ";
    modalButtonClass: string = "property-modal-btn";

    private _editorName: string;
    private _oldDirective : string;

    ngOnInit() {
        this.fixEditor();

        this.valueChanged.pipe(debounceTime(500)).subscribe(v => {
            let oldHistoryValue: any;
            if (this.instance.properties || this.instance.__unbound) {
                oldHistoryValue = {};
                oldHistoryValue[this.property.name] = _.cloneDeep(this.property.value);
            }
            if (oldHistoryValue) {
                let newValue = {};
                newValue[this.property.name] = _.cloneDeep(v.val);
                this.recordPropertyHistory(this.instance, this.property.name, oldHistoryValue, newValue);
            }
            this.valueUpdated.next(v);
        });

        this.inputResized.subscribe(expanded => this.resizeClass = expanded ? 'wide' : '');

        this.openWizard.subscribe(w => this.openModal());
    }

    ngOnChanges(ch) {
        if (_.isArray(this.property.value))
            this.counter = this.property.value.length;
        else
            this.counter = 0;

        this.fixEditor();
    }

    onSelectProperty() {
        let propertyDescr: PropertyRef = {
            name: this.property.name,
            displayedName: this.property.displayedName            
        };
    
        this.displayDescription.emit(propertyDescr);
    }

    private fixEditor() {

        if (!this.property.editor) {
            let type = this.property.originalTypeName ? this.property.originalTypeName.toLowerCase() : null;
            if (type) {
                if (type == 'boolean')
                    this.property.editor = { directive: 'switchBox' };
                else if (type == 'string')
                    this.property.editor = { directive: 'text' };
                else if (type.indexOf('[]') != -1)
                    this.property.editor = { directive: 'collection' };
                else
                    this.property.editor = { directive: 'object' };
            }
            else
                this.property.editor = { directive: 'unknown' };
        }

        if (this.property.editor.directive == "enum" && !this.property.editor.provider) {
            this.property.editor.provider = "enum";
        }

        if (this.property.editor && this.property.editor.directive) {
            this._editorName = this.property.editor.directive;
            if (this._editorName == "boolean")
                this._editorName = 'switchBox';
            else if (this._editorName == "object" ||
                this._editorName == "collection")
                this._editorName = "modal";
            else if (this._editorName == "enum")
                this._editorName = "selector";
        }

        this._oldDirective = this.property.editor.directive;
        
        if(this.rowMode && this.property.editor && this.property.editor.provider == 'propertyConditionInitiators'){
            this.modalButtonCaption = `${this.label}[${this.counter}] . . . `;
            this.modalButtonClass = "property-modal-btn-initiators";
        }
        else{
            this.modalButtonCaption = " . . . ";
            this.modalButtonClass = "property-modal-btn";
        }

    }

    openModal() {
        let context: ModalPropertiesContext = {
            property: this.property,
            metadataService: this._metadataService,
            value: _.cloneDeep(this.property.value),
            instance: this.instance,
            instanceType: this.instance.typeName || this.instance.__type
        };

        let comp: Type<any>;
        let dialogOptions: MatDialogConfig = { disableClose: true };
        switch (this.property.editor.directive) {
            case "collection":
                if (this.rowMode && this.property.editor.provider === 'propertyConditionInitiators') {
                    comp = InitiatorComponent;
                    dialogOptions = { disableClose: true, height: '600px', width: '800px' }
                }
                else {
                    comp = ModalCollectionComponent;
                }
                break;
            case "object":
                comp = ModalObjectComponent;
                break;
            case "modal":
            case "text":
                if(this.property.editor.provider == "targetMatrixRows" )
                    dialogOptions = { disableClose: true, height: '550px', width: '700px' };
                comp = ModalContainerComponent;
                break;
            case "labelLookup":
                comp = LabelSearchComponent;
                break;
            case "expression":
                comp = EditConditionComponent;
                dialogOptions = { disableClose: true, height: '600px', width: '950px' }
                break;
            case "propertyCondition":
                comp = PropertyConditionsComponent;
                dialogOptions = { disableClose: true, height: '600px', width: '800px' }
                break;
            case "propertyConditionInitiators":
                break;
        }

        if (comp) {
            let dial = this._dialog.open(comp,  dialogOptions);
            dial.componentInstance.initContext(context);
            dial.afterClosed().subscribe(result => {
               if (result || this.property.name==="HTMLText")
                    this.valueChanged.next(new ValueUpdatedData(this.property.name, result));
           });
        }
    }

    modalEditor(editor): string {
        if (editor === "modal" || editor === "propertyCondition" || editor === "expression")
            return editor;
        return null;
    }

    private isEmpty(v: any): boolean {
        if (this.property.originalTypeName.toLowerCase() == 'boolean')
            return !(v === true || v === false);
        if (_.isNumber(v))
            return false;
        return _.isEmpty(v);
    }

    private recordPropertyHistory(instance: any, path: string, oldValue: any, newValue: any) {
        let c = new PropertyHistoryData(instance, path, oldValue, newValue, false);
        this._studioStateService.writeHistory(RecordCommand.propertyChange, c);
    }

    toggleFavorite(ctrl: boolean) {
        if (ctrl) {
            this.property.favorite = !this.property.favorite;
            this.setFavorite.emit(this.property);
        }
    }

}