import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';

import { AppModule } from './app/app.module';
import { environment } from './environments/environment';

if (environment.production) {
  enableProdMode();
}

const hr = window.location.href;
const i = hr.lastIndexOf('/index.html');
const iwcf = window.location.search.indexOf("&wcf=");

if( i==-1 || iwcf == -1){
    // developer's default settings -- should be removed
    environment.portalUrl = "https://localhost/CamstarPortal";
    environment.wcfUrl = "https://localhost/CamstarWCFServices";
    environment.portalMode = "Apollo";
}
else{
    environment.portalUrl = hr.substr(0, i - "/PortalStudio".length);
    environment.wcfUrl = unescape(window.location.search.substr(iwcf+5));
    if(window.location.host !== "localhost" &&  environment.wcfUrl.indexOf("//localhost/") != -1){
         environment.wcfUrl = unescape(window.location.search.substr(iwcf+5)).replace("localhost/", window.location.host +"/");
    }
    
    environment.portalMode = window.location.search.substr(12, iwcf - 12);
}

platformBrowserDynamic().bootstrapModule(AppModule);
