    errorMessage: any;

    public matchDependency(updated: UpdatedInfo, property: IPropertyMetadata): boolean {
        let reload = false;
        if (!updated.is(UpdateDataTypes.property)) {
            return;
        }
        let upd = <PropertyUpdatedData>updated.data;
        let path = upd.path.toLowerCase();
        if (path == property.name.toLowerCase()) {
            // ignore itself changes
            reload = false;
        }
        else {
            if (property.dependencies) {
                property.dependencies.forEach(d => {
                    if (d.toLowerCase() == path)
                        reload = true;
                    else {
                        // test consolidated dependencies
                        let cd = <string[]>this.consolidatedDependencies[d]
                        if (cd && cd.indexOf(path) != -1)
                            reload = true;
                    }
                });
            }
        }

        return reload;
    }

    public getSelectableItems(property: IPropertyMetadata, instance: any): Observable<ISelectableItem[]> {
        let providerName = property.editor.provider;
        if( !providerName && property.editor.options){
            // try to parse
            this._metadataService.parseOptions(property);
            providerName = property.editor.provider;
        }

        if( providerName ){
            let provider: (p: IPropertyMetadata, f: (v: ISelectableItem[]) => void, instance?: any) => void = this[providerName];
            if (_.isFunction(provider)) {
                let pr = provider.call(this, property, instance);
                if(pr instanceof Promise)
                    return from(pr);
                else
                    return pr;
            }
        }

        // error
        console.error("No provider for " + property.name);
        let empty : ISelectableItem[] = [];
        return from([empty]);
    }

    public camstarServices(property: IPropertyMetadata): Observable<ISelectableItem[]> {
        return this.portalService.getCamstarServices().pipe(
            map(services => {
                var selectableServices: ISelectableItem[] = [];
                this.addEmptyItem(property, selectableServices);
                if (services) {
                    services.forEach(srv => {
                        selectableServices.push( new SelectableItem(srv.ClassName, srv.ClassName, srv.ClassName));
                    });
                }
                this.cache["camstarServices"] = selectableServices;
                return selectableServices;
            }));
    }

    public enum(property: IPropertyMetadata): Promise<ISelectableItem[]> {
        let enums = property.enumValues;
        let items: ISelectableItem[] = [];

        this.addEmptyItem(property, items);

        if (!enums) {
            // load from metadata type 
            let mdType = this._metadataService.getMetadataType(property.originalTypeName);
            if (mdType) {
                enums = mdType.enumValues;
            }
        }
        if (enums) {
            enums.forEach(e =>
                items.push(new SelectableItem(e.name, e.value, null))
            );
        }
        return Promise.resolve<ISelectableItem[]>(items);
    }

    public elements(property: IPropertyMetadata): Promise<ISelectableItem[]>{
        let filter: (arg0: ISelectableItem) => ISelectableItem;
        let result: ISelectableItem[] = [];

        let root = this._studioStateService.getRoot();
        let allElements: ISelectableItem[] = [];
        if (root.type == 'Webpart') {
            let addingItem = new SelectableItem(root.name, root.name, root.typeName, 0);
            addingItem.type = root.type;
            addingItem.fullType = root['_fullTypeName'];

            let childItems = this.getAllElements(root, 1, root.name);
            addingItem.childCount = childItems.length;
            allElements.push(addingItem);
            allElements = allElements.concat(childItems);
        }
        else {
            allElements = this.getAllElements(root);
        }

        this.addEmptyItem(property, result);

        if (property.editor.filter)
            filter = Filters[property.editor.filter];

        allElements.forEach(el => {
            let fEl = _.isFunction(filter) ? <ISelectableItem>filter(el) : el;
            if (fEl)
                result.push(fEl);
        });

        // Add dependency if it is not set
        if (!property.dependencies)
            property.dependencies = ["__elements__"];
        return Promise.resolve<ISelectableItem[]>(result);
    }

    private getAllElements(element: IElement, level: number = 0, parentPath: string=''): ISelectableItem[] {
        let ret: ISelectableItem[] = [] ;
        if (element) {
            _.sortBy(element.items, (i:any) => i.name)
                .forEach(item => {
                    let path = parentPath ? parentPath + '.' + item.name : item.name;

                    let addingItem = new SelectableItem(item.name, path, item.typeName, level);
                    addingItem.type = item.type;
                    addingItem.fullType = item._fullTypeName;

                    let childItems = this.getAllElements(item, level + 1, path);
                    addingItem.childCount = childItems.length;
                    ret.push(addingItem);
                    ret = ret.concat(childItems);
                });
            if (element.typeName === 'JQDataGrid') {
                if (element.properties) {
                    let settings = element.properties.settings;
                    if (settings && settings.columns) {
                        let addInlineControls = settings.__type === "GridDataSettingsItemList" && settings.editorSettings && 
                            settings.editorSettings.editingMode === 0; // Inline.
                        _.forEach(settings.columns, col => {
                            let value = `${parentPath}.${element.name}.${col.name}`;
                            ret.push(new SelectableItem(col.name, value, "Control", level));
                            if (addInlineControls)
                                ret.push(new SelectableItem(`${col.name}<Inline>`, `${value}<Inline>`, "Control", level));
                        });
                    }
                }                
            }

            if (element.properties && element.properties.actions) {
                // Add actions
                ret.push(new SelectableItem('Actions', 'Actions', 'Actions', level, true));

                element.properties.actions.forEach(a => {
                    let it = new SelectableItem(a.name, 'Actions.' + a.name, a.__type, level + 1);
                    it.type = 'actionButton';
                    it.fullType = a._fullTypeName;
                    ret.push(it);
                });
            }
        }
        return ret;
    }

