    public pageLoadedContracts(pageName: string) : Observable<ISelectableItem[]>{
        return this.portalService.getElement(pageName, "page").pipe( map(e=>{
            let dc = val(e.properties, "dataContract");      
            if( dc ){
                let dataMembers = val(dc, "dataMembers") as any[];
                if( dataMembers ){
                    let res : SelectableItem[] = [new SelectableItem('', '', '')];
                    dataMembers.forEach(d=> res.push(new SelectableItem(val(d, "name"))));
                    return res;
                }
            }
            return [];
        }));
    }

