    public imageFiles(property: IPropertyMetadata, instance: any): Observable<ISelectableItem[]> {
        let obs : Observable<ISelectableItem[]>;
        if( Object.keys(this.cache).find(k=> k == "imageFiles")){
            // load from the cache
            obs = of(this.cache["imageFiles"]);
        }
        else{
            obs = this.portalService.getImages().pipe(map(
                images => {
                    let res = _.concat(this.addEmptyItem(property),
                        _.map<ImageFile, ISelectableItem>(images, im => <ISelectableItem>{ name: im.Name, value: im.Name, type: 'image', img: environment.portalUrl + '/' + im.Source, level: 0 }));
                    this.cache["imageFiles"] = res;
                    return res;
                }));
            }
        return obs;
    }

    public elementProperties(property: IPropertyMetadata, instance: any): Observable<ISelectableItem[]> {

        let ctrl: IElement;
        let typeName: string;
        if (instance && property.dependencies && property.dependencies.length > 0) {
            let c = null;
            if( property.dependencies.find(d=> {
                    let pv = val(instance, d);
                    if(_.isString(pv))
                        c =  this._studioStateService.findElement(pv);
                    return c!==undefined && c!==null; 
                }) !== undefined
            ){
                ctrl = c;
            }
        }
        else {
            // for current active element
            ctrl = this._studioStateService.activeElement;
        }

        if (ctrl) {
            typeName = ctrl['_fullTypeName'] || ctrl['codeBehind'];
            if (!typeName) {
                if (ctrl.typeName != 'JQDataGrid')
                    typeName = "Camstar.WebPortal.FormsFramework.WebControls." + ctrl.typeName;
                else
                    typeName = "Camstar.WebPortal.FormsFramework.WebGridControls." + ctrl.typeName;
            }
            let typeDescriptorSection = !_.isEmpty(property.editor.parameters) ? property.editor.parameters[0] : 'Properties';
            let typeDescriptor = this._metadataService.typeDescriptors[typeName];
            if (typeDescriptor) {
                let res = this.buildListOfTypeDescriptor(property, typeName, typeDescriptorSection);
                return of(res);
            }

            return this.portalService.getTypeDescriptor(typeName)
                .pipe( map(
                    result => {
                    if (result) {
                        // save descriptor into the cache
                        this._metadataService.typeDescriptors[typeName] = result;
                        return this.buildListOfTypeDescriptor(property, typeName, typeDescriptorSection);
                    }
                }));

        }
        else {
            return of([]);
        }
    }

    public handlers(property: IPropertyMetadata, instance: any): Observable<ISelectableItem[]> {
        
        let typeName: string;
        let handlerSource = val(instance, "handlerSource");

        switch(handlerSource){
            case 0: /* self */
                typeName  = this._studioStateService.activeElement['_fullTypeName'];
                if( !typeName )
                    typeName  = this._studioStateService.activeElement.codeBehind;
                break;
            case 1: /* control */{
                    let ctrlName = val(instance, "control");
                    if( ctrlName ){
                        let ctrl =  this._studioStateService.findElement(ctrlName);
                        if( ctrl )
                            typeName = ctrl['_fullTypeName'];
                    }
                }
                break;
            case 2: /* web part */
                {
                    let wpOwner  = this._studioStateService.findElementOwner(this._studioStateService.activeRootElement, this._studioStateService.activeElement);
                    if(wpOwner)
                        typeName = wpOwner['codeBehind'] || wpOwner['_fullTypeName'];
                }
                break;
            case 3: /* page */
                typeName = "Camstar.WebPortal.PortalFramework.WebPartPageBase";
                break;
            case 4: /* service */
                typeName = "Camstar.WebPortal.FormsFramework.WCFService";
                break;
            case 5: /* static method */
                break;                
        }

        if(!typeName)
            return of( [new SelectableItem("","","")]);

        let typeDesciptor = this._metadataService.typeDescriptors[typeName];
        const section = "Handlers";
        if (typeDesciptor) {
            let res = this.buildListOfTypeDescriptor(property, typeName, section);
            return of(res);
        }
        else{
            return this.portalService.getTypeDescriptor(typeName)
                .pipe( map(result => {
                    if (result) {
                        // save descriptor into the cache
                        this._metadataService.typeDescriptors[typeName] = result;
                        return this.buildListOfTypeDescriptor(property, typeName, section);
                    }
                }));        
        }
    }
    
    public clientHandlers(property: IPropertyMetadata, instance: any): Observable<ISelectableItem[]> {
        
        let typeName: string;
        let handlerSource = val(instance, "handlerSource");

        switch(handlerSource){
            case 0: /* self */
                typeName  = this._studioStateService.activeElement['_fullTypeName'];
                break;
            case 1: /* control */{
                    let ctrlName = val(instance, "control");
                    if( ctrlName ){
                        let ctrl =  this._studioStateService.findElement(ctrlName);
                        if( ctrl )
                            typeName = ctrl['_fullTypeName'];
                    }
                }
                break;
            case 2: /* static method */
                break;                
        }
    
        if(!typeName)
            return of([]);            

        let typeDesciptor = this._metadataService.typeDescriptors[typeName];
        const section = "ClientMethods" ;
        if (typeDesciptor) {
            let res = this.buildListOfTypeDescriptor(property, typeName, section);
            return of(res);
        }
        else{
            return this.portalService.getTypeDescriptor(typeName)
                .pipe( map(result => {
                    if (result) {
                        // save descriptor into the cache
                        this._metadataService.typeDescriptors[typeName] = result;
                        return this.buildListOfTypeDescriptor(property, typeName, section);
                    }
                }));        
        }
    }

