import { Component, Input, OnInit, ElementRef, ViewChild } from '@angular/core';
import { ValueUpdatedData } from '../../../common/objects/common';
import { Subject } from 'rxjs';
import { IPropertyMetadata } from '../../../common/objects/viewModel';

import _ from 'lodash';

@Component({
    selector: 'csi-editor-text',
    template: 
        `<div class="text">
            <div *ngIf="!!resized" class="resize" [ngClass]="expanded?'wide':''" (click)="toggleInputWidth()">
                <i class="grow fa fa-step-backward" aria-hidden="true"></i>
                <i class="shrink fa fa-step-forward" aria-hidden="true"></i>
            </div>
            <input *ngIf="inputType!='area'" class="csi-property-text" [attr.type]="inputType" [class.modal-wiz]="modalWizard"
                    #textInputCtl
                    (focusout)="toggleInputWidth(false)" [readonly]="readOnly" [value]="value" 
                    (keyup)="changeText()" (paste)="changeText()" (cut)="changeText()" (input)="changeText()" />

            <textarea *ngIf="inputType=='area'" class="csi-property-text csi-property-textarea" 
                    #textInputCtl
                    [readonly]="readOnly" [value]="value" 
                    (keyup)="changeText()" (paste)="changeText()" (cut)="changeText()" (input)="changeText()">
            </textarea>

            <div *ngIf="modalWizard" class="modal-wiz">
                <span class="modal-wiz" (click)="openModal()"> . . . </span>
            </div>
        </div>`,
    styles:[`
    .csi-property-textarea {
        max-width: 100%;
        width: 100%;
        height: 223px;
        border: solid 1px #aaaaaa;
    }


    .csi-property-text {
        max-width: 100%;
        width: 100%;
        height: 27px;
        border: solid 1px #aaaaaa;
        padding-left: 7px;
    } 

    .text {
        margin-left: -10px;
        display: flex;
    }
    .text input.modal-wiz {
        border-right: none;
    }
    
    .text input[type="date"] {            
        width: 127px;   // for some unknown reason date input is wider then regular text                
    }
    
    .text div.modal-wiz {
        border-bottom: solid 1px #aaaaaa;
        border-right: solid 1px #aaaaaa;
        border-top: solid 1px #aaaaaa;
    }
    
    .text span.modal-wiz {
        width: 25px;
        display: inline-block;
        cursor: pointer;
        padding: 1px 1px 1px 2px;
        vertical-align: bottom;
    }
    
    .text.date-value{
        margin-left: 0px;
    }
    
    .resize {
        width: 10px;
        padding: 0;
        min-width: 10px;
        max-width: 10px;
        align-self: center;
        color: darkgrey;
    }
   .grow {
        display: block;
        color: white;
    }

    .shrink {
        display: none;
    }

    .shrink:hover .grow {
        color: darkgrey;
    }

    .wide .grow {
        display: none;
    }

    .wide .shrink {
        display: block;
    }
`]
})

export class TextEditorComponent implements OnInit {
    @Input() value: string;
    @Input("property") __property: any; 
    @Input() valueChanged: Subject<ValueUpdatedData>;
    @Input() wizardDialog: Subject<any>;
    @Input() resized: Subject<boolean>;

    @ViewChild("textInputCtl") textInputCtl : ElementRef;

    get property(): IPropertyMetadata { return this.__property; }

    expanded: boolean = false;
    get modalWizard(): boolean {
        if(this.property && this.property.editor ){
            if(this.property.editor.provider)
                return true;
            if( this.property.editor.parameters && this.property.editor.parameters.length && this.property.editor.parameters[0] === "longText")
                return true;            
        }
        return false;
    }
    inputType: string = 'text';
    readOnly: boolean;
    typename: string;

    constructor() {         
    }

    ngOnInit() {

        this.typename = this.property.originalTypeName;
        this.readOnly = this.property.readOnly;

        // Convert to localized
        if (this.typename == "DateTime") {
            if (this.value){            
                this.value = new Date(Date.parse(this.value)).toLocaleString();
            }
            this.inputType = 'datetime'
        }
        else if (this.typename == "Int32" || this.typename == "int" || this.typename == "px") {
            this.inputType = 'number';
            if (this.value === undefined)
                this.value = '';
        }

        if (this.property.editor.options && this.property.editor.options.find(o=> o == 'area') ) {
            this.inputType = 'area';
            if (this.value === undefined)
                this.value = '';
        }

    }

    valChanged(s: string) {

        let convertedValue : any = s;
        this.value = s;
        // Convert string back to value
        if (this.typename == "DateTime") {
            // DateTime cannot be required because there is no case where user inputs date-time
            if (s) {
                this.value = new Date(s).toISOString();
                convertedValue = new Date(s);
            }
        }
        else if (this.typename == "Int32" || this.typename == "int" || this.typename == "px") {
            let n = parseFloat(s);
            if (!_.isNaN(n)) {
                this.value = n.toString();
                convertedValue = n;
            }
            else {
                this.value = '';
                convertedValue = null;
            }
        }

        this.valueChanged.next(new ValueUpdatedData(this.property.name, convertedValue, this.typename));
    }

    toggleInputWidth(exp?: boolean, s?: string) {

        if (exp === undefined)
            this.expanded = !this.expanded;
        else
            this.expanded = exp;

        // Set focus to the text input . It's required to correctly fire blur event
        if (this.expanded) {
            this.setFocus();
        }

        if( this.resized )
            this.resized.next(this.expanded);
    }

    changeText() {
        if (this.readOnly)
            return;

        if( this.textInputCtl ){
            this.valChanged(this.textInputCtl.nativeElement.value);
        }
    }

    openModal() {
        if(!this.readOnly)
            this.wizardDialog.next(this.modalWizard);
    }

    private setFocus(){
        if( this.textInputCtl ){
            this.textInputCtl.nativeElement.focus();
        }
    }
}

