import { Component, Input, Output, EventEmitter } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

import { StudioSubjects } from '../../../common/objects/common';
import { InitiatorDefinition } from '../../../common/objects/viewModel';
import { ModalPropertiesContext } from '../properties';

@Component({
    selector: 'csi-prop-cond-initiator',
    templateUrl: 'initiator.html',
    styles:[`
    :host {
        height: 100%;        
    }

    .csi-initiator {
        flex: 1 0;
        display: flex;
        flex-direction: column;
    }

    .csi-initiator .title {
        background-color: #DCDCDC;
        padding: 15px;
    }
      
    .csi-initiator .initiator-list {
        border-top: 1px solid black;
        overflow-x: hidden;
        overflow-y: auto;
        flex-grow: 1;
    }
    .csi-initiator .initiator-list .initiator-item {
        display: flex;
        align-items: center;
    }

    .csi-initiator .initiator-list .initiator-item > i.delete {
        font-size: 20px;
        padding: 0 10px 0 10px;
        cursor: pointer;
    }
    `]
}) 

export class InitiatorComponent {

    @Input() initiators: InitiatorDefinition[];
    @Output() onValueChanged = new EventEmitter<any>();

    arrayOfSubjects: StudioSubjects[] = [];
    private context: ModalPropertiesContext;

    get dialogRef(): MatDialogRef<InitiatorComponent> {
        return this._dialogRef;            
    }

    constructor(private _dialogRef: MatDialogRef<InitiatorComponent>) {
    }

    initContext(context: ModalPropertiesContext) {
        this.context = context;
        if (this.context.value)
            this.initiators = this.context.value;

        if (this.initiators) {
            this.initiators.forEach(it => this.addSubjects(it));
        }
    }

    addInitiator(): void {
        if (!this.initiators)
            this.initiators = [];

        let initiator = new InitiatorDefinition();
        this.initiators.push(initiator);
        this.addSubjects(initiator);
    }

    deleteInitiator(i: number): void {
        this.arrayOfSubjects[i].property.unsubscribe();
        //this.arrayOfSubjects[i].selectItem.unsubscribe();
        this.arrayOfSubjects.splice(i, 1);

        this.initiators.splice(i, 1);
    }

    private addSubjects(initiator): void {
        let subjects = new StudioSubjects();
        this.arrayOfSubjects.push(subjects);

        subjects.property.subscribe(n => {
            this.selectItem(initiator);
        });

        subjects.panel.subscribe(p => {
            this.selectItem(initiator);
        });
    }

    ok(): void {
        this.context.value = this.initiators;
        this._dialogRef.close(this.initiators);
    }

    cancel(): void {
        this._dialogRef.close();
    }

    private selectItem(item:any){
        console.warn('select ite', item);
    }

}