import { NgModule, Optional, SkipSelf } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatOptionModule } from '@angular/material/core';
import { MatDialogModule } from '@angular/material/dialog';
import { MatInputModule } from '@angular/material/input';
import { MatMenuModule } from '@angular/material/menu';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatRadioModule } from '@angular/material/radio';
import { MatSnackBarModule } from '@angular/material/snack-bar';

//dialog
import { ConfirmDialogComponent } from './dialog/confirm.dialog.component';
import { InputDialogComponent } from './dialog/input.dialog.component';
//directives
import { DialogDecorDirective } from './directives/dialogDecor.directive';
import { DraggableDirective } from './directives/draggable.directive';
import { DroppableDirective } from './directives/droppable.directive';
//pipe
import { LabelPipe } from './pipe/label.pipe';
//services
import { ConvertorService } from '../common/services/convertor.service';
import { DataAccessors } from '../common/services/dataAccessors';
import { DragService } from './drag.service';
import { LabelService } from '../common/services/label.service';
import { MetadataService } from '../common/services/metadata.service';
import { ValueProviderService } from '../common/services/dataValue.service';
import { ClosableDirective } from './directives/closable.directive';
import { PanelNamePipe } from './pipe/panel-name.pipe';
import { PageGroupingService } from '../common/services/pageGrouping.service';
import { ContextMenuPopupDirective } from './context-menu.directive';
import { InputDialogAddGroupDirective } from './inputdialog-addGroup.directive';


const COMPONENTS = [
    ConfirmDialogComponent,
    InputDialogComponent,
    DialogDecorDirective,
    DraggableDirective,
    DroppableDirective,
    ClosableDirective,
    ContextMenuPopupDirective,
    InputDialogAddGroupDirective,
    LabelPipe,
    PanelNamePipe
];
const MATMODULES = [
    MatAutocompleteModule,
    MatDialogModule,
    MatInputModule,
    MatMenuModule,
    MatOptionModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatSnackBarModule
]

@NgModule({
    imports: [        
        BrowserAnimationsModule,
        CommonModule,
        FormsModule,
        ReactiveFormsModule,
        MATMODULES,
    ],
    declarations: [
        COMPONENTS
    ],
    exports: [
        CommonModule,
        FormsModule,
        ReactiveFormsModule,
        COMPONENTS,
        MATMODULES
    ],
    entryComponents: [
        ConfirmDialogComponent,
        InputDialogComponent
    ],
    providers: [
        ConvertorService,
        DataAccessors,
        DragService,
        LabelService,
        MetadataService,
        ValueProviderService,
        PageGroupingService
    ]
})
export class SharedModule {
    constructor( @Optional() @SkipSelf() parentModule: SharedModule) {
        if (parentModule) {
            throw new Error(
                'SharedModule is already loaded.');
        }
    }
}
