import { LayoutActions } from "../../../common/objects/common";

export class LayoutMenuItem {
    public caption : string;
    public label : string;
    public enabled: boolean;

    constructor(public action: LayoutActions, enabled: boolean = false){
        let cp = LayoutMenuItem._captions_and_labels.find(a=> a[0] == action );
        this.caption = cp[1];
        this.label = cp[2];
        this.enabled = enabled;
    }

    private static _captions_and_labels : [LayoutActions,string,string][] = [
        [LayoutActions.merge, "Merge", "PS_ACTION_MERGE"],
        [LayoutActions.split, "Split", "PS_ACTION_SPLIT"],
        [LayoutActions.removeRow, "Remove Row", "PS_ACTION_REMOVE_ROW"],
        [LayoutActions.removeColumn, "Remove Column", "PS_ACTION_REMOVE_COLUMN"],
        [LayoutActions.insertRowBefore, "Insert Row Before", "PS_ACTION_INSERT_ROW_BEFORE"],
        [LayoutActions.insertColumnBefore, "Insert Column Before", "PS_ACTION_INSERT_COLUMN_BEFORE"],
        [LayoutActions.insertRowAfter, "Insert Row After", "PS_ACTION_INSERT_ROW_AFTER"],
        [LayoutActions.insertColumnAfter, "Insert Column After","PS_ACTION_INSERT_COLUMN_AFTER"],
        [LayoutActions.none, "",""],
        [LayoutActions.reset, "Reset","PS_ACTION_RESET_SELECTION"],
        [LayoutActions.toggleCellPos, "Toggle Cell Position","PS_ACTION_TOGGLE_CELLPOS"]
    ];
}
