import { Directive, ElementRef, AfterViewInit, Input, OnDestroy, Renderer2 } from '@angular/core';
import { MatDialogRef, DialogPosition } from '@angular/material/dialog';
import {DragService } from '../drag.service';
import {DragInfo, dragModes} from '../drag';
import { uuid } from '../../common/objects/common';

export class MouseDiff{
    x: number;
    y: number;
}

@Directive({
        selector: '[csi-dialog-decor]'        
})
export class DialogDecorDirective implements AfterViewInit, OnDestroy {

    @Input() dialog: MatDialogRef<any>;
    @Input() dialogHeight?: number;
    @Input() dialogWidth?: number;

    private dragHandle: HTMLElement;
    private resizeHandle: HTMLElement;
    private leftPanel : HTMLDivElement;

    private modalWindow: HTMLElement;
    private resetPosizionOnResizing = true;

    private minSize = {width: undefined, height: undefined};
    private id = uuid.id();

    constructor(private _element: ElementRef, private _rd: Renderer2, private _drag: DragService) {
    }

    ngAfterViewInit(): void {
        this.modalWindow = this._element.nativeElement.parentElement.parentElement;
        // Header is a handle
        this.dragHandle = this._element.nativeElement.children[0];
        this.modalWindow.style.position = 'relative';
        this.dragHandle.classList.add('cursor-draggable');

        const moveId = "move-dialog" + "@" + this.id;
        this._drag.add(dragModes.moveWindow, moveId, 
                (m:DragInfo) => this.moveIt(m),  null, this.modalWindow, null);

        this._element.nativeElement.setAttribute("dragDropId", moveId);
        this._element.nativeElement.classList.add("drag-element");

        this.minSize = {
            width: this.modalWindow.offsetWidth,
            height: this.modalWindow.offsetHeight
        };
        if (this.dialogHeight !== undefined)
            this.minSize.height = this.dialogHeight;
        if (this.dialogWidth !== undefined)
            this.minSize.width = this.dialogWidth;

        this.dialog.updateSize( 
            this.minSize.width + 'px', 
            this.minSize.height + 'px');
        
        this.createResizeHandle();

        const dragId = "resize-dialog" + "@" + this.id;
        this._drag.add(dragModes.resizeWindow, dragId, 
                (m:DragInfo) => this.resizeIt(m),  null, this.modalWindow, null);

        this.resizeHandle.setAttribute("dragDropId", dragId);
        this.resizeHandle.classList.add("drag-element");
    }   

    ngOnDestroy(){
        this.dragHandle.classList.remove("cursor-draggable");
        this._drag.remove(dragModes.moveWindow, this.dragHandle.id);
        this._drag.remove(dragModes.resizeWindow, this.resizeHandle.id);
    }

    private moveIt(d?: DragInfo){
        if(d && d.endOfDrag )
            return;
        let x = d? d.diffx : 0;
        let y = d? d.diffy : 0;
        let newPos : DialogPosition = {            
            top: (this.modalWindow.offsetTop + y)+'px', 
            left: (this.modalWindow.offsetLeft + x)+'px'
        };

        this.dialog.updatePosition(newPos);
    }

    private resizeIt(d: DragInfo){

        if( d.endOfDrag )
            return;

        if(this.resetPosizionOnResizing){
            this.moveIt();
            this.resetPosizionOnResizing = false;
        }

        let newSize = {
            width : this.modalWindow.offsetWidth + d.diffx, 
            height: this.modalWindow.offsetHeight + d.diffy
        };        

        if(newSize.width < this.minSize.width)
            newSize.width = this.minSize.width;
        if(newSize.height < this.minSize.height)
            newSize.height = this.minSize.height;

        this.dialog.updateSize( 
            newSize.width + 'px', 
            newSize.height + 'px');
    }

     resizePanel(d: DragInfo){
        let currentWidth = parseFloat(this.leftPanel.style.flexBasis);
        if( currentWidth == 0 || isNaN(currentWidth) )
            currentWidth = this.leftPanel.offsetWidth;

        this.leftPanel.style.flexBasis = (currentWidth + d.x).toString() + 'px';       
    }
    
    private createResizeHandle(){
        this.resizeHandle = this._rd.createElement("div");
        this._rd.addClass(this.resizeHandle, "resize-handle");
        this._rd.appendChild(this.modalWindow, this.resizeHandle);
    }
} 
