import { Component, Input, OnInit, OnChanges, OnDestroy, ChangeDetectionStrategy, ElementRef, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs'; 

import { StudioSubjects, IUIDesigner, PropertyUpdatedData, UpdatedInfo, uuid,} from '../../common/objects/common';
import {DragInfo } from '../../shared/drag';
import _ from 'lodash';
import { StudioStateService } from '../../common/services/studioStateService';

@Component({
    selector: 'csi-designPageflow',
    templateUrl: 'designPageflow.component.html',
    styles: [`
        :host {
            display: flex;
            flex-direction: column;
        }
        textarea {
            height: 100%;
            width: 100%;
            overflow: auto;
        }
    
        svg {
            width: 100%;
            overflow: auto;
        }
    
        .pageflowDesigner.allowDrop {
            background-color: rgb(222, 239, 220);
        }
    
        .pageflowDesigner > div.csi-selectable-item {
            display: none;
        }
    `],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PageflowDesignComponent implements OnInit, OnChanges, OnDestroy {
    @Input("element") __element: any;

    @ViewChild("svgbox", {static: false}) svgElement : ElementRef;
    @ViewChild("gLines", {static: false}) svgLinesContainer : ElementRef;
    
    get element(): IUIPageflowDesigner { return this.__element; }
    set element(v: IUIPageflowDesigner) { this.__element = v; }

    xmlns: string = "http://www.w3.org/2000/svg";
    
    constructor(private _studioStateService: StudioStateService, _el: ElementRef) {
        this.valueUpdatedSubscription = this.studioSubjects.property.subscribe(vc => this.onPropertyChanged(vc));        
    }

    ngOnInit(): void {
        // IE drop issue fix.
        if( this.svgElement && this.svgElement.nativeElement)
            if (!this.svgElement.nativeElement.parentElement)
                this.svgElement.nativeElement.parentElement = this.svgElement.nativeElement.parentNode;
    }

    ngOnChanges(ch): void {
        this.initDesignComponent(this.element);
        this.tempLine = new PfPathUI();
        this.tempLine.x1 = this.tempLine.x2 = this.tempLine.y1 = this.tempLine.y2 = 0;
        this.tempLine["hidden"] = true;
    }

    ngOnDestroy(): void {
        this.valueUpdatedSubscription.unsubscribe();
    }

    private initDesignComponent(element: IUIPageflowDesigner) {
        this.pages = [];
        this.paths = [];
        element.node = element.properties["node"];
        if (element) {
            if (!element.node)
                element.node = [];

            let startPage = '';
            _.forEach(element.node, e => {
                    if (e.nodeType === PfNodeTypeEnum.Start) {
                        if (e.transition && e.transition.length == 1)
                            startPage = e.transition[0].targetNode;
                    }
                    else if (e.nodeType === PfNodeTypeEnum.Activity) {
                        let p = new PfPageUI();
                        p.name = e.key;
                        var nodeCoord = this.getNodeCordinates(e);
                        p.x = nodeCoord[0];
                        p.y = nodeCoord[1];
                        if (!e.location)
                            e.location = p.x + "," + p.y;

                        p.isStart = p.name === startPage;
                        this.pages.push(p);
                        if (e.transition) {
                            _.forEach(e.transition, (trans) => {
                                if (trans.targetNode !== this.finalNodeKey) {
                                    let path = new PfPathUI();
                                    path.from = e.key;
                                    path.to = trans.targetNode;
                                    path.x1 = p.x;
                                    path.y1 = p.y + this.nodeHeight / 2;
                                    let toLocation = this.calculatePathCoordinates(trans.targetNode);
                                    path.x2 = toLocation[0];
                                    path.y2 = toLocation[1];

                                    this.paths.push(path);
                                }
                            });
                        }
                    }
                }
            );
        }
    }

    nodeSelected(e, node: PfPageUI) {
        if (!node.selected) {
            this.clearSelection();
            node.selected = true;

            let selectedNodeObj = _.find(this.element.node, n => n.key === node.name);
            if (selectedNodeObj) {
                selectedNodeObj.location = node.x + "," + node.y;
                selectedNodeObj.isStartPage = node.isStart;
                selectedNodeObj.type = this.pfNodeType;
                this._studioStateService.selectItem(selectedNodeObj);
                this.selectedElement = node;
            }
        }
        e.stopPropagation();
    }

    pathSelected(e, path: PfPathUI) {
        if (!path.selected) {
            this.clearSelection();
            path.selected = true;

            let nodeObj = _.find(this.element.node, (n) => n.key === path.from);
            if (nodeObj && nodeObj.transition) {
                let selectedTransObj = _.find(nodeObj.transition, (trans) => trans.targetNode === path.to);
                if (selectedTransObj) {
                    selectedTransObj.type = this.pfTransitionType;
                    this._studioStateService.selectItem(selectedTransObj);
                    this.selectedElement = path;
                }
            }
        }
        e.stopPropagation();
    }

    private getOffset() : any{
        let r = this.svgElement.nativeElement.getBoundingClientRect();
        return {top: r.top, left: r.left, ratioX: r.width / 1000, ratioY: r.height / 600}
    }

    addNewNode(dndItem: any, x: number, y: number): void {        

        if (dndItem && dndItem.name) {
            if (!_.some(this.pages, ['name', dndItem.name])) // no duplicate node allowed.
            {
                let pUI = new PfPageUI();
                pUI.name = dndItem.name;
                let off = this.getOffset();
                pUI.x = (x - off.left) / off.ratioX;
                pUI.y = (y - off.top) / off.ratioY;
                pUI.isStart = false;

                var newNodeObj = this.createNodeObj(pUI);
                this.pages.push(pUI);
                if (this.pages.length === 1)
                    this.setStartPage(pUI);

                this.element.node.push(newNodeObj);
                this.setDirty();
            }
        }
    }

    addNewPath(from: PfPageUI, to: PfPageUI): void {
        if (from !== to) {
            if (!_.some(this.paths, (path) => { path.from === from.name && path.to === to.name })) // no duplicate paths allowed.
            {
                let path = new PfPathUI();
                path.from = from.name;
                path.to = to.name;
                path.x1 = from.x;
                path.y1 = from.y + this.nodeHeight / 2;
                path.x2 = to.x;
                path.y2 = to.y + this.nodeHeight / 2;
                this.paths.push(path);

                let nodeObj = _.find(this.element.node, (n) => n.key === from.name);
                if (nodeObj) {
                    if (!nodeObj.transition)
                        nodeObj.transition = [];
                    nodeObj.transition.push(this.createTransitionObj(path));
                }
                this.setDirty();
            }
        }
    }

    svgMouseMove(e): void {
        if (this.capturedNode) {
            let dx = e.pageX - this.start.x;
            let dy = e.pageY - this.start.y;
            if (this.isHeaderCaptured) {
                this.moveNode(this.capturedNode, this.capturedNode.x + dx, this.capturedNode.y + dy);
            }
            else if (this.isBodyCaptured) {
                this.tempLine.x2 += dx;
                this.tempLine.y2 += dy;
                this.insertPage();
            }

            this.start = {x:e.pageX, y:e.pageY};
            e.stopPropagation();
        }
    }

    svgMouseUp(e): void {
        if (this.capturedNode) {
            this.isBodyCaptured = this.isHeaderCaptured = false;
            this.tempLine["hidden"] = true;
            this.skipSvgClick = true;

            if (this.capturedNode.selected) {
                let selectedNodeObj = _.find(this.element.node, n => n.key === this.capturedNode.name);
                if (selectedNodeObj) {
                    selectedNodeObj.location = this.capturedNode.x + "," + this.capturedNode.y;    
                    this._studioStateService.selectItem(selectedNodeObj);
                }
            }
            
            this.capturedNode = null;
            e.stopPropagation();
        }
        else
            this.skipSvgClick = false;
    }

    svgClick(e): void {
        if (!this.skipSvgClick) {
            this.clearSelection();
            this._studioStateService.selectItem(this.element);
        }
        else
            this.skipSvgClick = false;
    }
    
    svgKeyUp(e): void {
        if ((e.key == "Delete" || e.key == "Del") && !(e.altKey || e.shiftKey || e.ctrlKey)) {
            let selectedNode = _.find(this.pages, (p) => p.selected);
            if (selectedNode)
                this.deleteNode(selectedNode);
            else {
                let selectedPath = _.find(this.paths, (p) => p.selected);
                if (selectedPath)
                    this.deletePath(selectedPath);
            }
        }
        e.stopPropagation();
    }

    nodeHeaderMouseDown(e, node: PfPageUI): void {
        this.capturedNode = node;
        this.start = {x:e.pageX, y:e.pageY};
        this.isHeaderCaptured = true;
        e.stopPropagation();
    }

    nodeBodyMouseDown(e, node: PfPageUI): void {
        this.capturedNode = node;
        this.start = {x:e.pageX, y:e.pageY};
        
        this.tempLine.x1 = node.x;
        this.tempLine.y1 = node.y + this.nodeHeight / 2;

        let off = this.getOffset();
        this.tempLine.x2 = (this.start.x - off.left) / off.ratioX;
        this.tempLine.y2 = (this.start.y - off.top) / off.ratioY;
        this.tempLine["hidden"] = false;
        this.isBodyCaptured = true;

        e.stopPropagation();
    }

    nodeBodyMouseUp(e, node: PfPageUI): void {
        if (this.isBodyCaptured) {
            this.addNewPath(this.capturedNode, node);
        }
        this.svgMouseUp(e);
        e.stopPropagation();
    }

    private onPropertyChanged(vc: UpdatedInfo) {
        if (this.selectedElement && vc.data) {
            let data = <PropertyUpdatedData>vc.data;
            if (data.path) {
                if (data.path === "IsStartPage") {
                    if (data.val)
                        this.setStartPage(this.selectedElement);
                    else
                        (<PfPageUI>this.selectedElement).isStart = false;
                }
                else if (data.path === "Location") {
                    if (data.val) {
                        let coord = this.getLocationCoordinates(data.val);
                        if (coord)
                            this.moveNode(this.selectedElement, coord[0], coord[1]);
                    }
                }
            }
        }
    }

    private createNodeObj(node: PfPageUI): PfNode {
        let p = new PfNode();
        p.key = node.name;
        p.transition = [];
        p.nodeType = PfNodeTypeEnum.Activity;
        p.isStartPage = false;
        p.location = node.x + "," + node.y;
        p["__type"] = this.pfNodeType;
        return p;
    }

    private createTransitionObj(path: PfPathUI): PfTransition {
        let trans = new PfTransition();
        if (path)
            trans.targetNode = path.to;
        trans["__type"] = this.pfTransitionType;
        return trans;
    }

    private moveNode(node: PfPageUI, x, y) {
        if (node) {
            node.x = x;
            node.y = y;
            var pageName = node.name;
            _.forEach(this.paths, (path) => {
                if (path.from === pageName) {
                    path.x1 = node.x;
                    path.y1 = node.y + this.nodeHeight / 2;
                }
                else if (path.to === pageName) {
                    path.x2 = node.x;
                    path.y2 = node.y + this.nodeHeight / 2;
                }
            });
            this.insertPage();
            this.setDirty();
        }
    }

    private insertPage(){
        // TODO: remove when IE 11 will not be supported.
        this.svgElement.nativeElement.insertBefore(this.svgLinesContainer.nativeElement, this.svgLinesContainer.nativeElement);                    
    }

    private deleteNode(node: PfPageUI): void {
        _.remove(this.pages, (n) => n === node);
        _.remove(this.paths, (path) => (path.from === node.name) || (path.to === node.name));
        _.remove(this.element.node, (n) => n.key === node.name);
        _.forEach(this.element.node, (n) => {
            if (n.transition)
                _.remove(n.transition, (trans) => trans.targetNode === node.name);
        });
        if (this.pages.length === 1)
            this.setStartPage(this.pages[0]);

        this.clearSelection();
        this._studioStateService.selectItem(this.element);
        this.setDirty();
    }

    private deletePath(path: PfPathUI): void {
        _.remove(this.paths, (p) => p === path);
        _.forEach(this.element.node, (n) => {
            if (n.key === path.from && n.transition)
                _.remove(n.transition, (trans) => trans.targetNode === path.to);
        });
        this.clearSelection();
        this._studioStateService.selectItem(this.element);
        this.setDirty();
    }

    private calculatePathCoordinates(key: string): number[] {
        var coord = [0, 0];
        let node = _.find(this.element.node, (node) => node.key === key);
        if (node) {
            coord = this.getNodeCordinates(node);
            coord[1] += this.nodeHeight / 2;
        }
        return coord;
    }

    private getLocationCoordinates(locationXY: string): number[] {
        var coord = [NaN, NaN];
        if (locationXY) {
            var xy = locationXY.split(',');
            if (xy.length === 2) {
                coord[0] = parseFloat(xy[0]);
                coord[1] = parseFloat(xy[1]);
            }
        }
        if (coord[0] !== NaN && coord[1] !== NaN)
            return coord;

        return null;
    }

    private getNodeCordinates(node: PfNode) {
        var coord = [0, 0];
        if (node.location)
            coord = this.getLocationCoordinates(node.location);
        else {
            let nodeLayout = _.find(this.element.nodeLayout, (layout) => layout.key === node.key);
            if (nodeLayout)
                coord = [nodeLayout.x, nodeLayout.y];
        }
        return coord;
    }

    private setStartPage(node: PfPageUI) {
        if (!node.isStart) {
            _.forEach(this.pages, p => { p.isStart = false; });
            node.isStart = true;

            let startNodeObj = this.getStartNodeObj();
            if (!startNodeObj.transition || !startNodeObj.transition.length) {
                startNodeObj.transition = [];
                startNodeObj.transition.push(this.createTransitionObj(null));
            }
            startNodeObj.transition[0].targetNode = node.name;
        }
    }

    private clearSelection(): void {
        _.forEach(this.pages, p => { p.selected = false; });
        _.forEach(this.paths, p => { p.selected = false; });
        this.selectedElement = null;
    }

    private getStartNodeObj(): PfNode {
        return _.find(this.element.node, (n) => n.nodeType === PfNodeTypeEnum.Start);
    }

    private setDirty() {
        if (!this.element.isDirty) {
            this.element.isDirty = true;
            this.studioSubjects.menu.next();    // just refresh the action menu
        }
    }

    onDrop(d: DragInfo, targetModel:any){
        this.addNewNode(d.dragModel, d.x, d.y);
    }

    getDropOptions(){
        return {
            id: uuid.id(),
            disabled: false,
            types: "page-ref", 
            drop:  (d,tm)=> this.onDrop(d,tm),
            over:  (d : DragInfo,m) => {}, 
            model: null
        };
    }

    
    private get studioSubjects(): StudioSubjects { return this._studioStateService.subjects; }
    private valueUpdatedSubscription: Subscription;
    pages: PfPageUI[];
    paths: PfPathUI[];
    private start: { x: number; y: number};
    private isHeaderCaptured: boolean;
    private isBodyCaptured: boolean;
    tempLine: PfPathUI;
    private skipSvgClick: boolean;
    private capturedNode: PfPageUI;
    private selectedElement: any;
    private nodeHeight = 100;
    private finalNodeKey = "--final--";
    private pfNodeType = "PageFlowNode";
    private pfTransitionType = "PageTransition";
}

enum PfNodeTypeEnum {
    Activity = 0,
    Start = 1,
    Finish = 2
}

class PfTransition {
    targetNode: string;
    ruleCondition: any;
    name: string;
    type: string;
}

class PfNode {
    key: string;
    nodeType: PfNodeTypeEnum; 
    transition: PfTransition[];
    location: string;
    isStartPage: boolean;
    type: string;
}

class PfNodeLayout {
    key: string;
    x: number;
    y: number;
}

class PfPageUI {
    width: number;
    x: number;
    y: number;
    isStart: boolean;
    selected: boolean;
    name: string;
}

class PfPathUI {
    from: string;
    to: string;
    name: string;
    x1: number;
    y1: number;
    x2: number;
    y2: number;
    selected: boolean;
}

interface IUIPageflowDesigner extends IUIDesigner {
    node: PfNode[];
    nodeLayout: PfNodeLayout[];
}
