import { IWizardState } from '../../../common/objects/common';
import { FormArray } from '@angular/forms';
import { GridDataModes } from '../../../common/objects/viewModel';

export class GridDataSource implements IWizardState {
    public element: any;
    public gridDataMode: string;

    constructor(element: any) {
        this.element = element;
    }
    next(form: any): IWizardState {
        this.save(form);
        this.gridDataMode = GridDataModes[this.element.properties.settings.gridDataMode];
        if (this.gridDataMode == "SelectionValues")
            return new GridFieldExpression(this.element);
        if (this.gridDataMode == "PredefinedQuery" || this.gridDataMode == "AdHocQuery")
            return new GridQuery(this.element);
        return new GridEditMode(this.element);
    }
    back(form: any): IWizardState {
        return null;
    }

    save(form: any): any {
        //create properties & settings node
        let dataMode = form.get('gridDataMode').value;

        let settings;
        switch (dataMode) {
            case "ItemList":
                settings = {
                    __type: 'GridDataSettingsItemList',
                    gridDataMode: GridDataModes.ItemList,
                    selectionModeField: SelectionMode.SingleRowSelect,
                    allowDataNavigationField: true,
                    isRequiredOnSubmitField: true
                };
                break;

            case "SubentityList":
                settings = {
                    __type: 'GridDataSettingsSubentityList',
                    gridDataMode: GridDataModes.SubEntityList,
                    selectionModeField: SelectionMode.SingleRowSelect,
                    allowDataNavigationField: true,
                    isRequiredOnSubmitField: true
                };
                break;
            case "SelectionValues":
                settings = {
                    __type: 'GridDataSettingsSelVal',
                    gridDataMode: GridDataModes.SelectionValues,
                    selectionModeField: SelectionMode.SingleRowSelect,
                };
                break;
            case "PredefinedQuery":
                settings = {
                    __type: 'GridDataSettingsPredefinedQuery',
                    gridDataMode: GridDataModes.PredefinedQuery,
                    selectionModeField: SelectionMode.SingleRowSelect,
                };
                break;
            case "AdHocQuery":
                settings = {
                    __type: 'GridDataSettingsAdHocQuery',
                    gridDataMode: GridDataModes.AdHocQuery,
                    selectionModeField: SelectionMode.SingleRowSelect,
                };
                break;
            case "Generic":
                settings = {
                    __type: 'GridDataSettingsGeneric',
                    gridDataMode: GridDataModes.Generic,
                    selectionModeField: SelectionMode.SingleRowSelect,
                };
                break;
            case "DataTable":
                settings = {
                    __type: 'GridDataSettingsDataTable',
                    gridDataMode: GridDataModes.DataTable,
                    selectionModeField: SelectionMode.SingleRowSelect,
                };
                break;
        }

        this.element.properties = { settings: settings };
        return this.element;
    }
    validate(form: any): string {
        return null;
    }
}


export class GridEditMode implements IWizardState {
    public element: any;

    constructor(element: any) {
        this.element = element;
    }
    next(form: any): IWizardState {
        this.save(form);
        return new GridFieldExpression(this.element);
    }
    back(form: any): IWizardState {
        return new GridDataSource(this.element);
    }
    save(form: any): any {
        let editingMode = form.get('editingMode').value;
        let allowDataNavigation = editingMode == 'Disabled';

        this.element.properties.settings.editorSettings = {
            __type: 'JQGridEditorSettings',
            editingMode: editingMode
        };
        this.element.properties.settings.allowDataNavigation = allowDataNavigation;
        return this.element;
    }
    validate(form: any): string {
        return null;
    }
}

export class GridFieldExpression implements IWizardState {
    public element: any;
    public gridDataMode: string;

    constructor(element: any) {
        this.element = element;
        this.gridDataMode = GridDataModes[this.element.properties.settings.gridDataMode];
    }

    next(form: any): IWizardState {
        this.save(form);
        return new GridActionButton(this.element);
    }
    back(form: any): IWizardState {
        if (this.gridDataMode == "SelectionValues")
            return new GridDataSource(this.element);
        if (this.gridDataMode == "PredefinedQuery" || this.gridDataMode == "AdHocQuery") {
            return new GridQuery(this.element);
        }
        return new GridEditMode(this.element);
    }
    save(form: any): any {
        this.element.properties.settings.fieldExpressions = form.get('fieldExpressions').value;
        if (this.gridDataMode === "ItemList")
            this.element.properties.settings.requestFieldExpressions = form.get('requestFieldExpressions').value;
        if (this.gridDataMode === "SelectionValues")
            this.element.properties.settings.selectionValuesExpressions = form.get('requestFieldExpressions').value;
        return this.element;
    }
    validate(form: any): string {
        let fieldExpression = form.get('fieldExpressions').value;
        let requestFieldExpression = form.get('requestFieldExpressions').value;
        if (this.gridDataMode === "SelectionValues" && fieldExpression === null && requestFieldExpression == null)
            return 'INCOMPLETEFIELDEXPRESSION';
        return null;
    }
}

export class GridActionButton implements IWizardState {
    public element: any;
    public gridDataMode: string;

    constructor(element: any) {
        this.element = element;
        this.gridDataMode = GridDataModes[this.element.properties.settings.gridDataMode];

    }
    next(form: any): IWizardState {
        this.save(form);
        let fieldExpression = this.element.properties.settings.fieldExpressions;
        if (this.gridDataMode == "PredefinedQuery" ||
            this.gridDataMode == "AdHocQuery" ||
            (fieldExpression && fieldExpression.length > 0))
            return new GridColumnSetup(this.element);
        return new GridName(this.element);
    }
    back(form: any): IWizardState {
        this.clear(form);
        return new GridFieldExpression(this.element);
    }
    save(form: any): any {
        //form.get('requestFieldExpressions').value;
        this.element.properties.settings.navigatorActions = [
            { Action: 'Add', Enable: form.get('actionButtons.enableAdd').value, Visible: form.get('actionButtons.visibleAdd').value },
            { Action: 'Edit', Enable: form.get('actionButtons.enableEdit').value, Visible: form.get('actionButtons.visibleEdit').value },
            { Action: 'Delete', Enable: form.get('actionButtons.enableDelete').value, Visible: form.get('actionButtons.visibleDelete').value },
            { Action: 'Refresh', Enable: form.get('actionButtons.enableRefresh').value, Visible: form.get('actionButtons.visibleRefresh').value },
            { Action: 'ExportData', Enable: form.get('actionButtons.enableExportData').value, Visible: form.get('actionButtons.visibleExportData').value },
        ]
        return this.element;
    }
    validate(form: any): string {
        let queryText = form.get('query.queryText').value;
        if (this.gridDataMode == "AdHocQuery" && !queryText)
            return 'QUERYTEXTEMPTY';
        return null;
    }
    private clear(form: any): void {
        form.get('actionButtons.enableAdd').setValue(false);
        form.get('actionButtons.enableEdit').setValue(false);
        form.get('actionButtons.enableDelete').setValue(false);
        form.get('actionButtons.visibleAdd').setValue(false);
        form.get('actionButtons.visibleEdit').setValue(false);
        form.get('actionButtons.visibleDelete').setValue(false);

        form.get('actionButtons.enableAdd').disable();
        form.get('actionButtons.enableEdit').disable();
        form.get('actionButtons.enableDelete').disable();
        form.get('actionButtons.visibleAdd').disable();
        form.get('actionButtons.visibleEdit').disable();
        form.get('actionButtons.visibleDelete').disable();
    }
}

export class GridColumnSetup implements IWizardState {
    public element: any;
    constructor(element: any) {
        this.element = element;
    }
    next(form: any): IWizardState {
        this.save(form);
        return new GridRow(this.element);
    }
    back(form: any): IWizardState {
        this.clear(form);
        return new GridActionButton(this.element);
    }
    save(form: any): any {
        let columns = form.get('columns').value;
        let selectedColumns = [];

        columns.forEach(col => {
            if (col.selected)
                selectedColumns.push({
                    __type: col.columnType,
                    editable: col.editable,
                    hidden: col.hidden,
                    name: col.id,
                    labelName: col.labelName,
                    width: col.width
                });
        })
        this.element.properties.settings.columns = selectedColumns;
        return this.element;
    }
    clear(form: any): void {
        let columns = <FormArray>form.get('columns');
        columns.controls.splice(0, columns.controls.length);
    }
    validate(form: any): string {
        return null;
    }
}

export class GridRow implements IWizardState {
    public element: any;
    constructor(element: any) {
        this.element = element;
    }
    next(form: any): IWizardState {
        this.save(form);
        return new GridName(this.element);
    }
    back(form: any): IWizardState {
        return new GridColumnSetup(this.element);
    }
    save(form: any): any {
        let rowId = form.get('rowId').value;
        if (rowId != "auto") {
            let columns = this.element.properties.settings.columns;
            columns.forEach(col => col.isRowID = (col.ID == rowId));
        }

        this.element.properties.settings.selectionMode = form.get('selectionMode').value;

        let submitMode = form.get('submitDataMode').value;

        if (submitMode != "Disabled") {
            let mappings = form.get('mappings').value;
            let submitMappings = [];

            mappings.forEach(map => {
                submitMappings.push({ columnName: map.ColumnName, objectPropertyName: map.submitField });
            });

            this.element.properties.settings.submitModeSpecified = true;
            this.element.properties.settings.submitColumnMappings = submitMappings;
        }

        return this.element;
    }
    validate(form: any): string {
        return null;
    }
}

export class GridQuery implements IWizardState {
    public element: any;
    public gridDataMode: string;

    constructor(element: any) {
        this.element = element;
        this.gridDataMode = GridDataModes[this.element.properties.settings.gridDataMode];
    }
    next(form: any): IWizardState {
        this.validate(form);
        this.save(form);
        return new GridFieldExpression(this.element);
    }
    back(form: any): IWizardState {
        this.clear(form);
        return new GridDataSource(this.element);
    }
    save(form: any): any {
        if (this.gridDataMode == "PredefinedQuery") {
            this.element.properties.settings.queryType = form.get('query.type').value;
            this.element.properties.settings.query = form.get('query.definedQuery').value;            
        }            
        if (this.gridDataMode == "AdHocQuery")
            this.element.properties.settings.queryText = form.get('query.queryText').value;

        let parameters = form.get('parameters').value;
        if (parameters) {
            this.element.properties.settings.QueryParameters = [];
            parameters.forEach(param => {
                this.element.properties.settings.QueryParameters.push({
                    ConnectionType: param.type,
                    Name: param.name,
                    Key: param.value
                });
            });
        }

        return this.element;
    }
    private clear(form: any): void {
        if (this.gridDataMode == "PredefinedQuery" || this.gridDataMode == "AdHocQuery") {
            let parameters = <FormArray>form.get('parameters');
            parameters.controls.splice(0, parameters.controls.length);
        }
    }
    validate(form: any): string {
        let definedQuery = form.get('query.definedQuery').value;
        if (this.gridDataMode == "PredefinedQuery" && !definedQuery)
            return 'INCOMPLETEQUERYNAME';
        let queryText = form.get('query.queryText').value;
        if (this.gridDataMode == "AdHocQuery" && !queryText)
            return 'INCOMPLETEQUERYTEXT';
        return null;
    }
}

export class GridName implements IWizardState {
    public element: any;
    public gridDataMode: string;

    constructor(element: any) {
        this.element = element;
        this.gridDataMode = GridDataModes[this.element.properties.settings.gridDataMode];
    }
    next(form: any): IWizardState {
        this.save(form);
        return null;
    }
    back(form: any): IWizardState {
        if (this.gridDataMode == "PredefinedQuery" || this.gridDataMode == "AdHocQuery")
            return new GridRow(this.element);
        return new GridActionButton(this.element);
    }
    save(form: any): any {
        let name = form.get('name').value;
        this.element.name = name;
        this.element.properties.name = name;
        this.element.properties.labelName = form.get('titleLabel').value;
        this.element.properties.labelText = form.get('titleText').value;
        return this.element;
    }
    validate(form: any): string {
        return null;
    }
}

export class GridColumn {
    name: string;
    labelName: string;
    typeName: string;
    width: number;
    editable: boolean;
    required: boolean;
    requiredSpecified: boolean;
}

const enum SelectionMode {
    MultiRowSelect,
    CheckBox,
    SingleRowSelect,
    SingleRowSelectWithPostback,
    Disable
}