import { Component, Input, OnInit, ViewChild, ElementRef, AfterViewChecked } from "@angular/core";
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { ModalPropertiesContext } from '../properties';
import { QueryGridParameter, DataContractConnectionType, QueryBoundGridParameter } from '../../../common/objects/viewModel';
import { ValueProviderService } from "../../../common/services/dataValue.service";
import { ISelectableItem, val } from "../../../common/objects/common";
import { PortalService } from "../../../common/services/portal.service";

import _ from 'lodash';

@Component({
    selector: "csi-query-builder",
    styleUrls: ['queryBuilder.component.less'],
    templateUrl: "queryBuilder.component.html"
})
export class QueryBuilderComponent implements OnInit, AfterViewChecked {
    @Input() context: ModalPropertiesContext;

    @ViewChild("queryListElement") queryListElement : ElementRef;

    get textValue(): string { return <string>(this.context.value); }
    set textValue(v: string) { this.context.value = v; }

    get queryType(): string { return this.context.instance.queryType.toString(); }
    set queryType(v: string) { this.context.instance.queryType = parseInt(v); this.typeAheadFilter = ''; this.getQueryList(); }

    get typeAheadFilter(): string { return this._filter; }
    set typeAheadFilter(v: string) { this._filter = v; this.getQueryList(); }

    parameters: queryParameter[] = [];
    isPredefined: boolean;
    queryList: Observable<ISelectableItem[]>;
    private gridMode = true;
    private queryParametersField : string = "queryParameters";
    private _scroll_to_selected = false;

    private _filter: string;

    constructor(private _valueService: ValueProviderService, private _portal: PortalService) {
    }

    ngOnInit(): void {
        this.context.title = "Query Builder";
        this.context.beforeClose = (ok: boolean) => this.onBeforeClose(ok);
        if( this.context.instance.__type == "GridDataSettingsPredefinedQuery")
            this.isPredefined = true;        
        else if( this.context.instance.__type == "GridDataSettingsAdHocQuery")
            this.isPredefined = false;
        else if( this.context.instance.__type == "SelectionDataSourceMap") {
            this.isPredefined = val(this.context.instance, "QueryExecutionMode") === 1;
            this.gridMode = false;
            this.queryParametersField = "Parameters";            
        }

        if (this.isPredefined) {
            if (this.context.instance.queryType === undefined)
                this.context.instance.queryType = 0;
            this.getQueryList();            
            if( this.textValue )
                this._scroll_to_selected = true;            
        }

        let qparams = val( this.context.instance, this.queryParametersField) as any[];
        if (qparams) {            
            // load initial parameters
            this.parameters = qparams.map<queryParameter>(p => new queryParameter(p.name, p.key));
        }

        if( !this.isPredefined)
            this.changeText(this.textValue);
    }

    ngAfterViewChecked(){
        if(this._scroll_to_selected){
            if(this.queryListElement ){
                let d = <HTMLDivElement>this.queryListElement.nativeElement;
                let sel = d.querySelector("#q_" + this.textValue);
                if( sel ){
                    this._scroll_to_selected = false;
                    sel.scrollIntoView();
                }
            }
        }
    }
    
    changeText(v) {
        this.textValue = v;
        this.getAdHocQueryParameters(v);
    }

    selectQuery(q: ISelectableItem) {
        if (q.childCount > 0)
            this.getPredefQueryParameters(q.name);
        else
            this.parameters = [];

        this.textValue = q.name;
    }

    private getAdHocQueryParameters(qText: string) {
        let prms = qText ? qText.match(/(\?\w*)/ig) : [];

        let newPrms = _.chain(prms)
            .filter(x => x.length > 1)
            .map(p => p.substr(1)).uniq().value()
            .map(p => new queryParameter(p));

        // take values from existing 
        newPrms.forEach(p => {
            let pd = this.parameters.find(px => px.name == p.name);
            if (pd) p.value = pd.value;
        });
        this.parameters = newPrms;
    }

    private getQueryList() {
        let th = this.typeAheadFilter ? this.typeAheadFilter.toLowerCase() : '';
        this.queryList = this._valueService.getSelectableItems(this.context.property, this.context.instance)
            .pipe( map(qs => qs.filter(q => th ? q.name.toLowerCase().indexOf(th) != -1 : true)));
    }

    private onBeforeClose(ok: boolean): boolean {
        if (ok) {
            let ps : any[];
            if( this.gridMode){
                ps = this.parameters.map<QueryGridParameter>(p => {
                    let existingParameter: QueryGridParameter = this.context.instance.queryParameters ? this.context.instance.queryParameters.find(qp => qp.name == p.name) : null;
                    let q = new QueryGridParameter();
                    q.connectionType = existingParameter ? existingParameter.connectionType : DataContractConnectionType.StaticValue;
                    q.name = p.name;
                    q.key = existingParameter ? existingParameter.key : p.value;
                    q['__tempValue'] = p.value; // the value for further request the column list

                    return q;
                });
            }
            else{
                ps = this.parameters.map<QueryBoundGridParameter>(p => {
                    let prms = val(this.context.instance, "parameters");
                    let existingParameter: QueryBoundGridParameter = prms ? prms.find(qp => qp.name == p.name) : null;
                    let q = new QueryBoundGridParameter();
                    q.connectionType = existingParameter ? existingParameter.connectionType : DataContractConnectionType.StaticValue;
                    q.name = p.name;
                    q.key = existingParameter ? existingParameter.key : p.value;
                    return q;
                });
                
            }

            val(this.context.instance, this.queryParametersField, ps);
        }
        // reflect parameters 
        return true;
    }

    private getPredefQueryParameters(qText: string): void {
        this._portal.getQueryParameters(parseInt(this.queryType), qText)
            .subscribe(p => {
                this.parameters = p.map(px => new queryParameter(px.Text));
            });
    }
}

class queryParameter {
    constructor(public name: string, public value: string = '', public type: string = "String") { }

}
