import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AuthorizationService } from './authorization.service';
import { WCFService } from '../common/services/wcf.service';

import { LoginDialogComponent } from './login.dialog.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
    selector: 'csi-start',
    template: ``
})

export class StartComponent implements OnInit {
    constructor(
        private _authService: AuthorizationService,
        public _dialog: MatDialog,
        private _wcfService: WCFService,
        private _router: Router) { }

    ngOnInit(): void {
        this._authService.isLoggedIn()
            .subscribe(isLoggedIn => {
                if (isLoggedIn) {
                    this.setAuthorization(this._authService.authorizationHeader);
                }
                else if (isLoggedIn == false) {
                    let dialog = this._dialog.open(LoginDialogComponent, { disableClose: true });
                    //init context
                    let context = { message: '' };
                    dialog.componentInstance.initContext(context);
                    dialog.afterClosed().subscribe(authorizationHeader => {
                        if (authorizationHeader) {
                            let isToken = authorizationHeader.indexOf('Bearer') > -1;
                            if (isToken) {
                                this.setAuthorization(authorizationHeader);
                                this._authService.setAuthorizationHeader(authorizationHeader);
                            }
                        }
                    });
                }
            });
    }
    private setAuthorization(authHeader: string) {
        //this._studioService.setAuthorization(authHeader);
        this._wcfService.setAuthorization(authHeader);
        this._router.navigate(['studio']);
    }
}

