import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ModalPropertiesContext } from '../properties';
import { SetDataValueItem } from '../../../common/objects/viewModel';
import * as _ from 'lodash';

@Component({
    selector: 'csi-value-condition',
    styles: [`
        :host{
            min-height: 400px;
            min-width: 700px;

            display: flex;
            flex-direction: column;
            flex: 1 1 auto;            
        }
        .csi-value-condition{
            flex: 1 1 auto;            
        }
        .condition-condition {
            overflow: auto;
        }
    `],
    templateUrl: 'valueCondition.html'
})

export class ValueConditionComponent {
    private context: ModalPropertiesContext;

    valueIfTrue: SetDataValueItem;

    get dialogRef(): MatDialogRef<ValueConditionComponent> {
        return this._dialogRef;
    }

    constructor(private _dialogRef: MatDialogRef<ValueConditionComponent>) {}

    initContext(context: ModalPropertiesContext): void {
        this.context = context;
        if (!context.value) 
            this.valueIfTrue = new SetDataValueItem();        
        else
            this.valueIfTrue = _.cloneDeep(context.value);
    }

    valueChanged(v: SetDataValueItem): void {
        this.valueIfTrue = v;
    }

    ok(): void {
        this.context.value = this.valueIfTrue;
        this._dialogRef.close(this.context);
    }

    cancel(): void {
        this._dialogRef.close(this.context);
    }
}