import { DragItem } from "./drag.service";

export enum dragModes {none, moveWindow, resizeWindow, resizePanel, dragElement, reorder};

export class DragInfo {
    public x: number;
    public y :number;

    public diffx: number;
    public diffy :number;

    public dragMode : dragModes = dragModes.none;
    public endOfDrag = false;

    public dropZoneIndex: number = -1;
    public dropZoneElement: HTMLElement;
    public leaveZone: boolean = false;
    public dragModel : DragItem;
    public ctrlPressed : boolean = false;
    
    constructor(X: number, Y: number, start: DragInfo, ctrlKey: boolean){
        this.x = X;
        this.y = Y;
        this.ctrlPressed = ctrlKey;

        if( start ){

            this.diffx = X - start.x;
            this.diffy = Y - start.y;

            start.x = this.x;
            start.y = this.y;

            this.dragMode = start.dragMode;
        }
    }
}