import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ModalPropertiesContext } from '../properties';
import { StudioSubjects } from '../../../common/objects/common';

@Component({
    selector: 'csi-modal-container',
    styles:[
    `
    :host{
        min-height: 550px;
        min-width: 450px;
        height: 100%;
        width: 100%;

        display: flex;
        flex-direction: column;
    }

    .field-expression-container{
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;        
    }
    .portal-settings-container, .longtext-container, .query-builder-container, .target-matrix-rows-container {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;        
    }


    .csi-property-textarea.long-text{
        flex: 1 1 auto;
    }

    .webi-reports-container{
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;        
    }

    `],
    templateUrl: './modalContainer.html'
})

export class ModalContainerComponent implements OnInit {
    context: ModalPropertiesContext;
    errorMessage: string;
    contentType: string;
    subjects: StudioSubjects;

    get dialog(): MatDialogRef<ModalContainerComponent> {
        return this._dialog;
    }

    get readonly(): boolean {
        return this.context && this.context.property && this.context.property.readOnly;
    }

    constructor(
        private _dialog: MatDialogRef<ModalContainerComponent>,
        private _chDetection: ChangeDetectorRef) {
        this.subjects = new StudioSubjects();
    }

    initContext(context: any) {
        this.context = context;
    }

    ngOnInit() {
        let propEditor = (this.context && this.context.property) ? this.context.property.editor : undefined;

        if (propEditor && propEditor.provider)
            this.contentType = propEditor.provider;

        else 
            if(propEditor && propEditor.parameters && propEditor.parameters.length && propEditor.parameters[0] == "longText"){
                this.contentType = 'longText';
            }
        else
            this.contentType = 'portalSettings';
    }

    getTitle() {
        return this.context.title;
    }

    touch() {
        this._chDetection.detectChanges();
    }


    ok() {
        let closing = this.context.beforeClose ? this.context.beforeClose(true) : true;
        if( closing )
            this._dialog.close(this.context.value);
    }

    cancel() {
        let closing = this.context.beforeClose ? this.context.beforeClose(false) : true;
        if( closing )
            // restore
            this._dialog.close();
    }

}
