import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { PortalService } from './portal.service';

@Injectable()
export class LabelService {
    constructor(private _portalService: PortalService) {
        this.load();
    }

    public load(){
        this.getLangDictionary();
    }

    public getLabel(labelName: string, defaultValue: any): string {
        return this.getLabelwithArguments(labelName, defaultValue, null);
    }

    public getLabelwithArguments(labelName: string, defaultValue: string, args: any[]): string {
        if (!defaultValue) return;
        if (this._dictionary && this._dictionary[labelName]) {
            if (args)
                return this.substituteString(this._dictionary[labelName], args);
            return this._dictionary[labelName];
        }
        if (args)
            return this.substituteString(defaultValue, args);
        return defaultValue;
    }

    public getLabelCategories(): Observable<any[]> {
        return Observable.create(obs => {
            if (this._categories) {
                obs.next(this._categories)
                obs.complete();
            }
            else {
                this._categories = [];
                this._portalService.getLabelCategories()
                    .subscribe(categories => {
                        this._categories = categories.sort(c=> c.name);
                        obs.next(categories);
                        obs.complete();
                    },
                    error => {
                        console.log(<any>error);
                    });
            }
        });
    }

    public getLabels(categoryId: string, labelSearchName: string, labelSearchText: string): Observable<any[]> {
        return Observable.create(obs => {
            this._portalService.getLabels(categoryId, labelSearchName, labelSearchText).subscribe(labels => {
                obs.next(labels);
                obs.complete()
            },
                error => {
                    console.log(<any>error);
                });
        });
    }

    private substituteString(value: string, args: string[]): string {
        for (let i = 0; i < args.length; i++) {
            value = value.replace(`{${i}}`, args[i]);
        }
        return value;
    }

    private getLangDictionary(): void {
        this._dictionary = [];
        this._portalService.getStudioLabels()
            .subscribe(labels => {
                this._dictionary = labels;
            },
            error => {
                console.log(<any>error);
            });
    }

    private _dictionary: any;
    private _categories: any;
}