import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { DebugResponse, textLine } from './traceLog';
import { WCFService } from '../../common/services/wcf.service';
import * as _ from 'lodash';

@Component({
    selector: 'csi-resubmit',
    template: `
    <div class="csi-modal">
        <header>
            <span class="title">{{'Resubmit' | label: 'PS_SRV_TRACE_RESUBMIT'}}</span>
            <span class="close-icon" (click)="close('cancel')"><i class="fa fa-times" aria-hidden="true"></i></span>
        </header>
        <main>        
            <div class="csi-resubmit">
                <span class="title">{{'Request' | label: 'PS_SRV_TRACE_RESUBMIT_REQUEST'}}</span>                
                <csi-raw-text [text]="requestTxt" [viewOnly]="false" (textChange)="reqChanged($event)"></csi-raw-text>

                <span class="title">{{'Response' | label: 'PS_SRV_TRACE_RESUBMIT_RESPONSE'}}</span>                
                <csi-raw-text [text]="responseLines" [viewOnly]="true"></csi-raw-text>
                
                <div class="result" [class.error]="submitSuccess===false" [class.success]="submitSuccess===true">{{submitMessage}}</div>
            </div>
        </main>
        <footer>           
            <div class="btns">
                <div class="commit"><span>{{'Commit' | label: 'PS_SRV_TRACE_RESUBMIT_COMMIT'}}</span><input type="checkbox" [(ngModel)]="isCommit"></div>
                <span class="default-btn" (click)="submit()">{{'Submit' | label: 'PS_SRV_TRACE_SUBMIT'}}</span>
                <span class="primary-btn" (click)="close('cancel')">{{'Close' | label: 'PS_CLOSE'}}</span>
            </div>
        </footer>
    </div>        
    `
})

export class ResubmitRequestComponent {

    public set debugResponse(d: DebugResponse) {
        this.responseLines = [];
        let doc = new DOMParser().parseFromString(d.Response, "text/xml");
        this.makeLinesFromXml(doc.documentElement);

        this.requestTxt = d.Request;
    }
    isCommit: boolean = true;

    responseLines: textLine[] = [];
    requestTxt: string;

    submitSuccess: boolean;
    submitMessage: string;

    constructor(private _wcf: WCFService, private _dialogRef: MatDialogRef<ResubmitRequestComponent>) {
    }

    close(action: string): void {       
        this._dialogRef.close();
    }

    submit() {
        let debRequest = {
            IncludeRRXML: true,
            IncludeTimestamps: true,
            TraceToResponseXML: true,
            DisableCommit: !this.isCommit
        };
        this._wcf.directAccessSubmit(this.requestTxt, debRequest).subscribe(r => {

            this.responseLines = [];
            let doc = new DOMParser().parseFromString(r.response, "text/xml");
            this.makeLinesFromXml(doc.documentElement);

            if (r.result.IsSuccess === undefined) {
                this.submitSuccess = false;
                this.submitMessage = r.result.ExceptionData ? r.result.ExceptionData.Description : 'Some error occurs';
            }
            else {
                this.submitSuccess = r.result.IsSuccess;
                this.submitMessage = r.result.Message || 'Success!';
            }
        });
    }

    reqChanged(s: string) {
        this.requestTxt = s;
    }


    makeLinesFromXml(t: Node, level: number = 0) {
        let s = '<' + t.nodeName;

        _.forEach((<any>t).attributes, a => s += (` ${a.nodeName}='${a.nodeValue}'`));

        let v: string = '';
        let hasValue = false;
        let hasChildren = false;
        let childNodes: Node[];

        if (t.nodeType == 1) {
            childNodes = _.filter(t.childNodes, c => c.nodeType == 1);
            if (childNodes.length>0) {
                hasChildren = true;
            }
        }

        if (!hasChildren) {
            _.forEach(t.childNodes, c => v += ((c.nodeType == 3 || c.nodeType == 4) ? c.textContent : ''));
            hasValue = v.trim().length > 0;
        }

        if (hasValue) {
            s += '>';
            // if multiline add \n otherwise the value will be in the same line with open tag
            if (v.indexOf('\n') == -1) {
                s += (v + '</' + t.nodeName + '>');
                hasValue = false;
            }
        }
        else {
            if (!hasChildren)
                s += '/>';
            else
                s += '>';
        }

        // open <tag>
        this.responseLines.push(<textLine>{ level: level, text: s });
        if (hasValue) {
            v.split('\n').forEach(v => this.responseLines.push(<textLine>{ level: 0, text: v }));
        }

        if (hasChildren) {
            // add child nodes
            _.forEach(childNodes, c => this.makeLinesFromXml(c, level + 1));
        }

        if (hasValue || hasChildren) {
            // closed <tag>
            this.responseLines.push(<textLine>{ level: level, text: '</' + t.nodeName + '>' });
        }
    }

}

