import { Component, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { IUIDesigner, LayoutActions, DesignModes} from '../../common/objects/common';
import { LayoutMenuItem } from './objects/layoutMenuItem';
import { StudioStateService } from '../../common/services/studioStateService';

@Component({
    selector: 'csi-design-toolbar',
    templateUrl: 'toolbar.component.html',
    styleUrls:[`toolbar.component.less`]
})
export class DesignToolbarComponent implements OnInit, OnChanges {
    @Input() designElement: IUIDesigner;
    @Input() layoutActions : LayoutActions[];
    @Output() onModeChanged = new EventEmitter<DesignModes>(); 

    DesignModesEnum = DesignModes;
    DesignModesValues = 
        Object.keys(DesignModes).filter(k=> k.length==1).map(k1=>{return {k: k1, v:DesignModes[k1].match(/[A-Z][a-z]+|[0-9]+/g).join(" ")}; });

    //mode = this.DesignModesEnum.Design // must be check / [class.designMode]="mode==DesignModesEnum.Design">
    get layoutMenu() : LayoutMenuItem[] {return this.designElement.__layoutMenu;}
    set layoutMenu(lm : LayoutMenuItem[]) { this.designElement.__layoutMenu = lm;}

    activeMode : DesignModes;

    constructor(private _studioService: StudioStateService){
    }

    ngOnInit(){
        // Init menu
        this.layoutMenu = this._layoutMenuItems.map( a=> new LayoutMenuItem(a));
    }

    ngOnChanges(ch:SimpleChanges){
        if( ch.layoutActions){
           let acts =  ch.layoutActions.currentValue as LayoutActions[];
           if( acts ){
                if( !this.layoutMenu )
                    this.layoutMenu = this._layoutMenuItems.map( a=> new LayoutMenuItem(a));
                this.layoutMenu.forEach(m=>{
                    m.enabled = acts.indexOf(m.action) != -1;
                });
            }
        }
        if( ch.designElement ){
            this.activeMode = ch.designElement.currentValue.__designMode;
        }
    }

    sliderValueChanged($event, el: IUIDesigner) {
        let newScaleVal: number = $event;
        if (newScaleVal !== el.scaleValue) {
            el.scaleValue = newScaleVal;
        }
    }

    modeChanged(v:DesignModes){
        this.designElement.__designMode = this.activeMode;
        this.onModeChanged.emit(this.activeMode);
    }

    isLayoutCmdEnabled(m:LayoutMenuItem) : boolean{
        return this.layoutActions && this.layoutActions.indexOf(m.action)!=-1;
    }

    executeCommand(ev: Event, m : LayoutMenuItem){        
        this._studioService.layoutCommand(m.action);
    }

    private _layoutMenuItems = [
        LayoutActions.merge, 
        LayoutActions.split, 
        LayoutActions.none, 
        LayoutActions.removeRow, 
        LayoutActions.removeColumn, 
        LayoutActions.insertRowBefore, 
        LayoutActions.insertColumnBefore, 
        LayoutActions.insertRowAfter, 
        LayoutActions.insertColumnAfter, 
        LayoutActions.none,
        LayoutActions.reset,
        LayoutActions.toggleCellPos
    ];
}
