import { Component, Input, OnInit } from '@angular/core';
import { IOMTypeDescriptor, OMType} from '../../../common/objects/viewModel';
import { PortalService } from '../../../common/services/portal.service';
import { ModalPropertiesContext} from '../properties';
import { FieldDirectoryBase, OMTypeDescriptor } from '../../../common/objects/fieldDirectoryBase';
import { StudioStateService } from '../../../common/services/studioStateService';

@Component({
    selector: 'csi-field-expression',
    styles:[
        `
        :host{
            flex: 1 1 auto;
            display: flex;
            flex-direction: column;    
            height: 100%;
        }
        .csi-field-expression {
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
            height: 100%;
        }

        .csi-field-expression .result-row {
            display: flex;
            flex-direction: row;
            flex: 0 0 25px;
        }

        .csi-field-expression .result-row > input {
            width: 100%;
            padding: 5px;
            height : 25px;
        }

        .csi-field-expression .result-row > span.btn {
            padding: 2px 4px 6px 4px;
            text-align: center;
            border: 1px solid black;
            height : 25px;
        }

        .csi-field-expression .resultItem {
            padding: 4px;
            padding-bottom: 6px;
            background-color: white;
            cursor: pointer;
        }

        .csi-field-expression .resultItem:hover {
            background-color: aquamarine;
        }

        .csi-field-expression .subtitle {
            height: 25px;
            flex: 0 0 auto;
            display:flex;
            align-items: center;

        }

        .service-list {
            height: calc(100% - 120px);
        }

        `
    ],
    templateUrl: 'fieldExpression.html'
})

export class FieldExpressionComponent extends FieldDirectoryBase implements OnInit{

    @Input() context: ModalPropertiesContext;

    get result(): string {
        return this.context && this.context.value;
    };
    set result(v: string) {
        if (this.context)
            this.context.value = v;
    };
    resultItems: string[] = [];

    get readonly(): boolean {
        return this.context && this.context.property && this.context.property.readOnly;
    }

    constructor(portal: PortalService, state: StudioStateService) {
        super(portal, state);
    }

    ngOnInit() {
        this.context.title = "Field Expressions Builder";
        if (this.context.property.value) {

            this.resultItems = (<string>this.context.property.value).split(',');
            this.selectResultItem(this.resultItems[0]);
        }
        this.loadServices();
    }

    selectItem(item: IOMTypeDescriptor) {
        if (item.ItemType == OMType.Service || item.ItemType == OMType.Type)
            return;

        this.result = (<OMTypeDescriptor>item).getFieldExpression();
    }

    addResultItem() {
        if (this.resultItems.indexOf(this.result) == -1) {
            this.resultItems.splice(0, 0, this.result);
            this.result = "";
        }
    }

    selectResultItem(ri: string) {
        if (this.result)
            this.addResultItem();
        this.result = ri;
        let i = this.resultItems.indexOf(ri);
        this.resultItems.splice(i, 1);
    }

    servicesLoaded() {
        let primarySrv = '';

        if (this.result && this.result[0] != '.') {
            primarySrv = this.result.split('.', 1)[0];
        }
        else {
            if(this.context.metadataService)
                primarySrv = this.context.metadataService.getPrimaryService();
        }        
        this.search = primarySrv;
    }

}

