import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule, ExtraOptions } from '@angular/router';

import { StartComponent } from '../authorization/start.component';
import { StudioComponent } from '../studio/studio.component';

const initRoutes: Routes = [
    { path: 'studio', component: StudioComponent },
    { path: '', component: StartComponent, pathMatch: 'prefix' },
    { path: '**', component: StartComponent}
];

const appRoutes: Routes = [
    ...initRoutes
]

export const appRoutingProviders: any[] = [];
export const Routing: ModuleWithProviders<RouterModule> = RouterModule.forRoot(appRoutes, <ExtraOptions>{ enableTracing: false });
