import { Type, Injectable } from '@angular/core';
import { IDesignControl } from '../../../common/objects/common';
//import {COMPONENT_DIRECTIVES as Cmp} from './control.components';
import { BooleanSwitchComponent } from './templates/BooleanSwitch.component';
import { BusinessObjectComponent } from './templates/businessObject.component';
import { ButtonComponent } from './templates/button.component';
import { JQTabContainer } from './templates/jqTabContainer.companent';
import { RibbonContainer } from './templates/ribbonContainer.companent';
import { SectionDropDown } from './templates/sectionDropDown.companent';
import { ToggleContainer } from './templates/toggleContainer.companent';
import { WebPartsWrapperControl } from './templates/webPartsWrapperControl.companent';

import { WorkflowNavigatorComponent } from './templates/workflowNavigator.componen';
import { CheckBoxComponent } from './templates/checkBox.component';
import { ColorPickerComponent } from './templates/colorPicker.component';
import { ContainerListComponent } from './templates/containerList.component';
import { ContainerListGridComponent } from './templates/containerListGrid.component';
import { CrystalReportComponent } from './templates/crystalReport.component';
import { DataCollectionComponent } from './templates/dataCollection.component';
import { DateChooserComponent } from './templates/dateChooser.component';
import { DropDownListComponent } from './templates/dropDownList.component';
import { DurationComponent } from './templates/duration.component';
import { FileInputComponent } from './templates/fileInput.component';
import { GenericComponent } from './templates/generic.component';
import { HTMLPanelComponent } from './templates/htmlPanel.component';
import { IframeComponent } from './templates/iframe.component';
import { ImageComponent } from './templates/image.component';
import { InquiryComponent } from './templates/inquiry.component';
import { InstructionTextComponent } from './templates/instructionText.component';
import { JQDataGridComponent } from './templates/jqDataGrid.component';
import { LabelComponent } from './templates/label.component';
import { LinkButtonComponent } from './templates/linkButton.component';
import { MultiSelectPickListComponent } from './templates/multiSelectPickList.component';
import { NamedObjectComponent } from './templates/namedObject.component';
import { PageTabComponent } from './templates/pageTab.component';
import { PIMetricComponent } from './templates/piMetric.componen';
import { PlmVisComponent } from './templates/plmVis.component';
import { RadioButtonComponent } from './templates/radioButton.component';
import { ReadOnlyComponent } from './templates/readOnly.component';
import { RevisionedObjectComponent } from './templates/revisionedObject.component';
import { ScaleControlComponentBase } from './templates/scaleControl.component';
import { ScrollableMenuComponent } from './templates/scrollableMenu.component';
import { SlideMenuComponent } from './templates/slideMenu.component';
import { StatusMessageComponent } from './templates/statusMessage.component';
import { TextBoxComponent } from './templates/textBox.component';
import { TextEditorComponent } from './templates/textEditor.component';
import { TitleComponent } from './templates/title.component';


@Injectable()
export class ControlMappingService {

    constructor(){}

    public getControlComponent(typeName: string): Type<IDesignControl> {
        switch (typeName) {
            case 'BooleanSwitch':
                return BooleanSwitchComponent;
            case 'WebiReport':
                return BusinessObjectComponent;
            case 'Button':
            case 'FlyoutDropDown':
                return ButtonComponent;
            case 'CheckBox':
                return CheckBoxComponent;
            case 'ColorPicker':
                return ColorPickerComponent;
            case 'ContainerList':
                return ContainerListComponent;
            case 'ContainerListGrid':
                return ContainerListGridComponent;
            case 'CrystalReport':
                return CrystalReportComponent;
            case 'DataCollection':
            case 'ShopFloorDCControl':
                return DataCollectionComponent;
            case 'DateChooser':
                return DateChooserComponent;

            case 'JQTabContainer':
                return JQTabContainer;

            case 'RibbonContainer':
                return RibbonContainer;
            case 'SectionDropDown':
            case 'SectionDropDownMobile':
                return SectionDropDown;
            case 'ToggleContainer':
                return ToggleContainer;
            case 'WebPartsWrapperControl':
                return WebPartsWrapperControl;
            case 'BooleanDropDown':
            case 'DropDownList':
            case 'DiscoveryAreaControl':
                return DropDownListComponent;
            case 'Duration':
                return DurationComponent;
            case 'FileInput':
            case 'FileBrowse':
                return FileInputComponent;
            case 'ChartControl':
            case 'Checklist':
            case 'Generic':
            case 'LastChangeInfo':
            case 'ModelingObjectList':
            case 'SPCChartControl':
            case 'UserFields':
            case 'ViewDocumentsControl':
            case 'WebPartContainer':
            case 'WorkflowViewerControl':
            case 'IntelligenceReportObject':
            case 'Breadcrumb':             
                return GenericComponent;
            case 'PlmVisWebControl':
                return PlmVisComponent;
            case 'IFrame':
            case 'PagePanel':
                return IframeComponent;
            case 'ImageControl':
                return ImageComponent;
            case 'InquiryControl':
                return InquiryComponent;
            case 'InstructionText':
                return InstructionTextComponent;
            case 'JQDataGrid':
                return JQDataGridComponent;
            case 'Label':
                return LabelComponent;
            case 'LinkButton':
                return LinkButtonComponent;
            case 'MultiSelectPickList':
                return MultiSelectPickListComponent;
            case 'NamedObject':
            case 'NamedSubentity':
                return NamedObjectComponent;
            case 'PageTabContainer':
                return PageTabComponent;
            case 'PIMetric':
                return PIMetricComponent;
            case 'RadioButton':
            case 'RadioButtonList':
                return RadioButtonComponent;
            case 'ReadOnly':
            case 'ReadOnlyDate':
            case 'ReadOnlyEnum':
                return ReadOnlyComponent;
            case 'RevisionedObject':
                return RevisionedObjectComponent;
            case 'Scales':
                return ScaleControlComponentBase;
            case 'ScrollableMenu':
                return ScrollableMenuComponent;
            case 'SlideMenu':
            case 'TaskListSlideMenu':
                return SlideMenuComponent;
            case 'StatusMessage':
                return StatusMessageComponent;
            case 'TextBox':
                return TextBoxComponent;
            case 'TextEditor':
                return TextEditorComponent;
            case 'TitleControl':
                return TitleComponent;
            case 'WorkflowNavigator':
                return WorkflowNavigatorComponent;
            case 'HTMLPanel':
                return HTMLPanelComponent;
            default:
                return GenericComponent;
        }
    }
}
