import { Component, Input, Output, EventEmitter, ViewChild, OnChanges, SimpleChanges} from '@angular/core';
import 'brace/index';
import 'brace/theme/eclipse';
import 'brace/snippets/text';
import 'brace/mode/xml';
import 'brace/snippets/xml';
import 'brace/ext/language_tools.js';
import 'brace/ext/searchbox';
import { StudioStateService } from '../../common/services/studioStateService';
import { Observable } from 'rxjs';

@Component({
    selector: 'xml-textEditor',
    host: { 'class': 'flex-column-grow' },
    template: `
        <div class="xml-editor">
            <ace-editor #editor [text]="xmlText | async" [mode]="'xml'" style="height:100%" (textChanged)="onValueChanged($event)"
                [durationBeforeCallback]="2000"
                [theme]="'eclipse'" 
                [options]="aceOptions" ></ace-editor>
        </div>
        `,
    styles:[`
            .xml-editor{
                height: 100%;
                width: 100%;
                padding-top: 1px;
            }
    `]
})

export class XmlTextEditorComponent implements OnChanges {
    @Input("xml") xmlText: Observable<string>;
    @Input() zoom: number;
    @Output() onTextChanged: EventEmitter<string> = new EventEmitter<string>();
    @ViewChild('editor', {static: true}) editor : any;

    constructor(
        private _studioService : StudioStateService
        ) {}

    public aceOptions: any = {
        enableBasicAutocompletion: true,
        enableSnippets: true,
        enableLiveAutocompletion: true
      };    

    ngOnChanges(ch : SimpleChanges){
        if( ch.zoom && ch.zoom.currentValue){         
            let fs = Math.round((12 * ch.zoom.currentValue) / 100.0);
            this.editor.getEditor().setFontSize(fs);
        }
    }

    onValueChanged(xml : string): void {
        this.onTextChanged.emit(xml);
    }

    getActiveLine(xml: string): number {
        if (xml && this._studioService.activeElement) {
            let activeElementName = this._studioService.activeElement.name;
            if (activeElementName) {
                return xml.indexOf("Name=\"" + activeElementName + "\"");
            }
        }
    }
}