import { Pipe, PipeTransform } from '@angular/core';
import { LabelService } from '../../common/services/label.service';

@Pipe({
    name: 'label',
})

export class LabelPipe implements PipeTransform {

    constructor(private _label: LabelService) { }

    transform(defaultText: string, labelName: string, ...labelArgs: any[]): any {
        if (!labelName) return;
        return this._label.getLabelwithArguments(labelName, defaultText, labelArgs);
    }
}