import {Injectable, Pipe, PipeTransform} from '@angular/core';
import { OMTypeDescriptor } from '../../common/objects/fieldDirectoryBase';

@Pipe({
    name: 'fieldDirectorySearchFilter',
    pure: false
})

@Injectable()
export class FieldDirectorySearchFilter implements PipeTransform {

    constructor() {
    }

    transform(inp: OMTypeDescriptor[], search: string): OMTypeDescriptor[] {

        let res: OMTypeDescriptor[] = [];
        let includedNested: boolean = false;
        search = search.toLowerCase();

        if (search) {

            inp.forEach(it => {
                if (it._nesting == 0 && it.Name.toLowerCase().indexOf(search) != -1) {
                    // service included 
                    res.push(it);

                    // include all nested items
                    includedNested = true;
                }
                else {
                    if (includedNested) {
                        if (it._nesting > 0) {
                            res.push(it);
                        }
                        else {
                            includedNested = false;
                        }
                    }
                } 
            });
        }
        else {
            res = inp;
        }
        return res;
    }
}
