import {Injectable, Pipe, PipeTransform} from '@angular/core';
import { IPropertyMetadata } from '../../common/objects/viewModel';
import { Group, ViewTabs } from './propertyList';

@Pipe({
    name: 'propertyFilter'
})

@Injectable()
export class PropertyFilter implements PipeTransform {

    transform(inp: IPropertyMetadata[] | Group[], complexFilter: any): IPropertyMetadata[] | Group[] {

        let prm: { groupMode: boolean, group: Group, search: string, tab: ViewTabs, favorites: string[] } = complexFilter;
        if (inp) {
            if (!prm.groupMode) {
                let filtered: IPropertyMetadata[] =
                    (<IPropertyMetadata[]>inp)
                        .filter(p2 => {
                            if (prm.tab == ViewTabs.favorites)
                                return prm.favorites ? prm.favorites.findIndex(fp => fp == p2.name.toLowerCase()) != -1 : false;

                            else if (prm.tab == ViewTabs.grouped) 
                                return p2.category.toLowerCase() == prm.group.category.name;

                            else if (prm.tab == ViewTabs.sorted) 
                                return true;                            
                        })
                        .filter(p3 => prm.search ? (p3.displayedName || p3.name).toLowerCase().indexOf(prm.search) != -1 : true);

                return filtered;
            }
            else {
                let ng = (<Group[]>inp).filter(g =>
                    (prm.tab == ViewTabs.favorites || prm.tab == ViewTabs.sorted) ?
                        g.category.name == "favorites" :
                        g.category.name != "favorites");
                if(ng.every(gs => !gs.expanded) ){
                    // expand first group if no one expanded
                    if( ng.length )
                        ng[0].expanded = true;
                }
                return ng;
            }
        }
        return inp;
    }
}