import { Component, Input, OnInit, OnDestroy, ElementRef, ViewChild, Renderer2, AfterViewChecked} from '@angular/core';
import { IPropertyMetadata } from '../../../common/objects/viewModel';
import { ISelectableItem, ValueUpdatedData, isIE } from '../../../common/objects/common';
import { Observable,Subject, Subscription } from 'rxjs';
import {map, switchAll} from "rxjs/operators";
//import { FlexibleConnectedPositionStrategy } from '@angular/cdk/typings/overlay/position/flexible-connected-position-strategy';

import { MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { MatOptionSelectionChange } from '@angular/material/core';
import _ from 'lodash';
import { PortalService } from '../../../common/services/portal.service';
import { FlexibleConnectedPositionStrategy } from '@angular/cdk/overlay/position/flexible-connected-position-strategy';

@Component({
    selector: 'csi-editor-selector',
    templateUrl: 'selector.html',
    styles: [`
        .csi-editor-selector{
            display: flex;
        }
        .input-container input.textInput {
            flex: 1 1;
            width: 20px;
            border: none;
            height: 25px;
            cursor: pointer;
            text-overflow: ellipsis;
            padding-left: 4px;
            margin-top: 1px;
            outline: none;
        }
        .input-container input[type=file] {
            display: none;
        }
        .input-container  {            
            display: flex;           
            flex: 1 1; 
            border: solid 1px #aaaaaa;
        }
        
        .input-container input::-ms-clear {
            display: none;
        }

        .selector-emptyOption  {
            min-height: 17px;            
        }

        .selector-option {
            text-overflow: inherit;
            width: 100%;            
            line-height: normal;
            height: auto;
            padding: 0 5px;
        }

        .option-text{
            display: inline-block;
            font-size: 12px;
            font-family: "Segoe UI";
            line-height: normal;
        }

        .popup-arrow {
            flex: 0 0 10px;
            background: no-repeat left url(Content/images/downarrow.png);        
            cursor: pointer;
        }

        .image-icon {
            height: 16px;
            width: 16px;
            align-self: center;
        }

        .upload-button{
            width: 18px;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            background-color: #eeeeee;
            cursor: pointer;
        }
        .upload-button.sound{
            width: 18px;
            background-size: 12px 12px;
        }
        .upload-button:hover{
            border: solid 1px #aaaaaa;
        }
        `
    ]
})

export class SelectorEditorComponent implements OnInit,  OnDestroy, AfterViewChecked {
    @Input("property") __property: any;
    @Input() valueChanged: Subject<ValueUpdatedData>;
    @Input() resized: Subject<boolean>;
    @Input() items: Observable<ISelectableItem[]>;
    @Input() value: number | string;

    @ViewChild(MatAutocompleteTrigger) acTrigger: MatAutocompleteTrigger;
    @ViewChild("uploadFileCtl") uploadFileCtl : ElementRef;

    get property(): IPropertyMetadata { return this.__property; }

    displayedIcon: string;
    textBoxClass: string;
    nameFilter = "";
    readOnly: boolean;

    uploadedFile: any;  // File
    uploadedFileName : string;
    fileExtAccept : string;
    showUploadBtn = false;
    fileItemType  : string;

    private canvas: HTMLCanvasElement;
    private canvasContext: CanvasRenderingContext2D;
    private oldTriggerFunctions: any;

    private fileDataReader = new Subject<any>();
    private fileDataReaderSubs : Subscription;
    private uploadDirectory : string;
    private uploadType : string;
    
    private uploadedFiles : ISelectableItem[]= [];
    private optionPanelWidth: number = 0;
    private acPatched = false;    

    constructor(private _render: Renderer2, private _portalService: PortalService) {
    }

    ngOnInit() {
        this.readOnly = this.property.readOnly;
        if(this.property.editor && this.property.editor.provider){
            let fp =  this.property.editor.provider;
            if(fp == "imageFiles"){
                this.uploadDirectory = "Images";
                this.fileExtAccept = ".png";
                this.uploadType = "image/png";
                this.fileItemType = "image";
                this.showUploadBtn = true;
            }
            else if(fp == "soundFiles"){
                this.uploadDirectory = "Sounds";
                this.fileExtAccept = ".mp3";
                this.uploadType = "audio/mpeg";    
                this.fileItemType = "sound";
                this.showUploadBtn = true;
            }
        }

        this.fileDataReaderSubs = this.fileDataReader.pipe(
                map(b => this._portalService.uploadFile(this.uploadedFileName, this.uploadDirectory, b) ),
                switchAll())
            .subscribe(() => {                    
            let newFile = <ISelectableItem>{ 
                name: this.uploadedFileName, 
                value: this.uploadedFileName, 
                type: this.fileItemType, 
                img: this.uploadDirectory + '/' + this.uploadedFileName,
                level: 0 
            };

            this.uploadedFiles.push(newFile);
            this.value = this.uploadedFileName;
            this.valChanged(newFile);
            
            this.items = this.items.pipe(map(m=> {
                // In case the uploaded images already contained in the list
                this.uploadedFiles = this.uploadedFiles.filter(i=> m.findIndex(mx=> mx.name == i.name) == -1 );
                return _.concat(m, this.uploadedFiles);
            }));

        });        
    }

    ngOnDestroy() {
        if( this.fileDataReaderSubs)
            this.fileDataReaderSubs.unsubscribe();

        if(this.acPatched)
            this.triggerUnPatch();
    }

    ngAfterViewChecked() {
        if( !this.acPatched && this.acTrigger ){
            this.triggerPatch();
            this.acPatched = true;
        }
    }

    filterTextChange(ev: Event){
        this.nameFilter = (<HTMLInputElement>ev.target).value;
    }

    clickDownArrow(ev: MouseEvent) {
        if (!this.readOnly && this.acTrigger.panelOpen!==true ) 
            this.acTrigger.openPanel();
        else 
            this.acTrigger.closePanel();
        ev.stopPropagation();
    }

    getMaxWidth(items: ISelectableItem[]): number {

        if (!this.canvas) {
            this.canvas = <HTMLCanvasElement>document.getElementById("selector-canvas");
            if (!this.canvas) {
                this.canvas = this._render.createElement("canvas");
                this.canvas.id = "selector-canvas";
            }
            if (!this.canvasContext) {
                this.canvasContext = this.canvas.getContext("2d");
                this.canvasContext.font = "12px Segoe UI";
            }
        }

        let maxOpt = _.maxBy(items, op => this.displayVal(op).length);
        if (maxOpt) {
            let txt = this.displayVal(maxOpt);
            let m = this.canvasContext.measureText(txt);
            let w = m.width + 10 /*padding*/ + (maxOpt.level * 10) /*indentation*/ + (maxOpt.type=="image"?16:0);
            // scrollbar in IE11 wider
            if( isIE )
                w += 22;
            return w;
        }

        return 0;
    }

    selectOption(opt: MatOptionSelectionChange, item: ISelectableItem) {
        if (opt.isUserInput) {
            this.valChanged(item);
        }
    }

    valChanged(item: ISelectableItem) {

        if (item.disabled) {
            // Do Nothing 
            return;
        }
        this.value = item.value;
        this.displayedIcon = item.img;
        if( this.fileItemType == "sound")
            this.displayedIcon = "Content/images/portalStudioIcons/sound.png";

        this.valueChanged.next(new ValueUpdatedData(this.property.name, item.value, item.fullType));
    }

    getDisplayedValue(items: ISelectableItem[]) : string {

        let dv;
        let v = this.value;
        if (v === undefined || v === null || v === '' ){
            if(items && items.length > 0){
                // Default value - first in the list
                dv = this.displayVal(items[0]);
                this.displayedIcon = items[0].img;
            }
            else {
                // Default value - blank
                dv = "";
                this.displayedIcon = null;    
            }
        }
        else {
            // value is not empty - resolve by items
            let comp: (si: ISelectableItem) => boolean;

            if ((v != null && v !== undefined) && typeof v == 'object' && Object.getPrototypeOf(v).hasOwnProperty("comparer"))
                comp = Object.getPrototypeOf(v).comparer;

            let d = items.find(i => comp ? comp.call(v, i) : (i.value === v));
            dv = d ? this.displayVal(d) : v;

            this.displayedIcon = d && d.img;

            if( this.fileItemType == "sound")
                this.displayedIcon = "Content/images/portalStudioIcons/sound.png";

            this.optionPanelWidth = this.getMaxWidth(items);
        }   
        
        return dv;
    }

    displayVal(item: ISelectableItem) {
        return item.name;
    }

    fileBtnClick() {
        let fileElement = this.uploadFileCtl.nativeElement as HTMLInputElement;
        if (fileElement) {
            fileElement.focus();
            fileElement.click();
        }
    }

    fileChanged(e : Event) {
        let fileName = "";
        let fileElement = this.uploadFileCtl.nativeElement as HTMLInputElement;
        if (fileElement) {
            if (fileElement.files && fileElement.files.length > 0) {
                let file : File = fileElement.files[0];
                if (file.type == this.uploadType) {
                    fileName = file.name;
                    this.fileReaderObs(file);
                }
            }
        }
        this.uploadedFileName = fileName;
    }

    private fileReaderObs(file : File) {
        let reader = new FileReader();
        reader.onload = () => {            
            // convert to byte array            
            let res : Uint8Array = new Uint8Array((<any> reader).result);
            let data : Uint8Array[] = [];
            for( let i = 0; i<res.length; i++)
                data.push(<any> res[i]);
            this.fileDataReader.next(data);
        }        
        reader.readAsArrayBuffer(file);
    }


    // next 2 methods may not be compatible with other Material version !!!
    private triggerPatch(): void {
        
        if (!this.acTrigger['_getHostWidth'] || !this.acTrigger['_getOverlayPosition']) {
            console.warn('MdAutocompleteTrigger does not have _getHostWidth or _getOverlayPosition.');
            console.error("Selector's option panel is not resized.");
            return;
        }
        this.oldTriggerFunctions = {};
        this.oldTriggerFunctions._getHostWidth = this.acTrigger['_getHostWidth'];
        this.acTrigger['_getHostWidth'] = () => {
            let oldWidth = this.oldTriggerFunctions._getHostWidth.call(this.acTrigger);
            return this.optionPanelWidth > oldWidth ? this.optionPanelWidth : oldWidth;
        };

        this.oldTriggerFunctions._getOverlayPosition = this.acTrigger['_getOverlayPosition'];
        this.acTrigger['_getOverlayPosition'] = () => {
            let ovrPos = <FlexibleConnectedPositionStrategy> this.oldTriggerFunctions._getOverlayPosition.call(this.acTrigger);
            ovrPos
                .withPositions([
                    { originX: 'end', originY: 'bottom', overlayX: 'end', overlayY: 'top' },
                    { originX: 'end', originY: 'top' , overlayX: 'end', overlayY: 'bottom' }
                ])
            return ovrPos;
        };
    }

    private triggerUnPatch(): void {
        if (this.oldTriggerFunctions) {
            this.acTrigger['_getHostWidth'] = this.oldTriggerFunctions._getHostWidth;
            this.acTrigger['_getOverlayPosition'] = this.oldTriggerFunctions._getOverlayPosition;
            delete this.oldTriggerFunctions;
        }
    }

}
