import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'csi-confirm-dialog',
    templateUrl: 'confirm.dialog.component.html'
})

export class ConfirmDialogComponent {
    context: ConfirmDialogContext;
    actions: DialogAction[];

    constructor(private _dialog: MatDialogRef<ConfirmDialogComponent> ) {}

    initContext(context: ConfirmDialogContext)
    {
        this.context = context;
        this.actions = this.context.actions;
    }

    execute(action: string): void {        
        this._dialog.close(action);
    }
}
export class DialogAction {
    action: string;
    name: string;   
    isPrimary: boolean = false;
}
export class ConfirmDialogContext {
    message: string;
    title: string;        
    actions: DialogAction[];
}


