import { Component, OnInit, OnChanges, Input } from '@angular/core';
import { IPersSection, IPersDataContract, IControl, IPersWebPart, IPersVirtualPageInfoSection, IPersControls, IElement, IUIComponentRef, PersSectionType } from '../../common/objects/common';
import { PanelUpdatedData } from '../../panel/panel';
import { PortalService } from '../../common/services/portal.service';
import { LabelService } from '../../common/services/label.service';
import { PersonalizationTypes } from '../../common/services/metadata.service';
import { StudioStateService } from '../../common/services/studioStateService';

import * as _ from 'lodash';

@Component({
    selector: 'csi-pers-report',
    host: {
        'style': 'flex-grow: 2; display: flex; flex-direction: column;'
    },
    templateUrl: 'personalizationReport.component.html'
})

export class PersonalizationReportComponent implements OnInit, OnChanges {
    @Input("element") __element: any;
    get element(): IElement { return this.__element; }
    set element(v: IElement) { this.__element = v; }

    constructor(private _studioStateService: StudioStateService, private _labelService: LabelService, private _portalService: PortalService) {
    }

    private metadata: PersonalizationTypes;
    private controls: IControl[];
    //private errorMessage: any;
    sections: IPersSection[] = [];
    webpartSections: IPersWebPart[] = [];

    // Component Labels
    private vpSectionLbl: string;
    private descrLbl: string;
    private titleLbl: string;
    private behaviorLbl: string;
    private primarySvcLbl: string;
    private lastModLbl: string;
    private lastModByLbl: string;
    private codeBehindLbl: string;
    private createdByLbl: string;
    private typeNameLbl: string;
    private workspaceLbl: string;
    private dataContractsLbl: string;
    private nameLbl: string;
    private connTypeLbl: string;
    private keyLbl: string;
    private propLbl: string;
    private openWebPartLbl: string;
    private controlNameLbl: string;
    private controlLbl: string;

    ngOnInit(): void {        
        this.load();
    }

    ngOnChanges(ch): void {
        this.load();
    }

    private parseDateString(dateStr: string): string {
        let formattedStr: string = '';
        if (!isNaN(Date.parse(dateStr))) {
            let hour: number = Number(dateStr.split('T')[1].split(':')[0]);
            let min: number = Number(dateStr.split('T')[1].split(':')[1]);
            let ampm: string = hour >= 12 ? 'PM' : 'AM';
            let year: number = Number(dateStr.split('-')[0]);
            let month: number = Number(dateStr.split('-')[1]);
            let day: number = Number(dateStr.split('-')[2].substr(0, dateStr.split('-')[2].indexOf('T')));
            let d = new Date(year, month, day, hour, min);
            hour = d.getHours() <= 12 ? d.getHours() : d.getHours() - 12;
            let minStr: string = min >= 10 ? min.toString() : '0' + min.toString()
            formattedStr = month.toString() + '/' + day.toString() + '/' + year.toString() + ' ' + hour + ':' + minStr + ' ' + ampm;
        }
        return formattedStr;
    }

    private parsePropertyName(propName: string): string {
        let newName: string = '';
        for (var i = 0; i < propName.length; i++) {
            let cur = propName.charAt(i);
            if (i === 0) {
                newName += cur.toUpperCase();
            }
            else {
                if (cur === propName.charAt(i).toUpperCase()) {
                    newName += ' ' + cur;
                }
                else {
                    newName += cur;
                }
            }
        }
        return newName;
    }

    private loadLabels() {
        this.vpSectionLbl = this._labelService.getLabel('PS_VPSECTIONNAMELBL', 'Virtual Page Basic Information');
        this.descrLbl = this._labelService.getLabel('PS_DESCRIPTIONLBL', 'Description');
        this.titleLbl = this._labelService.getLabel('PS_TITLELBL', 'Title');
        this.behaviorLbl = this._labelService.getLabel('PS_BEHAVIORLBL', 'Behavior');
        this.primarySvcLbl = this._labelService.getLabel('PS_PRIMARYSVCLBL', 'Primary Service Type');
        this.lastModLbl = this._labelService.getLabel('PS_LASTMODIFIEDLBL', 'Last Modified');
        this.lastModByLbl = this._labelService.getLabel('PS_LASTMODIFIEDBYLBL', 'Last Modified By');
        this.codeBehindLbl = this._labelService.getLabel('PS_CODEBEHINDLBL', 'Code Behind');
        this.createdByLbl = this._labelService.getLabel('PS_CREATEDBYLBL', 'Create By');
        this.typeNameLbl = this._labelService.getLabel('PS_TYPENAMELBL', 'Type Name');
        this.workspaceLbl = this._labelService.getLabel('PS_WORKSPACELBL', 'Workspace');
        this.dataContractsLbl = this._labelService.getLabel('PS_DATACONTRACTSLBL', 'Data Contracts');
        this.nameLbl = this._labelService.getLabel('PS_NAMELBL', 'Name');
        this.connTypeLbl = this._labelService.getLabel('PS_CONNECTIONTYPELBL', 'Connection Type');
        this.keyLbl = this._labelService.getLabel('PS_KEYLBL', 'Key');
        this.propLbl = this._labelService.getLabel('PS_PROPERTYLBL', 'Property');
        this.openWebPartLbl = this._labelService.getLabel('PS_OPENWEBPARTLBL', 'Open Web Part');
        this.controlNameLbl = this._labelService.getLabel('PS_CONTROLNAMELBL', 'Control Name');
        this.controlLbl = this._labelService.getLabel('PS_LAYOUT_CONTROLS', 'Controls');
    }

    private async load() {
        let md = await this._portalService.getMetadata().toPromise();
        if (md) {
            this.metadata = md;
        }
        let ctrls = await this._portalService.getControls().toPromise();
        if (ctrls) {
            this.controls = ctrls;
        }

        this.loadLabels();
        let sections: IPersSection[] = [];
        let webpartSections: IPersWebPart[] = [];
        this._portalService.getElement(this.element.name, this.element.type).subscribe(resp => {
            if (resp.type === 'Page') {
                let behaviorType: string = '';
                if (this.metadata) {
                    let behaviorTypeMD = this.metadata['pagebehaviortype'];
                    if (behaviorTypeMD) {
                        let enumVals = behaviorTypeMD.enumValues;
                        if (enumVals) {
                            behaviorType = enumVals[resp.properties['behavior']].name;
                        }
                    }
                }
                let vpInfoSection: IPersVirtualPageInfoSection = {
                    Name: this.vpSectionLbl,
                    Description: resp.properties['description'],
                    DescriptionLbl: this.descrLbl,
                    PageName: resp.name,
                    Title: resp.properties['title'],
                    TitleLbl: this.titleLbl,
                    Behavior: behaviorType,
                    BehaviorLbl: this.behaviorLbl,
                    PrimaryServiceType: resp.properties['primaryServiceType'],
                    PrimaryServiceTypeLbl: this.primarySvcLbl,
                    LastModified: this.parseDateString(resp.properties['lastModified']),
                    LastModifiedLbl: this.lastModLbl,
                    LastModifiedBy: resp.properties['lastModifiedBy'],
                    LastModifiedByLbl: this.lastModByLbl,
                    CodeBehind: resp.properties['codeBehind'],
                    CodeBehindLbl: this.codeBehindLbl,
                    CreatedBy: resp.properties['createdBy'],
                    CreatedByLbl: this.createdByLbl,
                    IsOpen: true,
                    Type: PersSectionType[PersSectionType.VirtualPageInfoSection],
                    TypeName: resp.properties.typeName,
                    TypeNameLbl: this.typeNameLbl,
                    Workspace: resp.properties.workspaceCode,
                    WorkspaceLbl: this.workspaceLbl
                };               

                if (resp.properties && resp.properties.workspaceCode) {
                    vpInfoSection.Workspace = resp.properties.workspaceCode
                }

                vpInfoSection.DataContracts = this.getDataContractsForSection(resp);
                sections.push(vpInfoSection);
            }
            else if (resp.type == 'Webpart') {
                let wpsection = this.parseWebpartItem(resp);
                if (wpsection) {
                    webpartSections.push(wpsection);                    
                }
            }

            if (resp.items) {
                _.forEach(resp.items, i => {
                    if (i.type === 'Webpart') {
                        let wpsection: IPersWebPart = this.parseWebpartItem(i);
                        if (wpsection) {
                            wpsection.WorkspaceContent.push(wpsection);
                            webpartSections.push(wpsection);                            
                        }
                    }
                });
            }
            this.sections = sections;
            this.webpartSections = webpartSections;
            this._studioStateService.activateElement(this.element);
        });
    }

    private parseWebpartItem(wp: any): IPersWebPart {
        let wpsection: IPersWebPart = {
            IsOpen: true,
            Name: wp.name,
            TypeName: wp._fullTypeName,
            TypeNameLbl: this.typeNameLbl,
            Controls: [],
            DataContracts: [],
            WebPartName: wp.name,
            NameLbl: this.nameLbl,
            Title: wp.properties ? wp.properties.title : '',
            TitleLbl: this.titleLbl,
            WorkspaceContent: [],
            Workspace: '',
            WorkspaceLbl: this.workspaceLbl,
            IsReference: wp.isReference,
            Type: PersSectionType[PersSectionType.WebPart],
            OpenWebPartLbl: this.openWebPartLbl,
            ControlLbl: this.controlLbl
        };
          
        wpsection.DataContracts = this.getDataContractsForSection(wp);
                
        if (wp.items) {
            let controls: any[] = [];
            _.forEach(wp.items, i => {
                if (i.type == 'Control') {
                    let cntrl = this.parseControlItem(i);
                    controls.push(cntrl);
                    wpsection.Controls = controls;
                }
                else if (i.type == 'Webpart') {
                    this.parseWebpartItem(i);
                }
            });            
        }
        return wpsection;
    }

    private parseControlItem(c: IElement): IPersControls {
        let cntrl: IPersControls = {
            Name: c.name,
            NameLbl: this.controlNameLbl,
            IsOpen: true,
            ControlProperties: [],
            Workspace: '',
            Type: PersSectionType[PersSectionType.Control]
        };

        let cntrlProps = this.parseProperties(c.properties, c.typeName);
        cntrl.ControlProperties = cntrlProps;

        return cntrl;
    }

    private parseProperties(props: any, propType: string): any[] {
        let ary: any[] = [];        

        for (var p in props) {
            if (p !== 'name') {
                let prop: any = {
                    Name: '',
                    Value: ''
                };
                // Enums
                if (this.metadata) {
                    let md = this.metadata[p.toLowerCase() + 'type'] ? this.metadata[p.toLocaleLowerCase() + 'type'] : this.metadata[p.toLowerCase() + 's'];
                    if (md && md.enumValues) {
                        prop.Name = this.parsePropertyName(p);
                        prop.Value = md.enumValues[props[p]].name;
                    }
                    else {                        
                        let pType = this.metadata[propType.toLowerCase()];
                        if (pType) {
                            // Get control property to check if enum
                            let cntrlProps = pType.properties;
                            if (cntrlProps && cntrlProps.length) {
                                let cp = cntrlProps.find(x => {
                                    return x.name.toLowerCase() == p.toLowerCase();
                                });
                                if (cp) {
                                    // Check if control property is an enum
                                    let mdType = this.metadata[cp.originalTypeName.toLowerCase()];
                                    if (mdType && mdType.enumValues) {
                                        prop.Name = this.parsePropertyName(p);
                                        // Set property value to enum name
                                        prop.Value = mdType.enumValues[props[p]].name;
                                    }
                                    else {
                                        let valObj = props[p];
                                        if (!(valObj instanceof (Object))) {
                                            prop.Name = this.parsePropertyName(p);
                                            if (prop)
                                                prop.Value = valObj;
                                        }
                                        else {
                                            let valCntrl = this.controls.find(x => {
                                                return x.type == valObj.type;
                                            });
                                            if (valCntrl) {
                                                prop.Name = this.parsePropertyName(p);
                                                prop.Value = valCntrl.typeName;
                                            }
                                        }
                                    }
                                }                                
                            }                            
                        }
                        else {
                            let cntrlMD = this.controls.find(x => {
                                return x.name.toLowerCase() == propType.toLowerCase();
                            });
                            if (cntrlMD) {
                                prop.Name = cntrlMD.name;
                                prop.Value = cntrlMD.typeName;
                            }
                            else {
                                prop.Name = this.parsePropertyName(p);
                                prop.Value = '';
                            }
                        }
                    }
                    if (prop.Value) {
                        ary.push(prop);
                    }
                }
            }
        }
        return ary;
    }

    private getDataContractsForSection(element: any): IPersDataContract[] {         
        let ary:IPersDataContract[] = [];
        let dcSection: IPersDataContract = {
            Name: this.dataContractsLbl,
            IsOpen: true,
            HeaderRows: [
                this.nameLbl, this.connTypeLbl, this.keyLbl, this.propLbl
            ],
            Type: PersSectionType[PersSectionType.DataContract],
            SortAsc: true,           
            DataMembers: [],
            DataMembersLbl: this.dataContractsLbl
        };        
        if (element.properties && element.properties.dataContract && element.properties.dataContract.dataMembers) {               
            dcSection.DataMembers = _.map(element.properties.dataContract.dataMembers, x => {               
                return {
                    Name: x["property"],
                    ConnectionType: x["name"],
                    Key: x["__type"],
                    Property: x["key"]
                };             
            });
            ary.push(dcSection);
        }        
        return ary;
    }

    openReferencedWebPart(refWebPart: IPersWebPart) {       
        let cmd = new PanelUpdatedData();               
        cmd.open = true;
        let elementRef: IUIComponentRef = <IUIComponentRef> {
            name: refWebPart.Name,
            type: 'Webpart',
            isStatic: !refWebPart.IsReference,
            isLocked: false,
            isReference: refWebPart.IsReference,
            isInDeveloping: false,
            hasPublishContent: false,
            owner: ''
        };
        cmd.elementRef = elementRef;
        this._studioStateService.subjects.panel.next(cmd);                    
    }

    toggleVisibility(s: IPersSection) {            
        s.IsOpen = !s.IsOpen;      
    }   
}