import { IElement, IUIDesigner, IUIComponentRef, StartPage, StudioSubjects, UpdatedInfo, PropertyUpdatedData, val, ILabel, DesignModes, LayoutActions, TopMenuState} from '../objects/common';
import { WorkspaceItem, IPropertyMetadata } from '../objects/viewModel';
import { LayoutCell, ICell, ICellItem } from '../../layout/design/objects/layoutCell';
import { HistoryLog, LogItem, ElementHistoryData, LayoutHistoryData, RecordCommand, HistoryObjectType, PropertyHistoryData, HistoryOperation, IHistory } from '../objects/historyLog';
import * as _ from 'lodash';
import { map } from 'rxjs/operators';
import { of, Observable } from 'rxjs';
import { PanelSettings } from '../../panel/panel';
import { LayoutManager } from '../../layout/design/objects/layoutManager';
import { Injectable } from "@angular/core";

export class CellVisitorContext {
    owner: IElement;
    data: any;
    tabContainer: IElement;

    constructor(_data: any = {}) {
        this.data = _data;
    }
};
export declare type CellVisitor = (cell: LayoutCell, context: CellVisitorContext) => boolean;

export class ItemVisitorContext {
    owner: IElement;
    data: any;
    tabContainer: IElement;

    constructor(public searchingItem: any, _data: any = {}) {
        this.data = _data;
    }
};
export declare type ItemVisitor = (container: IElement, context: ItemVisitorContext) => boolean;

@Injectable()
export class StudioStateService {
    public openedElements: IElement[];
    public cachedWebparts: IUIDesigner[];
    public webPartList : any;

    public activeRootElement: any;
    
    public get activeElement(): any {return this.getRoot() && this.getRoot().__selectedElement ; }
    public set activeElement(e: any) {if(this.getRoot())  this.getRoot().__selectedElement = e; }

    public get activeWorkspace(): number { return this.activeRootElement && this.activeRootElement.__workspace !== undefined ? this.activeRootElement.__workspace : -1; }
    public set activeWorkspace(ws: number) { this.activeRootElement.__workspace = ws; }

    public panels: IUIComponentRef[] = [];
    public displayCellPosition = false;

    public hideStart: boolean;
    public showSplash: boolean;

    public subjects: StudioSubjects;
    public elementInDesign: IElement;

    private _checkedElements: { n: string, t: string }[];
 
    get checkedElements(){ return this._checkedElements;}
    set checkedElements(val){ this._checkedElements = val;}

    private workspaces: WorkspaceItem[];
    _alertService: any;

    constructor() {
        this.openedElements = [];
        this.cachedWebparts = new Array();
        this.hideStart = false;
        this.showSplash = true;
        this.subjects = new StudioSubjects();
        this._checkedElements = [];

        //setup startPage
        let startPage = new StartPage('Start Page', 'StartPage');
        this.addOpenedElement(startPage);
    }

    public checkAll(AllCheckedElements:any, items: any, doCheck: boolean, checkedSubj: any) {
        let tempCheckedElements;
        items.forEach(items => {
                AllCheckedElements = items.map(el => <any>{ n: el.name, t: el.type });
            if (doCheck){
                tempCheckedElements = this._checkedElements;
                 AllCheckedElements = AllCheckedElements.filter(function(el){
                    return tempCheckedElements.findIndex((item) => item.n === el.n) === -1;
                });

                this._checkedElements = this._checkedElements.concat(AllCheckedElements);
            }
            else{   
                let AllCheckedElements = items.map(el => <any>{ n: el.name, t: el.type });
                this._checkedElements = this._checkedElements.filter( function(el) { 
                    return AllCheckedElements.findIndex( (item) => item.n === el.n) === -1;
                });                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                         
            };
            checkedSubj.next(this._checkedElements);
        }); 
    }

    public checkElement(el: IUIComponentRef, checkedSubj: any): void {
            let foundIndex = this._checkedElements.findIndex(e => e.n == el.name && e.t == el.type);
            if (foundIndex != -1)
            this._checkedElements.splice(foundIndex, 1);
            else
            this._checkedElements.push({ n: el.name, t: el.type });
            
            checkedSubj.next(this._checkedElements);
    }

    public isChecked(name: string, type: string): boolean {
        return this._checkedElements.findIndex(e => e.n == name && e.t == type) != -1;
    }

    public getRoot(): IUIDesigner {
        return <IUIDesigner>(this.activeRootElement);
    }

    public addOpenedElement(element: IElement): void {
        if (element !== undefined) {
            if( this.isDesignedElement(element) && (element as IUIDesigner).scaleValue === undefined)
                (element as IUIDesigner).scaleValue = 100;

            this.openedElements = [...this.openedElements, element];
            this.activateRootElement(element);
            this.activateElement(element);
        }
    }

    public activateElement(element: IElement): void {
        const root = this.getRoot();
        if( !element || !element.type ){
            root.__selectedElement = null;
        }

        if( element ){
            if( !(element.__maint || element.__unbound)){
                this.refreshHowLong(root);                
                let oldSelectedOwner = root.__selectedElement ? this.findOwnerOfElement(<IElement> root.__selectedElement) : null;
                root.__selectedElement = element as IElement;
                this.refreshElement(this.findOwnerOfElement(element));
                if( oldSelectedOwner ){
                    this.refreshElement(oldSelectedOwner);
                }
            }
        }

        this.refreshTopMenu();
    }

    public removeItem(el: IElement, owner:IElement = null){
        const k = _.findKey(owner.items, w=> w.name == el.name);
        if(k){
            delete owner.items[k];
            if( el.type.toLowerCase() == "webpart"){
                owner.removedWebParts = owner.removedWebParts || [];
                owner.removedWebParts.push(el.name);
            }
        }
    }

    public activateCell(cell: ICell): void {

        if( !cell )
            return;

        this.getRoot().__selectedElement = cell;

        this.subjects.propertyPanel.next(cell);
        this.refreshTopMenu();
    }

    public updateLayoutMenu(layoutActions: LayoutActions[]){
        this.subjects.layoutMenuUpdate.next(layoutActions);
    }

    public refreshElement(element: any){
        if( !element || (element && element.isRoot) )
            this.refreshRoot();
        else
            this.subjects.refresh.next(element);
    }

    public refreshControl(control: IElement){
        const owner = this.findOwnerOfElement(control);
        this.subjects.refresh.next(owner);
    }

    public propertyChanged(p: [any, any, IPropertyMetadata, any]){
        // let newValue : any = p[0];
        // let oldValue : any = p[1];
        // let property : IPropertyMetadata = p[2];
        let instance : any = p[3];
        this.setDirty();
        let owner = this.findOwnerOfElementOrCell(instance);
        if(owner)
            this.refreshElement(owner);
        this.refreshTopMenu();
    }

    public refreshElementPanel(panelType: string){
        this.subjects.refreshElementPanel.next(panelType);
    }

    public refreshHowLong(element): void {
        if (element && this.activeRootElement === element) {
            let properties = this.activeRootElement.properties;
            if (properties && properties["lastModified"]) {
                let ms = Date.parse(properties["lastModified"]);
                if (ms > 0) {
                    let diffMin = Math.floor((Date.now() - ms) / (1000 * 60));
                    let days = Math.floor(diffMin / (60 * 24));
                    let howLongValue = "";
                    if (days > 0) {
                        howLongValue = days + " days, ";
                        diffMin -= days * 60 * 24;
                    }
                    let hours = Math.floor(diffMin / 60);
                    if (hours > 0) {
                        howLongValue += hours + " hours, ";
                        diffMin -= hours * 60;
                    }
                    if (diffMin >= 0)
                        howLongValue += diffMin + " mins";
                    properties["howLong"] = howLongValue;
                }                
            }            
        }
    }

    public activateRootElement(element: IElement = null): boolean {
        if( !element )
            element = this.getRoot();

        //only activate existing elements
        let existingElement = this.openedElements.find(oe => oe == element);
        if (existingElement && existingElement != this.activeRootElement) {

            if (this.activeRootElement && this.activeRootElement.id == existingElement.id)
                return false;

            this.activeRootElement = existingElement;

            if (!this.activeRootElement.__designMode)
                this.activeRootElement.__designMode = DesignModes.Controls;

            return true;
        }
        return false;
    }

    public activateExistingOpenedElement(elementName: string): IElement {
        var len = this.openedElements.length;
        for (var i = 0; i < len; i++)
            if (this.openedElements[i].name === elementName) {
                this.activateRootElement(this.openedElements[i]);
                return this.openedElements[i];
            }
        return null;
    }

    public replaceRoot(e:IUIDesigner){
        let existingElementIndex =  this.openedElements.findIndex( oe => oe.name == e.name);
        if( existingElementIndex != -1 ){
            this.openedElements[existingElementIndex] = e;
        }
    }

    public updatePropertyPanel(ref : any){
        this.subjects.propertyPanel.next(ref);
    }

    public searchPanelText(panel:PanelSettings, txt: string){
        panel.searchInput.next(txt);
    }

    public refreshRoot(displayMode?:DesignModes){
        let r = this.getRoot();
        this.subjects.refreshRoot.next(displayMode || r.__designMode);
        if( r.type == "Webpart" )
            this.subjects.refresh.next(<any>r);
    }

    public setDirty() {
        this.getRoot().isDirty = true;
        this.refreshTopMenu();    // just refresh the action menu
    }

    public layoutCommand(cmd:LayoutActions){
        const layoutManager: LayoutManager = this.elementInDesign && this.elementInDesign.__layoutManager;
        if( layoutManager ){
            layoutManager.executeCommand(cmd);            
            this.setDirty();
            this.refreshElement(this.elementInDesign);
        }
    }

    public selectCell(cell: LayoutCell){
        let cellOwner = this.findCellOwner(cell);
        if (cellOwner && cellOwner['__type'] == 'TabDefinition') {
            cellOwner = this.findCellTabContainer(cell);
        }
        
        if (cellOwner) {
            if(this.elementInDesign && this.elementInDesign != cellOwner){
                // clear old selecton
                this.elementInDesign.__layoutManager.clearSelection();
            }
            this.elementInDesign = cellOwner;
            let layoutManager: LayoutManager = cellOwner.__layoutManager;
            if( layoutManager.selectCell(cell)){
                this.activateCell(cell);
                this.updateLayoutMenu(layoutManager.getCommands());
            }            
        }
    }

    public replaceElement(oldElement: IElement, newElement: IElement): void {
        let index = _.findIndex(this.openedElements, ['name', newElement.name]);
        if (index > -1) {
            this.openedElements[index] = newElement;
            this.activateRootElement(newElement);
        }
    }

    public removeOpenedElement(element: IElement): void {
        let index = this.openedElements.indexOf(element);
        if (index > -1) {
            //Remove from openedElements
            this.openedElements.splice(index, 1);

            //Reset Active Elements 
            if (this.openedElements.length > 0) {
                let newElement = this.openedElements[this.openedElements.length - 1]
                this.activateRootElement(newElement);
                this.activateElement(newElement);
            }
            else {
                this.activateElement(null);
                this.activeRootElement = null;
            }
        }
    }

    public getUniqueName(name: string, items: any, nameKey: string = "name") : string {
        let txt: string = name;
        let index: number = 0;

        // find digital tail of existing name
        let rx = /(\d+)(?!.*\d)/.exec(name);
        if(rx){
            txt = name.substr(0, rx.index);
            index = parseInt(rx[0]);
        }

        while(_.some(items, it => val(it, nameKey) === name)){
            name = txt + (index++).toString();            
        }

        return name;
    }

    //Edit Element Methods        
    public addElement(newElement: any, cell: LayoutCell | ICell): boolean {
        let cellOwner = newElement.type == "Webpart" ? this.activeRootElement : this.findCellOwner(cell);
        if (!cellOwner)
            return false;
        let newName = this.getUniqueName(newElement.name, cellOwner.items);
        newElement.name = newName;
        if(newElement.properties)
            newElement.properties.name = newName;

        cellOwner.items = cellOwner.items || {};
        cellOwner.items[newElement.id] = newElement;

        if( newElement.type == "Webpart"){
            // Add items from the added element to the root
            _.filter(newElement.items, it => !_.isEmpty(it.items))
                .forEach(w=>cellOwner.items[w.id] = w);
        }
        
        let cellItem: ICell = cell;
        if (cell.cell)
            cellItem = cell.cell;

        // add reference to the cell items
        cellItem.items = cellItem.items || {};        
      let nextKey=  this.generateNewIndex(cellItem)

        cellItem.items[nextKey] = 
            <ICellItem> {
                _ref: true, 
                id: newElement.id, 
                name: newElement.name, 
                type: newElement.type.toLowerCase()  
            };            
        cellItem.isEmpty = false;

        this.setDirty();
     
        return true;
    }

    public replaceWebpart(old: IElement, wp: IElement){
        let owner = this.findOwnerOfElement(old);
        let el = owner.items[old.id];

        // replace exising
        el.items = wp.items;
        el.properties = wp.properties;
        el.layout = wp.layout;
        el.typeName = wp.typeName;                
    }

    public changeElementName(newName: string): void {
        // If the name of page or webpart changed the name in the tab will not be changed until saving
        if (this.activeElement != this.activeRootElement) {
            // Sync changes in references
            let cont: CellVisitorContext = new CellVisitorContext({ oldName: this.activeElement.name, newName: newName });
            let vis: CellVisitor = (cell: LayoutCell, context: CellVisitorContext) => {
                let stop = false;
                _.forEach(cell.items, it => {
                    if (it.name == cont.data.oldName) {
                        it.name = cont.data.newName;
                        stop = true;
                    }
                });
                return stop;
            };
            this.cellIterator(this.activeRootElement, vis, cont);
            this.activeElement["name"] = newName;

            this.subjects.nameChanged.next(newName);
        }
    }

    public historyDo(op: HistoryOperation): Promise<UpdatedInfo> {
        let upd = new UpdatedInfo();
        let cell: LayoutCell;
        let hist: HistoryLog = this.activeRootElement.history;
        let item: LogItem = hist.pop(op);
        if (item) {
            let oType = item.specificData.objectType;
            if (oType == HistoryObjectType.Control || oType == HistoryObjectType.Webpart) {
                let data = <ElementHistoryData>(item.specificData);
                let itemCmd = item.command;
                if (op == HistoryOperation.redo) {
                    // swap operation opposite
                    if (itemCmd == RecordCommand.add)
                        itemCmd = RecordCommand.remove;
                    else if (itemCmd == RecordCommand.remove)
                        itemCmd = RecordCommand.add;                    
                }
                let owner : IElement;
                switch (itemCmd) {          
                    case RecordCommand.remove:
                        // The element should be added back to the cell
                        cell = this.findCellById(this.activeRootElement, data.cellId);
                        if (cell){
                            this.addElement(data.element, cell);
                            owner = this.findOwnerOfElementOrCell(cell);
                        }
                        break;
                    case RecordCommand.add:
                        // The element should be removed from the cell
                        owner = this.findOwnerOfElementOrCell(data.element);
                        this.removeElement(data.element);
                        break;
                    case RecordCommand.move:
                        // The element should be moved from the cell to fromCell
                        cell = this.findCellById(this.activeRootElement, op == HistoryOperation.undo ? data.fromCellId : data.cellId);
                        if (cell){
                            this.moveElement(data.element, cell);
                            owner = this.findOwnerOfElement(data.element);
                        }
                        break;
                    case RecordCommand.replace:
                        if( op == HistoryOperation.undo)
                            this.replaceWebpart(data.element, data.oldElement);
                        else
                            this.replaceWebpart(data.oldElement, data.element);
                        break;
                }
                if( owner )
                    this.refreshElement(owner);
                else
                    this.refreshRoot();
            }
            else if (oType == HistoryObjectType.Layout) {
                let data = <LayoutHistoryData>(item.specificData);
                data.layoutOwner.layout.tableCells = op == HistoryOperation.undo ? data.layoutBefore : data.layoutAfter;
                this.refreshRoot();
            }
            else if (oType == HistoryObjectType.Property) {
                let data = <PropertyHistoryData>(item.specificData);
                if (item.command == RecordCommand.propertyChange) {
                    // restore value
                    let vals = op == HistoryOperation.undo ? data.oldValues : data.newValues;
                    _.forEach(vals, (v, propertyName: string) => {
                        propertyName = propertyName.toLowerCase();
                        let properties = data.instance.properties || data.instance;
                        let pk = _.findKey(properties, (v, k: string) => k.toLowerCase() == propertyName) || propertyName;
                        upd.data = new PropertyUpdatedData(v, properties[pk], pk);
                        properties[pk] = v;
                    });
                    this.selectItem(data.instance);
                }
            }
        }

        return Promise.resolve<UpdatedInfo>(upd);
    }

    public selectItem(item){
        this.subjects.propertyPanel.next(item);
    }

    public cellIterator(cellOwner: IElement, visitor: CellVisitor, context: CellVisitorContext): boolean {
        let stop = false;

        if (!cellOwner.isReference && cellOwner.layout && cellOwner.layout.tableCells) {
            _.forEach(cellOwner.layout.tableCells, rows => {
                if (stop) return false;

                _.forEach(rows, cell => {
                    if (stop) return false;

                    context.owner = cellOwner;
                    stop = visitor(cell, context);
                    if (stop) return false;
                        
                    _.forEach(cell.items, it => {
                        if (stop) return false;
                        if (it.layout) {
                            stop = this.cellIterator(it, visitor, context);
                            if (stop) return false;                                
                        }                    

                        let item = it;
                        if (it._ref === true) {
                            if (it.type == "webpart")
                                item = _.find<IElement>(this.activeRootElement.items, i => i.name == it.name);
                            else
                                item = _.find<IElement>(cellOwner.items, i => i.name == it.name);

                            if (!item) 
                                console.warn(it.name + ' is not found ', it);                                
                        }
                        if (item) {
                            if (item.properties && item.properties.tabDefinitions) {
                                context.tabContainer = item;
                                _.forEach(item.properties.tabDefinitions, tab => {
                                    stop = this.cellIterator(tab, visitor, context);
                                    if (stop)
                                        return false;
                                });
                            }
                            else if (item.layout) {
                                stop = this.cellIterator(item, visitor, context);
                                if (stop) return false;
                            }
                        }
                    });
                });
            });
        }
        return stop;
    }

    public itemIterator(itemContainer: IElement, visitor: ItemVisitor, context: ItemVisitorContext): boolean {
        let stop = false;

        if (itemContainer.items) {
            stop = visitor(itemContainer, context);
            if (stop) return true;

            _.forEach(itemContainer.items, (item) => {
                stop = this.itemIterator(item, visitor, context);
                if (stop) return false;
            });
        }
        return stop;
    }

    public findOwnerOfItem(el: IElement): IElement {

        let visitor: ItemVisitor = (container: IElement, context: ItemVisitorContext): boolean => {
            if (_.findKey(container.items, it => it == context.searchingItem)) {
                context.data.found = container;
                return true;       // stop iteration
            }
            return false; // continue iterating
        };

        let visitorContext = new ItemVisitorContext(el, { found: null });
        if (this.itemIterator(this.activeRootElement, visitor, visitorContext))
            return visitorContext.data.found;

        return undefined;

    }

    public findItemByName(itemName : string): IElement {

        let visitor: ItemVisitor = (container: IElement, context: ItemVisitorContext): boolean => {
            let v = _.find(container.items, it => it.name == context.searchingItem)
            if (v) {
                context.data.found = v;
                return true;       // stop iteration
            }
            return false; // continue iterating
        };

        let visitorContext = new ItemVisitorContext(itemName, { found: null });
        if (this.itemIterator(this.activeRootElement, visitor, visitorContext))
            return visitorContext.data.found;

        return undefined;
    }

    public findChildElement(name: string, cellId?: string) : IElement {
        let cell = this.findCellById(this.activeRootElement, cellId);
        if( cell ){
            let owner = this.findCellOwner(cell); // owner = webpart or page
            if( owner )
                return _.find(owner.items, c=> c.name == name);
        }

        return null;
    }

    public removeElement(element: IElement) {
        //remove element from items list
        let cell = this.findCellByElement(element);
        let cellOwner = this.findCellOwner(cell);
        let removed = false;
        const lm = cellOwner.__layoutManager;        
        
        if( lm ){
            removed = lm.removeCellItem(cell, element);
            if( removed ) 
                this.removeItem(element, cellOwner);
        }            
    }

    public removeElementFromCell(el?: IElement, action?:string){
        el = el || this.activeElement;
        let cell = this.findCellByElement(el);
        let owner = this.findCellOwner(cell);

        let removed = false;
        const lm = owner.__layoutManager;        
        if( lm ){
            removed = lm.removeCellItem(cell, el);
            if( removed ) 
                this.removeItem(el, owner);
        }

        this.getRoot().isDirty = removed;
        if (removed) {
            this.setDirty();

            if(action !== "move"){
                let hr = new ElementHistoryData(this.activeElement.type == 'Control' ? HistoryObjectType.Control : HistoryObjectType.Webpart);
                hr.cellId = cell.id;
                hr.element = this.activeElement;
                this.writeHistory(RecordCommand.remove, hr);
            }
          
            this.activateElement(owner);
            this.refreshElement(owner);        
            this.refreshTopMenu(); 
        }

        return removed;
    }

    public moveElement(sourceElement: IElement, targetCell: LayoutCell, sourceCell?: LayoutCell, sourceOwner? : IElement, copyEl?: IElement ): boolean {
        let ret = false;
        if (sourceElement) {
            sourceCell = sourceCell || this.findCellByElement(sourceElement);
            sourceOwner = sourceOwner || this.findCellOwner(sourceCell);
            let targetOwner = this.findCellOwner(targetCell);
            if(sourceOwner.name == targetOwner.name ){
                // move item in the same owner
                const k = _.findKey(sourceCell.items, i=> i.name == sourceElement.name);
                if(k && copyEl===undefined){
                    let ref = sourceCell.items[k];
                   delete sourceCell.items[k];
                    if(!targetCell.items)
                        targetCell.items = {};
                    let nextKey=  this.generateNewIndex(targetCell)
                    targetCell.items[nextKey] = ref;
                    // add  to history !!!
                }
                if (copyEl) {
                    this.addElement(copyEl, targetCell);
                }   
            }
            else{
                if( copyEl===undefined && this.removeElementFromCell(sourceElement, "move")){             
                    this.addElement(sourceElement, targetCell); 
               }
               else{
                    this.addElement(copyEl, targetCell);
               }               
            }

        if (sourceElement == this.activeElement)
              this.activateElement(null);
              ret = true;         
        }

        if( !ret )
            window.alert("Move failed. Source item is not found.");

        return ret;
    }
    
    public generateNewIndex(cell:any): number {
        let keysOfItems = Object.keys(cell.items);
        let nextKey = keysOfItems.length;
        while(keysOfItems.includes(nextKey + "")){
            nextKey = nextKey + 1;
        }

        return nextKey
    }

    public getRefWebpartsFromState(elementName: string): IUIDesigner {
        let wp = this.cachedWebparts.find(ref => ref.name == elementName);
        return wp ? _.cloneDeep(wp) : null;
    }

    public storeRefWebpart(element:IUIDesigner) {
        const elementName = element.name;
        if( !this.getRefWebpartsFromState(elementName)){
            this.cachedWebparts = [...this.cachedWebparts, element];
        }
    }

    public findCellOwner(cell: any): IElement {
        return this.findElementByCellId(this.activeRootElement, cell.id);
    }

    public findCellTabContainer(cell: any): IElement {
        return this.findElementByCellId(this.activeRootElement, cell.id, true);
    }

    public findCellByElement(element: IElement): LayoutCell {

        let visitor: CellVisitor = (cell: LayoutCell, context: CellVisitorContext): boolean => {
            let elName = context.data.elementName;
            if (_.find(cell.items, it => it.name == elName)) {
                context.data.found = cell;
                return true;       // stop iteration
            }
            return false; // continue iterating
        };

        let visitorContext = new CellVisitorContext({ 'elementName': element.name, found: null });
        if (this.cellIterator(this.activeRootElement, visitor, visitorContext))
            return visitorContext.data.found;

        return undefined;
    }

    public findOwnerOfElementOrCell(element: IElement | ICell | LayoutCell): any {
        let e = element as any;
        if( e.__type === "GridLayoutCell"){
            return this.findCellOwner((e as LayoutCell).cell);
        }
        else if(e.type === "cell"){
            return this.findCellOwner(e as ICell);
        }
        else{
            return this.findOwnerOfElement(<IElement>element);
        }
    }
        
    public findOwnerOfElement(element: IElement): any {

        let visitor: CellVisitor = (cell: LayoutCell, context: CellVisitorContext): boolean => {
            let elName = context.data.elementName;
            if (_.find(cell.items, it => it.name == elName)) {
                context.data.found = context.owner;
                return true;       // stop iteration
            }
            return false; // continue iterating
        };

        let visitorContext = new CellVisitorContext({ 'elementName': element.name, found: null });
        if (this.cellIterator(this.activeRootElement, visitor, visitorContext))
            return visitorContext.data.found;

        return undefined;
    }

    public isControlNameUnique(cell: any, name: string, owner?: IElement): boolean {
        let isUnique = true;
        owner = owner || this.findCellOwner(cell);
        if (owner && owner.items)
            isUnique = !_.some(owner.items, it => it["name"] == name);
        return isUnique;
    }

    private findElementByCellId(element: any, cellId: string, isTabContainer = false): any {

        let visitor: CellVisitor = (cell: LayoutCell, context: CellVisitorContext): boolean => {
            if (cell.id == context.data.cellId) {
                context.data.found = !isTabContainer ? context.owner : context.tabContainer;
                return true;       // stop iteration
            }

            return false; // continue iterating
        };

        let visitorContext = new CellVisitorContext({ 'cellId': cellId, found: null });
        if (this.cellIterator(element, visitor, visitorContext))
            return visitorContext.data.found;       
                
        return undefined;
    }

    public findCellById(element: any, cellId: string): LayoutCell {

        let visitor: CellVisitor = (cell: LayoutCell, context: CellVisitorContext): boolean => {
            if (cell.id == context.data.cellId) {
                context.data.found = cell;
                return true;       // stop iteration
            }
            return false; // continue iterating
        };

        let visitorContext = new CellVisitorContext({ 'cellId': cellId, found: null });
        if (this.cellIterator(element, visitor, visitorContext))
            return visitorContext.data.found;

        return undefined;
    }

    public getObjPath(o: any, so: any, stack: string[]): boolean{
        let found = false;
        if( so == o)
            return true;
        
        if(typeof so != "undefined" && so != null && typeof so == "object"){
            _.forEach( so, (v, k:any) => {
                if( _.isNumber(k) )
                    k = k.toString();
                if( !k.startsWith("__") && typeof v != "undefined" && v != null && typeof v == "object") {
                    stack.push(k);
                    if(this.getObjPath(o, v, stack) ){
                        // found
                        found = true; 
                        return false;
                    }
                    stack.pop();
                    // continue searching
                    return true;
                }
            });
        }
        return found;
    }

    public refreshTopMenu(menu?: TopMenuState): void {
        if( menu )
            this.getRoot().__topMenu = menu;
        this.subjects.menu.next(this.getRoot().__topMenu);
    }

    setComponentRef(element: IUIDesigner): void {
        if (element.type && this.panels) {
            for (let el of this.panels) {
                if (el.name === element.name && el.type === element.type) {
                    element.componentRef = el;
                    break;
                }
            }
        }
    }

    removeComponentRef(componentRef: IUIComponentRef) {
        if (componentRef && this.panels) {
            let index = this.panels.indexOf(componentRef);
            if (index > -1)
                this.panels.splice(index, 1); // remove item
        }
    }

    public addOrUpdateComponentRef(componentRef: IUIComponentRef) {
        if (componentRef) {
            let type = componentRef.type;
            if (this.panels) {
                let index = this.panels.map(item => { return (item.type === type) ? item.name : "" }).indexOf(componentRef.name);
                if (index > -1) // update
                    this.panels[index] = componentRef;
                else // add
                    this.panels.push(componentRef);
            }
        }
    }

    public renameComponentRef(componentRef: IUIComponentRef, oldName: string) {
        if (componentRef) {
            let type = componentRef.type;
            if (this.panels) {
                let index = this.panels.map(item => { return (item.type === type) ? item.name : "" }).indexOf(oldName);
                if (index > -1) // update
                    this.panels[index] = componentRef;
            }
        }
    }

    public getCustomWorkspaceColor(): string {
        let color: string;
        let rootWs: number = this.activeRootElement ? this.activeRootElement['__workspace'] : 0;
        if (rootWs != 0) {
            let ws = _.find(this.workspaces, w => w.ID == rootWs);
            if (ws) 
                color = ws.Color;            
        }
        return color;
    }

    public isModelingBehavior() {
        if (this.activeRootElement)
            return !_.isEmpty(this.activeRootElement.__maint);

        return false;
    }
    public getWorkspaceCodeById(workspace: number): string {
        let wsCode = '';
        if (workspace != -1) {
            let ws = _.find(this.workspaces, w => w.ID == workspace);
            if (ws)
                wsCode = ws.Code;
        }
        return wsCode;
    }
    public isDesignedElement(element: IElement){
        return ["page", "webpart", "pageflow"].findIndex(e=> (element.type || "").toLowerCase() == e) != -1;
    }
    public findElement(path: string, parent?: IElement): IElement {

        if (!path)
            return null;

        let el: IElement = null;

        if (parent === undefined)
            parent = this.getRoot();

        let pathParts = path.split('.');
        let parentItem: IElement;
        if (parent['isRoot'] && parent.type == 'Webpart')
            parentItem = parent;
        else
            parentItem = _.find(parent.items, (it: IElement) => it.name == pathParts[0]);

        if (parentItem) {
            if (pathParts.length > 1)
                el = _.find(parentItem.items, (it: IElement) => it.name == pathParts[1]);
            else
                el = parentItem;

            if( pathParts.length > 2 ){
                parentItem = el;
                el = _.find(parentItem.items, (it: IElement) => it.name == pathParts[2]);
            }
        }
        return el;
    }

    public findElementOwner(startElement: IElement, el: IElement): IElement {

        let found: IElement = null;

        if (startElement.items) {
            _.forEach(startElement.items, (it) => {
                if (!found) {
                    if (it == el) {
                        found = startElement;
                    }
                    else {
                        if (it.items)
                            found = this.findElementOwner(it, el);
                    }
                }
            });
        }
        return found;
    }

    public getPrimaryService(el?: any): string {
        if (el == undefined)
            el = this.activeRootElement;
        let ps: string = '';

        if (el.properties && el.properties.primaryServiceType) {
            return el.properties.primaryServiceType;
        }

        if (el.type == 'Webpart' && el.items) {
            _.forEach(el.items, e => {
                if (!ps) {
                    ps = this.getPrimaryService(e);
                }
            });
        }

        return ps;
    }

    public writeHistory(rc: RecordCommand, hItem: IHistory): void {
        let r = this.getRoot();
        if (r) {
            if (!r.history)
                r.history = new HistoryLog();
            r.history.recordHistoryItem(rc, hItem);
        }
    }

    public isNameValid(name: string, instance: any) : Observable<boolean> {
        let exist = false;
        if(instance && instance.type){
            let owner: IElement;
            if(instance.type == "Control")
                owner = this.findOwnerOfItem(this.activeElement);
            else if(instance.type == "Webpart")
                owner = this.activeRootElement;
            else if(instance.type == "Page")
                owner = null;
            else{
                console.error('validate name - unknown type', instance);
            }

            if( owner ){
                if( _.find(owner.items, v => v.name == name && v.id != this.activeElement.id))
                    exist = true;
            }
        }

        if( exist ){
            let err : ILabel = {
                Name: 'PS_NAME_EXISTS_ERROR',
                DefaultValue: '{0} with name {1} already exists. Changes are discarded.',
                arguments:[instance.type, name]
            };
            return this._alertService.errorModal(err)
                .pipe(map(d => false));           // always returns False - regardless of pressed button on the popup alert
        }

        return of(!exist);
    }

    public renameLayoutRefs(instance: any, oldname: string, newname: string) {
        let owner: IElement;
        if(instance.type === "Control")
            owner = this.findOwnerOfItem(this.activeElement);
        else if(instance.type === "Webpart")
            owner = this.findOwnerOfElement(instance);

        if( owner ){
            let lm = owner.__layoutManager;
            if( lm ){
                lm.renameLayoutRefs(oldname, newname);
            }
            else{
                console.log("no layout", oldname);
            }
        }
    }
}
