import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { DockPanel, DockTab, ContentSection, UpdatedInfo, StudioSubjects } from '../../common/objects/common';
import { PanelSettings } from '../panel';

@Component({
    selector: 'csi-dock-area',
    templateUrl: './dockArea.component.html',
    styles: [`
        :host{
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }
        
        .dock {
            display: flex;
            flex-direction: column;
            flex: 1 1;

            border: solid 1px grey;
            border-top-width: 0px;
        }
        .dock-panel {
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }
        .dock-panel.hidden{
            display: none;
        }
        .dock-panel .header {
            background-color: lightgray;
            font-weight: bold;
            padding: 5px;
            min-height: 28px;
        }

        .dock-panel .header .content {
            flex-grow: 1;
        }

        .dock-panel .footer {
            min-height: 24px;
            padding: 2px;
        }

        .dock-panel .footer ul > li > a {
            padding: 2px;
            cursor: pointer;
        }
    `]
})

export class DockAreaComponent implements OnInit {
    @Input() dockPanels: DockPanel[];
    @Input() subjects: StudioSubjects;
    @Output() panelCommand: EventEmitter<UpdatedInfo> = new EventEmitter<UpdatedInfo>();

    defaultPanel = new PanelSettings("Properties", "fa-wrench", false);
    ngOnInit() {
        this.defaultPanel.showRefresh = true;

        this.dockPanels.forEach(d => {
            if (d.tabs && d.tabs.length > 0 && !d.activeTab) {
                d.activeTab = d.tabs[0];
                if (d.activeTab.sections && d.activeTab.sections.length > 0 && !d.activeTab.expandedSection) {
                    d.activeTab.expandedSection = d.activeTab.sections[0];
                }
            }
        });
    }

    expand(tab: DockTab, section: ContentSection) {
        tab.expandedSection = section;
    }

    selectTab(p: DockPanel, t: DockTab) {
        p.activeTab = t;
        if (p.activeTab.sections && p.activeTab.sections.length && !p.activeTab.expandedSection) {
            p.activeTab.expandedSection = p.activeTab.sections[0];
        }
    }

    panelExec(upd: UpdatedInfo) {
        this.panelCommand.emit(upd);
    }
}