export class TraceLog {
    public items: TraceItem[] = [];
}

export class TraceItem {
    logItem: any;
    public DebugResponse: DebugResponse;

    public Parent: TraceItem;
    public Counter: number;

    public Elapsed: string; // timeSpan
    public Name: string;
    public Parameters: TraceItemAttribute[];
    public Children: TraceItem[];
    public Fault: boolean;
    public PageElapsedTime: string;
}

export class ServerTraceItem {
    public Name: string;
    public Value: string;
    public Attributes: TraceItemAttribute[];
    public Parameters: TraceItemParameter[] 
    public Children: ServerTraceItem[];

    public Started: string;
    public Ended: string;
    public StartedMemoryUsage: number;
    public EndedMemoryUsage: number;
}

export class DebugResponse {
    public Trace: ServerTraceItem;
    public Fault: boolean;
    public Request: string;
    public Response: string;
}

export class DebugRequest {
    public DisableCommit: boolean;
    public TraceToResponseXML: string;
    public IncludeTimestamps: boolean;
    public IncludeRRXML: boolean;
}

export class TraceItemParameter {
    public Name: string = '';
    public Expression: string = '';
    public Value: string = '';
}
export class TraceItemAttribute {
    public Name: string = '';
    public Value: string = '';
}

export class textLine {
    level: number;
    text: string;
}