import {Injectable, Inject } from '@angular/core';
import { DragInfo, dragModes} from "./drag";
import { fromEvent } from 'rxjs';
import { map, filter } from 'rxjs/operators';
import { DOCUMENT } from '@angular/common';
import { StudioStateService } from 'common/services/studioStateService';
import { DesignModes } from 'common/objects/common';

abstract class ItemBase {
  constructor(public id: string, public action: (d: DragInfo) => void, public model : any) {      
  }

  public isInside(m: HTMLElement, zoneStyle?:string) : boolean{
    
    let parent : HTMLElement = m;
    let count = 10;
    let found = false;
    const zId = "drop-" + this.id;

    while(count-- > 0 && parent && !found){
        if( zoneStyle )
            found = parent.classList.contains(zoneStyle);
        else
            found = parent.getAttribute("dragDropId") === zId;
        if( found ){
            break;
        }
        parent = parent.parentElement;
    }
    return found;
  }
}

export class DragItem extends ItemBase {
  constructor(public mode : dragModes, public id: string, public action: (d: DragInfo) => void, public dragType: string, public model: any, public hndClass : string){
    super(id, action, model);
  }
}

class DropZone extends ItemBase {

  constructor(public dragTypes : string[], public id: string, public action: (d: DragInfo) => void, model:any){
    super(id, action, model);
  }

  public accept(it: DragItem){
      // drag type of dragging item should be in the dragType list of the drop zone
      return it && (it.mode == dragModes.dragElement || it.mode == dragModes.reorder) && it.dragType && this.dragTypes.indexOf(it.dragType)!=-1;
  }

}

@Injectable()
export class DragService {
    startInputName: boolean = false;

    private _last: DragInfo;

    private _draggables : DragItem[] = [];

    // index of element in _draggables
    private _draggableWindows : number[] = [];
    private _draggableResizables : number[] = [];

    private _activeMoved : number = -1;
    private _activeResizable : number = -1;
    private _dragging = false;
    private _mouseDown = false;

    private _draggingItem : DragItem;  
    private _zones: DropZone[] = [];
    private _lastZone: DropZone;
    private _lastZoneElement : HTMLElement;
    private _startDragTime: number;
   
    constructor(@Inject(DOCUMENT) private _document:any, private _studioStateService: StudioStateService){
        this.initMouseHandlers();
    }

    // Adds drag item
    public add(mode: dragModes, id: string , action: (d: DragInfo) => void, dragType: string, model: any, hndClass : string){
        const dragItem = new DragItem(mode, id, action, dragType, model, hndClass);
        let i = this._draggables.push(dragItem) - 1;

        if( mode == dragModes.moveWindow){
            this._draggableWindows.push(i);
            this._activeMoved = i;
        }
        else if( mode == dragModes.resizeWindow){
            this._draggableResizables.push(i);
            this._activeResizable = i;
        }
    }

    public remove(mode: dragModes, id: string){
        let removedIndex : number;

        if( mode == dragModes.moveWindow){
            removedIndex = this._draggableWindows.pop();
            // active modal handler is the last item in _draggableWindows
            this._activeMoved = this._draggableWindows.length > 0 ? this._draggableWindows[this._draggableWindows.length-1] : -1;
        }
        else if( mode == dragModes.resizeWindow){
            removedIndex = this._draggableResizables.pop();
            // active modal handler is the last item in _draggableWindows
            this._activeResizable = this._draggableResizables.length > 0 ? this._draggableResizables[this._draggableResizables.length-1] : -1;
        }
        else if( mode == dragModes.dragElement || mode == dragModes.resizePanel){
            removedIndex = this._draggables.findIndex(d=> d.id == id);
        }

        if( removedIndex !== undefined && removedIndex != -1 )
            this._draggables.splice(removedIndex, 1);
    }

    public addZone(acceptedTypes: string[], elementId: string, action: (d: DragInfo) => void){
        if( this._zones.filter(z => z.id == elementId).length == 0)
            this._zones.push (new DropZone(acceptedTypes, elementId, action, null) );
    }

    public removeZone(id: any){
        // let i = this._zones.findIndex(z=> z.element == el);
        // if( i != -1){
        //     this._zones.splice(i, 1);
        // }
    }

    public getDragDropId(m: HTMLElement, className:string) : string {
        
        let parent : HTMLElement = m;
        let count = 10;
        let found = false;

        while(count-- > 0 && parent && !found){
            found = parent.classList.contains(className);
            if( found )
                return parent.getAttribute("dragDropId");
            parent = parent.parentElement;
        }
        return null;
    }


    public clear(dragTypes: string[], dropTypes: string[] ){
        if( dragTypes){
            this._draggables = this._draggables.filter(d=> dragTypes.indexOf(d.dragType)==-1);
        }
        if( dropTypes ){
            this._zones = this._zones.filter(z=> z.dragTypes.filter(d=> dropTypes.indexOf(d)!=-1).length==0);
        }
    }


    private getDraggabledItem (el:HTMLElement) : DragItem {
      
        let i : number = -1;
        let id = this.getDragDropId(el, "drag-element");
        if( id )
            i = this._draggables.findIndex( (d) => d.id == id );

        let found = false;

        if(i!=-1){
            switch(this._draggables[i].mode){
                case dragModes.moveWindow:
                    if( el.classList.contains("cursor-draggable") || el.parentElement.classList.contains("cursor-draggable") ){
                        found = this._activeMoved == i;
                    }
                    break;
                case dragModes.resizeWindow:
                    found = this._activeResizable == i;
                    break;
                case dragModes.dragElement:
                    if(!this._draggables[i].hndClass || (this._draggables[i].hndClass && el.classList.contains(this._draggables[i].hndClass)))
                        found = true;
                    else
                        found = false;
                    break;
                case dragModes.resizePanel:
                    found = true;        
                    break;
                case dragModes.reorder:
                    found = true;
                    break;
                default:
                    found = false;   
                    break;
            }
        }

        if( found )
            return this._draggables[i];

        return null;
    }

    private initMouseHandlers(){

        fromEvent<MouseEvent>(document, 'mouseover')
            .pipe(
                filter( m => this._dragging && this.isZone(m) ),
                map<MouseEvent,DragInfo>(m => this.getDragOverInfo(m))
            )
            .subscribe(c=> this.dragOverZone(c)) ;

        fromEvent<MouseEvent>(document, 'mousedown').subscribe(ev=> {                  
            if( ev.which == 1) {                
                this._mouseDown = true;
                this._startDragTime = Date.now();
                if( this.startDragging(ev.pageX, ev.pageY, <HTMLElement>ev.target ))
                    ev.preventDefault();
            }
        });

        fromEvent<MouseEvent>(document, 'mouseup').subscribe(ev=> this.mouseUp(ev));
      
        fromEvent<MouseEvent>(document, 'mousemove')
            .subscribe((e) => {
                let di = new DragInfo(e.pageX, e.pageY, this._last, e.ctrlKey);
                if (this._mouseDown) {
                    if (this._draggingItem && this._draggingItem.action) {
                        this._draggingItem.action(di);
                        this._last = di;
                        e.preventDefault();
                    }
                }
            });
    }  

    private isZone(m: MouseEvent) : boolean{
        let z = this.findZone(m);
        if( z ){
            return true;      
        }
        if(this._lastZone && (!z || this._lastZone != z) ){
            // should callback if any zone out
            return true;
        }
        return false;
    }  

    private findZone(m: MouseEvent) : DropZone {
        let acceptableZones = this._zones.filter(z=> z.accept(this._draggingItem));
        let zxs = acceptableZones.filter(z=> z.isInside(<HTMLElement> (m.target)));
        return zxs.length ? zxs[0] : null;
    }

    private findZoneElement(m: HTMLElement) : HTMLElement {
        let parent : HTMLElement = m;
        let count = 10;
        let found = false;

        while(count-- > 0 && parent && !found){
            found = parent.classList.contains("drop-zone");
            if( found )
                return parent;
            parent = parent.parentElement;
        }
        return null;
    }

    private findZoneIndex(m: MouseEvent) : number {
        let zone = this.findZone(m);
        return zone ? this._zones.findIndex(z=> z == zone) : -1;
    }

    getDragDropElement(id: string){
        return <HTMLElement>this._document.querySelector('[dragDropId="'+id+'"]');
    }

    private getDragOverInfo(m: MouseEvent) : DragInfo{
        let d = new DragInfo(m.pageX, m.pageY, undefined, (m as any).ctrlKey);    
        d.dropZoneIndex = this.findZoneIndex(m);
        d.dropZoneElement = this.findZoneElement(m.target as HTMLElement);
        m.preventDefault();
        return d;
    }

    private mouseUp(m: MouseEvent){
        if( this._dragging ){

            let d = new DragInfo(m.pageX, m.pageY, undefined, (m as any).ctrlKey);
            // leave zone 
            if( this._lastZone){
                if(this._studioStateService.activeRootElement.__designMode == DesignModes.Controls){
                    d.leaveZone = true;
                    if(this._lastZoneElement){
                        this._lastZoneElement.classList.remove("drop-allowed");
                        this._lastZoneElement.classList.remove("drop-not-allowed");
                    }
                    if(this._lastZone.action)
                        this._lastZone.action(d);
                }
                
            }

            d.endOfDrag = true;
            this._draggingItem.action(d);

            if( Date.now() - this._startDragTime > 500 )
                this.dropIt(d, m);

            this._dragging = false;
            this._draggingItem = null;
            this._last = null;
            this._lastZone = null;
            this._lastZoneElement = null;

            m.preventDefault();
        }

        this._mouseDown = false;
        this._draggingItem = null;
    }

    private dropIt(d: DragInfo, m: MouseEvent){
        d.dropZoneIndex = this.findZoneIndex(m);
        // drop action
        let z  = d.dropZoneIndex != - 1 ? this._zones[d.dropZoneIndex] : null;
        if( z && z.action ){
            d.dragModel = this._draggingItem.model;
            d.dropZoneElement = this.findZoneElement(m.target as HTMLElement);
            z.action(d);
        }    
    }

    private dragOverZone(d:DragInfo){
        
        let z  = d.dropZoneIndex != - 1 ? this._zones[d.dropZoneIndex] : null;

        if( this._lastZone ){
            d.leaveZone = true;
            if(this._lastZoneElement){
                this._lastZoneElement.classList.remove("drop-allowed");
                this._lastZoneElement.classList.remove("drop-not-allowed");
            }

            if( this._lastZone.action)
                this._lastZone.action(d);        
        }
        
        this._lastZone = z;
        this._lastZoneElement = d.dropZoneElement;

        if(z){      
            if( d.dropZoneElement ){
                d.dropZoneElement.classList.add("drop-allowed");
                d.dropZoneElement.classList.remove("drop-not-allowed");
            }
            d.leaveZone = false;

            if( z.action ){
                // Enter zone
                z.action(d);
            }            
        }  
        
    }

    private startDragging(X:number, Y: number, el:HTMLElement) : boolean{

        this._draggingItem = this.getDraggabledItem(el);
        if( this._draggingItem ){
            this._last = new DragInfo(X, Y, undefined, false);
            this._last.dragMode = this._draggingItem.mode;
            this._dragging = true;
            this._lastZone = null;
            return true;
        }

        return false;
    }
}
