import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'panelName'
})
export class PanelNamePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if( value ){
      let v = <string>value;
      if( v == "PortalMenuCommandChanges" || v == "PortalMenuSubMenuChanges")
        return "Caption";

     if(v.startsWith("ActionDef")) v = "PageAction" + v.slice("ActionDef".length);
     
     if( v.endsWith("Changes"))
        value = v.substr(0, v.lastIndexOf("Changes")); 
    }
    return (value || "") + " Name";
  }

}
