import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { MetadataService } from '../../../common/services/metadata.service';

@Component({
    selector: 'csi-field-expression-dialog',
    styles: [
        `
        :host{
            min-height: 450px;
            min-width: 400px;
            height: 100%;
            width: 100%;

            display: flex;
            flex-direction: column;
        }
        .csi-field-expression-dialog {
            main {
                height: 100%;
            }
        }
        .csi-field-expression {
            height: 85%;
        }

        
        `
    ],

    template: `
    <div class="csi-modal csi-field-expression-dialog" csi-dialog-decor [dialog]="dialog">

        <header>
            <span class="title">{{'Field Expression Builder' | label: 'PS_FIELD_EXPRESSION_BUILDER'}}</span>
            <span class="close-icon" (click)="cancel()"><i class="fa fa-times" aria-hidden="true"></i></span>
        </header>
        <main class="csi-field-expression">
            <csi-field-expression [context]="context" (getTitle)="getTitle()"></csi-field-expression>
        </main>
        <footer>
            <div class="btns">
                <span class="default-btn" (click)="cancel()">{{'Cancel' | label: 'PS_CANCEL'}}</span>
                <span class="primary-btn" (click)="ok()">{{'Ok' | label: 'PS_OK'}}</span>
            </div>
        </footer>
    </div>
    `
})
export class FieldExpressionDialogComponent {
    context: any;

    get dialog(): MatDialogRef<FieldExpressionDialogComponent> {
        return this._dialog;
    }

    constructor(
        private _dialog: MatDialogRef<FieldExpressionDialogComponent>,
        private _metadataService: MetadataService
    ) {
        this.context = {
            property: {},
            metadataService: this._metadataService
        };
    }

    initContext(value: string) {
        this.context.value = value;
    }

    getTitle() {
    }

    ok(): void {
        this._dialog.close(this.context.value);
    }

    cancel(): void {
        this._dialog.close();
    }
}