import { Injectable } from '@angular/core';
import { IMetadataType, IPropertyMetadata, PropertyCategory, PropertyDescriptions, WorkspaceItem } from '../objects/viewModel';
import { PortalService } from './portal.service';
import { Observable, merge} from 'rxjs';
import {map } from "rxjs/operators";

import _ from 'lodash';
import { UIComponentRef, IUIComponentRef } from '../objects/common';

export type PersonalizationTypes = { [name: string]: IMetadataType };


@Injectable()
export class MetadataService {
    public AllTypes: PersonalizationTypes;
    public get _types(): PersonalizationTypes {return this.AllTypes;}
    public AllSettings : any;    

    private static _typeDescriptors: any = {};

    private errorMessage: any;
    public get typeDescriptors() { return MetadataService._typeDescriptors; }

    public propertyDescriptions : PropertyDescriptions;
    public propertyCategories : PropertyCategory[];    
    public workspaces : WorkspaceItem[];
    public nonVirtualWebparts : string[];

    ready = false;

    constructor(private _portalService: PortalService) {

        this.propertyCategories = this.__propertyCategories.map( p=> new PropertyCategory(p));
        let mergedObs = merge(
            this._portalService.getMetadata().pipe(map(md => ["metadata", md])), 
            this._portalService.getDescriptions().pipe(map(ds => ["descriptions", ds])), 
            this._portalService.getWorkspaces().pipe(map(ws => ["workspaces", ws])), 
            this._portalService.getWebPartsList().pipe(map(ws => ["webparts", ws])),
            this._portalService.getCamstarServices().pipe(map(cs=> ["camstarServices", cs])),
            this._portalService.getSettings().pipe(map(cs=> ["portalSettings", cs])),
        );
      
        mergedObs.subscribe((m:[string, any])=>{
            let k = m[0];
            if( k == "metadata"){
                this.AllTypes = m[1]; 
                _.forEach(this.AllTypes, t=> _.forEach(t.properties, p=> this.parseOptions(p)));    
            }
            else if( k == "descriptions"){
                this.propertyDescriptions = <PropertyDescriptions>m[1];
            }
            else if( k == "workspaces"){
                this.workspaces = <WorkspaceItem[]>m[1];
            }
            else if( k == "webparts"){
                this.nonVirtualWebparts = (<IUIComponentRef[]>m[1]).map(w=> w.name);
            }
            else if( k == "camstarServices"){
                // Nothing to do
            }
            else if( k == "portalSettings"){
                this.AllSettings = m[1];
            }
        },
        (error) => {
            this.errorMessage = <any>error;
            console.error(this.errorMessage);
        },
        () => {
            // Complete
            this.ready = true;
        } 
        );
    }

    public getMetadataType(typeName: string): IMetadataType {

        // Even if the instance is null the property list is created by type
        let metadata: IMetadataType = null;
        if (typeName) {
            typeName = typeName.replace('[]', '').toLowerCase();
            metadata = <IMetadataType>this._types[typeName];
            if (!metadata && typeName.startsWith('om.')) {
                typeName = typeName.substr(3);
                metadata = <IMetadataType>this._types[typeName];
            }
            else if(!metadata && typeName.toLowerCase() == "UIComponentRef".toLowerCase()) {
                metadata = UIComponentRef.metadata;
            }
        }
        return metadata;
    }

    public getSubTypes(typeName: string): IMetadataType[] {
        let basePropertyTypeLc = typeName.toLowerCase();
        let basePropertyTypeExt = basePropertyTypeLc + '@';
        let res = [];
        let ct = this._types[basePropertyTypeLc];
        if (ct) {
            // Include current type
            if (ct._abstract !== true)
                res.push(ct);

            for (var c in this._types) {
                let pt = this._types[c];
                if (pt.baseTypeNames) {
                    pt.baseTypeNames.forEach((b) => {
                        if (b.toLowerCase() == basePropertyTypeLc || b.toLowerCase() == basePropertyTypeExt ) {
                            if (pt._abstract !== true)
                                res.push(pt);
                        }
                    });
                }
            }
        }
        return res;
    }

    public  getWorkspaceCodeById(workspace: number): string {
        let wsCode = '';
        if (workspace != -1) {
            let ws = _.find(this.workspaces, w => w.ID == workspace);
            if (ws)
                wsCode = ws.Code;
        }
        return wsCode;
    }

    public getPropertyCreatorType(type: IMetadataType, property: IPropertyMetadata) {
        let t = type;
        if (type.baseTypeNames) {
            type.baseTypeNames.forEach(bt => {
                bt = _.trimEnd(bt, '@');
                let tp = this.getMetadataType(bt);
                if (tp && tp.properties) {
                    if (tp.properties.find(vp => vp.name.toLowerCase() === property.name.toLowerCase())) {
                        t = tp;
                        return t;
                    }
                }
            });
        }
        return t;
    }

    // Gets value by case-insensitive key
    public getByKey(obj: any, k: string) {
        if (obj) {
            k = k.toLowerCase();
            return _.find(obj, (v, name: string) => { return name.toLowerCase() == k; });
        }
        return undefined;
    }

    public getPrimaryService(){
        return "";
    }

    public loadType(ns: string, typeName: string, withDependencies: boolean = true): Observable<PersonalizationTypes> {

        ns = ns.toLowerCase();
        let namesp: string = '';

        switch (ns) {
            case 'config':
                namesp = 'Camstar.WebPortal.PortalConfiguration'; break;
            case 'om':
                namesp = 'Camstar.WCF.ObjectStack'; break;
            case 'pers':
            default:
                break;
        }

        let pref = ns + '.';
        return this._portalService.getMetadataTypes(namesp, [typeName]).pipe(
            map( md => {
                _.forEach(md, m => {
                    m._namespace = ns;
                    m.properties.forEach(p=>{
                        if (!this.isSystemType(p.originalTypeName)) {
                            let pt = this.getMetadataType(pref + p.originalTypeName);
                            if (pt)
                                p.originalTypeName = pref + p.originalTypeName;                                
                        }
                    });
                    this.addWithNS(ns, m);
                });
                return md;
            }));
    }

    public isSystemType(t: string): boolean {
        const sysTypes: string[] = ['int32', 'boolean', 'string', 'number', 'int32[]', 'boolean[]', 'string[]', 'number[]'];
        t = t.toLowerCase();
        return sysTypes.indexOf(t) != -1;
    }

    public parseOptions(pr: IPropertyMetadata){
        let pe = pr.editor;
        if( pe && pe.options){
            // Parse options
            pr.editor.options.forEach(s => {
                let p = s.split('=');
                if (p.length > 1) {
                    if (p[0] == 'provider')
                        pe.provider = p[1];
                    else if (p[0] == 'filter')
                        pe.filter = p[1];
                    else if (p[0] == 'parameter') {
                        if (!pe.parameters) pe.parameters = [];
                        pe.parameters.push(p[1]);
                    }
                }
            });
        }
        if (pe && pe.directive == "enum" && !pr.enumValues) {
            // Load enum values
            if(pr.originalTypeName){
                let t : IMetadataType = this.AllTypes[pr.originalTypeName.toLowerCase()];
                if( t ) {
                    pr.enumValues = t.enumValues;                    
                }                    
            }
        }
    }

    private addWithNS(ns: string, m: IMetadataType) {
        // modify property types
        this._types[ns + '.' + m._name.toLowerCase()] = m;
    }    

    private __propertyCategories: string[] = ["Equivalency", "General", "Behavior", "Appearance", "Layout",    
        "System", "Data", "Selection Values", 
        "Predefined Buttons", "Dependency", "Paging", "History", "Report Settings", "Grid Options", 
        "Other", "Simple", "Advanced", "Favorites"
    ];
}
