import { Component, OnInit } from '@angular/core';
import * as _ from 'lodash';

import { MatDialogRef } from '@angular/material/dialog';
import { LabelService } from '../../../common/services/label.service';
import { ContentSection, StudioSubjects, BatchImportExportSettings } from '../../../common/objects/common';
import { Subject, Subscription } from 'rxjs';
import { MetadataService } from '../../../common/services/metadata.service';

type WS = { code: string, checked: boolean, name: string };

@Component({
    selector: 'batch-export-wizard',
    styleUrls: ['batchExport.component.less', '../../../shared/internalTabs.less'],
    templateUrl: 'batchExport.component.html'
})
export class BatchExportComponent implements OnInit {
    subjects: StudioSubjects;
    internalTabs: ContentSection[];
    activeTab: ContentSection;
    exportToFileEnabled: boolean = false;
    validationMessage: string;
    checkedSubj = new Subject<{ n: string, t: string }[]>();
    exportFileType: string;

    wsCodes: WS[];
    private checkedSubs: Subscription;
    private selectedItems: { n: string, t: string }[] = [];
    private get exportToXml(): boolean { return this.exportFileType == "xml"; }
    private get exportToZip(): boolean { return this.exportFileType == "zip"; }

    get dialog(): MatDialogRef<BatchExportComponent> {
        return this._dialog;
    }

    constructor(
        private _dialog: MatDialogRef<BatchExportComponent>,
        private _labelService: LabelService,
        private _metadataService: MetadataService) {

        this.wsCodes = this._metadataService.workspaces.map(w => <WS>{ code: w.Code, checked: false, name: w.Name });

        this.subjects = new StudioSubjects();
    }

    ngOnInit(): void {
        this.internalTabs = [
            <ContentSection>{ name: 'Pages', component: 'csi-element-panel', isSelectable: true, dataSource: 'Page' },
            <ContentSection>{ name: 'Web Parts', component: 'csi-element-panel', isSelectable: true, dataSource: 'Webpart' },
            <ContentSection>{ name: 'Pageflows', component: 'csi-element-panel', isSelectable: true, dataSource: 'Pageflow' }
        ];
        this.activeTab = this.internalTabs[0];
        this.checkedSubs = this.checkedSubj.subscribe(s => this.selectedItems = s);
    }

    ngOnDestroy(): void {
        if (this.checkedSubs) {
            this.checkedSubs.unsubscribe();
            this.checkedSubs = null;
        }
    }

    save(): void {
        if (this.validate())
            this._dialog.close(this.getSettings());
    }

    cancel(): void {
        this._dialog.close(null);
    }

    validate(): boolean {
        let res = false;
        if (_.some(this.wsCodes, w => w.checked)) {
            if (this.exportToFileEnabled) {
                if(this.exportToXml || this.exportToZip){
                    if (this.selectedItems.length > 0)
                        res = true;
                    else
                        this.validationMessage = this._labelService.getLabel("PS_BatchExport_Validation1", "No Pageflows, Pages or Web Parts are selected for export.");
                }else
                     this.validationMessage = this._labelService.getLabel("PS_BatchExport_Validation4", "File type is not selected");   
                
            }
            else
                this.validationMessage = this._labelService.getLabel("PS_BatchExport_Validation2", "Export to file was not selected.");
        }
        else
            this.validationMessage = this._labelService.getLabel("PS_BatchExport_Validation3", "Select at least one workspace to export.");

        return res;
    }

    getSettings(): BatchImportExportSettings {
        let settings = new BatchImportExportSettings();
        settings.ExportTo = this.exportToXml ? 0 : 1;
        settings.SelectedWorkspaces = this.wsCodes.filter(ws => ws.checked).map(ws1=> ws1.code);
        settings.Pages = this.selectedItems.filter(p=> p.t == "Page").map(p1 => p1.n).sort();
        settings.WebParts = this.selectedItems.filter(p=> p.t == "Webpart").map(p1 => p1.n).sort();
        settings.Pageflows = this.selectedItems.filter(p=> p.t == "Pageflow").map(p1 => p1.n).sort();
        return settings;
    }
}