import { IElement, LayoutBuilder, uuid } from '../../../common/objects/common';
import _ from 'lodash';

export class ControlTab {
    index: number;
    name: string;
    labelText: string;
    labelName: string;
    visible: boolean;
    elementTabModel: IElement;
    isActive: boolean;

    constructor(tab: any, index: number) {
        this.visible = tab.visible || tab.Visible;

        this.labelText = tab.labelText || tab.LabelText;
        this.labelName = tab.labelName || tab.LabelName;

        this.elementTabModel = {
            name : tab.name || tab.Name,
            id : uuid.id('tab'),
            layout: tab.layout || tab.Layout,
            type: "Control",
            typeName: '',
            items: {}
        };

        this.name = this.elementTabModel.name;
        this.index = tab.index !== undefined ? tab.index : tab.Index;
    }

    getTitle(): string {
        return this.labelText || this.labelName || this.name;
    }
}

export class ControlManager {
    elementModel: IElement;
    elementType: string;
    controlProperties: { [name: string]: any; };
    tabs: ControlTab[];
    activeTab: ControlTab;
    hasTabs: boolean;

    constructor(element: IElement) {
        this.elementModel = element;
        this.elementType = element.typeName;
        this.controlProperties = element.properties || {};

        this.setupTabs();
    }

    getControlModel(): IElement {
        return this.elementModel;
    }

    getControlProperty(name: string, defaultValue?: any, intToArray?: boolean): any {
        var propertyValue = this.controlProperties[name];
        if (propertyValue === undefined) {
            let valueFound = false;

            // then try to find case insensitive property value.
            let obj = this.controlProperties;
            for (let propName of name.split('.')) {
                valueFound = false;
                if (obj) {
                    for (let p of Object.getOwnPropertyNames(obj)) {
                        if (p.toLowerCase() === propName.toLowerCase()) {
                            obj = propertyValue = obj[p];
                            valueFound = true;
                            break;
                        }
                    }
                    if (!valueFound)
                        obj = propertyValue = null;
                }
                else
                    break;
            }

            if (!valueFound) {
                switch (name) {
                case 'label':
                    propertyValue = this.getControlProperty('labelText') || this.getControlProperty('labelName') || this.elementModel.name;
                    break;
                case 'id':
                    propertyValue = this.elementModel.id;
                    break;
                case 'typeName':
                    propertyValue = this.elementType;
                    break;
                case 'width':
                    propertyValue = this.getControlProperty('controlWidth');
                    break;
                case 'height':
                    propertyValue = this.getControlProperty('controlHeight');
                    break;
                case 'margin':
                    propertyValue = this.getControlProperty('controlMargin');
                    break;
                }
            }
        }
        if (intToArray) {
            let intProp = propertyValue || defaultValue;

            if (_.isNumber(intProp)){
                let i = parseInt(intProp.toString());
                propertyValue = new Array(i).fill(1);
            }
        }
        if (propertyValue === false)
            return false;

        return propertyValue || defaultValue;
    }

    getControlStyle(...attrs: string[]): any {
        let css = {};
        if (!attrs.length)
            attrs = ["width", "height", "margin"];

        for (let attr of attrs) {
            switch (attr) {
            case "width":
            case "height":
                let val = this.getControlProperty(attr);
                if (val && _.isNumber(val))
                    css[attr + ".px"] = val;
                break;
            case "margin":
                let margin = this.getControlProperty("margin");
                if (margin) {
                    // returned by property grid
                    if (typeof margin === 'string') {
                        let items = margin.split(",");
                        if (items.length === 4) {
                            margin = {};
                            margin.left = _.isNumber(items[0]) ? items[0] : 0;
                            margin.top = _.isNumber(items[1]) ? items[1] : 0;
                            margin.right = _.isNumber(items[2]) ? items[2] : 0;
                            margin.bottom = _.isNumber(items[3]) ? items[3] : 0;
                        }
                    }

                    if (margin.top)
                        css["margin-top.px"] = margin.top;
                    if (margin.right)
                        css["margin-right.px"] = margin.right;
                    if (margin.bottom)
                        css["margin-bottom.px"] = margin.bottom;
                    if (margin.left)
                        css["margin-left.px"] = margin.left;
                }
                break;
            }
        }
        
        return css;
    }

    // if active tab is the same returns false ( means: noChanged )
    public setActiveTab(tabOrName: ControlTab | string): boolean {
        let tab: ControlTab;
        if (_.isString(tabOrName)) {
            tab = this.tabs.find(t => t.name == <string>tabOrName);
        }
        else {
            tab = <ControlTab>tabOrName;
        }
        if (tab) {
            if (this.activeTab && this.activeTab == tab){
                return false;
            }
            this.activeTab = tab;
            // reset active flag
            this.tabs.forEach(t => t.isActive = false);
            this.activeTab.isActive = true;
        }
        else {
            this.activeTab = tab;
            this.tabs.forEach(t => t.isActive = false);
        }

        // save active flag in model
        this.elementModel['__activeTab'] = this.activeTab.name;
        return true;
    }

    public getActiveTabElement() {
        return this.activeTab ? this.activeTab.elementTabModel : null;
    }

    public getControlClass(){
        return "csi-" + this.elementType;
    }

    public static createNewControl(controlType: string, serverControlType: string): IElement {
        let newName = uuid.id(controlType + "-");
        return <IElement>{
            id: uuid.id(),
            name: newName,
            type: 'Control',
            typeName: controlType,
            items: null,
            layout: null,
            properties: { 'name': newName },
            isReference: false,
            _fullTypeName: serverControlType
        };
    }

    public static createEmptyWebpartModel(wpName: string, rows: number, cols: number): any {
        let wpTypeName = "WebPartDefinition";
        let codeBehind = "Camstar.WebPortal.WebPortlets.MatrixWebPart";
        let emptyWp = {
            _fullTypeName: codeBehind,
            id: uuid.id(),
            name: wpName,
            isReference: false,
            items: null,
            layout: LayoutBuilder.getLayout(rows, cols),
            properties: {
                __type: wpTypeName,
                isReference: false,
                name: wpName,
                typeName: codeBehind,
                created: new Date().toISOString()
            },
            type: "Webpart",
            typeName: wpTypeName
        };
        return emptyWp;
    }

    private setupTabs(): void {
        this.tabs = this.getControlTabs();
        this.hasTabs = (this.tabs && this.tabs.length > 0);
    }

    private getControlTabs(): ControlTab[] {
        var retVal: ControlTab[] = [];
        var tabDefinitions = this.getControlProperty("tabDefinitions", null);
        if (tabDefinitions) {
            retVal = _.map(tabDefinitions, (t, i:number) => new ControlTab(t, i));
            let firstVisibleIndex = tabDefinitions.findIndex(t => t.visible);
            if (firstVisibleIndex != -1)
                retVal[firstVisibleIndex].isActive = true;        
        }
        return retVal;
    }


}