import { Component, OnInit, OnDestroy } from '@angular/core';
import { IControl, IElement, LayoutBuilder } from '../../common/objects/common';
import { PanelAction, IContentPanel, PanelSettings } from '../panel';
import { LabelService } from '../../common/services/label.service';
import { PortalService } from '../../common/services/portal.service';

import _ from 'lodash';
import { StudioStateService } from '../../common/services/studioStateService';
import { Subscription } from 'rxjs';

@Component({
    selector: 'csi-control-panel',
    styleUrls: ["controlPanel.component.less"],
    templateUrl: "controlPanel.component.html"
})

export class ControlPanelComponent implements IContentPanel, OnInit, OnDestroy {

    settings: PanelSettings;

    controls: IControl[] = [];

    errorMessage: any;
    nameFilter: string;
    designerDropList : string[];
    private dropAreasSubs : Subscription;

    constructor(private _portalService: PortalService, private _labelService: LabelService, private _studioService: StudioStateService) {
        this.dropAreasSubs = this._studioService.subjects.dropAreas.subscribe(d=> {
            this.designerDropList = d.filter(dp=> dp.startsWith("drop_ctl_"));
        });
    }

    ngOnDestroy(){
        if( this.dropAreasSubs)
            this.dropAreasSubs.unsubscribe();
    }

    get viewMode(): string {
        return this.settings.viewMode || 'list';
    }

    init(s: PanelSettings) {
        let listModeLabel = this._labelService.getLabel('PS_LIST_MODE', 'List mode');
        let tileModeLabel = this._labelService.getLabel('PS_TILE_MODE', 'Tiles mode');
        let detailModeLabel = this._labelService.getLabel('PS_DETAIL_MODE', 'Details mode');

        s.actions = [
            <PanelAction>{ name: listModeLabel, imgClass: 'fa-list', modeClass: 'list' },
            <PanelAction>{ name: tileModeLabel, imgClass: 'fa-th', modeClass: 'tiles' },
            <PanelAction>{ name: detailModeLabel, imgClass: 'fa-list-ul', modeClass: 'detail' }
        ];
        s.showSearch = true;
        s.iconSrc = "fa-tasks";
        s.showRefresh = false;
        this.settings = s;

        return this.settings;
    }

    ngOnInit() {
        this._portalService.getControls()
            .subscribe(
            controls => {
                this.controls = _.filter(controls, c => c.type != 'TabContainer');

                // add initializer for container-type controls
                this.controls.forEach(tc => {
                    if (_.includes(['ToggleContainer', 'SectionDropDown','SectionDropDownMobile', 'RibbonContainer', 'WebPartsWrapperControl'], tc.type))
                        (<any>tc).controlInit = (ctl: IElement, owner: IElement, portal: PortalService) => this.buildDefaultLayout(ctl);
                    else if (tc.type == 'JQTabContainer')
                        (<any>tc).controlInit = (ctl: IElement, owner: IElement, portal: PortalService) => this.buildDefaultTabs(ctl);
                    else if (tc.type == 'ContainerListGrid')
                        (<any>tc).controlInit = (ctl: IElement, owner: IElement, portal: PortalService) => this.buildDefaultContainerListGrid(ctl);
                });

                this.settings.searchInput.subscribe(searchtext => this.nameFilter = searchtext);
            },
            error => this.errorMessage = <any>error);
    }



    private buildDefaultLayout(ctl: IElement) {
        ctl.layout = LayoutBuilder.getLayout(2, 1);
        ctl.properties.section = {
            __type: 'SectionDefinition',
            layout: null
        };
    }

    private buildDefaultTabs(ctl: IElement) {
        ctl.layout = LayoutBuilder.getLayout(2, 2);
        ctl.properties.tabDefinitions = [{ name: 'TabDefinition0', __type: 'TabDefinition' }];
    }

    private buildDefaultContainerListGrid(ctl: IElement) {

        let defColumns = [
            { __type: 'JQFieldReadonly', name: "Container", width: 70, labelName: "SelVal_Container", editable: false, isRowID: true },
            { __type: 'JQFieldReadonly', name: "Qty", width: 78, labelName: "Container_Qty", editable: false },
            { __type: 'JQFieldReadonly', name: "UOM", width: 85, labelName: "SelVal_UOM", editable: false },
            { __type: 'JQFieldReadonly', name: "Operation", width: 120, labelName: "SelVal_Operation", editable: false },
            { __type: 'JQFieldReadonly', name: "Product", width: 120, labelName: "SelVal_Product", editable: false },
            { __type: 'JQFieldReadonly', name: "ProductDescription", width: 170, labelName: "SelVal_ProductDescription", editable: false },
            { __type: 'JQFieldReadonly', name: "MfgOrder", width: 120, labelName: "SelVal_ManufacturingOrder", editable: false }
        ];

        ctl.properties = ctl.properties || {};
        ctl.properties.settings = {
            __type: 'GridDataSettingsSelVal',
            columns: defColumns,
            selectionMode: 3 /*JQGridSelectionMode.SingleRowSelectWithPostback*/,
            automation: {
                __type: 'GridAutomation',
                shrinkColumnWidthToFit: true
            },
            pagesRequested: 0,
            rowsPerPage: 10,
            fieldExpressions: ".Container",
            requestFieldExpression: ".Container",
            pager: { __type: 'JQGridPagerSettings', position: 1 /*HorizontalAlignment.Left*/, mode: 2 /* Auto*/ },
            navigator: 1 /* JQGridNavigatorMode.PagerOnly*/
        };
    }
}
