import { Component, Input, OnChanges,  } from '@angular/core'; //ChangeDetectorRef,NgZone, 
import { MatDialog } from '@angular/material/dialog';
import { environment } from '../../environments/environment';
import { PortalService } from '../common/services/portal.service';
import { PortalSettingsService } from '../common/services/portalSettings.service';
import { MetadataService } from '../common/services/metadata.service';
import { ModalContainerComponent } from '../panel/properties/valueEditors/modalContainer';
import { ExecuteCommand } from '../common/objects/common';
import { WorkspaceItem } from '../common/objects/viewModel';

import { LabelService } from '../common/services/label.service';
import { ConfirmDialogComponent } from '../shared/dialog/confirm.dialog.component';
import { StudioStateService } from '../common/services/studioStateService';

@Component({
    selector: 'csi-title-menu',
    templateUrl: 'title-menu.component.html',
    styleUrls: ["title-menu.component.less"]
})

export class TitleMenuComponent implements OnChanges {
    title: string = "Opcenter Execution Core";
    subtitle: string = " - Portal Studio 2.0";
    wsColor: string;

    @Input("activeElement") __activeElement: any;
    get workspaces(): WorkspaceItem[] { 
        return this._metadataService.workspaces;
    }
    get state() : StudioStateService {return  this._studioStateService; }

    _root:any;
    _activeWS:number;
    _minWS: number;
    _wsWithContent:any;
    _activeWsWithContent:boolean;
    _countWsWithContent: number;

    constructor(
        private _dialog: MatDialog,
        private _labelService: LabelService,
        private _metadataService: MetadataService,
        private _portalService: PortalService,
        private _studioStateService : StudioStateService,
        private _portalSettingsService: PortalSettingsService) {
        this.addWorkspaceStyles();
    }

    ngOnChanges(){
        this._root = this._studioStateService.getRoot();
        this._activeWS = this._metadataService.workspaces.reduce((prev, current) => (prev.ID > current.ID) ? prev : current).ID;
        if(this._root.workspaceIndexes){
            this._wsWithContent = this._root.workspaceIndexes.filter(el => el < this._activeWS && el !== 0);
            this._minWS =  this._wsWithContent.length > 0 ? Math.min.apply(Math, this._wsWithContent) : 100000;
            this._activeWsWithContent = this._root.workspaceIndexes.includes(this._activeWS);
            this._countWsWithContent = this._wsWithContent.length;
        }
    }

    isDisabled(selectedWS){
        if((selectedWS < this._activeWS && selectedWS >= this._minWS ) ||  
            (this._activeWS == selectedWS && this._activeWsWithContent) ||
            (this._activeWS == selectedWS && this._countWsWithContent) ||
            selectedWS == 0){
            return false;
        }else return true;
    }

    private addWorkspaceStyles(){
        let style = document.createElement("style");
        style.appendChild(document.createTextNode(""));
        document.head.appendChild(style);
        let sheet = style.sheet as CSSStyleSheet;
        let index = -1;
        this._metadataService.workspaces.forEach(w=>{
            // rule for design
            sheet.insertRule("div.workspace-"+ w.ID + " { box-shadow : inset 0 0 5px "+ w.Color +"; }" , ++index);

            // rule for option
            sheet.insertRule("option.workspace-"+ w.ID + " { background-color: "+ w.Color +"; }", ++index);
        });
    }

    logout(): void {
        this._portalService.logout()
            .subscribe(result => {
                if (result)
                    location.href = environment.portalUrl;
            });
    }

    backToPortal(): void {
        if(environment.portalMode == "Classic") 
            location.href = environment.portalUrl + "/Main.aspx";
        else 
            location.href = environment.portalUrl + "/default.htm#/screen/apollo";
    }

    portalSettings() {
        let context: any = {
            metadataService: this._metadataService,
            hideTypeName: true,
            disableAdding: true
        };

        let dial = this._dialog.open(ModalContainerComponent, { disableClose: true });
        dial.componentInstance.initContext(context);
        dial.afterClosed().subscribe(result => {
            if (result)
                // Save settings
                this._portalSettingsService.save().subscribe(r => { });
        });        
    }
   
    changeWorkspace(wsID: number|string) {
        if( typeof wsID == "string")
            wsID = parseInt(wsID);

        if (this._studioStateService.activeRootElement.isDirty) {

            let yesLabel = this._labelService.getLabel('PS_ACTION_YES', 'Yes');
            let noLabel = this._labelService.getLabel('PS_ACTION_NO', 'No');
            let cancelLabel = this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel');
            let titleLabel = this._labelService.getLabel('PS_CLOSE', 'Close');
            let messageLabel = this._labelService.getLabel('PS_SAVE_PENDING', 'Would you like to save pending changes?');
            let confirmContext = {
                title: titleLabel,
                message: messageLabel,
                actions: [
                    { action: 'yes', name: yesLabel, isPrimary: true },
                    { action: 'no', name: noLabel, isPrimary: false },
                    { action: 'cancel', name: cancelLabel, isPrimary: false }]
            };

            let dialog = this._dialog.open(ConfirmDialogComponent, { disableClose: true });

            dialog.componentInstance.initContext(confirmContext);
            dialog.afterClosed().subscribe(res => {
                if (res === "yes") {
                    // Save element 
                    this._studioStateService.subjects.execute.next(new ExecuteCommand("save",null, null));
                    this._studioStateService.subjects.execute.next(new ExecuteCommand("switchWorkspace", null, wsID));
                } 
                else if (res === "no") {
                    // Do not save element 
                    this._studioStateService.subjects.execute.next(new ExecuteCommand("switchWorkspace", null, wsID ));
                }
            });
        }
        else {
            // Do not save element 
            this._studioStateService.subjects.execute.next(new ExecuteCommand("switchWorkspace", null, wsID ));
        }

    }
}
