import { Injectable } from '@angular/core';
import { IElement, IElementRef, ILocalState, IUserProfile, IFavoriteProperty, PageGroupDef, WhereUsedItemListDef, PageGroupDefItem } from '../objects/common';
import _ from 'lodash';

@Injectable()

export class LocalService {
    _localState: ILocalState;

    constructor() {
        this.getStateFromLocalStorage();
    }

    getUserProfile(): IUserProfile {
        return this._localState.userProfile;
    }

    isDisplayCatalog(): boolean {
        return this._localState.displayCatalog;
    }

    isDisplayToolbox(): boolean {
        return this._localState.displayToolbox;
    }

    isDisplayProperty(): boolean {
        return this._localState.displayProperty;
    }

    isDisplayTraceViewer(): boolean {
        return this._localState.displayTraceViewer;
    }

    setUserProfile(userProfile: IUserProfile): boolean {
        this._localState.userProfile = userProfile;
        this.saveStateToLocalStorage();
        return true;
    }

    resetSettings(): void {
        this.initializeState();
        this.saveStateToLocalStorage();
    }

    toggleDisplayCatalog(): void {
        this._localState.displayCatalog = !this._localState.displayCatalog;
        this.saveStateToLocalStorage();
    }

    toggleDisplayToolbox(): void {
        this._localState.displayToolbox = !this._localState.displayToolbox;
        this.saveStateToLocalStorage();
    }

    toggleDisplayProperty(): void {
        this._localState.displayProperty = !this._localState.displayProperty;
        this.saveStateToLocalStorage();
    }

    toggleDisplayTraceViewer(): void {
        this._localState.displayTraceViewer = !this._localState.displayTraceViewer;
        this.saveStateToLocalStorage();
    }


    addToElementHistory(element: IElement): void {
        //added if clause for restrict number of types for recent elements
        if(element.type && ['page', 'webpart', 'pageflow'].indexOf(element.type.toLowerCase()) != -1) {
            this.removeElementHistory(element);
            let elementRef = { name: element.name, type: element.type };

            if (this._localState.elementHistory)
                this._localState.elementHistory = [elementRef, ...this._localState.elementHistory];
            else
                this._localState.elementHistory = [elementRef];
            this.saveStateToLocalStorage();
        }
    }

    removeElementHistory(elementRef: IElementRef): void {
        if (this._localState.elementHistory) {
            let existingElement = this._localState.elementHistory.find(eh => eh.name == elementRef.name && eh.type == elementRef.type);
            if (existingElement) {
                let index = this._localState.elementHistory.indexOf(existingElement);
                if (index != -1) {
                    this._localState.elementHistory.splice(index, 1);
                }
            }
        }
        this.saveStateToLocalStorage();
    }

    getElementHistory(): IElementRef[] {
        if (!this._localState.elementHistory)
            return null;
        return this._localState.elementHistory.slice(0, 6);
    }

    renameElementHistory(element: IElement, oldName: string): void {
        if (this._localState.elementHistory) {
            let existingElement = this._localState.elementHistory.find(eh => eh.name == oldName && eh.type == element.type);
            if (existingElement)
                existingElement.name = element.name;
        }
        this.saveStateToLocalStorage();
    }

    public isLogged(): boolean {
        if (this._localState && this._localState.userProfile && this._localState.userProfile.SessionID) {
            return true;
        }

        return false;
    }

    public getFavProperties(typeName: string): string[] {
        if (this._localState && this._localState.favProperties) {
            let fp = this._localState.favProperties.find(p => p.typeName == typeName);
            if (fp)
                return fp.names;

            return [];
        }
    }

    public setFavProperties(t: string, pnames: string[]): void {
        if (this._localState) {
            if (!this._localState.favProperties)
                this._localState.favProperties = [];

            let fp = this._localState.favProperties.find(p => p.typeName == t);
            if (!fp)
                this._localState.favProperties = [<IFavoriteProperty>{ typeName: t, names: pnames }];
            else
                fp.names = pnames;
            this.saveStateToLocalStorage();
        }
    }

    public addPageList(wName: string, pages: any[]): void {
        if (this._localState) {
            if (this._localState.whereUsedPageList) {

                this._localState.whereUsedPageList = this._localState.whereUsedPageList.filter(el => {
                    return el.wepPartName !== wName;
                })
                this._localState.whereUsedPageList.push(new WhereUsedItemListDef(wName, pages))
            }
            else{
                this._localState.whereUsedPageList = [new WhereUsedItemListDef(wName, pages)]
            }

            this.saveStateToLocalStorage();
        }
    }

    public addWebPartList(wName: string, pages: any[]): void {
        if (this._localState) {
            if (this._localState.whereUsedWebPartList){

                this._localState.whereUsedWebPartList = this._localState.whereUsedWebPartList.filter( el => {
                    return el.wepPartName !== wName;
                })
                this._localState.whereUsedWebPartList.push(new WhereUsedItemListDef(wName, pages))
            }
            else{
                this._localState.whereUsedWebPartList = [new WhereUsedItemListDef(wName, pages)]
            }

            this.saveStateToLocalStorage();
        }
    }

    public getPageList(): any {
        return this._localState.whereUsedPageList;
    }

    public getWebPartList(): any {
        return this._localState.whereUsedWebPartList;
    }

    public getPageGroups(): PageGroupDef[] {
        if (!this._localState.pageGroups) {
            this._localState.pageGroups = [];
        }
        return this._localState.pageGroups;
    }

    public addPageGroup(pageGroupName: string, deleted?: boolean): void {
        this._localState.pageGroups = this._localState.pageGroups || [];

        let pg = this._localState.pageGroups.find(n => n.groupName == pageGroupName);
        if (!pg) {
            const newGrpK = pageGroupName.replace(/\s/g, "").toLowerCase();
            this._localState.pageGroups.push(
                new PageGroupDef(pageGroupName, newGrpK, [], deleted));
        }
        if (pg && deleted) {
            pg.deleted = deleted;
        }
        this.saveStateToLocalStorage();
    }

    public editPageGroup(newName: string, oldName: string): void {
        if (this._localState.pageGroups) {
            const existGrp = this._localState.pageGroups.find(el => el.groupName == oldName);
            if (existGrp)
                existGrp.groupName = newName;
            existGrp.groupKey = newName.replace(/\s/g, "").toLowerCase();           
        }
        this.saveStateToLocalStorage();
    }

    public addPageToGroup(pageName: string, pageGroup: string, deleted?: boolean): void {
        const group = this._localState.pageGroups.find(n => n.groupName == pageGroup);
        if (!group) {
            this.addPageGroup(pageGroup);
        }
        let item = new PageGroupDefItem(pageName, deleted);
        let isDelItem = this._localState.pageGroups.find(n => n.groupName == pageGroup).items.find(i => i.deleted == true);
        if (!isDelItem)
            this._localState.pageGroups.find(n => n.groupName == pageGroup).items.push(item);
        else {
            isDelItem.deleted = false;
        }
        this.saveStateToLocalStorage();
    }

    public removeGroup(pageGroup: string) {
        if (this._localState.pageGroups) {
            _.remove(this._localState.pageGroups, (eh) => eh.groupName == pageGroup);
            this.saveStateToLocalStorage();
        }
    }

    public removePageFromGroup(pageName: string, pageGroup: string): void {
        this._localState.pageGroups = this._localState.pageGroups || [];
        const existingGroup = this._localState.pageGroups.find(eh => eh.groupName == pageGroup);
        let existingLocalPages : PageGroupDefItem[] = [];
        if (existingGroup) 
            existingLocalPages= _.remove(existingGroup.items, n => n.name == pageName);
        
        if(!existingLocalPages.length || !existingGroup)
            this.addPageToGroup(pageName, pageGroup, true);            
        this.saveStateToLocalStorage();
    }

    public resetSettingsPageGroups(): void {
        if (!this._localState)
            this._localState = <ILocalState>{};
        this._localState.pageGroups = new Array();
    }

    private initializeState(): void {
        if (!this._localState)
            this._localState = <ILocalState>{};

        this._localState.displayCatalog = true;
        this._localState.displayToolbox = true;
        this._localState.displayProperty = true;
        this._localState.displayTraceViewer = false;
        this._localState.elementHistory = new Array();
    }

    private getStateFromLocalStorage(): void {
        let localStorageState = localStorage.getItem('PS_LOCAL_SETTINGS');
        this._localState = <ILocalState>JSON.parse(localStorageState);

        //if localState hasn't be initialized
        if (!this._localState)
            this.initializeState();

        this.saveStateToLocalStorage();
    }

    private saveStateToLocalStorage(): void {
        // reduce element history to 10 recent items
        const historySize = 5;
        if (this._localState.elementHistory.length > historySize) {
            let l = this._localState.elementHistory.length;
            while (l-- > historySize)
                this._localState.elementHistory.pop();
        }
        localStorage.setItem("PS_LOCAL_SETTINGS", JSON.stringify(this._localState));
    }

}