import { Component, Input,  OnInit } from '@angular/core';

import { ModalPropertiesContext } from '../properties';
import {Observable, of} from "rxjs";
import {map} from "rxjs/operators";

import { ValueProviderService } from '../../../common/services/dataValue.service';
import { ISelectableItem } from '../../../common/objects/common';

@Component({
    selector: 'csi-webi-reports',
    template: `
        <div class='webi-reports'>
            <div class="search-report">
                <input type="search" [(ngModel)]="searchName" class="search-string">
            </div>
            <div class="report-list">
                <div *ngFor="let r of reports | async | elementFilter:searchName; index as ind" class="report-item" 
                        [class.selected-item]="ind==(selectedItemIndex|async)" (click)="onSelect(ind, r)">
                    {{r.name}}
                </div>
            </div>
        </div>`,
    styles:[`
        :host {
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }
        .webi-reports{
            display: flex;
            flex-direction: column;
            flex: 1 1;
            padding: 3px;
        }
        .search-report {
            display: flex;
            flex: 0 0 auto;
        }
        .search-string {
            flex: 1 1;
        }
        .report-list {
            flex: 1 1;
            overflow: auto;
        }
        .report-item {
            cursor: pointer;
        }
        .selected-item{
            background-color: #cccccc;
        }
    `]

})

export class WebiReportsComponent implements OnInit {  

    @Input() context: ModalPropertiesContext;

    reports: Observable<ISelectableItem[]>;
    selectedItemIndex: Observable<number>;
    selectedItemName: string;
    searchName: string;
    
    constructor(private _valueService: ValueProviderService) {
    }  
      
    ngOnInit(): void {
        this.context.title = "Webi Report Name";
        this.reports = this._valueService.intelligenceReports(this.context.property, this.context.instance);

        this.context.beforeClose = (ok: boolean) => this.onBeforeClose(ok);
        this.selectedItemName = this.context.value;
        this.selectedItemIndex = this.reports.pipe(map(rl=> rl.findIndex(rs=> rs.name == this.selectedItemName)));
    }

    private onBeforeClose(ok: boolean) : boolean {
        if( ok ){
            this.context.value = this.selectedItemName;
            return true;
        }

        return true;
    }

    onSelect(i: number, r: ISelectableItem){
        this.selectedItemIndex = of(i);
        this.selectedItemName = r.name;
    }

}
