import { Component, Input,  OnInit } from '@angular/core';

import { ModalPropertiesContext } from '../properties';

@Component({
    selector: 'csi-margins',
    host: {
        'class': 'flex-column-grow'
    },
    template: `
        <div class='csi-margins'>
            <div class='view'>
                <div class="top cell">
                    <input type="number" #pxTop [value]="data.top" (change)="changeVal('top',pxTop.value)" [autofocus]="true">
                </div>
                <div class="middle">
                    <div class='left cell'>
                        <input type="number" #pxLeft [value]="data.left" (change)="changeVal('left',pxLeft.value)">
                    </div>
                    <div class="center cell"> </div>
                    <div class='right cell'>
                        <input type="number" #pxRight [value]="data.right" (change)="changeVal('right', pxRight.value)">
                    </div>
                </div>
                <div class="bottom cell">
                    <input type="number" #pxBottom [value]="data.bottom" (change)="changeVal('bottom', pxBottom.value)">
                </div>
            </div>
        </div>`
})

export class MarginsComponent implements OnInit {  

    @Input() context: ModalPropertiesContext;

    data = {left:'', top:'', right: '', bottom:''};

    get val(): string { return <string>(this.context.value); }
    set val(v: string) { this.context.value = v; }

    constructor() {
    }  
      
    ngOnInit(): void {

        this.context.title = this.context.property.displayedName || this.context.property.name;

        if (this.val) {
            let v = this.val.split(',');
            this.data.left = v[0];
            this.data.top = v[1];
            this.data.right = v[2];
            this.data.bottom = v[3];
        }
    }

    changeVal(k: string, v: string) {
        this.data[k] = v;
        let m = this.data;
        this.val = [m.left || '0', m.top || '0', m.right || '0', m.bottom || '0'].join();
    }
}
