import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

import { PortalService } from '../common/services/portal.service';

@Injectable()
export class AuthorizationService {
    isLoginSubject = new BehaviorSubject<boolean>(undefined);
    authorizationHeader: string;
    authorizationMessage: string;

    constructor(private _portalService: PortalService) {}

    isLoggedIn(): Observable<boolean> {
        this._portalService.verifyLogon()
            .subscribe(
                authorizationHeader => {
                    if (authorizationHeader) {
                        this.authorizationHeader = authorizationHeader;
                        this.isLoginSubject.next(true);
                    }
                    else
                        this.isLoginSubject.next(false);
                },
                error => {
                    console.log(<any>error);
            });
        return this.isLoginSubject.asObservable();
    }

    login(username: string, password: string, domain:string): Observable<boolean> {
        this._portalService.login(username, password, domain)
        .subscribe(
            resultStatus => {                
                if(resultStatus.error)                     
                    this.authorizationMessage = resultStatus.error.ExceptionData.Description;

                if(resultStatus.data) {       
                    this.authorizationHeader = resultStatus.data;               
                    this.isLoginSubject.next(true);
                }                
            },
            error => {
                console.log(<any>error);
            }            
        );  
        return this.isLoggedIn();    
    }

    logout(): void {
        this._portalService.logout().subscribe(response=>{
            if(response) {
                this.authorizationHeader = null;
                this.isLoginSubject.next(false);
            }            
        });        
    }

    setAuthorizationHeader(header: string): void {
        if(header) {
            this.authorizationHeader = header;
            this.isLoginSubject.next(true);
        }        
    }
    
}