import { NgModule, Optional, SkipSelf }       from '@angular/core';

import { PanelModule } from '../../../panel/panel.module';
import { SharedModule } from '../../../shared/shared.module';

import { PropertyConditionsComponent } from './propertyCondition';
import { EditConditionComponent } from './editCondition';
import { ValueConditionComponent } from './valueCondition';
import { ConditionComponent } from './condition';
import { ConditionDisplayComponent } from './conditionDisplay';
import { ExpressionComponent } from './expression';
import { InitiatorComponent } from './initiator';

@NgModule({
    imports: [
        PanelModule,
        SharedModule,
    ],
    declarations: [
        PropertyConditionsComponent,
        EditConditionComponent,
        ValueConditionComponent,
        ConditionComponent,
        ConditionDisplayComponent,
        ExpressionComponent,
        InitiatorComponent,
    ],
    exports: [
        ConditionComponent,
        ExpressionComponent,
    ],
    entryComponents: [
        PropertyConditionsComponent,
        EditConditionComponent,
        ValueConditionComponent,
        InitiatorComponent,
        ExpressionComponent
    ],
    providers: [
    ]
})
export class PropertyConditionModule {
    constructor( @Optional() @SkipSelf() parentModule: PropertyConditionModule) {
        if (parentModule) {
            throw new Error(
                'PropertyConditionModule is already loaded. Import it in the StudioModule only');
        }
    }
}

