import { Component, Input, OnChanges, OnInit, SimpleChanges} from "@angular/core";
import { PortalService } from "../../../common/services/portal.service";
import { WhereUsedItem } from "../../../common/objects/viewModel";
import { Subject} from "rxjs";
import { WCFService } from '../../../common/services/wcf.service';
import { UIComponentRef }  from '../../../common/objects/common';
import { LabelService } from "../../../common/services/label.service";
import { LocalService } from "../../../common/services/local.service";


@Component({
    selector: 'where-used-report',
    templateUrl: 'whereUsed.component.html',
    host: {
        'style': 'flex-grow: 2; display: flex; flex-direction: column;'
    },
})

export class WhereUsedReportComponent implements OnChanges,OnInit{

    @Input() name: any;

    pageList =  new Subject<WhereUsedItem[]>();
    webPartList= new Subject<WhereUsedItem[]>();
    loading:boolean=true;
    refFound:boolean=true;
    allPages : string[] = [];
    workspaceLbl:string;
    nameLbl:string;

    private tempPages : WhereUsedItem[] = [];
    private allWebParts : string[] = [];
    private tempWebParts : WhereUsedItem[] = [];
  

    constructor(private _portalService: PortalService, 
                private _wcfService: WCFService,
                private _labelService:LabelService,
                private _localService: LocalService){
                   
    }

    private checkPage(){
        if( this.allPages.length == 0 && this.allWebParts.length==0){
            this.pageList.next(this.tempPages.reverse());
            let nameItem = this.name;
            this._localService.addPageList(nameItem, this.tempPages);
            this.loading=false;
            if(this.tempPages.length==0 && this.tempWebParts.length==0){
                this.refFound=false; 
            }
            else{
                this.refFound=true; 
            }

            return;
        }
        let page = this.allPages.pop();
        this._portalService.isUsed(page, "page", this.name).subscribe( wsList => {
            if( wsList !== "-"){                     
                this.tempPages.push(<WhereUsedItem>{name: page, workSpace : wsList });      
            }
            setTimeout(() => {
                this.checkPage();
            }, 0);
        });
    }

    private checkWebParts(){
        if( this.allWebParts.length == 0 && this.allPages.length == 0){
            this.webPartList.next(this.tempWebParts.reverse());
            let nameItem = this.name;
            this._localService.addWebPartList(nameItem, this.tempWebParts);
            this.loading=false;
            if(this.tempPages.length==0 && this.tempWebParts.length==0){
                this.refFound=false; 
            }
            else{
                this.refFound=true; 
            }

                     return;
        }
        let webParts = this.allWebParts.pop();
        this._portalService.isUsed(webParts,"Webpart",this.name).subscribe( wsList => {
            if( wsList !== "-"){              
               this.tempWebParts.push(<WhereUsedItem>{name: webParts, workSpace : wsList });
            }
            setTimeout(() => {
                this.checkWebParts();
           }, 0);
        });
 
    }

    private loadLabels() {
        this.workspaceLbl = this._labelService.getLabel('PS_WORKSPACELBL', 'Workspace');
        this.nameLbl = this._labelService.getLabel('PS_NAMELBL', 'Name');
    }

    private async load() {
        this.loadLabels();
        this.loading=true;
        this._wcfService.getElementRefs("UIVirtualPageMaint", "Page", true).subscribe( (pages: UIComponentRef[]) => {
            this.allPages = pages.map( p=> p.name);
            this.checkPage();  
                
        });

        this._wcfService.getElementRefs("WebPartMaint","Webpart", true).subscribe( (webParts: UIComponentRef[]) => {
            this.allWebParts = webParts.map( p=> p.name);
            this.checkWebParts();
         });
    }

    private checkLocalStorage(ch):void{
        let pageList = this._localService.getPageList();
        let webPartList = this._localService.getWebPartList();
       
        if(pageList || webPartList){
            let pages = pageList.filter(el => el.wepPartName == ch.name.currentValue);
            let webParts = webPartList.filter(el => el.wepPartName == ch.name.currentValue);
            if(!pages[0] && !webParts[0]){
                 this.load();
            }

            if(pages[0]){
                if(pages[0].wepPartName == ch.name.currentValue){
                    setTimeout(() =>  this.pageList.next(pages[0].items), 0)
                    this.loading=false;
                    if(pages[0].items.length==0 && webParts[0].items.length==0)
                    this.refFound=false;
                    else{
                        this.refFound=true;
                    }
                }
            }
          
            if(webParts[0]){
                if(webParts[0].wepPartName == ch.name.currentValue){
                    setTimeout(() =>   this.webPartList.next(webParts[0].items), 0)
                    this.loading = false;
                    if(pages[0].items.length==0 && webParts[0].items.length==0)
                    this.refFound=false;
                    else{
                        this.refFound=true;
                    }
                }
            }
        }else{
            this.load();
        }

    }

    ngOnInit(){

    }

    ngOnChanges(ch : SimpleChanges): void {
        this.loading = false;
        this.loadLabels();
        this.checkLocalStorage(ch);
    }
   
}