import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormArray } from '@angular/forms';

import { Observable } from 'rxjs';
import { IElement, IWizardState } from '../../../common/objects/common';
import { GridColumnDefinition, IMetadataEnumValue, GridDataModes } from '../../../common/objects/viewModel';

import { LabelService } from '../../../common/services/label.service';
import { MetadataService } from '../../../common/services/metadata.service';
import { PortalService } from '../../../common/services/portal.service';

import { GridDataSource } from './gridWizard';
import { ConfirmDialogComponent } from '../../../shared/dialog/confirm.dialog.component';
import { FieldExpressionDialogComponent } from '../../../panel/properties/valueEditors/fieldExpressionDialog';
import { LabelSearchComponent } from '../../../panel/properties/valueEditors/labelSearch';

import * as _ from 'lodash';
import { StudioStateService } from '../../../common/services/studioStateService';

@Component({
    selector: 'csi-grid-wizard',
    styleUrls : ['gridWizard.component.less'],
    templateUrl: 'gridWizard.component.html'
})
export class GridWizardComponent implements OnInit {
    public state: IWizardState;
    public cell: any;
    public gridForm: FormGroup;
    public gridDataMode: string;
    public gridSourceType: string;
    public errorMessage: string = "";
    
    searchText: string;
    private enums: { [t: string]: IMetadataEnumValue[] } = {};
    private get settings() { return this.state.element.properties.settings; }

    constructor(
        private fb: FormBuilder,
        private _dialog: MatDialog,
        private _dialogRef: MatDialogRef<GridWizardComponent>,
        private _labelService: LabelService,
        private _metadataService: MetadataService,
        private _portalService: PortalService,
        private _studioStateService: StudioStateService
    ) { }

    ngOnInit(): void {
        this.gridDataMode = 'ItemList';
        this.gridForm = this.fb.group({
            gridDataMode: this.gridDataMode,
            editingMode: 'Disabled',
            fieldExpressions: null,
            requestFieldExpressions: null,
            actionButtons: this.fb.group({
                enableAdd: this.fb.control({ value: false, disabled: true }),
                enableEdit: this.fb.control({ value: false, disabled: true }),
                enableDelete: this.fb.control({ value: false, disabled: true }),
                enableRefresh: true,
                enableExportData: true,
                visibleAdd: this.fb.control({ value: false, disabled: true }),
                visibleEdit: this.fb.control({ value: false, disabled: true }),
                visibleDelete: this.fb.control({ value: false, disabled: true }),
                visibleRefresh: true,
                visibleExportData: true,
            }),
            query: this.fb.group({
                type: 0,
                searchText: '',
                queryText: '',
                definedQuery: '',
            }),
            queryList: [],
            parameters: this.fb.array([]),
            columns: this.fb.array([]),
            mappings: this.fb.array([]),
            name: this.state.element.name,
            checkAllColumn: false,
            selectedColumnType: 'JQFieldReadonly',
            titleText: '',
            titleLabel: '',
            rowId: "auto",
            selectionMode: 'singleRowSelect',
            submitDataMode: "Disabled"           
        });
        //Column
        const checkAllColumn = this.gridForm.get('checkAllColumn');
        checkAllColumn.valueChanges.subscribe(value => {
            let cols = this.gridForm.get('columns').value;            
            cols.forEach(col => col.selected = value);
            
            this.gridForm.get('columns').setValue(cols);
            this.gridForm.updateValueAndValidity();
        });    

        // Search Text
        let queryGroup = this.gridForm.get('query');
        queryGroup.get('searchText').valueChanges.subscribe(x => { 
            this.searchText = x;
            this.clearQueryParameters();
        });     
    }

    initializeNewGrid(grid: IElement, cell: any) {
        this.state = new GridDataSource(grid);
        this.cell = cell;
    }

    backStep() {
        this.state = this.state.back(this.gridForm);
    }
    
    nextStep() {
        let valid = this.validate();
        if (valid) {
            this.state = this.state.next(this.gridForm);
            this.initializeNextForm();
        }
    }

    save() {
        let gridName = this.gridForm.get('name').value;
        if (gridName.length > 0) {
            let isNameUnique = this._studioStateService.isControlNameUnique(this.cell, gridName);
            if (!isNameUnique) {
                let titleLabel = this._labelService.getLabel('PS_DUPLICATION_ERROR', 'Duplication Error');
                let messageLabel = this._labelService.getLabelwithArguments('PS_CONTROL_EXISITS_WARNING', 'The control with name {0} already exists.  The grid control cannot be created.', [gridName]);
                let okLabel = this._labelService.getLabel('PS_OK', 'Ok');
                let confirmContext = {
                    title: titleLabel,
                    message: messageLabel,
                    actions: [{ action: 'ok', name: okLabel, isPrimary: true }]
                };

                this.displayWarning(confirmContext);
                return;
            }
        }

        let grid = this.state.save(this.gridForm);
        // add and fix properties
        let s = grid.properties.settings;
        grid.properties.gridDataMode = GridDataModes[<string>this.gridDataMode];
        delete s.gridDataMode;
        if (s.editorSettings)
            s.editorSettings.editingMode = this.getEnumValue('JQEditingModes', s.editorSettings.editingMode);
        if (s.selectionModeField) {
            s.selectionMode = s.selectionModeField;
            delete s.selectionModeField;
        }

        if (s.isRequiredOnSubmitField) {
            s.isRequiredOnSubmit = s.isRequiredOnSubmitField;
            delete s.isRequiredOnSubmitField;
        }
        if (s.allowDataNavigationField) {
            s.allowDataNavigation = s.allowDataNavigationField;
            delete s.allowDataNavigationField;
        }

        if (s.navigatorActions && !_.isEmpty(s.navigatorActions)) {
            s.navigatorActions.forEach(n => {
                n.__type = 'JQNavigatorAction';
                n.Action = this.getEnumValue('JQGridNavActionType', n.Action);
            });
        }

        this._dialogRef.close(grid);
    }
    cancel() {

        //alert user of losing data
        this._dialogRef.close();
    }

    private validate(): boolean {
        let valid = true;
        let validStatus = this.state.validate(this.gridForm);
        if (validStatus) {
            let titleLabel, messageLabel;
            let okLabel = this._labelService.getLabel('PS_OK', 'Ok');
            let cancelLabel = this._labelService.getLabel('PS_CANCEL', 'Cancel');
            titleLabel = this._labelService.getLabel('PS_DATA_WARNING', "Data Warning");
            switch (validStatus) {
                case "INCOMPLETEQUERYNAME":
                    messageLabel = this._labelService.getLabel('PS_QUERY_NAME_NOT_SPECIFIED', "Query name is not specified.  Do you want to proceed to the next step?");
                    break;
                case 'INCOMPLETEQUERYTEXT':
                    messageLabel = this._labelService.getLabel('PS_QUERY_TEXT_NOT_SPECIFIED', "Query text is not specified.  Do you want to proceed to the next step?");
                    break;
                case 'QUERYTEXTEMPTY':
                    messageLabel = this._labelService.getLabel('PS_QUERY_TEXT_NOT_SPECIFIED', "Query text is Empty.  Define columns manually."); //Need to remove cancel action
                    break;
                case 'INCOMPLETEFIELDEXPRESSION':
                    messageLabel = this._labelService.getLabel('PS_FIELD_EXPRESSION_NOT_SPECIFIED', "Neither Field Expressons nor Request Field Expressions are specified.  Do you want to proceed to the next step?");
                    break;
            }
            let warningContext = {
                title: titleLabel,
                message: messageLabel,
                actions: [
                    { action: 'ok', name: okLabel, isPrimary: true },
                    { action: 'cancel', name: cancelLabel, isPrimary: false }]
            };

            this.displayWarning(warningContext).subscribe(result => {
                if (result == 'ok') {
                    this.state = this.state.next(this.gridForm);
                    this.initializeNextForm();
                }
            });
            valid = false;
        }
        return valid;
    }

    private displayWarning(context: any): Observable<any> {
        let dialog = this._dialog.open(ConfirmDialogComponent, { disableClose: true });
        dialog.componentInstance.initContext(context);
        return dialog.afterClosed();
    }

    private initializeNextForm(): void {
        let state = this.state.constructor.name;
        if (state == "GridQuery" && this.gridDataMode == "PredefinedQuery")
            this.onQueryTypeChange();
        if (state == "GridColumnSetup")
            this.initColumnForm(this.gridDataMode);
        if (state == "GridRow")
            this.initRowForm(this.gridDataMode);
        if (state == "GridActionButton")
            this.initActionButtonForm(this.gridDataMode);
    }
    
    //Grid Mode
    gridModes: any =[
        { value: 'ItemList', text: 'Item List', iconUrl:'Content/images/portalStudioIcons/gw_item_list.png', description: this._labelService.getLabel('PS_GRID_DATASOURCE_ITEMLIST', 'Supports any typed array as the data source. Can be bound to field expressions and employs regular value loading from the Camstar server. Item List grids can also work in an unbound mode, such as without field expressions. The ItemType must be defined and data loading and submitting should be implemented in the code when in an unbound mode. Paging within the Camstar server is not allowed.')},
        { value: 'SubentityList', text: 'SubEntity List', iconUrl:'Content/images/portalStudioIcons/gw_sub_list.png', description: this._labelService.getLabel('PS_GRID_DATASOURCE_ITEMLIST', 'Inherits data from the Item List. Supports a particular bound mode for the item type derived from the SubEntity class.')},
        { value: 'SelectionValues', text: 'Selection Values', iconUrl:'Content/images/portalStudioIcons/gw_selection_val.png', description: this._labelService.getLabel('PS_GRID_DATASOURCE_ITEMLIST', 'Works in a bound mode and loads data from the selection values request. Data built from selected rows can be submitted.')},
        { value: 'PredefinedQuery', text: 'Predefined Query', iconUrl:'Content/images/portalStudioIcons/gw_predefined_query.png', description: this._labelService.getLabel('PS_GRID_DATASOURCE_ITEMLIST', 'References a System or User query. Predefined Query grids allow data submission to a field using field expressions.')},
        { value: 'AdHocQuery', text: 'AdHoc Query', iconUrl:'Content/images/portalStudioIcons/gw_adhoc_query.png', description: this._labelService.getLabel('PS_GRID_DATASOURCE_ITEMLIST', 'Stores the custom query directly on the virtual page. AdHoc Query grids allow data submission to a field using field expressions.')}
    ];
    selectGridMode(mode: string): void {
        this.gridDataMode = mode;
        this.gridForm.get("gridDataMode").setValue(mode);
    }

    //Edit Mode
    editModes: any = [
        { value: 'Disabled', text: 'Disabled', iconUrl: 'Content/images/portalStudioIcons/gw_disabled.png', description: this._labelService.getLabel('PS_GRID_EDITMODE_DISABLED_DESCRIPTION', 'Editing is not allowed.')},
        { value: 'Inline', text: 'Inline', iconUrl: 'Content/images/portalStudioIcons/gw_inline.png', description: this._labelService.getLabel('PS_GRID_EDITMODE_INLINE_DESCRIPTION','Editing occurs directly in the row.')},
        { value: 'FormControls', text: 'FormControls', iconUrl: 'Content/images/portalStudioIcons/gw_form_controls.png', description: this._labelService.getLabel('PS_GRID_EDITMODE_FORMCONTROLS_DESCRIPTION','The columns are bound to the editing controls.')},
        { value: 'Popup', text: 'Popup', iconUrl: 'Content/images/portalStudioIcons/gw_popup.png', description: this._labelService.getLabel('PS_GRID_EDITMODE_POPUP_DESCRIPTION','Editing occurs in an external pop-up form.')},
        { value: 'LitePopup', text: 'LitePopup', iconUrl: 'Content/images/portalStudioIcons/gw_lite_popup.png', description: this._labelService.getLabel('PS_GRID_EDITMODE_LITEPOPUP_DESCRIPTION','Editing occurs in a web part on the current page.')}
    ];
    selectGridSourceType(type: string): void {
        this.gridSourceType = type;
        this.gridForm.get("editingMode").setValue(type);
    }    

    //Field Expression
    openFieldExpressionDialog(formControlName: string): void {
        //open dialog
        let formControl = this.gridForm.get(formControlName);

        let newdialog = this._dialog.open(FieldExpressionDialogComponent, { width: '450px', disableClose: true });
        newdialog.componentInstance.initContext(formControl.value);
        newdialog.afterClosed().subscribe(result => {
            this.gridForm.get(formControlName).setValue(result);
            this.gridForm.updateValueAndValidity();
        });
    }

    //Action Button
    initActionButtonForm(dataMode: string): void {
        if (dataMode == 'ItemList' || dataMode == 'SubentityList') {
            let editMode = this.gridForm.get('editingMode').value;
            if (editMode != "Disabled") {
                this.gridForm.get('actionButtons.enableAdd').enable();
                this.gridForm.get('actionButtons.enableEdit').enable();
                this.gridForm.get('actionButtons.enableDelete').enable();
                this.gridForm.get('actionButtons.visibleAdd').enable();
                this.gridForm.get('actionButtons.visibleEdit').enable();
                this.gridForm.get('actionButtons.visibleDelete').enable();
            }
        }
    }

    //Query
    get parameters(): FormArray {
        return <FormArray>this.gridForm.get('parameters');
    }

    onQueryTypeChange(): void {
        let qtype = this.gridForm.get('query.type').value;
        this._portalService.getQueryData(qtype == 0 ? 'QueryList_System' : 'QueryList_User')
            .subscribe(result => {                
                this.gridForm.get('queryList').setValue(result);
                this.clearQueryParameters();
                this.gridForm.updateValueAndValidity();
            });
    }

    onQuerySelect(query: any): void {
        let queryList = this.gridForm.get('queryList').value;
        queryList.forEach(qry => qry.selected = (query.Text == qry.Text));

        this.state.back(this.gridForm);
        this.gridForm.get('query.definedQuery').setValue(query.Text);
        if (query.ParamCount > 0) {
            let type = this.gridForm.get('query.type').value;
            this._portalService.getQueryParameters(type, query.Text).subscribe(result => {
                let parameters = <FormArray>this.gridForm.get('parameters');
                result.forEach(param => {
                    parameters.push(this.fb.group({
                        name: param.Text,
                        type: param.Description,
                        value: ''
                    }));
                });
                this.gridForm.updateValueAndValidity();
            });
        }
        else {
            //no parameter records to show
        }
    }

    private clearQueryParameters(): void {
        const parameters = <FormArray>this.gridForm.controls['parameters'];        
        for (let i = parameters.length-1; i >= 0; i--) {
            parameters.removeAt(i)
        }
    }

    //Columns
    public columnTypes: string[] =
    ["JQDropDownList", "JQDateChooser", "JQNamedObject", "JQNamedSubentity", "JQTextBox", "JQRevisionedObject", "JQFieldCheckBox", "JQFieldReadonly"];
    get columns(): FormArray {
        return <FormArray>this.gridForm.get('columns');
    }

    initColumnForm(dataMode: string): void {
        switch (dataMode) {
            case 'ItemList':
            case 'SubentityList':
                this.loadColumnsFromFieldExpression();
                break;
            case 'SelectionValues':
                this.loadColumnsFromSelectionValues();
                break;
            case 'AdHocQuery':
            case 'PredefinedQuery':
                this.loadColumnsFromQuery(dataMode);
                break;
        }
    }
    toggleColumnSelection(cb: any): void {
        let columns = this.gridForm.get('columns').value;
        columns.forEach(col => col.selected = cb.checked);
        this.gridForm.updateValueAndValidity();
    }

    loadColumnsFromFieldExpression(): void {

        let fieldExpression = this.settings.requestFieldExpressions || this.settings.fieldExpressions;

        this._portalService.resolveGridColumns(this.addService(fieldExpression), false).subscribe(columns => {
            if (_.isArray(columns))
                this.loadColumns(columns, fieldExpression);
            else {
                // display error
            }
        });
    }

    loadColumnsFromSelectionValues(): void {
        let fieldExpression = this.settings.selectionValuesExpressions || this.settings.fieldExpressions;

        this._portalService.resolveGridColumns(this.addService(fieldExpression), true).subscribe(columns => {
            if (_.isArray(columns))
                this.loadColumns(columns, fieldExpression);
            else {
                // display error
            }
        });
    }

    loadColumnsFromQuery(dataMode: string): void {
        let fieldExpression = this.state.element.properties.settings.selectionValuesExpressions;
        let qtype = this.gridForm.get('query.type').value;
        let parameters = this.gridForm.get('parameters').value;
        let queryOptions = {
            QueryType: qtype,
            StartRow: 1,
            RowSetSize: 1
        };

        let queryParameters = [];
        parameters.forEach(param => queryParameters.push({ Name: param.name, Value: param.value }));

        if (dataMode == "AdHocQuery") {
            let queryText = this.gridForm.get('query.queryText').value;
            if (queryText.indexOf('?') > 0) {
                parameters.forEach(param => queryText.replace('?', param.value));
            }
            this._portalService.getAdHocQueryColumns(queryText, queryOptions).subscribe((columns : GridColumnDefinition[]) => {
                this.loadColumns(columns, fieldExpression);
            });
        }
        else {
            let queryText = this.gridForm.get('query.definedQuery').value;
            this._portalService.getQueryColumns(queryText, queryOptions, queryParameters).subscribe((columns:GridColumnDefinition[]) => {
                this.loadColumns(columns, fieldExpression);
            });
        }
    }

    loadColumns(cols: GridColumnDefinition[], fieldExpression: string): void {
        if (cols) {
            let columns = <FormArray>this.gridForm.get('columns');
            _.forEach(cols, c => columns.push(this.fb.group({
                columnTypeIndex: '',
                columnType: c.__type,
                id: c.name,
                editable: c.editable,
                required: c.required,
                width: c.width,
                selected: false,
                hidden: false,
                labelName: c.labelName
            })));
            this.gridForm.updateValueAndValidity();
        }
    }

    private addService(f: string): string {
        if (f && f.startsWith('.')) {
            let serviceType = this._studioStateService.getPrimaryService();
            if (serviceType)
                return serviceType + f;
        }
        return f;
    }

    onCreateColumn(): void {
        let columns = <FormArray>this.gridForm.get('columns');
        let columnType = this.gridForm.get('selectedColumnType').value;
        columns.push(this.fb.group({
            columnTypeIndex: '',
            columnType: columnType,
            id: 'Column',
            editable: false,
            required: false,
            width: 200,
            selected: false,
            hidden: false,
            labelName: ''
        }));
        columns.updateValueAndValidity();
    }


    onMoveColumn(id: string, direction: string): void {
        let columns = <FormArray>this.gridForm.get('columns');
        let columnsObj = columns.getRawValue();
        let column, index;

        for (let i = 0; i < columns.length && !index; i++) {
            if (columnsObj[i].id == id) {
                column = columns.controls[i];
                index = i;
            }
        }
        if (direction == "up") {
            if (index == 0)
                return;
            let oldControl = columns.at(index-1);
            columns.setControl(index-1,column);
            columns.setControl(index,oldControl);
            
        }
        if (direction == "down") {
            if (index == columns.length)
                return;
            let oldControl = columns.at(index+1);
            columns.setControl(index+1,column);
            columns.setControl(index,oldControl);
        }
    }


    //Row Setup
    public displaySubmitDataMode;
    public gridSelectionModes: any[] = [
        { text: "MultiRowSelect", value: "multiRowSelect" },
        { text: "CheckBox", value: "checkBox" },
        { text: "SingleRowSelect", value: "singleRowSelect" },
        { text: "SingleRowSelectWithPostback", value: "singleRowSelectWithPostback" },
        { text: "Disable", value: "disable" }];
    public rowColumns: any[];
    public submitFields: any[];

    get mappings(): FormArray {
        return <FormArray>this.gridForm.get('mappings');
    }

    initRowForm(dataMode: string): void {
        this.rowColumns = [{ text: "--AUTO--", value: "auto" }];
        this.submitFields = [{ text: "", value: "" }];
        let columns = this.gridForm.get('columns').value;
        let mappings = <FormArray>this.gridForm.get('mappings');
        columns.forEach(col => {
            if (col.selected) {
                this.rowColumns.push({ text: col.id, value: col.id });
                mappings.push(this.fb.group({ columnName: col.id, submitField: '' }));
            }
        });
        this.displaySubmitDataMode = (dataMode == "SelectionValues")
    }

    onChangeSubmitMode(): void {
        //load submitFields
        let fieldExpression = this.state.element.properties.settings.fieldExpressions;
        if (fieldExpression) {
            this._portalService.resolveGridColumns(fieldExpression, false).subscribe(columns => {
                if (_.isArray(columns)) {
                    _.forEach(columns, col => {
                        let text = col.labelName;
                        let colExp = col.name.substring(1, col.name.length).split(',');
                        this.submitFields.push({ text: text, value: colExp[3] });
                    });
                }
            });
        }
    }

    openLabelSearch(): void {
        let label = this.gridForm.get('titleLabel');
        let context = {
            value: label.value
        }
        let newDialog = this._dialog.open(LabelSearchComponent, { disableClose: true });
        newDialog.componentInstance.initContext(context);
        newDialog.afterClosed().subscribe(result => {
            if (result)
                label.value.setValue(result);
        });
    }

    private getEnumValue(typeName, stringVal: string): number {
        if (stringVal) {
            if (!this.enums[typeName]) {
                this.enums[typeName] = this._metadataService.getMetadataType(typeName).enumValues;
            }
            let nvPairs = this.enums[typeName];
            stringVal = stringVal.toLowerCase();
            let v = _.find(nvPairs, (e) => e.name.toLowerCase() == stringVal);
            if (v)
                return v.value;
        }

        return undefined;
    }

}