import { Injectable } from "@angular/core";
import { LocalService } from "./local.service";
import { ContextMenuItem, ContextMenuItemTypes, GroupDef, IUIComponentRef, ContextMenuItemPositions } from "../objects/common";
import { Observable, of } from "rxjs";
import { PortalService } from "./portal.service";
import { LabelService } from "./label.service";
import { StudioStateService } from "./studioStateService";
import { PortalSettingsService } from "./portalSettings.service";
import _ from 'lodash';
import { map } from "rxjs/operators";

@Injectable()
export class PageGroupingService {
    private systemGroups = [
        this.grp('PS_ALLPAGECATEGORY', 'All'),
        this.grp('PS_CHGMGMTPAGECATEGORY', 'Change Management'),
        this.grp('PS_GENERALPAGECATEGORY', 'General'),
        this.grp('PS_MDLPAGECATEGORY', 'Modeling'),
        this.grp('PS_QUALITYPAGECATEGORY', 'Quality'),
        this.grp('PS_SHOPFLOORPAGECATEGORY', 'Shopfloor'),
        this.grp('PS_UNDEFINEDPAGECATEGORY', 'Undefined')
    ];

    private SpecialGroups = ["all","undefined"];
    private _pageAssignments: PageAssignment[] = [];

    constructor( _portalService: PortalService,
            private _labelService: LabelService, 
            private _localService: LocalService,
            private _settingsService : PortalSettingsService,
            private _studioStateService:StudioStateService) {
        
        // load groups from Settings and merge with local storage
        this.assignPages();
    }
    
    public getContextMenuItems(dataType: string, data: string): ContextMenuItem[] {

        let res = [new ContextMenuItem(ContextMenuItemTypes.caption, data, {linePos: ContextMenuItemPositions.below})];
        if (dataType == 'page') {
            const page = data;
            const pageGroups = this.systemGroups.map(g=> new ContextMenuItem(ContextMenuItemTypes.checked, g.groupName, {key: g.groupKey}));
            // get groups for the page
            res = res.concat(pageGroups.filter(pg=>! this.SpecialGroups.includes(pg.key.toLowerCase())));
            this._localService.getPageGroups().map(p => new GroupDef(p.groupName, 'fa-folder', p.groupKey))
                .forEach(gr => {
                    if (!pageGroups.find(n => n.text == gr.groupName))
                        res.push(new ContextMenuItem(ContextMenuItemTypes.checked, gr.groupName, {key:gr.groupKey}));
                });
            res.push(new ContextMenuItem(ContextMenuItemTypes.selected, "Add a new group", {linePos : ContextMenuItemPositions.above, nameItem: page, key: "addToNewGrp"}));
            res.forEach(r=> {
                    if(r.type == ContextMenuItemTypes.checked){
                        r.checked = !! this._pageAssignments.find(p=> p.name == page && p.groupKey == r.key);
                        r.nameItem = page;
                    }
            });
        }
        else if (dataType == 'group') {
            res.push(new ContextMenuItem(ContextMenuItemTypes.selected, "Edit", {nameItem: data, key: "edit"}));
            res.push(new ContextMenuItem(ContextMenuItemTypes.selected, "Remove", {nameItem: data, key: "remove"}));
            res.push(new ContextMenuItem(ContextMenuItemTypes.selected, "Add a new group", {linePos: ContextMenuItemPositions.above , nameItem: "addGroup", key: "add"}));
            res.push(new ContextMenuItem(ContextMenuItemTypes.selected, "Restore default settings", {nameItem : data, key: "restore"}));
        }
        return res;
    }

    public getPageGroups(pageItems: Observable<IUIComponentRef[]>): Observable<GroupDef[]> {
        return pageItems.pipe(map( pitems =>{
            const removedGroup = this._localService.getPageGroups().filter(p=> p.deleted);

            // system groups without removed
            const grps = this.systemGroups.filter(g=> !removedGroup.find(r=> g.groupKey == r.groupKey));
            // add local groups that not exists
            this._localService.getPageGroups().forEach(lgr => {   
                if (lgr.deleted !== true && !grps.find(n => n.groupName == lgr.groupName))
                    grps.push(new GroupDef(lgr.groupName, 'fa-folder', lgr.groupKey));
            });


            // Fill group items
            grps.forEach(g => {
                const gk = g.groupKey;
                if(gk == this.SpecialGroups[0] /*All*/){
                    g.items = of(pitems);
                }
                else if(gk == this.SpecialGroups[1] /* Undefined */){
                    g.items = of(pitems.filter(it => !this._pageAssignments.find(a=> a.name == it.name)));
                    g.count = g.items.pipe(map(it => it.length));            
                }
                else {
                    g.items = of(pitems.filter(it => this._pageAssignments.find(a=> a.name == it.name && a.groupKey == gk)));
                }
            });

            return grps.sort((a, b) => a.groupName.localeCompare(b.groupName));
        }));
    }

    public getWebPartGroups(): GroupDef[] {
        return [
            this.grp('PS_GENERALPAGECATEGORY', 'General'),
            this.grp('PS_INTELLIGENCELBL', 'Intelligence'),
            this.grp('PS_VIRTUALWEBPARTSCTGY', 'Virtual Web Parts')
        ];

    }

    public toggle(item: ContextMenuItem) {
        item.checked = !item.checked;
        if (item.checked && item.type != ContextMenuItemTypes.selected) {
            this._localService.addPageToGroup(item.nameItem, item.text);
        }
        else{
            this.removePageFromGroup(item);
        }

        this.refreshGroups();
    }

    public isSystemGroup(name: string) : boolean{
        return !!this.systemGroups.find(g=> g.groupName == name);
    }

    private grp(nameLabelName: string, nameLabelText: string, icon: string = 'fa-folder'): GroupDef {
        let lbl = this._labelService.getLabel(nameLabelName, nameLabelText);
        if (lbl.endsWith("({0})"))
            lbl = lbl.replace("({0})", "");
        let key = nameLabelText.replace(" ", "").toLowerCase();

        return new GroupDef(lbl, icon, key, null, null, false);
    }

    private assignPages(){
        this._pageAssignments = [];
        const studioSettings = this._settingsService.getSectionData("studioSettings");
        Object.keys(studioSettings)
            .filter(kx=> /PagesField$/.test(kx) )
            .forEach(k=>
                studioSettings[k].forEach( 
                    (m:any) => this._pageAssignments.push(new PageAssignment(m.nameField, k.substr(7, k.length-17) /* remove default and PagesField */))
                )
        );

        this._localService.getPageGroups().forEach(p=>{
            if(p.items)
                p.items.forEach(i=>{
                    if(i.deleted === true)
                        _.remove(this._pageAssignments, (pa) => pa.name == i.name && p.groupKey == pa.groupKey);
                    else
                        this._pageAssignments.push(new PageAssignment(i.name, p.groupKey));
                });
        });
    }

    public restoreSettings() {
        this._localService.resetSettingsPageGroups();
    }

    public addGroup(name: string) {
        this._localService.addPageGroup(name);
        this.refreshGroups();
    }

    public removeGroup(groupName: string) {
        const sgrp = this.systemGroups.find(n => n.groupName == groupName);
        if (!sgrp) {
            this._localService.removeGroup(groupName);
        }
        else {
            // If system group = add group to local with deleted status
            this._localService.addPageGroup(groupName, true)
        }
    }

    public moveToGroup(namePage: string, nameGroup: string) {
        const allGrp = this._localService.getPageGroups()
        const gr = allGrp.find(n => n.groupName == nameGroup);
        if (!gr) {
            this._localService.addPageGroup(nameGroup);
        }
        this._localService.addPageToGroup(namePage, nameGroup);
        this.refreshGroups();
    }

    public editGroup(newName: string, oldName: string) {
        this._localService.editPageGroup(newName, oldName);
        this.refreshGroups();
    }

    public removePageFromGroup(item: ContextMenuItem) {
        this._localService.removePageFromGroup(item.nameItem, item.text);
    }

    private refreshGroups(){
        this.assignPages();
        this._studioStateService.refreshElementPanel("Page");
    }

}

class PageAssignment{
    constructor(public name: string, public groupKey: string){
        this.groupKey = groupKey.toLowerCase();
    }
}
