import { IElement, LayoutActions, LayoutBuilder, DesignModes } from '../../../common/objects/common';
import { ICell, LayoutCell, ICellItem } from './layoutCell';
import { HistoryLog, LayoutHistoryData, RecordCommand } from '../../../common/objects/historyLog';
import _ from 'lodash';
import { CellSelection } from './cellSelection';

export class LayoutManager {

    public layoutMode: DesignModes;
    public selection : CellSelection;

    get layoutModel(): any {
        if (!this.elementModel.layout) { // referenced webparts for now.
            this.createEmptyLayout(0,0);
        }
        return this.elementModel.layout;
    }
    set layoutModel(model: any) { this.elementModel.layout = model; }

    get tblRows() : any[][] { return  (this.layoutModel && this.layoutModel.tableCells as any[][]) || []; }
    set tblRows(tbl : any[][]) { this.layoutModel.tableCells = tbl; }
    
    get rowLength(): number { return this.layoutModel.rowNumber; }
    set rowLength(rows: number) { this.layoutModel.rowNumber = rows; }

    get colLength(): number { return this.layoutModel.columnNumber; }
    set colLength(cols: number) { this.layoutModel.columnNumber = cols; }

    get isStatic(): boolean { return this.layoutModel.isStatic === true; }

    get isActive(): boolean { return this.selection.isSelected; }

    get allCells() : LayoutCell[]{ return _.flatten(this.matrix); }

    private _oldTblRows : any[][];

    constructor(private elementModel: IElement/* Page | WebPart */, private _historyLog: HistoryLog) {
        this.fillLayoutMatrix();
        this.selection = new CellSelection();
    }

    public refresh(): void {
        this.fillLayoutMatrix();
    }

    public reset() : void {
        this._oldTblRows = [];
        this.clearSelection();
    }

    public renameLayoutRefs(oldname: string, newname: string) {
        this.allCells.forEach(c=> {            
            if( c.items ){
                let r = _.find(c.items,  (ci:any) => ci.name == oldname );
                if( r ){
                    r.name = newname;
                    // stop processing
                    return false;
                }
            }
        });
    }

    private createEmptyLayout(rows: number, cols: number) {
        this.layoutModel = {};
        this.rowLength = rows;
        this.colLength = cols;
        this.layoutModel.tableCells = [];
        this.layoutModel.__type = 'GridLayout';
        this.layoutModel.isStatic = false;
    }

    // tableCells -> matrix.
    private fillLayoutMatrix() {

        
        if(this._historyLog){
            if( _.isEqual(this._oldTblRows, this.tblRows) && this._historyLog.undoCount == 0 && this._historyLog.redoCount == 0){
                // no changes -- layout is not rebuilt
                return;
            }
         }    

        let grps : any[];
        let currentIndex = 0;
        if(this.elementModel.properties && this.elementModel.properties.collapsibleGroups ){            
            grps = [];
            this.elementModel.properties.collapsibleGroups.forEach((g:any)=>{
                for(let i=0; i<g.rows; i++){
                    grps.push(currentIndex);
                }
                currentIndex++;
            });
        }

        if (this.tblRows && this.tblRows.length > 0) {
            this.matrix =
                _.map( this.tblRows, (tableCells, i) => {
                    return _.map( tableCells, (tc, j) => {
                        let lc = new LayoutCell(tc.col, tc.row, tc);
                        if(grps && grps.length)
                            lc.sectionRowNumber = grps[i];
                        return lc;
                    });
                });
        }
        else {
            // if there is no layout (UserFieldsWebPart case) - add one cell.
            this.rowLength = this.colLength = 1;
            this.layoutModel.isStatic = false;
            this.matrix = Array<LayoutCell[]>([new LayoutCell(0,0)]);
        }
        this._oldTblRows = _.cloneDeep(this.tblRows);        
    }

    public selectCell(cell: LayoutCell) : boolean{
        let selected = false;
        this.selection.select(cell, this.matrix);
        selected = true;
        return selected;
    }

    public removeCellItem(cell:ICell, element:IElement): boolean {
        let removed = false;

        if (element) {

            // remove reference
            let itemKey = _.findKey(cell.items, (item : ICellItem, key) => item.name == element.name);
            if (itemKey) {

                // add removed items to the removed list
                if (cell.items[itemKey].isStatic){                    
                    cell.items[itemKey].typeName = element._fullTypeName;
                    this.layoutModel.removedItems = this.layoutModel.removedItems || [];
                    this.layoutModel.removedItems.push(cell.items[itemKey]);
                }
                delete cell.items[itemKey];
                removed = true;

                // reorder item refs
                let ind = 0;
                let newItems : {[index:string] : ICellItem} = {};
                _.map( cell.items, (ci:ICellItem) => {newItems[ind.toString()] = ci; ind++;} );
                cell.items = newItems;
            }
        }
        return removed;
    }


    private removeMatrixColumn(tbl: any[][], index : number, key = 'col'){
        const spanKey = key + 'Span';
        tbl.forEach( (cells) => {
            let indexToRemove = index;
            let spannedCell = cells.find(cr=> cr[spanKey] > 1 && indexToRemove >= cr[key] && indexToRemove < (cr[key] + cr[spanKey]));
            if( spannedCell ){
                spannedCell[spanKey]--;
                if( spannedCell[key] == indexToRemove)
                indexToRemove++;
            }
            let removedCell = cells.splice(indexToRemove, 1)[0];
            this.layoutModel.removedCells = this.layoutModel.removedCells || [];
            // remove items if there is any
            _.forEach(removedCell.items, it=> this.removeCellItem(removedCell, it));

            this.layoutModel.removedCells.push(removedCell);
            _.range( indexToRemove, cells.length ).forEach(i=> cells[i][key]-- );
        });
    }

    // handle remove column command.
    private removeColumn(): void {

        if (this.colLength < 2) {
            // do not allow to remove --- keep visible at least 1 column
            return;
        }        
        this.removeMatrixColumn(this.tblRows, this.selection.start.col);
        this.colLength--;
        this.clearSelection();
    }

    // transpose matrix
    private transpose(t: any[][] ) : any[][]{
        return t[0].map((col, i) => t.map((row) => row[i]));
    }

    // handle remove row command.
    private removeRow(): void {

        if (this.rowLength < 2) {
            // do not allow to remove the last row
            return;
        }

        // transpose matrix
        let transposedTbl = this.transpose(this.tblRows);
        this.removeMatrixColumn(transposedTbl, this.selection.start.row, 'row');

        // transpose back
        this.tblRows = this.transpose(transposedTbl);
        this.rowLength--;
        this.clearSelection();
    }

    // handle split command.
    private splitSelectedCells(): void {
        this.selection.split(this.tblRows);
        this.clearSelection();
    }

    // handle Merge command.
    private mergeSelectedCells(): void {
        this.selection.merge(this.tblRows);
        this.clearSelection();
    }

    public clearSelection(): void {
        this.selection.clear(this.tblRows);
        this.fillLayoutMatrix();
    }

    private insertRow(rowIndex: number): void {
        let transposed = this.transpose(this.tblRows);
        this.insertColumnMatrix(transposed, rowIndex, 'row');
        this.tblRows = this.transpose(transposed);
        this.rowLength++;
        this.clearSelection();
    }

    private insertColumnMatrix(tbl:any[][], indexToInsert : number, key = 'col'){
        const spanKey = key+ 'Span';
        tbl.forEach( (cells, rIndex) => {
            let colIndex = indexToInsert;
            let cellHidden = false;
            let spannedCell = cells.find(cx=> cx[spanKey] > 1 && colIndex > cx[key] && colIndex < (cx[key] + cx[spanKey]));
            if( spannedCell ){
                spannedCell[spanKey] ++;
                cellHidden = true;
            }
            let nc = key == 'col' ? 
                        LayoutBuilder.newCell(rIndex, colIndex, cellHidden) : 
                        LayoutBuilder.newCell(colIndex, rIndex, cellHidden);
            if( colIndex == this[key+'Length']  )
                cells.push(nc);
            else{
                cells.splice( colIndex, 0, nc);
                _.range( colIndex+1, cells.length ).forEach(i=> cells[i][key]++ );
            }            
        });        
    }

    private insertColumn(colInsertAtPosition: number): void {
        this.insertColumnMatrix(this.tblRows, colInsertAtPosition);
        this.colLength++;
        this.clearSelection();
    }

    public resetSelected(): void {
        this.clearSelection();
    }

    public executeCommand(cmd: LayoutActions) {

        let oldLayout = this.cloneLayout();

        switch (cmd) {
            case LayoutActions.merge:
                this.mergeSelectedCells(); break;
            case LayoutActions.split:
                this.splitSelectedCells(); break;
            case LayoutActions.insertRowBefore:
                if (this.selection.isSelected)
                    this.insertRow(this.selection.start.row);
                break;
            case LayoutActions.insertRowAfter:
                if (this.selection.isSelected)
                    this.insertRow(this.selection.start.row+this.selection.start.rowSpan);
                break;
            case LayoutActions.insertColumnBefore:
                if (this.selection.isSelected)
                    this.insertColumn(this.selection.start.col);
                break;
            case LayoutActions.insertColumnAfter:
                if (this.selection.isSelected)
                    this.insertColumn(this.selection.start.col+this.selection.start.colSpan);
                break;
            case LayoutActions.removeRow:
                this.removeRow(); break;
            case LayoutActions.removeColumn:
                this.removeColumn(); break;
            case LayoutActions.reset:
                break;
            case LayoutActions.toggleCellPos:
                break;
        }

        if (cmd != LayoutActions.reset && cmd != LayoutActions.toggleCellPos) {
            // record into the history
            let newLayout = this.cloneLayout();
            this._historyLog.recordHistoryItem(RecordCommand.layoutChange, new LayoutHistoryData(cmd, oldLayout, newLayout, this.elementModel));
        }
        this.resetSelected();
    }

    private cloneLayout(): any {
        return this.layoutModel.tableCells.map(
            row => row.map(cell => {
                let c: LayoutCell = _.clone(cell);
                c.isSelected = false;
                let clonedItems = {};
                _.forEach(cell.items, it => clonedItems[it.id] = _.clone(it));
                c.items = clonedItems;
                return c;
            })
        );
    }

    public getCommands(){
        return this.selection.getEnabledCommands().cmdEnabled;
    }

    public findLayoutCell(c: ICell) : LayoutCell {
        return _.flatten(this.matrix).find(cell=> cell.name == c.name);
    }

    public findLayoutCellById(id:string) : LayoutCell {
        return _.flatten(this.matrix).find(cell=> cell.id == id);
    }

    // ViewModel of layout
    private matrix: LayoutCell[][];
}
