import { Component, Input, OnChanges, Output, EventEmitter, ElementRef, AfterViewChecked } from '@angular/core';
import { Subject } from 'rxjs';
import { AlertService } from '../../alert/alert.service';
import { ILabel } from '../../common/objects/common';

@Component({
    selector: 'csi-hierarchy',
    templateUrl: 'hierarchy.component.html',
    styles:[`
        :host{
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }

        .csi-hierarchy {
            height: 100%;
            overflow: auto;
            border: solid 1px black;
        }
        .csi-hierarchy .list {
            display: flex;
            flex-direction: column;
        }
        .csi-hierarchy .list .item {
            white-space: nowrap;
        }

        .csi-hierarchy .list .item i[hidden] {
            display: none;
        }
  
        .csi-hierarchy .list .item .name {
            padding: 1px;
            cursor: pointer;
        }

        .csi-hierarchy .list .item .name.selected {
            background-color: aquamarine;
        }
  
        .csi-hierarchy .list .item .name:hover {
            background-color: cornflowerblue;
        }
    `]
})

export class HierarchyComponent implements OnChanges, AfterViewChecked {

    @Input() data: any;
    @Input() childrenField: string;
    @Input() search: Subject<string>;

    @Output() onSelect = new EventEmitter<any>();
    @Output() getNodeName = new EventEmitter<any>();

    list: listItem[];
    selectedItem: listItem;
    divList: HTMLElement;

    constructor(private _el: ElementRef, private _alertService: AlertService) {
        this.list = [];
    }   

    ngOnChanges(ch) {
        if (ch.data && ch.data.currentValue) {
            this.build(ch.data.currentValue, 0);
        }

        if (ch.search && ch.search.currentValue) {
            this.search = ch.search.currentValue;
            this.search.subscribe(s => this.doSearch(s));
        }
    }

    ngAfterViewChecked() {
        this.divList = this._el.nativeElement.getElementsByClassName('list')[0];
    }

    doSearch(searchString: string) {
        let si: listItem = null;
        if (searchString) {
            // searching starts from selectedItem if set
            let startIndex = this.selectedItem ? this.list.indexOf(this.selectedItem) : -1;
            si = this.list.find((l, i: number) => (i > startIndex) && (l.name.toLowerCase().indexOf(<string>(searchString)) != -1));
            // If next item isn't found keen previous item
            if(!si && this.selectedItem){
                this._alertService.confirmModal(<ILabel>{Name: "PS_NO_MORE_MATCHES", DefaultValue: "No more matches can be found. Click OK to start from the beginning"})
                    .subscribe(ok=> {
                        if( ok ){
                            this.selectedItem = null;
                            this.search.next(searchString);
                        }
                    });
            }
        }

        this.selectedItem = si;
        this.onSelect.emit(this.selectedItem ? this.selectedItem.node : null);
        if (this.selectedItem)
            this.divList.children[this.list.indexOf(this.selectedItem)].scrollIntoView();

    }

    expand(l: listItem) {
        l.expanded = !l.expanded;

        let i = this.list.indexOf(l);
        // hide children
        for (i++; i < this.list.length; i++) {
            if (this.list[i].level > l.level)
                this.list[i].hidden = !l.expanded;
            else
                break;
        }
    }

    selectItem(l: listItem) {
        this.selectedItem = l;
        this.onSelect.emit(l.node);
    }

    private build(node, level: number) {
        let nx = { node: node, name: '' };
        this.getNodeName.emit(nx);
        let it = <listItem>{ expanded: true, level: level, name: nx.name, childrenNumber: 0, hidden: false, node: node };
        this.list.push(it);

        let children = <any[]>node[this.childrenField];
        if (children && children.length) {
            it.childrenNumber = children.length;
            children.forEach(c => {
                this.build(c, level+1);
            });
        }
    }
}


class listItem {
    level: number;
    name: string;
    expanded: boolean = true;
    childrenNumber: number = 0;
    hidden: boolean = false;
    node: any;
}

