import { uuid, val } from "../../../common/objects/common";

export interface ICellItem {
    _ref: boolean;
    type: string;
    name: string;
    id: string;
    isStatic?: boolean;
    typeName?: string;
}

export interface ICell {
        id: string;
        name: string;
        type: string;
        properties: any;

        col: number;
        colSpan: number;
        row: number;
        rowSpan: number;

        isHidden?: boolean;
        isEmpty?: boolean;
        isSelected?: boolean;
        items: { [index: string]: ICellItem };
        itemLength?: number;
        cell?: ICell;
        sectionRowNumber? : number;
}

export class LayoutCell implements ICell
{ 
    public cell: ICell;

    get id(): string {return this.cell.id;}
    set id(id: string) {this.cell.id = id;}

    get name(): string { return this.cell.name; }
    set name(name: string) { this.cell.name = name; }

    get col(): number { return this.cell.col; }
    set col(col: number) { this.cell.col = col; }

    get colSpan(): number { return this.cell.colSpan;}
    set colSpan(colSpan: number) {this.cell.colSpan = colSpan;}

    get lastCol() : number {return this.cell.col + this.cell.colSpan-1;}
    
    get row(): number {return this.cell.row;}
    set row(row: number) {this.cell.row = row;}

    get rowSpan(): number {return this.cell.rowSpan;}
    set rowSpan(rowSpan: number) {this.cell.rowSpan = rowSpan;}

    get sectionRowNumber(): number {return this.cell.sectionRowNumber;}
    set sectionRowNumber(v: number) {this.cell.sectionRowNumber = v;}

    get lastRow() : number {return this.cell.row + this.cell.rowSpan-1;}

    get isHidden(): boolean {return this.cell.isHidden;}
    set isHidden(isHidden: boolean) {this.cell.isHidden = isHidden;}

    get isEmpty(): boolean {return this.cell.isEmpty;}
    set isEmpty(isEmpty: boolean) {this.cell.isEmpty = isEmpty;}

    get isSelected(): boolean {return this.cell.isSelected;}    
    set isSelected(isSelected: boolean) {this.cell.isSelected = isSelected;}
    
    get items(): { [index: string]: ICellItem } {return this.cell.items;}
    set items(items: { [index: string]: ICellItem }) {this.cell.items = items;}

    get itemLength(): number {return Object.keys(this.cell.items).length;}

    get properties() : any { return this.cell.properties; }
    get style() : any { return this.cell.properties.style; }

    get cellStyle() { return this.getCellStyle(); }

    get orientation(): string {

        let i = 0;
        if(this.style && this.style.Orientation)
            i = this.style.Orientation;
        return ['native','vertical','horizontal'][i];
    }

    get horizontalAlignment(): string {
        if(this.style && this.style.HorizontalAlignment && this.style.HorizontalAlignment!=0){
            return ['left','right','mddle'][this.style.HorizontalAlignment-1];
        }
        return null;
    }
    
    get verticalAlignment(): string {
        let va = (this.style && this.style.VerticalAlignment) || 0;
        if(va!=0){
            return ['top','bottom','mddle'][va-1];
        }
        return null;
    }

    private getCellStyle() : any{
        let st = {};
        const column = 1;
        const row = 2;
        if(this.style &&  typeof val(this.style, "orientation") != "undefined" ){
            if( val(this.style, "orientation") == column)
                st["flex-direction"] = "column";
            else if( val(this.style, "orientation") == row)
                st["flex-direction"] = "row";
        }

        // Vertical alignment

        if(this.style &&  typeof val(this.style, "verticalAlignment") != "undefined" ){
           
            if( val(this.style, "orientation") ==  column){
                if( val(this.style, "verticalAlignment") == 1)  // top
                    st["justify-content"] = "flex-start";
                else if( val(this.style, "verticalAlignment") == 2) // bottom
                    st["justify-content"] = "flex-end";
                else if( val(this.style, "verticalAlignment") == 3) // middle
                    st["justify-content"] = "center";
            }
            if( val(this.style, "orientation") ==  row){
                if( val(this.style, "verticalAlignment") == 1)  // top
                    st["align-items"] = "flex-start";
                else if( val(this.style, "verticalAlignment") == 2) // bottom
                    st["align-items"] = "flex-end";
                else if( val(this.style, "verticalAlignment") == 3) // middle
                    st["align-items"] = "center";
            }
        }

        if(this.style &&  typeof val(this.style, "HorizontalAlignment") != "undefined" ){
            if( val(this.style, "orientation") ==  column){
                if( val(this.style, "horizontalAlignmen") == 1)  // left
                    st["align-items"] = "flex-start";
                else if( val(this.style, "HorizontalAlignment") == 2) // right
                    st["align-items"] = "flex-end";
                else if( val(this.style, "HorizontalAlignment") == 3) // center
                    st["align-items"] = "center";
            }
            if( val(this.style, "orientation") ==  row){
                if( val(this.style, "horizontalAlignmen") == 1)  // left
                    st["justify-content"] = "flex-start";
                else if( val(this.style, "HorizontalAlignment") == 2) // right
                    st["justify-content"] = "flex-end";
                else if( val(this.style, "HorizontalAlignment") == 3) // center
                    st["justify-content"] = "center";
            } 
        }

        return st;
    }

    get isStatic(): boolean { return this.cell.properties.isStatic; }

    get staticName() { return this.cell.properties.staticName as string; }
    set staticName(s: string) { this.cell.properties.staticName = s; }

    get position() {return `[${this.row}${this.rowSpan>1?('-' + this.lastRow):''},${this.col}${this.colSpan>1?( '-' +this.lastCol):''}]`;}
    
    type: string;
    __type = "GridLayoutCell";

    constructor(rowPos: number, colPos: number, cell?: any){
        if (cell) {
            this.cell = cell;
        }
        else {
            var newCell: ICell = <ICell>{
                name: 'cell_' + rowPos + '_' + colPos,
                col: colPos,
                row: rowPos,
                rowSpan: 1,
                colSpan: 1,
                isEmpty: true,
                isHidden: false,
                items: {},
                id: uuid.id('cell'),
                type: "cell",
                typeName: "GridLayoutCell",
                properties: { 
                    __type: "GridLayoutCell", 
                    style: { "__type": "Style" }, 
                    isStatic: false
                }
            };
            this.cell = newCell;
        }
    }
}

