import { Component, OnInit, ChangeDetectorRef, OnDestroy} from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

import { StudioSubjects, val, ILabel, uuid} from '../../../common/objects/common';
import { IMetadataType, IPropertyMetadata, ListItemActions } from '../../../common/objects/viewModel';
import { ReadOnlyLevel, PanelUpdatedData } from '../../panel';
import { DragInfo } from '../../../shared/drag';
import _ from 'lodash';
import { MetadataService } from '../../../common/services/metadata.service';
import { PropertyListComponent } from '../propertyList';
import { Subscription } from 'rxjs';
import { AlertService } from '../../../alert/alert.service';
import { StudioStateService } from '../../../common/services/studioStateService';
import { DragService } from '../../../shared/drag.service';
import { DropDataInfo } from '../../../shared/directives/droppable.directive';


@Component({
    selector: 'csi-modal-collection',
    styleUrls: ["./modalCollection.less"],
    templateUrl: './modalCollection.html'
})
export class ModalCollectionComponent implements OnInit, OnDestroy {
    context: any;

    // Instance is an item of the collection
    instance: any = null;
    subjects: StudioSubjects;

    // base type of collection 
    basePropertyType: string;

    // types derived from base type
    propertyTypes: IMetadataType[];

    // type of active collection item
    activePropertyType: IMetadataType;
    activePropertyTypeName: string;

    nameProperty: IPropertyMetadata;

    // the collection itself
    collection: any[];
    items: any[]

    errorMessage: string;
    itemType: string;
    cdoRefType: string;
    readOnly: ReadOnlyLevel;
    hideTypeInName = false;
    editable: boolean;
    reorderingTop: boolean;

    private modelingBehavior: boolean = false;
    get dialog(): MatDialogRef<ModalCollectionComponent> {
        return this._dialog;
    }
    private nameInputSubs: Subscription;
    private originalName: string;

    constructor(
        private _dialog: MatDialogRef<ModalCollectionComponent>,
        private _alert: AlertService,
        private _chDetection: ChangeDetectorRef,
        private _drag : DragService,
        private _studiStateService: StudioStateService) {
        this.subjects = new StudioSubjects();
    }

    initContext(context: any): void {
        this.context = context;
    }

    ngOnInit() {

        this.basePropertyType = this.context.property.originalTypeName;
        if (this.basePropertyType) {
            if (this.basePropertyType.indexOf('[]') != -1) {
                this.basePropertyType = this.basePropertyType.replace('[]', '');
            }
        }
        else {
            this.errorMessage = "Error: Property type is not defined. Editing impossible";
            return;
        }

        if (this.context.property.editor && this.context.property.editor.options) {
            let it = this.context.property.editor.options.find(o => o.startsWith("itemType="));
            if (it) {
                this.itemType = it.split('=')[1];
                this.cdoRefType = this.itemType;
            }
        }

        this.propertyTypes = _.sortBy((this.context.metadataService as MetadataService).getSubTypes(this.basePropertyType), t => t._displayedName || t._name);
        if (this.propertyTypes.length) {
            // select first
            this.selectType(this.propertyTypes[0]);
        }

        if (this.context.hideTypeName !== undefined) {
            this.hideTypeInName = this.context.hideTypeName;
        }
        else {
            if (this.propertyTypes.length == 1 && this.nameProperty)
                this.hideTypeInName = true;
        }

        this.modelingBehavior = this._studiStateService.isModelingBehavior();
        this.readOnly = this.context.property.readOnly ? ReadOnlyLevel.value : undefined;
        this.editable = (this.readOnly === undefined);

        this.collection = _.cloneDeep(this.context.value);

        if (this.collection == null)
            this.collection = [];
        
        if(this.collection) this.collection.map(el => el.__id = uuid.id());
        this.buildItems();

        this.subjects.property.subscribe(upd => {
            if (this.modelingBehavior) {
                if (this.instance.__action !== ListItemActions.add)
                    this.instance.__action = ListItemActions.change;
            }
        });

        this.subjects.panel.subscribe(p => {
            // Set additional instance name verification
            let data = <PanelUpdatedData>p;
            let propertyList = data.contentCreated as PropertyListComponent;
            if (propertyList && propertyList.panel.nameInput) {
                this.nameInputSubs = propertyList.panel.nameInput.onValChange.subscribe(n => {
                    this.setInstanceName(n);
                });
            }
        });

        this.selectItem(this.instance);
    }

    ngOnDestroy() {
        if (this.nameInputSubs)
            this.nameInputSubs.unsubscribe();

        this._drag.clear(['collection-item'],null);
    }

    touch() {
        this._chDetection.detectChanges();
    }

    ok() {
        _.forEach(this.collection, it => {delete it.__parent;  delete it.__id;});
        this._dialog.close(this.collection);
    }

    cancel() {
        _.forEach(this.collection, it => {delete it.__parent;  delete it.__id;});
        this._dialog.close();
    }

    selectItem(item: any) {
        this.instance = item;
        if (this.instance != null) {
            if (this.instance.__type && this.propertyTypes.length > 1)
                this.selectType(this.instance.__type);

        }
        else {
            this.instance = null;
            this.activePropertyType = null;
            if (this.propertyTypes.length > 0) {
                // select first
                this.selectType(this.propertyTypes[0]);
            }
        }

        if (this.instance) {
            this.instance['__selectedInModalCollection'] = true;
            this.originalName = this.getInstanceName();
            this.instance.__parent = this.context.instance;
        }

        this.subjects.propertyPanel.next(this.instance);
        this.touch();
    }

    selectType(selectedType: string | IMetadataType) {
        if (!_.isString(selectedType)) {
            this.activePropertyType = <IMetadataType>selectedType;
        }
        else {
            let parsed = (<string>selectedType).split('.');
            if (parsed.length > 1)
                selectedType = parsed[1];

            this.activePropertyType = this.propertyTypes.find(mdType => mdType._name == <string>selectedType);
        }

        this.activePropertyTypeName = this.activePropertyType._name.replace('[]', '');
        this.nameProperty = this.activePropertyType.properties.find(p =>
            p.name.toLowerCase() == 'name' ||
            (p.displayedName && p.displayedName.toLowerCase() == 'name') ||
            (p.editor && p.editor.parameters && p.editor.parameters.find(ep => ep == 'asItemName') !== undefined));
    }

    add(): void {
        // TODO: this should be unique name validation
        let itemType = this.activePropertyType._name;
        if (this.activePropertyType._namespace)
            itemType = this.activePropertyType._namespace + '.' + itemType;

        let newItem = { __type: itemType, __index: this.collection.length + 1, __parent: this.context.instance };
        if (this.modelingBehavior)
            newItem['__action'] = ListItemActions.add;

        if (this.activePropertyType.properties) {
            if (this.nameProperty) {
                if( (this.nameProperty.editor && this.nameProperty.editor.parameters && this.nameProperty.editor.parameters.find(ep => ep == 'asItemName') !== undefined) || this.cdoRefType )
                    newItem[this.nameProperty.name] = "";    
                else
                    newItem[this.nameProperty.name] = this.generateNewName();
            }
        }
        if (this.itemType) {
            newItem['CDOTypeName'] = this.itemType;
        }

        (<any>newItem).__id = uuid.id();

        if (this.collection == null)
            this.collection = [newItem];
        else
            this.collection.push(newItem);

        this.buildItems();
        this.selectItem(newItem);
    }

    delete(item): void {
        if (this.modelingBehavior) {
            item['__action'] = ListItemActions.delete;
        }
        else {
            let i = _.findIndex(this.collection, item);
            if (i !== undefined && i != -1) {
                this.collection.splice(i, 1);
            }
        }
        this.buildItems();
        this.selectItem(null);
    }

    private generateNewName(): string {
        return this._studiStateService.getUniqueName(this.activePropertyType._name + '0', this.collection, this.nameProperty.name);
    }

    getName(el) {
        let res = '';
        let n = this.nameProperty ? val(el, this.nameProperty.name) : '';
        if (n)
            res = n;

        if (!this.hideTypeInName && el.__type) {
            let p = el.__type.split('.');
            if (p.length > 1)
                res += '[' + p[1] + ']';
            else
                res += '[' + el.__type + ']';
        }
        return res;
    }

    getDropOptions(el:any) : DropDataInfo {
        // item is undefined for type selector to be able to insert before 0 position
        return <DropDataInfo>{
            id: 'dz_'+(el.__id),
            disabled: false,
            types: 'collection-item',
            drop: (d, tm) => this.onDrop(d, tm),
            over: (d, m) => this.onOver(d, m),
            model: el
        };
    }


    getInstanceName() {
        return this.nameProperty ? this.instance[this.nameProperty.name] : this.instance.name;
    }

    setInstanceName(name: string) {
        let nameKey = this.nameProperty ? this.nameProperty.name : 'name';
        let nameLc = name.toLowerCase();
        if (_.find(this.collection, v => val(v, nameKey).toLowerCase() == nameLc && v != this.instance)) {
            let err: ILabel;
            if( this.context.duplicationErrorLabel ){
                err = this.context.duplicationErrorLabel;
            }
            else {
                err = <ILabel>{
                    Name: 'PS_NAME_EXISTS_ERROR',
                    DefaultValue: '{0} with name {1} already exists. Changes are discarded.',
                    arguments: [this.activePropertyTypeName, name]
                }
            };
            this._alert.errorModal(err).subscribe(r => {
                this.instance[nameKey] = this.originalName;
                this.selectItem(this.instance);
            });
        }
        else
            this.instance[nameKey] = name;
    }

    private buildItems(): void {
        this.items = _.filter(this.collection, v => v.__action !== ListItemActions.delete);
    }

    private onDrop(d: DragInfo, dropInf: DropDataInfo) {
        const targetModel = dropInf.model;        
        // Reorder 
        let sourceIndex = this.collection.findIndex(c => c === d.dragModel);
        if (sourceIndex != -1) {
            let targetIndex = targetModel ? this.collection.findIndex(c => c === targetModel) : -1;
            const moveDown = targetIndex >= sourceIndex;
            let removedItem = this.collection.splice(sourceIndex, 1)[0];
            targetIndex = targetModel ? this.collection.findIndex(c => c === targetModel) : -1;
            if( moveDown )
                targetIndex ++;
            // if target index = -1 the item is inserted at 0 position
            this.collection.splice(targetIndex, 0, removedItem);
        }
        this.buildItems();
    }

    private onOver(d: DragInfo, mdl: any) {        
        if (!d.leaveZone) {            
            if (mdl)
                mdl.__reordering = true;
            else
                this.reorderingTop = true;
        }
        else {
            if (mdl)
                delete mdl.__reordering;
            else
                this.reorderingTop = false;
        }
    }
}
