    public login(username: string, password: string, domain: string): PortalResultObs<string> {
        let pwd = <IEncryptedField>{ Value: password, IsEncrypted: false };
        let tzOffset = (new Date()).getTimezoneOffset() / 60;

        let profile = { Name: username, Password: pwd, UTCOffset: (tzOffset<0 ? ("-PT" + -tzOffset) : ("PT"+tzOffset)) +"H" };
        let body = { profile: profile, domain: domain };

        let resProc = (result: any)=>{
            if (result.LogonResult.IsSuccess) {
                this._profile = <IUserProfile>result.profile;
                if( !this._profile.UTCOffset )
                    this._profile.UTCOffset = "PT0H";
                this._localService.setUserProfile(this._profile);
                this.keepSessionAlive();
                return new PortalResult(this.getAuthorizationHeader());
            }
            else {
                return new PortalResult(null, <IResultStatus> result.LogonResult);
            }
        };

        return <PortalResultObs<string>>this.post<PortalResult<string>>('Logon', body, resProc);
    }

    private keepSessionAlive(): void {
        let timeoutTime = 300000;
        setInterval(() => {
            this.post<any>('SessionReset')
                .subscribe(
                    (response: any) => {
                },
                error => {
                    this.handleError(error);
                }
                );
        }, timeoutTime);
    }

    public checkUserLoggedIn(): boolean {
        let profile = this._localService.getUserProfile();
        if (profile)
            return true;
        return false;
    }

    public verifyLogon(): Observable<any> {
        return this.post<IUserProfile>("VerifyLogon", undefined, "profile")
            .pipe(map(profile => {
                if (profile) {
                    this._profile = profile;
                    this._localService.setUserProfile(this._profile);
                    this.keepSessionAlive();
                    return this.getAuthorizationHeader();
                }
                return undefined;
            }), catchError(this.handleError));
    }

