    public getPageMaps(): Observable<IUIComponentRef[]> {
        return this.post<string>("LoadPageMapping", undefined, "xml")
            .pipe(map((xml: string) => {
                let xmlDoc = new DOMParser().parseFromString(xml, "application/xml");
                let res: IUIComponentRef[] = [];
                let nl = xmlDoc.documentElement.getElementsByTagName("PageMappingItem");
                _.forEach(nl, p => {
                    let tg = p.getElementsByTagName("Parameters");
                    let itemParameters : string[] = [];
                    _.forEach(tg, t=> itemParameters.push(t.attributes["Name"].value) );

                    if (p.attributes && p.attributes["Name"]) {
                        let pmref = new UIComponentRef();
                        pmref.name = p.attributes["Name"].value;
                        pmref.type = 'pageMap';
                        pmref.componentName = pmref.name;
                        pmref.isReference = true;
                        pmref.__mapParameters = itemParameters;
                        res.push(pmref);                            
                    }
                });
                return res;
            }));
    }

