    public getTargetMatrix(name: string): Observable<any> {
        if (!name) {
            // create new page map
            let res = { __type: "TargetMatrixItem" };
            name = "TargetMatrixItem" + '-' + new Date().getMilliseconds().toString(); // new name
            res["name"] = name;
            return new Observable<any>(subscriber => subscriber.next(res));
        }
        return this.post<string>("LoadTargetMatrixItem", { 'name': name }, "xml")
            .pipe( map((xml:string) => this._getTargetMatrixItemFromXml(xml)));
    }

    private _getTargetMatrixItemFromXml(xml: string) : IElement{
        let xmlDoc = new DOMParser().parseFromString(xml, "text/xml");
        let name = xmlDoc.documentElement.getAttribute("Name");
        let res: IElement = <IElement>{
            id: uuid.id(),
            name: name,
            type: 'TargetMatrixItem',
            typeName: 'TargetMatrixItem',
            items: {}
        };

        let rows = [];
        let columns = [];

        for(let i=0; i<xmlDoc.documentElement.childNodes.length; i++){
            let p : any  = xmlDoc.documentElement.childNodes.item(i);
            if (p.localName == "Columns") {
                let attrs = (p as any).attributes;
                if( attrs ){
                    let nameAttr = attrs.getNamedItem('Name');
                    let captionAttr = attrs.getNamedItem('Caption');
                    columns.push({
                        name: nameAttr && nameAttr.textContent, caption: captionAttr && captionAttr.textContent, __type: 'TargetMatrixColumn'
                    });
                }
            }
            else if (p.localName == "Rows") {
                let cells = columns.map(cx=> { return {name: cx.name, value: null, __type: 'TargetMatrixCell'};});
                for(let ii=0; ii<p.childNodes.length; ii++){
                    let c = p.childNodes.item(ii) as any;
                    if( c.attributes ){
                        let nameAttr = c.attributes.getNamedItem('Name');
                        let valAttr = c.attributes.getNamedItem('Value');
                        let colName = nameAttr.textContent;
                        let clx = cells.find(c1=> c1.name == colName);
                        clx.value = valAttr && valAttr.textContent;
                    }
                }

                let tvAttr = (p as any).attributes && (p as any).attributes.getNamedItem('TargetValue');
                rows.push({
                    targetValue: tvAttr && tvAttr.textContent,
                    cells: cells,
                    __type: 'TargetMatrixRow'
                });
            }
        }
        res['columns'] = columns;
        res['rows'] = rows;
        return res;
    }

