    private post<T>(op: string, prms:any = null, responseOut : string | ((pr : any) => any) = undefined): Observable<T> {

        let resField = op + "Result";
        if( op.startsWith("metadata/"))
            resField = "MetadataOperationResult";
        else if( op.startsWith("component/"))
            resField = "ComponentOperationResult";

        return this._http.post<any>(this.getOperationUrl(op), prms && JSON.stringify(prms), this.getHeaders())
            .pipe(map( parsedResponse => {
                let ret : T;
                
                if( parsedResponse && parsedResponse[resField] !== undefined){
                    if( responseOut ){
                        // Function
                        if( typeof responseOut == "function" ){
                            ret = <T>( <((pr : any) => any)>responseOut)(parsedResponse);
                        }
                        else{
                            // string
                            ret = <T>parsedResponse[responseOut as string];                                            
                        }
                    }
                    else{ 
                        // Field
                        ret = <T> parsedResponse[resField];
                    }    
                }

                return ret;

            }), catchError(this.handleError));
    }

    public saveTargetMatrixItem(name: string, targetMatrixItem: any): Observable<IResultStatus> {

        let initDocXml = 
        `<?xml version="1.0" encoding="utf-16"?>
            <TargetMatrixItem xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" Name="${name}" xmlns="camstar.com/Portal.Personalization.Model">
            </TargetMatrixItem>`;

        let xmlDoc = new DOMParser().parseFromString(initDocXml, "text/xml");
        let ns = "camstar.com/Portal.Personalization.Model";

        let de = xmlDoc.documentElement;
        let cols = val(targetMatrixItem, "columns");
        _.forEach(cols, c=>{
            let cl = xmlDoc.createElementNS(ns, "Columns");
            cl.setAttribute("Name", c.name);
            cl.setAttribute("Caption", c.caption);
            de.appendChild(cl);
        });

        let rows = val(targetMatrixItem, "rows");
        _.forEach(rows, r=>{
            let rl = xmlDoc.createElementNS(ns, "Rows");
            rl.setAttribute("TargetValue", r.targetValue);
            if( r.cells ){
                r.cells.forEach((cell, index)=>{
                    let cll = xmlDoc.createElementNS(ns, "Cells");
                        cll.setAttribute("Name", cell.name);
                        cll.setAttribute("Value", cell.value);
                        rl.appendChild(cll);
                    });
            }
            de.appendChild(rl);
        });

        let oSerializer = new XMLSerializer();
        let xml = oSerializer.serializeToString(xmlDoc);

        return this.post<IResultStatus>('SaveTargetMatrix', { 'name': name, 'xml': xml });
    }

