    public copyElement(element: IElement, newName: string, workspaceCode : string): Observable<IElement> {
        
        if (element.__unbound && element.__unbound !== "NDO") {
            let compToSave = _.cloneDeep<IElement>(element);
            compToSave.name = newName;
            return of<IElement>(compToSave);
        }

        // Copy unbound elements must be implement

        return this.componentCall<IElement>("copy", element.name, element.type || element.__maint, workspaceCode, newName);
        
    }
    private componentCall<T>(op: string, name: string, type: string, workspaceCode: string = "", data: string = null): Observable<T> {
        let prm = {
            'name': name, 
            'type': type.toLowerCase(), 
            'workspace' : workspaceCode,
            'content': data
        };

        return this.post<IApiResult>('component/'+op, prm)
            .pipe(map((response: any) => this.processPortalResponse(response)));
    }

    private metadataCall<T>(op: string, data: any = null, dataArray : string[] = []): Observable<T> {
        let prm = {inpParams: [data].concat(dataArray)};

        return this.post<IApiResult>('metadata/'+op, prm)
            .pipe(
                map((response: any) => this.processPortalResponse(response) )
            );
    }

    private processPortalResponse(response: IApiResult) : any {    
        if (response){
            let res = response.status;
            if(res.IsSuccess){
                if(response.content){
                    let content = JSON.parse(response.content); 
                        if (content.type !== undefined && (content.type == "Page" || content.type == "Webpart")) {
                           if (response.status.Message) {
                               content.__statusMessage = response.status.Message;
                           }    
                        }
                        
                    return content;
                }
                else
                    return res;
            }
            else{
                // The error handling must be improved
                if( res.ExceptionData && res.ExceptionData.Description)
                    alert(res.ExceptionData.Description);

                return res;
            }
        }
        else {
            let errRes = <IResultStatus>{IsSuccess: false, Message: "No result"}
            console.error(errRes.Message, response);
            return errRes;
        }
    }

