    public getPageByTemplateType(template: string): Observable<IUIDesigner> {
        return Observable.create(obs => {
            let type = "Page";
            if(template == "blank") {
                this.getElement(null, type).subscribe(page=> { 
                    obs.next(page);
                    obs.complete();     
                });            
            }
            else {                                     
                this.getElement(template, type).subscribe(page => {
                    page.isDirty = true;
                    page.isNew = true;
                    obs.next(page);
                    obs.complete(); 
                });                
            } 
        });
    }


    public exportComponent(type: string, name: string, isNew: boolean = false, workspace: string): Observable<IApiResult> {
       
        let optPar = {
            'type': type, 
            'name' : name, 
            'workspace' : workspace + "" === '' ? 'all' : workspace,
            'isNew' : isNew.toString()
        };

        function dataEncodeUTF16(data) {
            let buf = new ArrayBuffer(data.length*2);
            let bufView = new Uint16Array(buf);
            for (let i=0; i < data.length; i++) {
                 bufView[i] = data.charCodeAt(i);
            }
            return bufView;
        }

        return this.post<any>('component/export', optPar) 
        .pipe(
            map((data: any) => {
                let dataUTF16 = dataEncodeUTF16(data.content);
                let blob = new Blob([dataUTF16], {type: "text/xml;charset=UTF-16;"});    
                let fileName = name + ".xml";

                // download file.
                if (navigator.msSaveBlob) // IE10+
                    navigator.msSaveBlob(blob, fileName);
                else { // other browsers.
                    let url = window.URL.createObjectURL(blob);
                    if ("download" in document.createElement("a")) {
                        let a = document.createElement("a");
                        a.style.display = "none";
                        a.href = url;
                        a.setAttribute("download", fileName);
                        a.click();
                    }
                }

                
                return data;
        
            }
        ));
    }

