import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';

import { PortalService } from '../common/services/portal.service';

@Component({
    selector: 'csi-login-dialog',
    templateUrl: 'login.dialog.component.html',
    styles: [
        `.main-content {
            padding: 15px;
        }
        .form-footer {
            padding: 10px;
        }`
    ]
})

export class LoginDialogComponent implements OnInit {    
    public loginForm: FormGroup;
    public dialogMessage: string;
    public errorMessage: string;
    constructor(    
        private _form: FormBuilder,       
        private _portal: PortalService,
        private _dialog: MatDialogRef<LoginDialogComponent>) {        
    }

    ngOnInit(): void {
        this.loginForm = this._form.group({            
            username: ['', Validators.required],        // default id maybe set
            password: ['', Validators.required],        // default password maybe set
            domain: ['.', Validators.required],
        });
    }

    initContext(context: any)
    {        
        if(context && context.message)
             this.dialogMessage = context.message;
    }    

    login(): void {
        let form = this.loginForm.value;
        this._portal.login(form.username, form.password, form.domain)
        .subscribe(resultStatus => {
            if(resultStatus.error) {
                this.errorMessage = resultStatus.error.ExceptionData.Description;
            }
                
            if(resultStatus.data)
                this._dialog.close(resultStatus.data); //authorizationHeader
        })    
    }

    cancel(): void {
        this._dialog.close();
    }
}


