import _ from 'lodash';
import { LayoutCell } from "./layoutCell";
import { LayoutActions } from '../../../common/objects/common';

export class CellSelection{
    public first: LayoutCell;
    public last: LayoutCell;
    public area: LayoutCell[] = [];

    public get isSelected() {return this.area.length > 0;}
    public get start() : LayoutCell {return this.isSelected ? this.area[0]/*top left cell*/ : this.first};
    public get end() : LayoutCell {return this.isSelected ? this.area[this.area.length-1]/*bottom right cell*/ : this.last};

    constructor(){
    }

    public clear(tbl? : any[][]) : boolean {
        let needRefresh = this.area.length > 0;
        this.first = null;
        this.last = null;
        this.area = [];

        if(tbl){
            tbl.forEach(cells=> cells.forEach(c=> c.isSelected = false));
        }
        return needRefresh;
    }

    public select(cell: LayoutCell, matrix: LayoutCell[][]){
        if( this.first && this.last)
            this.clear();

        if(!this.first)
            this.first = cell;
        
        else
            if( this.first == cell)
                this.clear();
            else
                this.last = cell;

        this.selectArea(matrix);        
    }

    private isInsideSelection(row: number, col: number){
        return row >= this.start.row && row <= this.end.lastRow && 
               col >= this.start.col && col <= this.end.lastCol;
    }

    public merge(tbl? : any[][]){
        if( this.area.length > 1){
            let mergedCellItems = _.map(this.start.cell.items, it=> it);

            if( tbl ){
                tbl.forEach( cells=> {
                    cells.filter(c1=> this.isInsideSelection(c1.row, c1.col) && c1 != this.start.cell)
                        .forEach(c=> {
                            c.isHidden = true;
                            if( !_.isEmpty(c.items) ){
                                _.values(c.items).forEach( v=> {
                                    v._fromCell = [c.row,c.col];    // save original cell for restoring on split
                                    mergedCellItems.push(v);
                                });
                                c.items = {};
                            }
                        });
                });
            }

            this.start.colSpan = this.end.lastCol - this.start.col + 1; 
            this.start.rowSpan = this.end.lastRow - this.start.row + 1; 

            this.start.cell.items = {};
            mergedCellItems.forEach(v=> this.start.cell.items[v.id]=v ) ;
            return true;
        }
        return false;
    }

    public split(tbl: any[][]){
        if( tbl ){
            tbl.forEach( cells=> {
                cells.filter(c1=> this.isInsideSelection(c1.row, c1.col) && c1 != this.start.cell)
                    .forEach(c=> {
                        c.isHidden = false;
                        c.colSpan = c.rowSpan = 1;
                        // restore items ??? 
                    });
            });            
        }
        this.start.colSpan = this.start.rowSpan = 1;
        return true;
    }

    public getEnabledCommands() : CellSelectionState{
        let selectedNumber = this.area.length;
        let res :LayoutActions[] = [];
        res.push(LayoutActions.toggleCellPos);
        if( selectedNumber > 0){
            
            if(this.start.colSpan > 1 || this.start.rowSpan>1){
                res.push(LayoutActions.split);
            }
            else if(this.start != this.end){
                res.push(LayoutActions.merge);
            }
            res.push(LayoutActions.removeColumn);
            res.push(LayoutActions.removeRow);

            res.push(LayoutActions.insertRowBefore);
            res.push(LayoutActions.insertRowAfter);

            res.push(LayoutActions.insertColumnBefore);
            res.push(LayoutActions.insertColumnAfter);

            res.push(LayoutActions.reset);
        }
        return new CellSelectionState(res);
    }

    private selectArea(matrix: LayoutCell[][]){
        let start = this.first;
        let end = this.last || start;
        let flatMatrix = _.flatten(matrix);

        if(start && end ){
            let rows = _.sortBy([start.row, end.lastRow]);
            let cols = _.sortBy([start.col, end.lastCol]);
            this.area = flatMatrix.filter(c=> c.row >= rows[0] && c.row <= rows[1] && c.col >= cols[0] && c.col <= cols[1] );
        }
        else
            this.area = [];

        // Hignlight selected cells
        flatMatrix.forEach(c=> c.isSelected = this.area.indexOf(c)!=-1);
    }
}

export class CellSelectionState{
    constructor(public cmdEnabled : LayoutActions[]){}
}
