import { NgModule, Optional, SkipSelf } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { MatDialogModule } from '@angular/material/dialog';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { LocalService } from '../common/services/local.service';
import { PortalService } from '../common/services/portal.service';
import { WCFService } from '../common/services/wcf.service';

import { AppComponent } from './app/app.component';

import { Routing } from './routing';
import { StudioStateService } from '../common/services/studioStateService';

export const COMPONENTS = [
    AppComponent,
]

@NgModule({
    imports: [
        CommonModule,
        HttpClientModule,
        MatDialogModule,
        BrowserAnimationsModule,
        Routing,
    ],
    exports: [
        COMPONENTS,
    ],
    declarations: COMPONENTS,
    providers: [
        LocalService,
        PortalService,
        StudioStateService,
        WCFService
    ]
})

export class CoreModule {
    constructor( @Optional() @SkipSelf() parentModule: CoreModule) {
        if (parentModule) {
            throw new Error(
                'CoreModule is already loaded. Import it in the AppModule only');
        }
    }
}

