import { Directive, OnDestroy, Input, AfterViewInit, HostListener } from "@angular/core";
import { MatMenuTrigger } from "@angular/material/menu";
import { ContextMenuItem } from "../common/objects/common";

@Directive({
    selector: '[context-popup-menu]'
})
export class ContextMenuPopupDirective implements OnDestroy, AfterViewInit {
    @Input('context-popup-menu') info: ContextMenuInfo;

    @HostListener('contextmenu',['$event']) onContextMenu(e: MouseEvent) {
        if( this.info.disabled === true || !this.info.trigger ){
            return;
        }

        let trigger = this.info.trigger;
        this.info.buildMenu.apply(this.info.execContext, [this.info.dataType, this.info.data, this.info.key]);
        
        event.preventDefault();
        // @ts-ignore
        const triggerElement: HTMLElement = trigger._element.nativeElement;
        triggerElement.style.setProperty('left', `${e.clientX}px`);
        triggerElement.style.setProperty('position', 'fixed');
        triggerElement.style.setProperty('top', `${e.clientY}px`);
        triggerElement.style.setProperty('visibility', 'hidden');
        trigger.menuData = { data: this.info.data };
        trigger.menu.focusFirstItem('mouse');
        trigger.openMenu();        
    }
    
    constructor(){        
    }

    ngAfterViewInit(){
    }

    ngOnDestroy(){
    }
}


export class ContextMenuInfo{
    dataType: string;
    data?: string;
    trigger: MatMenuTrigger;    
    buildMenu: (dataType: string, data:string, key?:string) => ContextMenuItem[];
    disabled: boolean;
    execContext: any;
    key?:string

}
