import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'elementFilter'
})
@Injectable()
export class ElementFilter implements PipeTransform {
    transform(elements: any[], filter: string): any[] {
        if (!elements || (elements && elements.length==0))
            return elements;       
                             
        if (filter !== undefined){
            const re = filter.toLowerCase();
            return elements.filter( it => it.name && it.name.toLowerCase().indexOf(re)!=-1);
        }
        return elements;
    }
}