import { Injectable } from '@angular/core';
import { IElement } from '../objects/common';
import { IGridLayout, ListItemActions } from '../objects/viewModel';
import { MetadataService } from './metadata.service';
import _ from 'lodash';

@Injectable()
export class ConvertorService {

    constructor() {
    }

    private cleanReferencedItem(wp:IElement){

        const propList = ["name","type","typeName","isReference","_fullTypeName","id"];
        let keyList = Object.keys(wp).filter( k=> propList.indexOf(k) == -1).map(ks=> ks );
        keyList.forEach( k=> delete wp[k]);
    }

    private rootWebparts: IElement[];

    // Used on saving and publishing
    public convertRefs(root: IElement) {
        if (root.layout && root.layout.__type === "GridLayout") {
            this.rootWebparts = root.items;            
            this.convertLayoutRefs(<IGridLayout>root.layout, root.items);
        }

        if (root.items) {
            _.forEach(root.items, wp => {
                if( wp.isReference){
                    this.cleanReferencedItem(wp);
                }
                if (wp.layout && wp.layout.__type === "GridLayout")
                    this.convertLayoutRefs(<IGridLayout>wp.layout, wp.items);
            });
        }
    }

    private convertLayoutRefs(layout: IGridLayout | any, items: IElement[]) {        
        if (layout && layout.tableCells) {
            _.forEach(layout.tableCells, row => {
                _.forEach(row, cell => {
                    if (cell.items) {
                        _.forEach(cell.items, it => {
                            let refItem = this.findItemByName(it.name, items);
                            if (refItem) {
                                it.typeName = refItem._fullTypeName;

                                if (it.type.toLowerCase() == "control") {
                                    if (refItem.typeName == "JQTabContainer") {
                                        _.forEach(refItem.properties.tabDefinitions, t => {
                                            this.convertLayoutRefs(t.layout, this.rootWebparts);
                                        });
                                    }
                                    else if (refItem.layout) {
                                        this.convertLayoutRefs(refItem.layout, this.rootWebparts);
                                    }
                                }
                            }
                        });
                    }
                });
            });
        }
    }

    public convertMaintChangesToModel(maint: any): any {

        let ns = "Camstar.WCF.ObjectStack";
        let type: string;

        if (!maint.__type && maint.Self) {
            maint.__type = maint.Self.CDOTypeName+':'+ns;
        }
        if (!type && maint.__type) {
            let xt: string[] = (<string>maint.__type).split(':');
            if (xt.length) {
                type = xt[0];
                ns = xt[1];
            }
        }

        if (!type) {
            console.warn('convertMaintChangesToModel: type unresolved', maint);
            type = "object";
        }
        else {
            let i = type.lastIndexOf("Maint");
            if (i != -1)
                type = type.substr(0, i) + "Changes";
        }
        let res = { typeName: type, __type: type };
        if (ns)
            res['__ns'] = ns;

        _.forEach(maint, (v, k) => {
            let ks = k.split('_');
            if (ks.length == 2) {
                let field = ks[1];
                if (field != "ObjectToChange") {
                    field = _.camelCase(field);
                    let val: any = undefined;
                    if (v) {
                        val = (v.Value) ? v.Value : v;
                        if (!_.isEmpty(val)) {
                            if (_.isArray(val)) {
                                let newarray = [];
                                _.forEach(val, (va, i) => {
                                    let av;
                                    if (this.isNDORef(va))
                                        av = { name: va['Name'], __type: 'NamedObjectRef', CDOTypeName: va['CDOTypeName'] };
                                    else
                                        av = this.convertMaintChangesToModel(va);
                                    av['__index'] = i;
                                    newarray.push(av);
                                });
                                val = newarray;
                            }
                            else if (_.isObject(val)) {
                                let objVal = <any>val;
                                if (objVal.CDOTypeName !== undefined && objVal.Name !== undefined) {
                                    // NDORef
                                    val = objVal.Name;
                                }
                                else {
                                    val = this.convertMaintChangesToModel(val);
                                }
                            }
                        }
                    }
                    if (val)
                        res[field] = val;
                }
            }
        });

        return res;
    }


    public convertModelToMaintChanges(obj, metadataService: MetadataService): any {
        let ns = obj.__ns || 'Camstar.WCF.ObjectStack';
        let ch = {
            __type : obj.__type + ':' + ns
        };

        let ownerType = metadataService.getMetadataType(obj.typeName || obj.__type);
        if (ownerType){
            if( ownerType._name != "NamedObjectRef") {
                _.forEach(ownerType.properties, p => {
                    let n = p.name.toLowerCase();
                    let propValue = _.find(obj, (v, k) => k.toString().toLowerCase() == n);
                    if (propValue) {
                        if (_.isArray(propValue)) {
                            let pArray = [];

                            // deleted items
                            let deletedCnt = 0;
                            let sorted = _.sortBy(propValue, ["__index"]);

                            sorted.forEach(pv => {
                                let i: number = pv['__index'];
                                i -= deletedCnt;

                                if (pv['__action'] == ListItemActions.delete) {
                                    pArray.push({ ListItemAction: ListItemActions.delete, ListItemIndex: i });
                                    deletedCnt++;
                                }
                                else if (pv['__action'] == ListItemActions.change) {
                                    let chVal = this.convertModelToMaintChanges(pv, metadataService);
                                    chVal['ListItemIndex'] = i;
                                    pArray.push(chVal);
                                }
                                else if (pv['__action'] == ListItemActions.add) {
                                    let chVal = this.convertModelToMaintChanges(pv, metadataService);
                                    pArray.push(chVal);
                                }
                            });
                            propValue = pArray;
                        }
                        else {
                            if( p.editor && p.editor.provider && p.editor.provider == "ndoInstances") {
                                let cdoType = p.editor.parameters[0];
                                if( cdoType.startsWith("#"))
                                    cdoType = obj.CDOTypeName;
                                propValue = { CDOTypeName: cdoType, Name: propValue };
                            }
                            else {
                                if (_.isString(propValue) || _.isNumber(propValue) || _.isBoolean(propValue)) {
                                    // Scalar field
                                    propValue = { Value: propValue };
                                }
                                else {
                                    // Object field
                                    propValue = this.convertModelToMaintChanges(propValue, metadataService);
                                    // check whether the value has just type only. it's considered as empty
                                    let keys = _.keys(propValue);
                                    if (keys.length ==0 || (keys.length == 1 && keys[0] == '__type'))
                                        propValue = null;

                                    // if property type == object type - delete the __type
                                    let t: string = propValue && propValue['__type'];
                                    if (t) {
                                        let tn = t.split(':');
                                        if (tn.length > 0 && tn[0] == p.originalTypeName) {
                                            delete propValue['__type'];
                                        }
                                    }
                                    if (propValue != null){
                                        if(obj.isNew === true) {
                                            propValue['FieldAction'] = 0; // 0 = create, 1- replace (default)
                                        }
                                        else{
                                            // If the object exists then omit the type because it cannot be changed after creation
                                            if(propValue['__type'])
                                                delete propValue['__type'];
                                        }
                                    }
                                }
                            }
                        }
                        if (propValue != null) {
                            let creatorType = metadataService.getPropertyCreatorType(ownerType, p);
                            ch[creatorType._name + '_' + p.name] = propValue;
                        }
                    }
                });
            }
            else{
                // NamedObjectRef
                ch['Name'] = obj.name || obj.Name;
            }
        }
        if (obj['__index'] !== undefined && obj['__action'] !== undefined) {
            if (obj['__action'] !== ListItemActions.add )
                ch['ListItemIndex'] = obj.__index;
            ch['ListItemAction'] = obj['__action']; // add, change, delete, replace
            if( ownerType && ownerType._name === "NamedObjectRef")
                delete ch['CDOTypeName'];
            else
                ch['CDOTypeName'] = obj.__type;
        }
        return ch;
    }

    public convertTargetMatrixToModel(tm: any, type?: string): any {
        return tm;
    }

    public convertSettingsToModel(st : any, typeName: string, metadataService: MetadataService): any {

        let res: any;
        let isArray = _.endsWith(typeName, '[]');

        if (_.isString(st) || _.isNumber(st) || _.isBoolean(st))
            return st;

        if (metadataService.isSystemType(typeName)) {
            if (st === undefined) {
                if (typeName.toLowerCase() == 'string')
                    st = null;
                return st;
            }
            if (!isArray)
                return st;
        }

        if (isArray) {
            res = [];
            if (st) {
                res = (<any[]>st).map( v => 
                    this.convertSettingsToModel(v, typeName.replace('[]', ''), metadataService)
                );
            }
        }
        else {
            // it's object
            let type = metadataService.getMetadataType(typeName);
            res = {};

            if( !type ){
                console.error("Metadata: type not found", typeName, st);
                return res;
            }

            res.__type = typeName;

            _.forEach(type.properties, pr => {
                let fn = (pr.name + 'Field').toLowerCase();
                let v = _.find(st, (v, k: string) => k.toLowerCase() == fn);

                let converted = this.convertSettingsToModel(v, pr.originalTypeName, metadataService);
                res[pr.name] = converted;

                if (pr.originalTypeName.toLowerCase() == 'boolean') {
                    pr.notNullable = true;
                }
            });
        }
        return res;
    }

    public convertModelToSettings(st): any {

        let res: any;

        if (_.isArray(st)) {
            res = (<any[]>st).map(v => this.convertModelToSettings(v));
        }

        else if (_.isObject(st)) {
            // it's object
            let stObj = <any>st;
            res = {};
            let stType: string = stObj.__type;
            if (_.isString(stType) && stType.indexOf('.') != -1)
                stType = stType.split('.')[1];

            delete stObj.name;
            delete stObj.__type;

            let isAllEmpty = true;
            delete stObj.__action;

            // add trailed 'Field'
            _.forEach(st, (v, k) => {

                let conv = this.convertModelToSettings(v);
                if (_.isArray(conv))
                    if (conv.length == 0)
                        conv = null;

                if (conv !== null && conv !== undefined)
                    isAllEmpty = false;

                let field = this.toCamelCase(k + 'Field');
                res[field] = conv;

                if (stType == 'GridSettings') {
                    ['VisibleRows', 'PagesRequested'].forEach(nf => {
                        if (k == nf) {
                            let specified = !(st[nf] === null || st[nf] === undefined);
                            res[this.toCamelCase(nf + 'FieldSpecified')] = specified;
                            if (specified)
                                isAllEmpty = false;
                        }
                    });
                }
            });

            if (isAllEmpty) {
                res = null;
            }
            else if (_.isArray(res) && res.length == 0) {
                res = null;
            }

            if (stType == "UIComponentDataContractLink") {
                if (res) {
                    res['__deletedField'] = false;
                    res['__deletedFieldSpecified'] = false;
                }
            }
            else if (stType == "DataSource") {
                if( res['loginPasswordField'] === undefined )
                    res['loginPasswordField'] = "";
            }
        }
        else {
            res = st;
        }

        return res;
    }

    public elementSerialize(element: IElement) : string{
        const ignoredKeys = ["history", "__layoutManager","__selectedElement",
        "__xml", "__designMode", "__zoomXml", 
        "isRoot", "__layoutMenu", "__workspace", "scaleValue", "componentRef", "isDirty", "workspaceIndexes",
        "activeElement"];

        return JSON.stringify(element, (k, v) => {
            if (ignoredKeys.indexOf(k) != -1 ) 
                return undefined;
            else
                return v;
            });
    }

    private toCamelCase(s: string) {
        if (s.toLowerCase() == 'cdonamefield') {
            s = 'CDONameField';
        }

        return s[0].toLowerCase() + s.substring(1);
    }

    private findItemByName(name: string, list: IElement[]) {
        let item = null;
        _.forEach(list, v => {
            if (v.name == name) {
                item = v;
                return false;
            }
        });
        return item;
    }

    private isNDORef(v): boolean {
        if (v['CDOTypeName'] && v['ID'])
            return true;
        else
            return false;
    }

}