import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { MetadataService } from './metadata.service';
import { PortalService } from './portal.service';
import { ConvertorService } from './convertor.service';
import { IMetadataType } from '../objects/viewModel';
import { AlertService } from '../../alert/alert.service';
import { val, ILabel } from '../../common/objects/common';
import {tap, concat, map} from "rxjs/operators";

import * as _ from 'lodash';

@Injectable()

export class PortalSettingsService {

    private settings: any = {};
    private configTypes : {[name:string] : IMetadataType};     

    constructor( @Inject(MetadataService) private _metadataService: MetadataService, 
            @Inject(PortalService) private _portalService: PortalService, 
            @Inject(ConvertorService) private _convertor: ConvertorService, 
            private _alert : AlertService) {
    }
    
    public save(): Observable<any> {

        let convSettings = this._convertor.convertModelToSettings(this.settings);

        // Languages should not be null
        if (!convSettings.languageSettingsField)
            convSettings.languageSettingsField = {};

        if (!convSettings.languageSettingsField.languagesField)
            convSettings.languageSettingsField.languagesField = [];

        return this._portalService.saveSettings(convSettings).pipe(
            tap(ps=> {
                if(ps.IsSuccess) 
                    this._alert.success(<ILabel> {Name: "PS_PSETTINGS_SAVED", DefaultValue: "Portal Settings are saved correctly."});
            }));
    }

    public getSections() : Observable<SettingSection[]> {
        return this._metadataService.loadType('config', 'PortalSettings').pipe(
            tap( md => { this.configTypes = md;} ), 
            concat(this._portalService.getSettings()),
            tap(sx=> {this.settings = this.convSettings(sx);}),
            map(ss => this.sectionNames.map(sn=> new SettingSection(sn, this.configTypes))));
    }

    private convSettings(conf:any){
        // response is a dictionary
     
        let cnvSettings = this._convertor.convertSettingsToModel(conf, 'config.portalsettings', this._metadataService);
        let settings = {};
        delete cnvSettings.__type;
        
        _.forEach(cnvSettings, (v, n) => {
            if (v === undefined || v === null)
                v = {};

            if (v['name'])
                v.__name = true;
            else
                v.name = n;
            settings[n] = v;
        });
        return settings;
    }

    public getSectionData(s: SettingSection | string) : any {
        if(typeof s == "string"){
            return this._metadataService.AllSettings[s + "Field"];
        }

        return val(this.settings, (<SettingSection>s).name);
    }

    public getSettingsData() {
        return this.settings;
    }

    private sectionNames = ["Default", "Studio", "Domain", "Theme", "Language", "TimeZone", "CurrentCulture", 
            "Grid", "Search", "SmartScanning", "Caching", "Intelligence", "CDOForms", "ExportGroups", 
            "StatusMessage", "Template", "Appearance"];            
}

export class SettingSection{
    public type: IMetadataType;
    public name : string;

    constructor( n: string, mdTypes: {[name:string] : IMetadataType}){
        this.name = n.toLowerCase() + 'settings';
        this.type = mdTypes && mdTypes[this.name];
    }
}