import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'hiddenItemFilter'
})
@Injectable()
export class HiddenItemFilter implements PipeTransform {
    transform(items: any[], hidden: boolean): any[] {
        if (hidden != undefined)
            return items.filter(item => item.isHidden == hidden);       
                                     
    }
}

