//class
export class Alert {
       
    time: number;
    modal: boolean = false;
    title: string;

    constructor(public type: AlertTypes, public message: string, data?: any) {
        this.time = Date.now();
    }

    get class(): string {
        return 'alert alert-' + AlertTypes[this.type] + ' alert-dismissible';
    }
}

//enums
export enum AlertTypes { info, danger, warning, success, choice }