import { Component } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';

import { TraceItem, TraceItemAttribute, TraceItemParameter, ServerTraceItem } from './traceLog';
import { TraceRawViewComponent } from './traceRawView.component';
import { ResubmitRequestComponent } from './resubmitRequest.component';
import { Subject } from 'rxjs';
import * as _ from 'lodash';

@Component({
    selector: 'csi-service-trace-view',
    templateUrl: 'serviceTraceView.component.html',
    styleUrls: ['serviceTraceView.component.less']
})

export class ServiceTraceViewComponent {

    public trace: TraceItem;

    strace: ServerTraceItem;
    searchSubj = new Subject<string>();

    get dialogRef(): MatDialogRef<ServiceTraceViewComponent> {
        return this._dialogRef;            
    }

    get elapsed(): string {
        if (this.strace && this.strace.Ended && this.strace.Started) {
            let endTime = this.getTime(this.strace.Ended);
            let startTime = this.getTime(this.strace.Started);

            let el = (endTime - startTime) / 1000.0;
            return el.toString();
        }
        else
            return '';
    }

    get memUsage(): string {
        if (this.strace && this.strace.EndedMemoryUsage !== undefined && this.strace.StartedMemoryUsage !== undefined) {
            let m = (this.strace.EndedMemoryUsage - this.strace.StartedMemoryUsage) / 1000.0;
            return m.toString();
        }
        else
            return '';
    }

    private getTime(ds: string): number{
        return parseInt(ds.substr(6));
    }

    constructor(
        private _dialogRef: MatDialogRef<ServiceTraceViewComponent>,
        private _dialog: MatDialog
        ) {

        this.strace = new ServerTraceItem();
    }    

    selectNode(st: ServerTraceItem) {
        this.strace = st || new ServerTraceItem();
    }

    close() {
        this._dialogRef.close();
    }

    doSearch(s: string) {
        this.searchSubj.next(s);
    }

    makeNodeName(p) {
        let node: ServerTraceItem = p.node;
        let name = node.Name;
        let getParam = (np: string) => node.Parameters.find(p => p.Name == np) || new TraceItemParameter();
        let getAttr = (na: string) => (node.Attributes.find(a => a.Name == na) || new TraceItemAttribute()).Value;

        switch (name){
            case '__cdoPerform':
                name = "Perform " + getAttr("__cdoName") + "." + getAttr("__method");
                break;
            case 'if':
                name += (' ' + getParam('Condition').Expression);
                break;
            case "Initialize":
                name = "Init " + getAttr("__cdoName") + "." + getAttr("__fieldName");
                break;
            case 'OnGetValue':
                name = 'Get ' + getAttr('__cdoName') + '.' + getAttr('__fieldName');
                break;
        }

        let firstParameter = node.Parameters.find(p => !!p.Value);
        if (firstParameter) {
            name += ('[' + firstParameter.Value + ']');
        }

        p.name = name;
    }

    rawViewOpen() {
        let dialog = this._dialog.open(TraceRawViewComponent, { disableClose: true });
        dialog.componentInstance.traceItem = this.trace.DebugResponse.Trace;
        dialog.afterClosed().subscribe(res => {
        });
    }

    resubmitOpen() {
        let dialog = this._dialog.open(ResubmitRequestComponent, { disableClose: true });
        dialog.componentInstance.debugResponse = _.cloneDeep(this.trace.DebugResponse);
        dialog.afterClosed().subscribe(res => {
        });
    }

}
