import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

import { DialogAction } from './confirm.dialog.component';

@Component({
    selector: 'csi-input-dialog',
    templateUrl: 'input.dialog.component.html'
})

export class InputDialogComponent {
    alertMessage: string;
    context: InputDialogContext;

    constructor(private _dialogRef: MatDialogRef<InputDialogComponent>) { }

    initContext(context: InputDialogContext) {
        this.context = context;
    }

    execute(action: string): void {
        if (action == 'ok') // assume that ok is submit
        {
            for (let i = 0; i < this.context.inputs.length; i++) {
                let input = this.context.inputs[i];
                if (input.value.length == 0) {
                    this.alertMessage = `The '${input.name}' field is required`;
                    return;
                }
            }
            this._dialogRef.close(this.context.inputs);
        }
        else
            this._dialogRef.close(action);
    }
}

export class DialogInput {
    name: string;
    type: string;
    text: string;
    value: string;
}

export class InputDialogContext {
    message: string;
    title: string;
    actions: DialogAction[];
    inputs: DialogInput[];
}
