import { Component } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';

import { EditConditionComponent } from './editCondition';
import { ValueConditionComponent } from './valueCondition';

import { IMetadataEnumValue, PropertyConditionExpression, SetDataValueItem, ConditionExpression } from '../../../common/objects/viewModel';
import { ModalPropertiesContext } from '../properties';
import { MetadataService } from '../../../common/services/metadata.service';
import * as _ from 'lodash';

@Component({
    selector: 'csi-property-condition',
    styleUrls: ['./propertyConditions.less'],
    templateUrl: 'propertyCondition.html'
})

export class PropertyConditionsComponent {
    newConditionName: string = '';
    conditions: PropertyConditionExpression[] = [];

    ownerProperties: string[];
    readOnly: boolean;

    private conditionComparisonEnum: IMetadataEnumValue[];
    private context: ModalPropertiesContext;

    get dialogRef(): MatDialogRef<PropertyConditionsComponent> {
        return this._dialogRef;
    }

    constructor(
        private _metadata: MetadataService,
        private _dialog: MatDialog,
        private _dialogRef: MatDialogRef<PropertyConditionsComponent>){

        this.conditionComparisonEnum = this._metadata.getMetadataType('ConditionComparisonType').enumValues;
    }

    initContext(context: ModalPropertiesContext): void {
        this.context = context;
        this.conditions = this.context.value ? _.cloneDeep(this.context.value) : [];
        this.conditions.forEach(c => {
            if (c.condition)
                c.condition.__shortDescription = this.getShortDesc(c.condition);
        });
        let instType = this._metadata.getMetadataType(this.context.instanceType.toLowerCase());
        if (instType && instType.properties) {
            this.ownerProperties = instType.properties.map(p => p.name);
        }
        this.readOnly = context.property.readOnly;
    }

    addCondition(name: string): void {
        if( !this.readOnly ){
            let c = new PropertyConditionExpression();
            c.description = name;
            this.newConditionName = '';
            this.conditions.push(c);
        }
    }

    editCondition(i: number): void {
        if( this.readOnly )
            return;
        
        let editConditionDialog = this._dialog.open(EditConditionComponent, { disableClose: true });
        let context = new ModalPropertiesContext();
        context.value = this.conditions[i].condition;
        editConditionDialog.componentInstance.initContext(context);
        editConditionDialog.afterClosed().subscribe((val: ConditionExpression) => {
            this.conditions[i].condition = val;
            if (this.conditions[i].condition)
                this.conditions[i].condition.__shortDescription = this.getShortDesc(this.conditions[i].condition);
        });
        this.conditions[i].isExpanded = false;
    }

    editValueCondition(i: number): void {
        if( this.readOnly )
            return;
        let valueConditionDialog = this._dialog.open(ValueConditionComponent, { disableClose: true });
        let context = new ModalPropertiesContext();
        context.value = this.conditions[i].valueIfTrue;
        valueConditionDialog.componentInstance.initContext(context);
        valueConditionDialog.afterClosed().subscribe((c: ModalPropertiesContext) => this.conditions[i].valueIfTrue = c.value);
        this.conditions[i].isExpanded = false;
    }

    deleteCondition(condition: PropertyConditionExpression): void {
        if( this.readOnly )
            return;
        let index = this.conditions.indexOf(condition);
        this.conditions.splice(index, 1);
    }

    ok(): void {
        this._dialogRef.close(this.conditions);
    }

    cancel(): void {
        this._dialogRef.close(null);
    }

    private getKey(s: SetDataValueItem): string {
        return "'" + ((s && s.key) || "") + "'";
    };

    private getShortDesc(exp: ConditionExpression): string {
        let f = '(';

        if (exp) {
            f += this.getKey(exp.value1);

            if (exp.comparisonType !== undefined) {
                let comparisonName = this.conditionComparisonEnum.find(c => c.value == exp.comparisonType).name;
                f += (' ' + comparisonName + ' ');
            }
            f += this.getKey(exp.value2);
        }
        f += ')';

        if (exp) {
            if (exp.and && exp.and.length) {
                f += " AND (...)";
            }
            if (exp.or && exp.or.length) {
                f += " OR (...)";
            }
        }

        return f;
    }


}