import { Component, Input,  OnInit } from '@angular/core';

import { StudioSubjects} from '../../../common/objects/common';
import { LabelService } from '../../../common/services/label.service';
import { PanelSettings } from '../../panel';
import { ModalPropertiesContext } from '../properties';
import { PortalSettingsService, SettingSection } from '../../../common/services/portalSettings.service';

import { Observable } from 'rxjs';


@Component({
    selector: 'csi-portal-settings',
    styles:[
        `
        :host{
            flex: 1 1 auto;
            display: flex;
            flex-direction: column;
            min-width: 600px;
        }

        .csi-portal-settings {
            flex: 1 1 auto;
            display: flex;
        }
        .left-panel {
            min-width: 10%;
            overflow: auto;
            flex: 1 1;
        }
        .left-panel .instances {
            height: 100%;
            border-right: solid 1px #aaaaaa;
        }
        .left-panel .instance {
            cursor: pointer;
            padding: 3px;
        }
        .left-panel .instance.selected {
            background-color: #aaaaaa;
            color: white;
        }

        .right-panel {
            overflow: auto;
            width: 70%;
            padding-left: 5px;
            display: flex;
            flex-direction: column;
        }
    }
    `],
    template: `
        <div class='csi-portal-settings'>
            <div class='left-panel'>
                <div class="instances">
                    <div *ngFor="let section of sections | async">
                        <div class="instance" [class.selected]="section===activeSection" (click)="selectSection(section)">{{section.type._name}}</div>
                    </div>
                </div>
            </div>
            <csi-panel-splitter direction="v" [panel]="rightPanel"></csi-panel-splitter>
            <div class='right-panel' #rightPanel>
                <csi-panel [component]="'csi-property-list'" [subjects]="subjects" [parameters]="{showDescription: false}"></csi-panel>
            </div>
        </div>
    `
})

export class PortalSettingsComponent implements OnInit {  

    @Input() context: ModalPropertiesContext;
    @Input() subjects: StudioSubjects;

    sections: Observable<SettingSection[]>;
    activeSection: SettingSection;
    
    constructor(private _labelService: LabelService, private _portalSettingsService : PortalSettingsService) {
    }  
      
    ngOnInit(): void {

        let portalSettingsLabel = this._labelService.getLabel('PS_PORTAL_SETTINGS', 'Portal Settings');
        this.context.title = portalSettingsLabel;
        this.sections = this._portalSettingsService.getSections();
        this.context.beforeClose = (ok : boolean )=> {
            if( ok )
                this.context.value = this._portalSettingsService.getSettingsData();
            return true;
        };
    }

    onPanelLoaded(panel: PanelSettings) {
        panel.nameInput.readonly = true;
        panel.visible = false;
    }

    selectSection(sec : SettingSection) {
        this.activeSection = sec;
        let inst = this._portalSettingsService.getSectionData(sec);
        this.subjects.propertyPanel.next(inst);
    }
}
