import { Directive, Input, AfterViewInit, ElementRef, OnDestroy } from '@angular/core';
import { DragService } from '../drag.service';
import { DragInfo } from '../drag';

export class DropDataInfo {
    disabled?: boolean = false;
    types: string;
    drop?: (d: DragInfo, dropInf: DropDataInfo) => void;
    over?: (d: DragInfo, dropInf: DropDataInfo) => void;
    id: string;
    model?: any;
}

@Directive({
    selector: '[csi-droppable]'
})
export class DroppableDirective implements AfterViewInit, OnDestroy {
    @Input('csi-droppable') info: DropDataInfo;

    constructor(private _el: ElementRef, private _drag: DragService) {
    }

    ngAfterViewInit() {        
        if (!this.info.disabled){
            this._drag.addZone(this.info.types.split(","), this.info.id, (m: DragInfo) => this.action(m));
            this._el.nativeElement.setAttribute("dragDropId", "drop-" + this.info.id);
            this._el.nativeElement.classList.add("drop-zone");
        }
    }

    ngOnDestroy() {
        if (!this.info.disabled)
            this._drag.removeZone(this.info.id);
    }

    private action(d: DragInfo) {
        if (d.endOfDrag) {
            if (this.info.drop && this.info.disabled !== true)
                this.info.drop(d, this.info);
        }
        else {
            if (this.info.over)
                this.info.over(d, this.info);
        }
    }

} 
