import { Component, EventEmitter, Input, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { IElement, IUIDesigner, ExecuteCommand } from '../common/objects/common';
import { ConfirmDialogComponent } from '../shared/dialog/confirm.dialog.component';
import { LabelService } from '../common/services/label.service';
import { StudioStateService } from '../common/services/studioStateService';

@Component({
    selector: 'csi-layout-tabs',
    templateUrl: 'tabs.component.html',
    styles:[`

    :host() {
        min-height: 30px;
        background-color: #dcdcdc;
        overflow-y: hidden;
        overflow-x: auto;
        display: flex;
        flex: 0 0 auto;
    }


    .csi-editor-tab {
        display: flex;        
        padding: 3px 10px;
        justify-content: center;
        background-color: #dcdcdc;
        border-right: 1px solid white;
        cursor: pointer;
        white-space: nowrap;
    }

    .csi-editor-tab:hover{
        background-color: #AFD7E1;
    }
    .csi-editor-tab:active{
        font-weight: bold;
        background-color: #CDE6EB;
    }

    .dirty-flag{
        padding-left: 3px;
    }

    .csi-editor-tab.active {
        font-weight: bold;
        background-color: #F0F0F0;
    }

    `]
})

export class LayoutTabsComponent {
    @Input("elements") __elements: any;
    @Input("activeElement") __activeElement: any;

    @Output() closeElement: EventEmitter<IElement> = new EventEmitter<IElement>();
    @Output() closeAndSaveElement: EventEmitter<IElement> = new EventEmitter<IElement>();

    get elements(): IUIDesigner[] { return this.__elements; }
    set elements(v: IUIDesigner[]) { this.__elements = v; }

    get activeElement(): IUIDesigner { return this.__activeElement; }
    set activeElement(v: IUIDesigner) { this.__activeElement = v; }

    constructor(
        private _dialog: MatDialog,
        private _studioState : StudioStateService,
        private _labelService: LabelService) { }

    activateSelectElement(element: IElement): void {
        this._studioState.subjects.execute.next(new ExecuteCommand("switchTab", element, null));
    }

    closeSelectElement(element: IUIDesigner): void {
        if (element.isDirty) {

            let yesLabel = this._labelService.getLabel('PS_ACTION_YES', 'Yes');
            let noLabel = this._labelService.getLabel('PS_ACTION_NO', 'No');
            let cancelLabel = this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel');
            let titleLabel = this._labelService.getLabel('PS_CLOSE', 'Close');
            let messageLabel = this._labelService.getLabel('PS_SAVE_PENDING', 'Would you like to save pending changes?');
            let confirmContext = {
                title: titleLabel,
                message: messageLabel,
                actions: [
                    { action: 'yes', name: yesLabel, isPrimary: true },
                    { action: 'no', name: noLabel, isPrimary: false },
                    { action: 'cancel', name: cancelLabel, isPrimary: false }]
            };

            let dialog = this._dialog.open(ConfirmDialogComponent, { disableClose: true });

            dialog.componentInstance.initContext(confirmContext);
            dialog.afterClosed().subscribe(res => {
                //TODO: Check to make sure logic isn't working properly
                // dialog.onDestroy.subscribe(() => {
                //     if (isSaved === true)
                //         this.closeAndSaveElement.emit(element);
                //     else if (isSaved === false)
                //         this.closeElement.emit(element);
                //     else {
                //         // if cancel = just the dialog is closed
                //     }
                // });

                // dialog.result.then(res => {
                //     if (res === "yes") {
                //         isSaved = true;
                //     } else if (res === "no") {
                //         isSaved = false;
                //     }
                // });
                if (res === "yes") {
                    this.closeAndSaveElement.emit(element);
                } else if (res === "no") {
                    this.closeElement.emit(element);
                }
            });
        }
        else
            this.closeElement.emit(element);
    }

    toggleBg(ev: MouseEvent, on: boolean){
        let t = ev.target as HTMLSpanElement;
        if(!t.classList.contains("csi-editor-tab"))
            t = t.parentElement;
        t.classList.toggle("csi-mousedown", on);
    }
}