import { Component, Input, OnInit } from '@angular/core';
import { IPropertyMetadata } from '../../../common/objects/viewModel';
import { ISelectableItem, ValueUpdatedData } from '../../../common/objects/common';
import { Subject } from 'rxjs';

@Component({
    selector: 'csi-editor-switchbox',
    template:
    `<div class="switchbox">
        <select #switchbox class="csi-property-selector" [value]="val" (change)="valChanged(switchbox.value)" [disabled]="property.readOnly">
            <option *ngFor="let option of selectableItems" [value]="option.value" [selected]="option.value==val">{{option.name}}</option>
        </select>
    </div>`,
    styles:[`    
        .switchbox:{}
        .csi-property-selector {
            width: 100%;
            height: 27px;
            border: solid 1px #aaaaaa;
            outline:none;
            padding-left:4px;
        }
        select::-ms-expand{
            color: #ffffff;
            border: none;
            width: 15px;
            padding-right:5px;
            background: no-repeat right url(Content/images/downarrow.png);        
        }

    `]
})

export class SwitchBoxEditorComponent implements OnInit{
    @Input("property") __property: any;
    @Input() valueChanged: Subject<ValueUpdatedData>;

    get property(): IPropertyMetadata { return this.__property; }

    constructor() { }

    val: string;
    selectableItems: ISelectableItem[] = [
        <ISelectableItem>{ name: 'Not Set', value: 'notset', typeName: 'boolean' },
        <ISelectableItem>{ name: 'True', value: 'true', typeName: 'boolean' },
        <ISelectableItem>{ name: 'False', value: 'false', typeName: 'boolean' }
    ];

    ngOnInit() {

        // Exclude NotSet if the property is not nullable
        if (this.property.notNullable === true) {
            this.selectableItems.splice(0, 1);
        }

        // Load value
        let n = 'notset';
        if (this.property.value === true || this.property.value === 'true')
            n = 'true';
        else if (this.property.value === false || this.property.value === 'false')
            n = 'false';
        else {// value = undefined 
            if (this.property.notNullable === true) {
                this.property.value = false;
                n = 'false';
            }
        }
        // TODO: the selectable items should be taken from metadata to localize name ( True -> Wahr )
        this.val = this.selectableItems.find(i => i.value == n).value.toString();
    }

    valChanged(s: string) {
        let v: boolean;  

        if (s !== undefined && s !== null && s !== '' && s !== 'notset') {
            v = (s === 'true');
        }
        
        this.val = s;
        this.valueChanged.next(new ValueUpdatedData(this.property.name, v, typeof v));
    }

}

