import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

import { MetadataService } from '../../../common/services/metadata.service';
import { ConditionExpression, SetDataValueItem, PropertyConditionExpression, InitiatorDefinition, IMetadataEnumValue } from '../../../common/objects/viewModel';

@Component({
    selector: 'csi-condition-display',
    styles:[
        `.csi-condition-display {
            display: flex;
            flex-direction: column;
            margin-left: 20px;
        }
        .csi-condition-display > .line {
              display: inline-flex;
        }          
        .csi-condition-display > [indent="1"].line { margin-left: 20px; }
        .csi-condition-display > [indent="2"].line { margin-left: 40px; }
        
        .csi-condition-display > .line > [bold="true"].item {
            font-weight: bold;
        }
        .csi-condition-display > .line > [link="true"].item {
            text-decoration: underline;
            color: blue;
            cursor: pointer;
        }
        `
    ],
    template: 
    `<div class="csi-condition-display">
        <div class="line" [attr.indent]="l.indent" *ngFor="let l of lines">
            <div class="item" [attr.bold]="it.isBold" [attr.link]="it.isLink" *ngFor="let it of l.items" (click)="editCondition(it)" >{{it.text}}&nbsp;</div>
        </div>
    </div>`
})

export class ConditionDisplayComponent implements OnInit {
    @Input() condition: PropertyConditionExpression;
    @Output() onEditCondition = new EventEmitter<any>();

    lines: line[] = [];
    conditionComparisonEnum: IMetadataEnumValue[];
    dataMemberConnectionTypeEnum: IMetadataEnumValue[];

    constructor(private _metadata: MetadataService) {
        this.conditionComparisonEnum = this._metadata.getMetadataType('ConditionComparisonType').enumValues;
        this.dataMemberConnectionTypeEnum = this._metadata.getMetadataType('DataMemberConnectionType').enumValues;
    }

    ngOnInit(): void {
        this.buildCondition(this.condition.condition);
        if (this.condition.valueIfTrue) {
            this.lines.push(new line(new txtItem('Then', true)));
            this.buildExpression(this.condition.valueIfTrue, "ThenValue");
        }
    }

    private buildCondition(cond: ConditionExpression): void {
        if (cond === undefined || cond == null)
            return;

        this.buildExpression(this.getField(cond, 'value1'), "Left");
        this.buildComparison(this.getField(cond, 'comparisonType'));
        this.buildExpression(this.getField(cond, 'value2'), "Right");

        ["And", "Or"].forEach(b => {
            let bwCond: ConditionExpression[] = this.getField(cond, b.toLowerCase());
            if (bwCond && bwCond.length) {
                this.lines.push(new line(new txtItem(b, true)));
                bwCond.forEach(cx => this.buildCondition(cx));
            }
        });
    }

    private buildExpression(d: SetDataValueItem, valName: string): void {
        if (d === undefined || d == null)
            return;

        let l = new line();
        if (valName == "ThenValue") {
            l.items.push(new txtItem("Value of the property"));
        }
        else {
            l.items.push(
                new txtItem("If Expression"),
                new txtItem(valName + " Value", true)
            );
        }
        l.items.push(new txtItem("equals value on connection type"));
        let connTypeValue: number = this.getField(d, 'connectionType');
        if (connTypeValue === undefined)
            connTypeValue = 0;
        let connType = this.dataMemberConnectionTypeEnum.find(t => t.value == connTypeValue);
        l.items.push(new txtItem(connType.name, false, true));
        this.lines.push(l);

        this.buildFieldVal(d, 'key', connType.name);
        this.buildFieldVal(d, 'property');
        this.buildFieldVal(d, 'requestDataMode');
        this.buildFieldVal(d, 'allowEmptyValue');

        let initiators = this.getField(d, 'initiators');
        if (initiators)
            initiators.forEach(init => this.buildInitiator(init))
    }

    private buildFieldVal(d: SetDataValueItem, f: string, displayedFieldName: string = ''): void {
        let v = this.getField(d, f);
        if (v !== undefined) {
            let l1 = new line();
            l1.indent = 1;
            l1.items.push(
                new txtItem('and Value on field'),
                new txtItem(displayedFieldName || f),
                new txtItem('equals'),
                new txtItem(v, false, true));
            this.lines.push(l1);
        }
    }

    private buildComparison(ct: number): void {
        if (ct === undefined)
            ct = 0;
        let c = this.conditionComparisonEnum.find(e => e.value == ct);
        this.lines.push(new line(new txtItem(c.name, true)));
    }

    private buildInitiator(init: InitiatorDefinition): void {
        let l1 = new line();
        l1.items.push(
            new txtItem('and Initiator'),
            new txtItem(this.getField(init, 'name'), false, true),
            new txtItem('is triggered by'),
            new txtItem(this.getField(init, 'key'), false, true),
            new txtItem('on event'),
            new txtItem(this.getField(init, 'event'), false, true));
        this.lines.push(l1);
    }

    private getField(o: any, f: string) : any {
        f = f.toLowerCase();
        let key = Object.keys(o).find(k => k.toLowerCase() == f);
        if (key) 
            return o[key];
        return undefined;        
    }

    editCondition(t: txtItem) {
        if (t.isLink)
            this.onEditCondition.emit();
    }
}

class txtItem {

    constructor(public text = '', public isBold = false, public isLink = false) {
    }
}

class line {
    items: txtItem[] = [];
    indent: number = 0;

    constructor(t?: txtItem ) {
        if (t)
            this.items.push(t);
    }
}