import { Component, ComponentRef, Input, ChangeDetectorRef, ChangeDetectionStrategy, ViewContainerRef, ViewChild, OnInit, OnDestroy } from '@angular/core'
import { ComponentFactoryResolver } from '@angular/core';
import { Subscription } from 'rxjs';
import {debounceTime, filter} from "rxjs/operators";

import { IElement, StudioSubjects, isContainerControl, RefreshInfo, IDesignControl, DesignModes } from '../../../common/objects/common';
import { ControlManager, ControlTab } from './controlManager';
import { ControlMappingService } from './controlMapping.service';
import { DesignComponent } from '../design.component';
import { StudioStateService } from '../../../common/services/studioStateService';

@Component({
    selector: 'csi-control-container',
    templateUrl: 'controlContainer.component.html',
    changeDetection: ChangeDetectionStrategy.OnPush,
    entryComponents: [DesignComponent]
})

export class ControlContainerComponent implements OnInit, OnDestroy {
    @Input("element") __element: any;
    @Input() showCollapsed: boolean;
    @Input() subjects: StudioSubjects;
    @Input() isReference : boolean;

    @ViewChild('controlHolder', { read: ViewContainerRef, static: true }) controlHolder: ViewContainerRef;

    componentRef: ComponentRef<IDesignControl>;

    public controlManager: ControlManager;
    private refreshSubscription: Subscription;

    get element(): IElement { return this.__element; }
    set element(v: IElement) { this.__element = v; }

    constructor(
        private _cd: ChangeDetectorRef, 
        private _studioState: StudioStateService,
        private _controlMappingService: ControlMappingService,
        private _componentfactoryResolver: ComponentFactoryResolver) {
    }

    ngOnInit() {
        this.controlManager = new ControlManager(this.element);
        this.buildComponent();
    }

    ngOnDestroy() {
        if (this.componentRef) {
            if (this.refreshSubscription)
                this.refreshSubscription.unsubscribe();
            this.componentRef.destroy();
        }
    }

    setActiveTab(tab : string|ControlTab) {
        if (this.controlManager.setActiveTab(tab))
            this.buildComponent();
        else
            this._studioState.selectItem(this.element);
    }

    private buildComponent() {

        if (this.componentRef)
            this.componentRef.destroy();

        const componentType = this._controlMappingService.getControlComponent(this.element.typeName);
        if( componentType ){
            const componentFactory = this._componentfactoryResolver.resolveComponentFactory<IDesignControl>(componentType);
            this.controlHolder.clear();
    
            this.componentRef = this.controlHolder.createComponent<IDesignControl>(componentFactory, 0);
            this.componentRef.instance.element = this.controlManager.getControlModel();

            //Load active tab for design elements
            if (isContainerControl(this.element.typeName)) {
                this.componentRef.instance.collapsible = true;
                this.componentRef.instance.isCollapsed = this.showCollapsed;
                this.componentRef.instance.isReference = this.isReference;
            }

            this.componentRef.instance.subjects = this.subjects;
            this.refreshSubscription = this.subjects
                .refresh.pipe(
                    filter(r => r.element == this.componentRef.instance.element),
                    debounceTime(700))
                .subscribe(r => this.refreshComponent(r));
            this._cd.markForCheck();
        }
        else{
            console.error("The component " + this.element.typeName + " was not resolved");
        }
    
    }

    activateControl(event: Event){
        if( this._studioState.getRoot().__designMode == DesignModes.Controls ){
            this._studioState.activateElement(this.element);
            event.stopPropagation();
        }
    }

    private refreshComponent(r: RefreshInfo) {
        this._cd.markForCheck();
    }
}
