import { Component, Input, Output, EventEmitter } from '@angular/core';
import * as _ from 'lodash';
import { textLine } from './traceLog';


@Component({
    selector: 'csi-raw-text',
    template: `
        <div class="csi-raw-text lines" *ngIf="viewOnly && !isPlainText">
            <div *ngFor="let l of lines">{{l}}</div>
        </div>
        <div class="csi-raw-text editable" *ngIf="!viewOnly">
            <textarea #editableText [attr.spellcheck]="false" (change)="txtChange(editableText.value)">{{text}}</textarea>
        </div>
        <div class="csi-raw-text" *ngIf="viewOnly && isPlainText">
            {{text}}
        </div>
    `
})

export class RawTextComponent {
    @Input() text: string | string[] | textLine[];
    @Input() viewOnly: boolean = true;

    @Output() textChange = new EventEmitter<string>();

    get isPlainText(): boolean {
        return this.text && typeof this.text == 'string';
    }

    get lines(): string[] {
        let res: string[] = [];

        if (!this.isPlainText) {
            let arTxt = <any[]>this.text;
            if (arTxt.length > 0) {
                if (typeof arTxt[0] == "string") {
                    res = <string[]>arTxt;
                }
                else {
                    res = (<textLine[]>arTxt).map(a => _.repeat('  ', a.level) + a.text);
                }
            }
        }
        return res;
    }

    txtChange(t: string) {
        this.textChange.emit(t);
    }
}
