import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import {map} from "rxjs/operators";

import { IUIComponentRef, IUserProfile, IResultStatus, IInfo, IRecordSet, IResponse, DirectAccessSubmitResult, UIComponentRef } from '../objects/common';
import { AlertService } from '../../alert/alert.service';

import { environment } from '../../../environments/environment';
import * as _ from 'lodash';

@Injectable()
export class WCFService {
    private _headers = {headers: null};
    userProfile: IUserProfile;

    static selValeCache: { [name: string]: IRecordSet } = {};

    constructor( @Inject(HttpClient) private _http: HttpClient, @Inject(AlertService) private _alertService: AlertService) {
    }

    //setAuthorization - sets authorization header security credentials after user is verified   
    public setAuthorization(authorizationHeader: string): void {
        this._headers.headers = new HttpHeaders({"Content-Type": "application/json"});
        // parse auth header 
        let ph = authorizationHeader.substr('Bearer '.length).split(';');
        // Name;SessionID;UTCOffset
        this.userProfile = <IUserProfile>{
            Name: ph[0],
            SessionID: { IsEncrypted: true, Value: ph[1] },
            UTCOffset: ph[2]
        };
    }

    public getSelectionValues(svc: string, field: string, force: boolean = false): Observable<IRecordSet> {

        if (!force) {
            // check the cache
            let rs = WCFService.selValeCache[svc];
            if (rs) {
                let rs = WCFService.selValeCache[svc];
                return new Observable<IRecordSet>((s) => { s.next(rs); s.complete(); });
            }
        }

        var inf: IInfo = <IInfo>{
            RequestSelectionValues: true,
            RequestSelectionValuesInfo: {
                Parameters: [
                    { Name: 'NameFilter', Value: '%' },
                    { Name: 'SpecificTypeOnly', Value: 0 },
                    { Name: 'WorkspaceCode', Value: environment.workspace }
                ]
            }
        };

        var infoData = {};
        infoData[(svc + "_Info_" + field)] = inf;

        return this.getData(svc, "GetEnvironment", { Info: infoData })
            .pipe( map((r: IResponse) => {
                if( r ){
                    let rs = <IRecordSet>(r.Environment[svc + "_Environment_" + field].SelectionValues);
                    WCFService.selValeCache[svc] = rs;
                    return rs;
                }
                else{
                    return null;
                }
            }));
    }

    private getData(svc: string, operation: string, requestData: any): Observable<IResponse> {

        let rqData = {
            userProfile: this.userProfile,
            request: requestData
        };

        return this._http.post<any>(this.getUrl(svc, operation), JSON.stringify(rqData), this._headers)
            .pipe(map( resp => {
                let status = <IResultStatus>resp[operation + "Result"];
                if (status.IsSuccess) {
                    return resp.result;
                }
                else {
                    this.alertError(status);
                    return null;
                }
            }));
    }

    public getElementRefs(cdo: string, type: string, force?: boolean): Observable<IUIComponentRef[]> {
        return this.getSelectionValues(cdo, 'ObjectListInquiry', force)
            .pipe(map((env: IRecordSet) => {
                let refs = (env.Rows || []).map(r => UIComponentRef.fromSelectionValueRow(r.Values, env.Headers, type ) );
                return refs;
            }));
    }

    public getElement(name: string, svc: string): Observable<any> {
        if (!name) {
            // create new empty object
            let cdoType = this.getCDOTypeName(svc);
            let cdoName =  cdoType == "ActionDef"? "PageAction" : cdoType;
            name = cdoName + '-' + new Date().getMilliseconds().toString(); // new name
            let res = { __type: this.getCDOTypeName(svc) + "Changes" };
            res[cdoType + "Changes_Name"] = name;

            return new Observable<any>(subscriber => subscriber.next(res));
        }

        return this.loadNDOInstance(name, svc)
            .pipe(map((ndo: any) => ndo));
    }

    public loadNDOInstance(name: string, svc: string): Observable<any> {
        var infoData = {};
        infoData[(svc + "_Info_" + "ObjectChanges")] = <IInfo>{
            RequestValue: true
        };

        let rqData = {
            userProfile: this.userProfile,
            request: { Info: infoData }
        };

        let cdo = _.camelCase(svc);
        rqData[cdo] = {};
        let maint = rqData[cdo];
        maint[svc + '_ObjectToChange'] = { 'Name': name };

        let operation = "Load";

        return this._http.post<any>(this.getUrl(svc, operation), JSON.stringify(rqData), this._headers)
            .pipe(map((resp: any) => {
                let status = <IResultStatus>resp[operation + "Result"];
                if (status.IsSuccess) {
                    return resp.result.Value[svc + '_ObjectChanges'];
                }
                else {
                    this.alertError(status);
                    return null;
                }
            }));
    }

    public saveNDOInstance(name: string, svc: string, changes: any): Observable<any> {

        let isNew: boolean = changes.__new !== undefined;

        let maint = {};
        maint[svc + '_ObjectToChange'] = { 'Name': name };

        let methods = [
            {
                ServiceMethod: 3,   // Load
                Cdo: maint
            },
            {
                ServiceMethod: 9,   // execute
                Cdo: changes
            }
        ];

        if (isNew) {
            methods[0].ServiceMethod = 5;       // new
            methods[0].Cdo = undefined;
        }

        let rqData = {
            userProfile: this.userProfile,
            methods: methods
        };

        let operation = "CommitTransaction";
        return this._http.post<any>(this.getUrl(svc, operation), JSON.stringify(rqData), this._headers)
            .pipe(map((response: any) => {
                let status = <IResultStatus>response[operation + "Result"];
                if (status.IsSuccess) {
                    return status;
                }
                else {
                    this.alertError(status);
                    return null;
                }
            }));
    }

    public deleteMaint(name: string, svc: string): Observable<IResultStatus> {

        let maint = {};
        maint[svc + '_ObjectToChange'] = { 'Name': name };

        let methods = [
            { 
                ServiceMethod: 0,   // delete
                Cdo: maint
            },
            {
                ServiceMethod: 9   // execute
            }
        ];

        let rqData = {
            userProfile: this.userProfile,
            methods: methods
        };

        return this._http.post<IResultStatus>(this.getUrl(svc, "CommitTransaction"), JSON.stringify(rqData), this._headers);
    }

    public directAccessSubmit(request: string, debugRequest?: any): Observable<DirectAccessSubmitResult> {
        let rqData = {
            userProfile: this.userProfile,
            inputXml: request
        };

        if (debugRequest)
            rqData.userProfile.DebugRequest = debugRequest;

        let operation = "Submit";

        return this._http.post<any>(this.getUrl("DirectAccess", operation), JSON.stringify(rqData), this._headers)
            .pipe(map((resp: any) => {
                let status = <IResultStatus>resp[operation + "SubmitResult"];
                return <DirectAccessSubmitResult>{ result: status, response: resp.outputXml };
            }));
    }

    private getUrl(svc: string, operation: string): string {
        return environment.wcfUrl + '/' + svc + 'Service.svc/web/' + operation;
    }

    private getCDOTypeName(svc: string): string {
        let res = svc;
        if (svc.endsWith("Maint"))
            res = svc.substr(0, svc.length - "Maint".length);

        if (svc == "ActionMaint")
            res = "ActionDef";

        return res;
    }

    // private handleError(error: Response) {

    //     let err;
    //     if (_.isFunction(error.json))
    //         err = error.json().error || 'Server error';
    //     else
    //         err = 'Server error';

    //     this.alertError(status);

    //     return Observable.throw(err);
    // }

    private alertError(status: any) {
        this._alertService.error(status);
    }
}
