import { Component, Input, OnChanges, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef, SimpleChanges} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import _ from 'lodash';
import { Subscription } from 'rxjs';

import { PortalService } from '../../common/services/portal.service';
import { AlertService } from '../../alert/alert.service';
import { LabelService } from '../../common/services/label.service';
import {
    IElement, IUIDesigner, IResultStatus, StudioSubjects, IDesignControl, DesignModes, ICellItemRef, uuid } from '../../common/objects/common';
import { LayoutManager } from './objects/layoutManager';
import { LayoutCell, ICell } from './objects/layoutCell';
import { ControlManager } from './control/controlManager';
import { ElementHistoryData, RecordCommand, HistoryObjectType } from '../../common/objects/historyLog';
import { GridWizardComponent } from './gridWizard/gridWizard.component';
import { ConfirmDialogComponent } from '../../shared/dialog/confirm.dialog.component';
import { StudioStateService } from '../../common/services/studioStateService';
import { MetadataService } from '../../common/services/metadata.service';
import { DragInfo } from '../../shared/drag';
import { DropDataInfo } from '../../shared/directives/droppable.directive';

@Component({
    selector: 'csi-design',
    templateUrl: 'design.component.html',
    styleUrls: ['design.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class DesignComponent implements OnChanges, OnDestroy, IDesignControl {
    @Input("element") __element: IUIDesigner;
    @Input() isRootWebpart: boolean = false;       // is root webpart
    @Input() isParentReference: boolean = false;
    copyEl:IElement;

    get element(): IUIDesigner { return this.__element; }

    DesignModesEnum = DesignModes;

    collapsible: boolean = false;
    designElement: IElement;
   
    get isCollapsed() {return this.designElement.__collapsed; }

    get isReference() {return this.designElement.isReference; }

    get layoutManager(): LayoutManager {return this.designElement.__layoutManager; }
    set layoutManager(lm: LayoutManager) {this.designElement.__layoutManager = lm; }

    elementIconClass: string;
    designClass = "csi-layoutManager";
    isTabbed = false;    
    controlMgr: ControlManager;
    nonVirtualWp = false;
    displayCellPosition() : boolean {return this._studioService.displayCellPosition;}

    get headerTitle() {return this.designElement && this.designElement.properties && (this.designElement.properties.title || this.designElement.properties.name); };

    get root() : IUIDesigner {return this._studioService.getRoot();}

    get mode(): DesignModes { return this.root.__designMode; }
    get readonly() { return this.root.__workspace !== 0; }

    get cellItemParams() : any{
        var r = {
            items: null,
            type: null,
            linked:this.isReference
        }
        if( this.designElement.type == "Webpart"){
            // look for controls
            r.type = "control";
            r.items = this.designElement.items;
        }
        else if(this.designElement.type == "Control"){
            // look for webparts
            r.type = "webpart";
            if(this.designElement.isReference )
                r.items = null;
            else
                r.items = this.root.items;
        }
        return r;
    }

    private refreshSubscription: Subscription;
    private selectSubscription: Subscription;
    private isAlwaysCollapsed:boolean = false;
    
    private get studioSubjects(): StudioSubjects { return this._studioService.subjects; }

     constructor(
        private _labelService: LabelService,
        private _portalService: PortalService,
        private _studioService: StudioStateService,
        private _cd: ChangeDetectorRef,
        private _alertservice: AlertService,
        private _metadataService: MetadataService,
        public _dialog: MatDialog
    ) {
        this.refreshSubscription = this.studioSubjects.refresh.subscribe(
            (el: any) => {
                if( el == this.designElement ){ 
                    this.designChanged();
                }
        });
    }

    getDropZoneOptions(cell: any ) : DropDataInfo {
        let dropTypes:string;
        if (this.designElement.type == "Control")
            dropTypes = "webpart-element,webpart-ref";
        else if (this.designElement.type == "Webpart")
            dropTypes = "control-toolbox,control-ref,control-field";
        return <DropDataInfo>{
            disabled: this.readonly,
            types: dropTypes,
            id: cell.id,
            drop: (d: DragInfo) => {                
                let cellId = d.dropZoneElement.parentElement.id;
                let cell: LayoutCell = this.layoutManager ? this.layoutManager.findLayoutCellById(cellId) : null;  
                this.addElementToCell(cell, d);
            }
        }
    }

    ngOnChanges(ch : SimpleChanges): void {
        if( ch.__element){
            this.designElement = ch.__element.currentValue;
            this.initDesignComponent();
        }
    }

    ngOnDestroy(): void {
        if (this.refreshSubscription)
            this.refreshSubscription.unsubscribe();

        if (this.selectSubscription)
            this.selectSubscription.unsubscribe();        
    }

    activateElement(e: Event, selected: IElement): void {
        if (selected) 
            if (this.mode != DesignModes.Controls) 
                return;                    
        this._studioService.activateElement(selected);
        if( e )
            e.stopPropagation();
    }

    reloadReference(): void {
        this.getReferenceWebpartFromServer();
    }

    toggleCollapse(): void {

        if( this.isAlwaysCollapsed)
            this.designElement.__collapsed = true;
        else
            this.designElement.__collapsed = !this.designElement.__collapsed;
        this.ping();
    }

    selectCell($event: Event, cell : LayoutCell) {        
        if (this.mode == DesignModes.Design) {
            this._studioService.selectCell(cell);
            if( $event )
                $event.stopPropagation();
        }            
    }

    private addElementToCell(cell:LayoutCell,  di:DragInfo): void {
        let dndItem : any = di.dragModel;

        if (!cell)
            return;

        if (this.mode != DesignModes.Controls) {
            // Reset active element if not a control mode
            this.activateElement(null, null);
            return;
        }

        // dropping enabled only in control mode
        if (!dndItem) {
            return;
        }

        if (_.isFunction(dndItem.addDragData)) {
            dndItem.addDragData();
            if (!dndItem.type)
                return;
        }

        // dndItem is IElement that is moved
        if (dndItem.id) {
            // find where the item was
            let fromElement = this._studioService.findElementOwner(this._studioService.activeRootElement, dndItem);
            let fromCell = this._studioService.findCellByElement(dndItem);
            if(  di.ctrlPressed ){
                this.copyEl=_.cloneDeep<IElement>(dndItem);              
                let copyName= uuid.id(this.copyEl.typeName + "-");

                this.copyEl.id=uuid.id();
                this.copyEl.name=copyName;       
                if(this.copyEl.properties){
                    this.copyEl.properties.name=copyName;
                }               
            }      
                if (this._studioService.moveElement(dndItem, cell, fromCell,undefined,this.copyEl)) {
                    this._studioService.getRoot().isDirty = true;
                      this.activateElement(null,dndItem);
     
                    // record into the history
                    if(di.ctrlPressed){
                        this.activateElement(null,this.copyEl);
                        this.recordElementToHistory(cell.id,this.copyEl,fromCell.id,RecordCommand.add);
                        this.copyEl=undefined;
                    }    
                    else{
                        this.recordElementToHistory(cell.id,dndItem,fromCell.id,RecordCommand.move);
                    }     
                    // Send refresh request if source is another element
                    if (fromElement != this.designElement)
                        this._studioService.refreshRoot();                
                }        
        }
        else {
            // Dragged control or webpart
            this.addNewControl(dndItem, cell);
        }
        this.setDirty();
        this._studioService.refreshElement(this.designElement);
    }

   private recordElementToHistory(cellId:string,item:any,fromCellId:string,command:number){
        let hr = new ElementHistoryData(HistoryObjectType.Control);
        hr.cellId = cellId;
        hr.element = item;
        hr.fromCellId = fromCellId;
        this._studioService.writeHistory(command, hr);
    }

    sliderValueChanged($event) {
        let newScaleVal: number = $event;

        if( this.mode == DesignModes.Xml){
            this.designElement.__zoomXml = newScaleVal;
        }

        if (newScaleVal !== (this.designElement as IUIDesigner).scaleValue) {
            (this.designElement as IUIDesigner).scaleValue = newScaleVal;
        }
    }

    private addNewControl(dndItem: any, cell: LayoutCell) {
        if (dndItem.type == "Webpart") {
            this.addWebpartControl(dndItem, cell);
        }
        else if (dndItem.type == "JQDataGrid" && dndItem.toolboxImage) {
            this.addDataGridControl(dndItem, cell);     // added by dragging from a control toolbox
        }
        else {
            let control = ControlManager.createNewControl(dndItem.type, dndItem.typeName);
            if (_.isFunction(dndItem.controlInit)) {
                let owner = this._studioService.findCellOwner(cell);
                dndItem.controlInit(control, owner, this._portalService);
            }
            this.addControlToCell(control, cell);
        }
    }

    private addWebpartControl(dndItem: any, cell: LayoutCell) {
        if (!dndItem.isStatic) {
            // virtual web part
            let titleLabel = this._labelService.getLabel('PS_WEBPART_TITLE', 'Add Webpart');
            let messageLabel = this._labelService.getLabel('PS_WEBPART_ADD', "Do you want to add the web part as a local copy or a reference to its content?");
            let copyLabel = this._labelService.getLabel('PS_COPY', 'Copy ');
            let referenceLabel = this._labelService.getLabel('PS_ACTION_REFERENCE', 'Reference');
            let confirmContext = {
                title: titleLabel,
                message: messageLabel,
                actions: [
                    { action: 'copy', name: copyLabel, isPrimary: false },
                    { action: 'reference', name: referenceLabel, isPrimary: false }]
            };

            let dialog = this._dialog.open(ConfirmDialogComponent, { disableClose: true });

            dialog.componentInstance.initContext(confirmContext);
            dialog.afterClosed().subscribe(res => {
                if (res == "cancel") {
                    return;
                }
                let isReference = (res == "reference");
                let ws = this._metadataService.getWorkspaceCodeById(this._studioService.getRoot().__workspace);
                this._portalService.getWebpartElement(dndItem.name, dndItem.type, ws)
                    .subscribe(wp => {
                        let webpart = <IUIDesigner>wp;
                        webpart.properties.name = webpart.name;
                        webpart.isReference = isReference;
                        if(this._studioService.addElement(webpart, cell)) {
                            this._studioService.refreshElement(this.designElement);
                        }
                    });
            });
        }
        else {
            let shortControlTypeName = _.last(dndItem.fullTypeName.split('.')) as string;
            let nonVirtWP = <IElement>{
                name: dndItem.name, type: 'Webpart', isStatic: true,
                isLocked: false, hasPublishContent: false, isInDeveloping: false, isReference: false, owner: null,
                properties: {
                    __type: shortControlTypeName,
                    name: dndItem.name,
                    isReference: false,
                    typeName: dndItem.fullTypeName
                },
                items: null,
                _fullTypeName: dndItem.fullTypeName,
                typeName: shortControlTypeName
            };
            if (this._studioService.addElement(nonVirtWP, cell)) {
                this.designChanged();
                this.setDirty();
            }
        }
    }

    private addDataGridControl(dndItem: any, cell: LayoutCell) {
        let titleLabel = this._labelService.getLabel('PS_GRID_ADD_TITLE', 'Grid Control Creation');
        let messageLabel = this._labelService.getLabel('PS_GRID_ADD_MSG', 'How do you want to create the DataGrid control?');
        let wizardLabel = this._labelService.getLabel('PS_GRID_WIZARD', 'Grid Wizard');
        let defaultLabel = this._labelService.getLabel('PS_GRID_ADD_DEFAULT', 'Create a Default Grid');
        let newGrid = ControlManager.createNewControl(dndItem.type, dndItem.typeName);
        let confirmContext = {
            title: titleLabel,
            message: messageLabel,
            actions: [
                { action: 'wizard', name: wizardLabel, isPrimary: false },
                { action: 'default', name: defaultLabel, isPrimary: false }]
        };

        let dialog = this._dialog.open(ConfirmDialogComponent, { disableClose: true });

        dialog.componentInstance.initContext(confirmContext);
        dialog.afterClosed().subscribe(res => {
            if (res == "wizard") {
                let dialog = this._dialog.open(GridWizardComponent, { width: '930px', height: '500px', disableClose: true });
                dialog.componentInstance.initializeNewGrid(newGrid, cell);
                dialog.afterClosed().subscribe(grid => {
                    this.addControlToCell(grid, cell);
                });
            }
            else
                this.addControlToCell(newGrid, cell);
        });
    }

    getItemControl(item:any) : any{
        const r = item.value as ICellItemRef;
        let items = this.designElement.items;
        if( this.designElement.type == "Control"){
            if( this.designElement.isReference ){
                const ownerWp = this._studioService.findElementOwner(this.root, this.designElement);
                if( ownerWp )
                    items = ownerWp.items; 
            }
            else{
                items = this.root.items;                
            }
        }

        const c = items && _.find(items, (it)=> r.name == it.name);
        if( !c ){
            // console.error("not found", r);
        }
        if( this.designElement.isReference)
            c.isReference = true;
        return c;
    }

    private addControlToCell(control: IElement, cell: LayoutCell): void {
        if (control) {
            let isSuccess = this._studioService.addElement(control, cell);
            if (isSuccess) {
                let hr = new ElementHistoryData(HistoryObjectType.Control);
                hr.cellId = cell.id;
                hr.element = control;
                this._studioService.writeHistory(RecordCommand.add, hr);
                this.activateElement(null,control);
                this._studioService.refreshControl(control);
            }
        }
    }

    isControlSelected(ct: IElement) {
        let sel = false;
        if( this._studioService.activeElement && this._studioService.activeElement.id == ct.id )
            sel = true;

        return sel;
    }

    isCellSelected(cell: ICell) {
        if( this.mode == DesignModes.Design)
            return cell.isSelected === true;

        return false;
    }

    get showEditorArea() : boolean {
        return this.mode != DesignModes.Xml && 
            this.mode != DesignModes.PersonalizationReport && 
            this.layoutManager && !this.isCollapsed;
    }

    private initDesignComponent(): void {
        let el : IElement = this.designElement;
        if (el.type == "Webpart" && this.isReference) {
            //reference webpart
            this.setupReferenceWebpart();
        }
        else if(el.type == "Webpart" && this._metadataService.nonVirtualWebparts.indexOf(el.name)!=-1){
            this.designElement = el;
            this.designElement.isReference = false;
            this.designElement.layout = null;
            this.layoutManager = null;
            this.nonVirtualWp = true;
            this.isAlwaysCollapsed = true;
        }
        else {
            // embedded webpart or combined control 
            this.setDesignElement(this.designElement);
        }
        if (this.isTabbed) {
            this.controlMgr = new ControlManager(this.designElement);
            this.setActiveTab(this.designElement['__activeTab'] || (this.controlMgr.tabs.length && this.controlMgr.tabs[0].name));
        }
    
        if (this.designElement && !this.designElement.isReference) {
            if (this.designElement.type) {
                if (this.designElement.type == 'Page')
                    this.elementIconClass = 'fa fa-list-alt';
                else if (this.designElement.type == 'Webpart')
                    this.elementIconClass = !this.nonVirtualWp ? 'fa fa-puzzle-piece' : 'fa fa-thumb-tack';
                else
                    this.elementIconClass = 'hidden';
            }
            if( this.layoutManager )
                this.layoutManager.reset();
            this.designChanged();
        }
    }

    private setActiveTab(tabname: string): void {
        if (this.controlMgr.setActiveTab(tabname)) {
            let tabElement = this.controlMgr.getActiveTabElement();
            if (tabElement)
                this.layoutManager = new LayoutManager(tabElement, this._studioService.getRoot().history);
            this.ping();
        }
    }

    private ping(){
        this._cd.detectChanges();
    }

    getWsColor(controlElement: IElement, cell: LayoutCell) : string {
        let cl = "";
        if( controlElement && controlElement.map ){
            _.forEach(controlElement.map, (wsValue:any, propName:string) => {
                _.forEach(wsValue, (wsVal, wsCode)=>{
                    const ws = this._metadataService.workspaces.find(w=> w.Code == wsCode);
                    if( ws ){
                        cl += (" workspace-" + ws.ID);
                    }
                });
            }); 
        }

        if (this.root.__workspace != 0) {
            if(controlElement && !controlElement.isStatic){
            }

            if (cell && cell.isStatic === false) {
                cl += (" workspace-" + this.root.__workspace);
            }
        }
        return cl;
    }

    private designChanged(): void {
        if( this.layoutManager )
            this.layoutManager.refresh();

        if (this.designElement.type == "Control") {

            if (this.isTabbed) {
                let actTabName = this.controlMgr.activeTab ? this.controlMgr.activeTab.name : '';
                let tabToSelect;
                if (actTabName) {
                    tabToSelect = this.controlMgr.tabs.find(t => t.name == actTabName);
                    if (!tabToSelect && this.controlMgr.tabs.length > 0)
                        tabToSelect = this.controlMgr.tabs[0];
                }

                if (tabToSelect)
                    this.setActiveTab(tabToSelect);
            }
        }
        if(this.root.__selectedElement)
            this._studioService.updatePropertyPanel(this.root.__selectedElement);
        this.ping();
    }

    private setupReferenceWebpart(): void {
        //check if reference web part already exist in studioState
        let existingWebpart = this._studioService.getRefWebpartsFromState(this.designElement.name);

        if (existingWebpart){            
            existingWebpart.isReference = true;
            this.setDesignElement(existingWebpart, true);
        }        
        else
            this.getReferenceWebpartFromServer();
    }

    private addWebpartToItems(webpart : IUIDesigner) : IUIDesigner{
        // replace it in items
        let wpr = _.find( this.root.items, it=> it.name == webpart.name);
        if( wpr ){
            this.root.items[wpr.id] = _.cloneDeep(webpart);
            wpr = this.root.items[wpr.id];
            wpr.isReference = true;
        } 
        return wpr;
    }

    private getReferenceWebpartFromServer(): void {
        //get webpart from server
        this._portalService.getWebpartElement(this.designElement.name, this.designElement.type, this._metadataService.getWorkspaceCodeById(this._studioService.getRoot().__workspace))
            .subscribe(wp => {
                let webpart = <IUIDesigner>wp;
                if (webpart.name && webpart.typeName) {
                    this._studioService.storeRefWebpart(webpart);
                    this.addWebpartToItems(webpart);
                    this.setDesignElement(webpart, true);
                }
                else {
                    // error of loading webpart
                    this._alertservice.error(<IResultStatus>(<any>webpart));
                }
                this.ping();
            });
    }

    private setDesignElement(element: IElement, isReference: boolean = false) {
        this.designElement = element;
        this.designElement.isReference = isReference;
        this.layoutManager = this.layoutManager || new LayoutManager(element, this.root.history);
        this.isTabbed = element.typeName == 'JQTabContainer';
        this.isAlwaysCollapsed = this.designElement.type == "Control" && this.designElement.isReference;

        if( typeof this.designElement.__collapsed == "undefined" )
            this.designElement.__collapsed = this.isAlwaysCollapsed;
               
        this.collapsible = !this.isTabbed; // && !this.isReference;

        let actCell = this._studioService.activeElement;
        if (actCell && actCell.type == 'cell') {
            let ow = this._studioService.findCellOwner(actCell);
            if (ow && ow.id == this.designElement.id) {
                // the cell was selected before refresh. It must be re-selected after layout creation
                let lc = this.layoutManager.findLayoutCell(actCell);
                if (lc)
                    this.selectCell(null, lc);
            }
        }
    }
    private setDirty() {
        this._studioService.setDirty();
    }
}
