import { IOMTypeDescriptor, IOMMetadata, IOMTypePosition, OMType, FieldTypeCode } from './viewModel';
import { PortalService } from '../services/portal.service';
import { IElement, StudioSubjects, TopMenuState } from '../objects/common';

import _ from 'lodash';
import { StudioStateService } from '../services/studioStateService';

export class FieldDirectoryBase {

    public search: string = '';
    public pdsearch: string = '';
    public parametricPanel: boolean;
    public errorMessage: string;
    public subjects: StudioSubjects;
    // service list
    public list: OMTypeDescriptor[] = [];

    // parametric data list
    public pdlist: OMTypeDescriptor[] = [];

    // relative or absolute field expression mode has global meaning
    public static RelativeMode: boolean = true;

    public get relativeMode(): boolean {
        return FieldDirectoryBase.RelativeMode;
    }
    public set relativeMode(r: boolean) {
        FieldDirectoryBase.RelativeMode = r;
    }

    get readonly(): boolean { return false; }
    protected portal: PortalService;
    protected studioState: StudioStateService;

    constructor(_portal: PortalService, _studioService: StudioStateService) {
        this.portal = _portal;
        this.studioState = _studioService;
    }

    public toggle(it: OMTypeDescriptor) {
        it._expanded = !it._expanded;

        if (it._expanded) {
            if (!it.ChildItems) 
                this.loadChildren(it);            
            else
                this.openCloseChildren(it, true);
        }
        else {
            this.openCloseChildren(it, false);
        }
    }
    public select(it: OMTypeDescriptor) {
        if (!it._selected) {
            _.forEach(this.list, (item) => {
                if (item._selected === true) {
                    item._selected = false;
                    return false;
                }
            });
            it._selected = true;
            const menu = new TopMenuState();
            menu.generate = true;
            menu.data = it;
            this.studioState.refreshTopMenu(menu);
        }
    }

    public togglePanels(onParametric: boolean) {
        if (onParametric) {
            // click on parametric - open if not opened
            if (!this.parametricPanel) {
                this.parametricPanel = true;
                this.loadParamtericData();
            }
        }
        else {
            // click on services - open if not opened
            if (this.parametricPanel) {
                this.parametricPanel = false;
            }
        }
    }

    public openCloseChildren(owner: OMTypeDescriptor, open: boolean) {

        let itemIndex = this.list.indexOf(owner);
        for (var i = itemIndex+1; i < this.list.length; i++) {
            let l = this.list[i];

            if (l.Owner && l.Owner == owner)
                l._hidden = !open;

            if (l.ChildItems && l.ChildItems.length) {
                this.openCloseChildren(l, open);
            }
        }
    }


    public loadChildren(typeDescriptor: OMTypeDescriptor) {

        let itemIndex = _.findIndex(this.parametricPanel ? this.pdlist : this.list, typeDescriptor) + 1;

        this.portal.getFieldsDirectory(typeDescriptor).subscribe(
            (om) => {
                if (om && om.ChildItems) {
                    om.ChildItems.forEach(ds => {
                        let c: OMTypeDescriptor = new OMTypeDescriptor(ds, typeDescriptor);
                        c.Owner = typeDescriptor;
                        if (!this.parametricPanel)
                            this.list.splice(itemIndex++, 0, c);
                        else
                            this.pdlist.splice(itemIndex++, 0, c);
                    });
                    typeDescriptor.ChildItems = om.ChildItems;
                }
            }
        );
    }

    public loadParamtericData(refresh?: boolean) {
        let pddIgnore = ['DataPointDetails', 'DataPointSummary', 'ParametricData', 'DPCollection'];

        if (refresh === true)
            this.pdlist = [];

        if (this.pdlist.length == 0) {
            this.portal.getFieldsDirectory(<IOMTypeDescriptor>{
                ItemType: OMType.Root, TypeName: 'ParametricData'}).subscribe(
                service => {
                    if (service && service.ChildItems) {
                        service.ChildItems.forEach(srv => {
                            if( pddIgnore.indexOf(srv.Name) == -1){
                                let it = new OMTypeDescriptor();
                                it.Name = srv.Name;
                                it.TypeName = srv.TypeName;
                                it.IsExpand = true;
                                it.setPadding();
                                this.pdlist.push(it);
                            }
                        });
                    }
                },
                error => this.errorMessage = <any>error);
        }
    }

    public loadServices(refresh?: boolean) {
        if (refresh === true)
            this.list = [];

        if (this.list.length == 0) {
            this.portal.getFieldsDirectory(<IOMTypeDescriptor>{
                ItemType: OMType.Root, TypeName: 'Service'}).subscribe(
                service => {
                    if (service && service.ChildItems) {
                        service.ChildItems.forEach(srv => {
                            let it = new OMTypeDescriptor();
                            it.Name = srv.Name;
                            it.TypeName = srv.TypeName;
                            it.IsExpand = true;
                            it.setPadding();
                            this.list.push(it);
                        });
                    }
                    this.servicesLoaded();
                },
                error => this.errorMessage = <any>error);
        }
    }

    servicesLoaded() {
    }

}

export class OMTypeDescriptor implements IOMTypeDescriptor {
    ChildItems: IOMTypeDescriptor[];
    FieldExpression: string;
    Name: string;
    TypeName: string;
    ItemType: OMType;
    IsExpand: boolean;
    Link: string;
    IsBrowseOnly: boolean;
    Metadata: IOMMetadata;
    Position: IOMTypePosition;
    Uid: string;
    Owner: IOMTypeDescriptor;
    _selected: boolean;
    // Nesting level
    _nesting: number;
    // image class
    _imageClass: string;
    //padding for indentation
    _padding: string;
    _expanded: boolean = false;
    _hidden: boolean = false;
    _draggable: boolean = false;

    // dragData
    type: string;
    typeName: string;

    constructor(ds?: IOMTypeDescriptor, owner?: OMTypeDescriptor) {
        if (ds) {
            this.IsBrowseOnly = ds.IsBrowseOnly;
            this.Metadata = ds.Metadata;
            this.TypeName = ds.TypeName;
            this.ItemType = ds.ItemType;
            this.Name = ds.Name;
            this.Uid = ds.Uid;
            this.Link = ds.Link;
            this.IsExpand = ds.IsExpand;
        }
        else {
            this.ItemType = OMType.Service;
        }

        this._draggable = this.ItemType == OMType.Field || this.ItemType == OMType.Property || this.ItemType == OMType.Link;

        if (!this.Metadata) {
            this.Metadata = <IOMMetadata>{ Description: '', FieldTypeCode: FieldTypeCode.Object };
        }

        this._imageClass = OMType[this.ItemType];
        if (this.Metadata) {
            if (this.Metadata.IsReadOnly)
                this._imageClass += ' readonly';

            if (this.Metadata.IsInherited)
                this._imageClass += ' inherited';

            if (this.Metadata.IsList)
                this._imageClass += ' list';

            if (this.Metadata.IsRequired)
                this._imageClass += ' required';
        }
        if (owner) {
            this.Owner = owner;
            this._nesting = owner._nesting + 1;
        }
        else
            this._nesting = 0;

        this.setPadding();

    }

    public setPadding() {
        let padding = this._nesting * 20 + 2;
        if (!this.IsExpand) {
            // the arrow is hidden for scalar. should compensate by padding
            padding += 18;
        }
        this._padding = padding.toString() + 'px';
    }

    public addDragData(): void {
        this.type = '';

        let typeMapping = {
            NamedObjectRef: 'NamedObject',
            ContainerRef: 'ContainerListGrid',
            RevisionedObjectRef: 'RevisionedObject',
            SubentityRef: 'NamedSubentity',
            NamedSubentityRef: 'NamedSubentity',
            String: 'TextBox',
            Boolean: 'CheckBox',
            Int32: 'TextBox',
            EncryptedField: 'TextBox',
            DateTime: 'DateChooser'
        };

        this.type = typeMapping[this.TypeName];
        if (!this.type)
            this.type = "TextBox";

        if (this.Metadata.IsEnum)
            this.type = 'DropDownList';

        if (this.Metadata.IsList)
            this.type = 'JQDataGrid';

        if (this.type != 'JQDataGrid' && this.type != 'ContainerListGrid')
            this.typeName = "Camstar.WebPortal.FormsFramework.WebControls." + this.type;
        else
            this.typeName = "Camstar.WebPortal.FormsFramework.WebGridControls." + this.type;
    }

    public getFieldExpression(): string {
        let owner: IOMTypeDescriptor = this.Owner;
        let res: string = this.Name;
        let type: string = '';

        while (owner) {
            if (owner.ItemType == OMType.Service) {
                res = (!FieldDirectoryBase.RelativeMode ? owner.Name : '') + '.' + res;
                break;
            }
            if (owner.ItemType == OMType.Type) {
                type = ':' + owner.Name;
            }
            else {
                res = owner.Name + type + '.' + res;
                type = '';
            }
            owner = owner.Owner;
        }

        return res;
    }

    public controlInit(ctl: IElement, owner: IElement, portal: PortalService, ) {

        if (!ctl.properties)
            ctl.properties = {};

        let fExp = this.getFieldExpression();

        // Take full field expression
        let oldRel = FieldDirectoryBase.RelativeMode;
        FieldDirectoryBase.RelativeMode = false;
        const fullExp = this.getFieldExpression();
        FieldDirectoryBase.RelativeMode = oldRel;
        const dataSubMode = fullExp.indexOf("ObjectChanges.") != -1 ? 2 : 0;

        if (ctl.typeName == 'JQDataGrid') {

            ctl.properties = {
                gridDataMode: 1 /*ItemList*/, 
                dataSubmissionMode:  dataSubMode,
                settings: {
                    __type: "GridDataSettingsItemList",
                    allowDataNaigation: true,
                    fieldExpressions: fExp,
                    columns: [],
                    editorSettings: {
                        __type: "JQGridEditorSettings",
                        editingMode: 0     // Inline
                    }
                }
            };

            portal.resolveGridColumns(fullExp).subscribe(
                (columns) => {
                    if (columns) {
                        ctl.properties.settings.columns = columns;
                    }
                }
            );

        }
        else {
            ctl.properties.fieldExpressions = fExp;
        }
        if (this.Metadata.IsRequired)
            ctl.properties.required = true;

        if (owner) {
            let n: string;
            // generate new name
            for (let index: number = 0; ; index++) {

                let suffix = "_" + this.Name + (index ? index.toString() : '');
                if (this.Owner) 
                    n = this.Owner.Name + suffix;                
                else 
                    n = owner.name + suffix;

                if (owner && owner.items && _.find(owner.items, (v,i) => v['name'] === n))
                    continue;
                else
                    break;
            }
            ctl.name = n;
        }
    }

}