import { Component, Input, Output, EventEmitter } from '@angular/core';
import { StudioSubjects } from '../../../common/objects/common';
import { SetDataValueItem  } from '../../../common/objects/viewModel';

@Component({
    selector: 'csi-condition',
    styles:[`

    :host {
        display: flex;
        flex-direction: column;
        flex: 1 1;
    }

    .csi-condition {
        display: flex;
        flex-direction: column;
        flex: 1 1 auto;
    }
    .csi-condition .control-row {
        display: flex;
        justify-content: flex-start;
    }
    
    .csi-condition .control-group {
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: center;
        height: 100%;
        padding: 5px 10px;
    }

    .csi-condition .control-group label {
        margin: 0px;
        font-weight: normal;
    }
    
    .csi-condition .control-group span {
        align-self: flex-start;
    }    

    .csi-condition select {
        min-width: 140px;
    }
  
    `],
    template: `<csi-panel [component]="'csi-property-list'" [subjects]="subjects" [dataSource]="'propertyCondition'"></csi-panel>`
})

export class ConditionComponent {
    @Input() valueItem: SetDataValueItem;
    @Output() onValueChanged = new EventEmitter<any>();

    subjects: StudioSubjects;

    constructor() {
        this.subjects = new StudioSubjects();
        this.subjects.panel.subscribe(p => {
            if (!this.valueItem)
                this.valueItem = new SetDataValueItem();
            this.subjects.propertyPanel.next(this.valueItem);
        });

        this.subjects.property.subscribe(n => {
            this.subjects.propertyPanel.next(this.valueItem);
            this.onValueChanged.emit(this.valueItem);
        });
    }
}

