import { Component } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { LocalService } from '../common/services/local.service';
import { LabelService } from '../common/services/label.service';
import { HistoryLog } from '../common/objects/historyLog';
import { ActionManager } from './actionManager';
import { PortalService } from '../common/services/portal.service';
import { StudioStateService } from '../common/services/studioStateService';
import { MetadataService } from '../common/services/metadata.service';
import { PortalSettingsService } from '../common/services/portalSettings.service';

@Component({
    selector: 'csi-action-menu',
    templateUrl: 'action-menu.component.html'
})

export class ActionMenuComponent {
    
    get history(): HistoryLog {
        if (this._studioStateService.activeRootElement)
            return this._studioStateService.activeRootElement['history'];
        return undefined;
    }
    
    private _actionManager: ActionManager;
    get actionManager(): ActionManager { return this._actionManager; }
    constructor(
        private _dialog: MatDialog,
        private _labelService: LabelService,
        private _localService: LocalService,
        private _studioStateService: StudioStateService,
        private _metadataService : MetadataService,
        private _portalService: PortalService,
        private _portalSettingsService: PortalSettingsService    
        ) {

        this._studioStateService.subjects.menu.subscribe(m=>{
            this._actionManager.updateMenu(m);
        });

        this._actionManager = new ActionManager(this._dialog, this._labelService, 
            this._localService, this._studioStateService, this._portalService, this._metadataService, this._portalSettingsService);
    }  
}
