import { Component, OnInit, OnDestroy } from '@angular/core';
import { PortalService } from '../../common/services/portal.service';
import { FieldDirectoryBase, OMTypeDescriptor } from '../../common/objects/fieldDirectoryBase';
import { IControl, StudioSubjects } from '../../common/objects/common';
import { PanelSettings, IContentPanel } from '../panel';
import { StudioStateService } from '../../common/services/studioStateService';
import { Subscription } from 'rxjs';

@Component({
    selector: 'csi-field-directory',
    styleUrls: ['./fieldDirectory.component.less'],
    templateUrl: 'fieldDirectory.component.html'
})

export class FieldDirectoryComponent extends FieldDirectoryBase implements IContentPanel, OnInit, OnDestroy{

    settings: PanelSettings;
    controls: IControl[] = [];
    errorMessage: any;
    designerDropList : string[];
    private dropAreasSubs : Subscription;

    constructor(portal: PortalService, private _studioService: StudioStateService ) {
        super(portal, _studioService);
        this.dropAreasSubs = this._studioService.subjects.dropAreas.subscribe(d=> {
            this.designerDropList = d.filter(dp=> dp.startsWith("drop_ctl_"));
        });
    }

    ngOnInit() {
        this.loadServices();
    }

    ngOnDestroy(){
        if( this.dropAreasSubs)
            this.dropAreasSubs.unsubscribe();
    }


    init(s: PanelSettings, subjects: StudioSubjects) {
        s.actions = [];
        s.showSearch = false;
        s.showActions = false;
        s.iconSrc = "fa-tasks";
        this.settings = s;
        this.subjects = subjects;
        return this.settings;
    }

    dragStart(dragData: OMTypeDescriptor) {

        dragData.addDragData();
        // find the control with same type. Can be used as dragging helper
        //let ctl = _.find(this.controls, c => c.type === dragData.type);
    }
}

