import { Component } from '@angular/core';

@Component({
    selector: 'batch-progress-popup',
    template: `
        <label style="display: block;">{{getTitle()}}</label>
        <div>
            <mat-progress-bar mode="indeterminate"></mat-progress-bar>
        </div>        
    `,
})
export class BatchProgressPopup {
    public title: string;

    getTitle(): string {
        return this.title;
    }
}