import {
    Component,
    EventEmitter,
    OnInit,
    Output
} from '@angular/core';

import { IElementRef, ExecuteCommand } from '../common/objects/common'
import { LocalService } from '../common/services/local.service';
import { StudioStateService } from '../common/services/studioStateService';

@Component({
    selector: 'csi-start-page',
    templateUrl: 'startPage.component.html',
    styleUrls: ['startPage.component.less']
})

export class StartPageComponent implements OnInit {
    @Output() onLoadElement = new EventEmitter<IElementRef>();

    elementHistory: IElementRef[];

    constructor(private _localService: LocalService, private _studioState: StudioStateService) {
    }

    ngOnInit(): void {
        this.elementHistory = this._localService.getElementHistory();
    }

    loadElement(element: IElementRef): void {
        this.onLoadElement.emit(element);
    }

    cmdExecute(command: string) {
        this._studioState.subjects.execute.next(new ExecuteCommand(command, null, null));
    }

    whatsNewLink() {
        window.alert('whatsNewLink : To be done!');
    }

    helpLink() {
        let host = window.location.origin;
        let path = window.location.pathname.split('/');
        let url = host+'/'+path[1]+'/OnlineHelpOutput/PortalStudio2_Help/PortalStudio2_Help.htm';
        window.open(url,"","resizable=yes");
    }
    cdnLink() {
        window.alert('cdnLink: To be done!');
    }
}