import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'csi-alert-modal',
    template: 
    `<div class="csi-modal">
        <header [csi-closable]="'close'" (onClose)="closeSelectElement($event)">
            <span class="title">{{title}}</span>
        </header>
        <main><div class="alert">{{messageText}}</div></main>
        <footer>
            <div class="btns">
                <span *ngIf="cancelBtnCaption" class="default-btn" (click)="execute('cancel')">{{cancelBtnCaption}}</span>
                <span class="primary-btn" (click)="execute('primary')">{{primaryBtnCaption}}</span>
            </div>
        </footer>
    `
})

export class AlertModalComponent {

    public messageText: string;
    public messageClass: string;
    public title: string;
    public cancelBtnCaption: string;
    public primaryBtnCaption: string;

    constructor(private dialogRef: MatDialogRef<AlertModalComponent>) {
    }

    execute(answer: string): void {
        this.dialogRef.close(answer);
    }

    closeSelectElement($ev : Event){

    }
}
