import { Component, OnInit } from '@angular/core';
import { IUIComponentRef, StudioSubjects, IResultStatus } from '../../common/objects/common';
import { PanelAction, IContentPanel, PanelSettings, PanelUpdatedData } from '../panel';
import { WCFService } from '../../common/services/wcf.service';
import { PortalService } from '../../common/services/portal.service';
import { Observable } from 'rxjs';

@Component({
    selector: 'csi-portal-object-panel',
    styleUrls: ['./portalObjectPanel.component.less'],
    template: `
    <div class="csi-object-panel {{ viewMode }}">
        <div *ngIf="viewMode == 'list'">
            <div class="csi-selectable-item" [title]="el.name" *ngFor="let el of (elements|elementFilter:nameFilter)" 
                    (dblclick)="selectOpen(el)" (click)="select(el)" [class.selected-item]="isSelected(el)">
                <div class="csi-noselect">
                    <span><i class="fa {{icon}}"></i>{{el.name}}</span>                
                </div>
            </div>
        </div>
        <div class="portalObj-container" *ngIf="viewMode == 'tiles'">
            <ul>
                <li class="portalObj-tile" *ngFor='let el of (elements | elementFilter:nameFilter)' 
                    (dblclick)="selectOpen(el)" (click)="select(el)" [attr.title]="el.name" [class.selected-item]="isSelected(el)">
                    <i class="fa fa-2x {{icon}}"></i>
                    <div class="tile-text" >{{el.name}}</div>                    
                </li>
            </ul>
        </div>
        
        <div class="portalObj-container" *ngIf="viewMode == 'detail'">
            <div class="col-captions">
                <div class="col-img"> </div>
                <div class="col-name">{{'Name' | label: 'PS_NAMELBL'}}</div>
                <div class="col-description">{{'Description' | label: 'PS_DESCRIPTIONLBL'}}</div>
            </div>
            <div class="detail-items">
                <div *ngFor="let el of (elements|elementFilter:nameFilter)" [title]="el.name" class="csi-selectable-item portalObj-detail-item" 
                        (dblclick)="selectOpen(el)" (click)="select(el)" [class.selected-item]="isSelected(el)">
                    <div class="col-img"><i class="fa {{icon}}"></i></div>
                    <div class="col-name">{{el.name}}</div>
                    <div class="col-description">{{el.description}}</div>
                </div>
            </div>
        </div>
    </div>`
})

export class PortalObjectPanelComponent implements IContentPanel, OnInit {

    settings: PanelSettings;

    elements: IUIComponentRef[] = [];

    errorMessage: any;
    nameFilter: string;
    selectedItem: {name: string, type: string};

    dataSources = {
        'menu': { svc: 'PortalMenuDefinitionMaint', icon: 'fa-list-alt' },
        'tab': { svc: 'PortalTabMaint', icon: 'fa-folder-o' },
        'action': { svc: 'ActionMaint', icon: "fa-cog" },
        'actionCategory': { svc: 'ActionCategoryMaint', icon: "fa-share-alt" },
        'actionRule': { svc: 'ActionRuleMaint', icon: "fa-exchange" },
        'pageMap': { svc: '*pageMap', icon: "fa-exchange" },
        'targetMatrix': { svc: '*targetMatrix', icon: "fa-exchange" }
    };

    icon: string;
    svc: string;
    private subjects: StudioSubjects;

    constructor(private _wcfService: WCFService, private _portalService: PortalService) {
    }

    get viewMode(): string {
        return this.settings.viewMode || "list";
    }

    init(s: PanelSettings, rootSubjects: StudioSubjects) {
        this.subjects = rootSubjects;
        let ds = this.dataSources[s.dataSource];
        if (ds) {
            this.svc = ds.svc;
            this.icon = ds.icon;
        }
        else {
            console.error('data source ' + s.dataSource + ' is not found');
        }

        s.actions = [
            <PanelAction>{ name: 'New', imgClass: 'fa-plus-circle', svc: this.svc },
            <PanelAction>{ name: 'List mode', imgClass: 'fa-list', modeClass: 'list' },
            <PanelAction>{ name: 'Tiles mode', imgClass: 'fa-th', modeClass: 'tiles' },
            <PanelAction>{ name: 'Details mode', imgClass: 'fa-list-ul', modeClass: 'detail' }
        ];
        s.showSearch = true;
        s.iconSrc = this.icon;
        s.refreshContent.subscribe(p => this.load(true));
        this.settings = s;

        return this.settings;
    }

    ngOnInit() {
        this.load();
    }
    
    load(forceReload: boolean = false): void {
        let observable: Observable<IUIComponentRef[] | IResultStatus>;
        if (this.svc[0] != '*') {
            observable = this._wcfService.getElementRefs(this.svc, this.settings.dataSource, forceReload);
        }
        else if (this.settings.dataSource == "pageMap")
            observable = this._portalService.getPageMaps();
        else if (this.settings.dataSource == "targetMatrix")
            observable = this._portalService.getTargetMatrices();

        if (observable)
            observable.subscribe(
                (elements : IUIComponentRef[]) => {
                    this.elements = elements;
                    this.settings.searchInput.subscribe(searchtext => this.nameFilter = searchtext);
                },
                error => this.errorMessage = <any>error);
    }

    selectOpen(el: IUIComponentRef) {
        let cmd = new PanelUpdatedData();
        cmd.elementRef = el;
        cmd.open = true;
        cmd.action = new PanelAction();
        cmd.action.svc = this.svc;
        this.subjects.panel.next(cmd);
    }

    isSelected(el: IUIComponentRef){
        return this.selectedItem && (el.name == this.selectedItem.name && el.type == this.selectedItem.type);
    }

    select(el: IUIComponentRef){
        //this.selectedItem = {name : el.name, type: el.type};
        //this.subjects.propertyPanel.next(el);
    }
}
