import { Subject } from 'rxjs';
import { IUpdatedData, UpdateDataTypes, IUIComponentRef, StudioSubjects } from '../common/objects/common';


//interfaces
export interface IContentPanel{
    init(settings: PanelSettings, subjects?: StudioSubjects): void;
    destroy?(subjects?: StudioSubjects): void;
}

export interface IPanelNameInput {
    label: string;
    val: string;
    readonly: boolean;
    onValChange?: Subject<string>;
    hidden: boolean;
}

//classes
export class PanelAction {
    name: string;
    imgClass: string;
    svc: string;
    component: string;
    modeClass: string;
}

export class PanelSettings {
    name: string;
    iconSrc: string;
    description: string;
    collapsed: boolean;
    showActions: boolean;
    showSearch: boolean;
    showNewAction: boolean;
    showName: boolean;    
    hideBorder: boolean;
    showRefresh: boolean;
    nameInput: IPanelNameInput;
    content: string;
    actions: PanelAction[];
    dataSource: string;
    readOnly: ReadOnlyLevel;
    viewMode: string;
    visible: boolean;    
    parameters: any;
    searchText: string;

    searchInput: Subject<string>;
    actionClick: Subject<string>;
    refreshContent: Subject<string>;

    constructor(name: string,
                iconSrc: string = "",
                collapsed: boolean = false,
                showActions: boolean = true,
                showSearch: boolean = true,
                showNewAction: boolean = true) {

        this.name = name;
        this.iconSrc = iconSrc;
        this.collapsed = collapsed;            
        this.showActions = showActions;        
        this.showSearch = showSearch;
        this.showNewAction = showNewAction;
        this.showRefresh = true;
        this.showName = false;
        this.visible = true;

        this.searchInput = new Subject<string>();
        this.actionClick = new Subject<string>();
        this.refreshContent = new Subject<string>();

        this.nameInput = <IPanelNameInput>{
            hidden: true,
            onValChange : new Subject<string>()
        };

        this.actions = [];
    }
}
export class PanelUpdatedData implements IUpdatedData {
    __dataType: UpdateDataTypes = UpdateDataTypes.panel;

    constructor() {
    }

    elementRef: IUIComponentRef;
    elementRefs: IUIComponentRef[];
    open: boolean;
    selectCmd: boolean;
    createNew: boolean;
    changeMode: boolean;
    dataRequest: any;
    action: PanelAction;
    contentCreated: IContentPanel;
}

//enums
export enum ReadOnlyLevel { value, full };