    public RevisionedObjectRef_accessor = new PropertyAccessor(
        (propertyContext: any, property: IPropertyMetadata) => {
            let v = <RevisionedObjectRef>val(propertyContext.properties, property.name);
            if (v) {
                let ro = new RevisionedObjectRef(v.name, v.revision, v.revisionOfRecord);
                ro.cdoTypeName = v.cdoTypeName;
                return ro;
            }
            return v;
        },
        (v: any, propertyContext: any, properties: IPropertyMetadata[], vt: string, property: IPropertyMetadata) => {
            val(propertyContext.properties, property.name, v);
        }
    );

    public GridColumns = new PropertyAccessor(undefined,
        (v: any, propertyContext: any, properties: IPropertyMetadata[], vt: string, property: IPropertyMetadata) => {
            let pname = property.name.toLowerCase();
            let fexKey = _.findKey(propertyContext, (pv, k: string) => k.toLowerCase() == pname);
            let oldFex = fexKey ? propertyContext[fexKey] : null;
            let requestSelVal = propertyContext.__type == "GridDataSettingsSelVal";

            propertyContext[fexKey] = v;
            property.value = v;

            let columnsProperty = _.find(properties, p => p.name.toLowerCase() == 'columns');

            // The dialog is called only if field expression is changed 
            let openDialog: boolean;
            if (v)
                openDialog = (v != oldFex);           
            else 
                openDialog = false;

            if (openDialog) {
                let fieldExp: string = v;
                if (fieldExp[0] == '.') {
                    // add service 
                    let root: IElement = this._studioStateService.activeRootElement;
                    let servKey = _.findKey(root.properties, (p, k) => k.toLowerCase() == 'primaryservicetype');
                    if (servKey)
                        fieldExp = root.properties[servKey] + fieldExp;
                    else {
                        this._alertService.warningModal(<ILabel>{ Name: 'PS_SERVICE_UNDEFINED', DefaultValue: 'Service name is not defined. Columns are not generated.' });
                        return;
                    }
                }
                let mdlLabel: ILabel = <ILabel>{ Name: 'PS_GENERATE_COLUMNS', arguments: [fieldExp], DefaultValue: 'Generate columns for {0} ?' };
                this._alertService.confirmModal(mdlLabel).subscribe( ok => {
                    if (ok) {
                        this._portalService.resolveGridColumns(fieldExp, requestSelVal).subscribe(
                            columns => {
                                if (_.isArray(columns)) {
                                    if (columnsProperty)
                                        columnsProperty.value = columns;
                                    propertyContext.columns = columns;
                                }
                                else {
                                    // error
                                    propertyContext[property.name] = property.value = null;
                                    this._alertService.errorModal(columns);
                                }
                            }
                        );
                    }
                });                
            }
        });

    public query_GridColumnsSetup = new PropertyAccessor(undefined,
        (v: any, propertyContext: any, properties: IPropertyMetadata[], vt: string, property: IPropertyMetadata) => {
            let pname = property.name.toLowerCase();
            let oldValue  = _.find(propertyContext, (pv, k: string) => k.toLowerCase() == pname);

            if( !oldValue && (v === undefined || v==null))
                return;

            propertyContext[property.name] = v;
            property.value = v;

            let columnsProperty = _.find(properties, p => p.name.toLowerCase() == 'columns');
            // replace parameters                        
            let query = v;
            if( pname=="querytext")
                _.forEach(propertyContext.queryParameters, c=> {query = query.replace('?'+c.name, `'${c.__tempValue}'`); delete c.__tempValue; });

            if (v !== oldValue) {
                let mdlLabel: ILabel = <ILabel>{ Name: 'PS_GENERATE_COLUMNS', arguments: [v], DefaultValue: 'Generate columns for {0} ?' };
                this._alertService.confirmModal(mdlLabel).subscribe( ok => {
                    if (ok) {
                        let params = _.map(propertyContext.queryParameters, (px:any) => <QueryParameter>{Name: px.name, Value : px.__tempValue });

                        let obs = pname=="querytext" ? 
                            this._portalService.getAdHocQueryColumns(query, <QueryOptions>{RowSetSize: 1}) :
                            this._portalService.getQueryColumns(query, <QueryOptions>{RowSetSize: 1}, params);

                        obs.subscribe(
                            columns => {                                
                                if (_.isArray(columns)) {
                                    if (columnsProperty)
                                        columnsProperty.value = columns;
                                    propertyContext.columns = columns;
                                }
                                else {
                                    // error
                                    propertyContext[property.name] = property.value = null;
                                    this._alertService.errorModal(columns);
                                }
                            }
                        );
                    }
                });                
            }
        });
            
