    public JQGridCustomScript = new PropertyAccessor(
        (propertyContext: any) => {
            let initVal: JQGridCustomScript[] = propertyContext.customScriptHandlers;
            // Processed only if we click in the modal collection editor
            //delete propertyContext['__selectedInModalCollection'];
            let resVal: JQGridCustomScript[];
            initVal = initVal || [];
            let isColumn = !(<string>propertyContext.__type).startsWith('GridDataSettings');
            let eventList = isColumn ?
                ['click', 'change', 'onAddCellAttributes'] :
                ['renderCompleted', 'rowDeleted', 'editingStarted'];

            resVal = eventList.map<JQGridCustomScript>(e =>
                initVal.find(iv => iv.Name === e || iv['name'] === e) || 
                <JQGridCustomScript>{ Name: e, Handler: '', __type: 'JQGridCustomScript' }
            );
            return resVal;
        },
        (v: any, propertyContext: any, properties: IPropertyMetadata[], vt: string, property: IPropertyMetadata) => {
            // cleanup empty handlers
            propertyContext.customScriptHandlers = _.filter<JQGridCustomScript>(v, e => !_.isEmpty(e['Handler']) || !_.isEmpty(e['handler']) );
        }
    );

    public SelectionDataSourceMap_SourceType = new PropertyAccessor(undefined,
        (v: any, propertyContext: any, properties: IPropertyMetadata[]) => {
            if (propertyContext.sourceType != v) {
                propertyContext.sourceType = v;
            }

            let mp = this.mapProperties(["StaticSource", "SetData", "Query", "QueryType", "QueryExecutionMode", "Parameters"], properties);
            _.forEach(mp, v => v.hidden = true);

            switch (v) {
                case 0: // StaticSource
                    mp['StaticSource'].hidden = false;
                    mp['QueryExecutionMode'].value = null;
                    delete propertyContext.queryExecutionMode;
                    break;
                case 1: // SetData
                    mp['SetData'].hidden = false;
                    mp['QueryExecutionMode'].value = null;
                    delete propertyContext.queryExecutionMode;
                    break;
                case 2: // Query
                    mp['Query'].hidden = false;
                    mp['QueryType'].hidden = false;
                    mp['QueryExecutionMode'].hidden = false;
                    mp['Parameters'].hidden = false;
                    break;
            }
        });

    public SelectionDataSourceMap_QueryExecutionMode = new PropertyAccessor(undefined,
        (v: any, propertyContext: any, properties: IPropertyMetadata[]) => {

            let changed = false;
            if (propertyContext.queryExecutionMode!= v) {
                propertyContext.queryExecutionMode = v;
                changed = true;
                propertyContext.query = '';
            }

            let mp = this.mapProperties(["Query", "QueryType"], properties);
            _.forEach(mp, v => v.hidden = true);

            switch (v) {
                case 0: // adhoc
                    mp['Query'].hidden = false;
                    break;
                case 1: // preDefined
                    mp['QueryType'].hidden = false;
                    mp['Query'].hidden = false;
                    break;
            }
            if (changed) {
                mp['Query'].value = '';
            }
        });

