import { Component, Input, OnChanges, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef, SimpleChanges} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import _ from 'lodash';

import { PortalService } from '../../common/services/portal.service';
import { LabelService } from '../../common/services/label.service';
import {
    IElement, IUIDesigner, val, LayoutActions, IDesignControl, DesignModes, ICellItemRef, IResultStatus } from '../../common/objects/common';
import { LayoutManager } from './objects/layoutManager';
import { LayoutCell, ICell } from './objects/layoutCell';
import { ElementHistoryData, RecordCommand, HistoryObjectType } from '../../common/objects/historyLog';
import { ConfirmDialogComponent } from '../../shared/dialog/confirm.dialog.component';
import { Subscription, Observable, of, concat } from 'rxjs';
import { map } from "rxjs/operators";
import { StudioStateService } from '../../common/services/studioStateService';
import { MetadataService } from '../../common/services/metadata.service';
import { DragInfo } from '../../shared/drag';
import { DragService } from '../../shared/drag.service';
import { DropDataInfo } from '../../shared/directives/droppable.directive';

@Component({
  selector: 'csi-design-root',
  templateUrl: './design-root.component.html',
  styleUrls: ['./design.component.less','./design-root.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DesignRootComponent implements OnChanges, OnDestroy, IDesignControl{
    @Input("root") __root : IUIDesigner; 

    root: IUIDesigner;
    element: IUIDesigner;

    DesignModesEnum = DesignModes;

    webPartItems: IElement[];
    oldMode: any;
    get layoutManager(): LayoutManager {return this.root.__layoutManager; }
    set layoutManager(lm: LayoutManager) {this.root.__layoutManager = lm; }
    elementIconClass: string;
    isNew: boolean = false;
    displayCellPosition() : boolean {return this._studioService.displayCellPosition;}
    xmlText : Observable<string>;
    get layoutActions() : Observable<LayoutActions[]> {return this._studioService.subjects.layoutMenuUpdate;}

    get headerTitle() {return this.root.properties.title || this.root.properties.name; };

    get mode(): DesignModes { return this.root ? this.root.__designMode : DesignModes.Controls; }
    set mode(m: DesignModes) { this.root.__designMode = m; }

    get readonly() { return this.root.__workspace !== 0; }

    private refreshRootSubscription: Subscription;
    
    private customWorkspaceColor: string;

    private get xmlChanged() {return this.root.__xmlChanged === true;};
    private set xmlChanged(x: boolean) { this.root.__xmlChanged = x;};

    constructor(
        private _labelService: LabelService,
        private _portalService: PortalService,
        private _studioService: StudioStateService,
        private _cd: ChangeDetectorRef,
        private _metadataService: MetadataService,
        public _dialog: MatDialog,
        private _drag : DragService
    ) {
        this.refreshRootSubscription = this._studioService.subjects.refreshRoot
            .subscribe(() => {
                this.designChanged();
            });
    }

    getDropZoneOptions(cell: any) : DropDataInfo {
        return <DropDataInfo>{
            types: "webpart-element,webpart-ref",
            id: cell.id,
            disabled: this.readonly,
            drop: (d: DragInfo) => {
                let cellId = d.dropZoneElement.parentElement.id;
                let cell: LayoutCell = this.layoutManager.findLayoutCellById(cellId);         
                this.addElementToCell(cell, d);
            }
        };
    }

    ngOnChanges(ch : SimpleChanges): void {
        if( ch.__root){
            this.customWorkspaceColor = this._studioService.getCustomWorkspaceColor();
            this._drag.clear(["control-ref","webpart-element"], ["control-toolbox","control-ref","control-field","webpart-element","webpart-ref"]);
            this.loadRefWebparts(ch.__root.currentValue);
        }
    }

    ngOnDestroy(): void {
        if (this.refreshRootSubscription)
            this.refreshRootSubscription.unsubscribe();
    }

    private loadRefWebparts(croot : IUIDesigner) {
        let con : Observable<IUIDesigner|IResultStatus>;
        let count=0; 

        if( croot.__designMode != DesignModes.Xml){

            this.getRefWebparts(croot).forEach(w=>{
                let ps = this._portalService.getWebpartElement(w, "Webpart", "");
                if(!con)
                    con = ps;
                else
                    con = concat(con, this._portalService.getWebpartElement(w, "Webpart", ""));
                count++;
            });

        }

        if( con ){
            con.subscribe(wp => {
                let webpart = <IUIDesigner>wp;
                if (webpart.name && webpart.typeName) {
                    this._studioService.storeRefWebpart(webpart);
                    this.addWebpartToItems(croot, webpart);
                }
                else {
                    // error of loading webpart
                    //this._alertservice.error(<IResultStatus>(<any>webpart));
                }

                count--;
                if( count == 0 ){
                    this.root = croot;
                    this.initDesign();
                    this.designChanged();
                }
            });
        }
        else{
            this.root = croot;
            this.initDesign();
            this.designChanged();            
        }
    }

    private addWebpartToItems(croot: IUIDesigner,  webpart : IElement) : IElement{
        // replace it in items
        let wpr = _.find( croot.items, it=> it.name == webpart.name);
        if( wpr ){
            croot.items[wpr.id] = _.cloneDeep(webpart);
            wpr = croot.items[wpr.id];
            wpr.isReference = true;
            wpr.__loaded = true;
        } 
        return wpr;
    }

    private getRefWebparts(d:IUIDesigner) : string[]{
        let wps :string[] = [];
        if( d.items ){
            wps = _.filter(d.items, (i)=> i.isReference).map( i=> i.name);
        }
        return wps;
    }

    getItemControl(item:any) : any{
        const r = item.value as ICellItemRef;
        const  items = this.root.items;

        const c = items && _.find(items, (it)=> r.name == it.name);
        if( !c )
            console.error("not found", r);
        return c;
    }

    modeChanged(mode:DesignModes) {               
        if(this._studioService.elementInDesign && this._studioService.elementInDesign.__layoutManager)
            this._studioService.elementInDesign.__layoutManager.clearSelection();
            
        this._studioService.elementInDesign = undefined;

        if(mode == DesignModes.Design){
            this.updateLayoutMenu();
        }

        if(this.mode != DesignModes.Xml){
            if( this.xmlChanged ) {
                this.applyXmlChanges().subscribe(isSuccess=>{
                    this.xmlChanged = !isSuccess;
                    this.root.__xml = null;
                });
                return;
            }
            this.root.__xml = null;
        }        

        this._studioService.activateElement(this.root);
        this._studioService.refreshRoot(mode);
    }

    activateElement(selected: IElement, s:string = "un"): void {
        if (selected) 
            if (this.mode != DesignModes.Controls) 
                return;            
        
        this._studioService.activateElement(selected);
        if( selected.type == "Webpart" || selected.type == "Page"){
            this.designChanged();
        }
    }

    selectCell($event: Event, cell : LayoutCell) {
        if (this.mode == DesignModes.Design) {
            this._studioService.selectCell(cell);
            if( $event )
                $event.stopPropagation();
        }            
    }

    public applyXmlChanges() : Observable<boolean> {
        let rootElement = this._studioService.getRoot();

        return this._portalService.applyXml(rootElement.name, rootElement.type, rootElement.__xml)
            .pipe(map( element => {
                if (element) {
                    this._studioService.replaceElement(rootElement, element);
                    element.isDirty = true;
                    element.isRoot = true;
                    element.__workspace = 0; // DEV
                    element.__xml = null;
                    this._studioService.refreshRoot();
                    this._studioService.refreshTopMenu();
                    return true;
                }
            },
            (error : any) => {
                let errorMessage = this._labelService.getLabel('PS_ERROR_IMPORTING_MESSAGE', "Error occurred on importing new item");
                console.error(error, errorMessage);
                //this.displayError(errorMessage);
                return false;
            }));
    }

    private updateLayoutMenu (cmds: LayoutActions[] = null){
        this._studioService.updateLayoutMenu(cmds || this.layoutManager.selection.getEnabledCommands().cmdEnabled);
    }

    private addElementToCell(cell:LayoutCell,  di:DragInfo): void {

        let dndItem : any = di.dragModel;

        if (!cell)
            return;

        if (this.mode != DesignModes.Controls) {
            // Reset active element if not a control mode
            this.activateElement(null);
            return;
        }

        // dropping enabled only in control mode
        if (!dndItem) {
            return;
        }

        if (_.isFunction(dndItem.addDragData)) {
            dndItem.addDragData();
            if (!dndItem.type)
                return;
        }
        if (dndItem.id) {
            // find where the item was
            let fromElement = this._studioService.findElementOwner(this._studioService.activeRootElement, dndItem);
            let fromCell = this._studioService.findCellByElement(dndItem);
            if (this._studioService.moveElement(dndItem, cell)) {
                this._studioService.getRoot().isDirty = true;

                this.activateElement(dndItem);

                // record into the history
                let hr = new ElementHistoryData(HistoryObjectType.Control);
                hr.cellId = cell.id;
                hr.element = dndItem;
                hr.fromCellId = fromCell.id;
                this._studioService.writeHistory(RecordCommand.move, hr);

                // Send refresh request if source is another element
                if (fromElement != this.root)
                    this._studioService.refreshRoot();
            }
            this.designChanged();
        }
        else {
            this.addWebpartControl(dndItem, cell);
        }
        this._studioService.setDirty();
    }

    sliderValueChanged(newScaleVal:number) {
        if( this.mode == DesignModes.Xml){
            this.root.__zoomXml = newScaleVal;
        }

        if (newScaleVal !== this.root.scaleValue) {
            this.root.scaleValue = newScaleVal;
        }
    }

     addWebpartControl(dndItem: any, cell: LayoutCell) {
        if (!dndItem.isStatic) {
            // virtual web part
            let titleLabel = this._labelService.getLabel('PS_WEBPART_TITLE', 'Add Webpart');
            let messageLabel = this._labelService.getLabel('PS_WEBPART_ADD', "Do you want to add the web part as a local copy or a reference to its content?");
            let copyLabel = this._labelService.getLabel('PS_COPY', 'Copy ');
            let referenceLabel = this._labelService.getLabel('PS_ACTION_REFERENCE', 'Reference');
            let confirmContext = {
                title: titleLabel,
                message: messageLabel,
                actions: [
                    { action: 'copy', name: copyLabel, isPrimary: false },
                    { action: 'reference', name: referenceLabel, isPrimary: false }]
            };

            let dialog = this._dialog.open(ConfirmDialogComponent, { disableClose: true });
            dialog.componentInstance.initContext(confirmContext);
            dialog.afterClosed().subscribe(res => {
                if (res == "cancel") {
                    return;
                }
                let isReference = (res == "reference");
                let ws = this._metadataService.getWorkspaceCodeById(this._studioService.getRoot().__workspace);
                this._portalService.getWebpartElement(dndItem.name, dndItem.type, ws)
                    .subscribe(wp => {
                        let webpart = <IUIDesigner>wp;
                        webpart.properties.name = webpart.name;
                        webpart.isReference = isReference;
                        if( !webpart.isReference){
                            // Remove some properties
                            delete webpart.properties.componentName;
                            delete webpart["devContentState"];
                            delete webpart["workspaceIndexes"];
                            delete webpart["isNew"];
                            delete webpart["isDirty"];
                            delete webpart["componentRef"];
                            delete webpart["isViewable"];
                            delete webpart["isTestable"];
                        }
                        if (this._studioService.addElement(webpart, cell)) {
                            let hr = new ElementHistoryData(HistoryObjectType.Webpart);
                            hr.cellId = cell.id;
                            hr.element = webpart;
                            this._studioService.writeHistory(RecordCommand.add, hr);
                            this._studioService.setDirty();
                            this._studioService.refreshRoot();
                        }
                    });
            });
        }
        else {
            let shortControlTypeName = _.last(dndItem.fullTypeName.split('.')) as string;
            let nonVirtWP = <IElement>{
                name: dndItem.name, type: 'Webpart', isStatic: true,
                isLocked: false, hasPublishContent: false, isInDeveloping: false, isReference: false, owner: null,
                properties: {
                    __type: shortControlTypeName,
                    name: dndItem.name,
                    isReference: false,
                    typeName: dndItem.fullTypeName
                },
                items: null,
                _fullTypeName: dndItem.fullTypeName,
                typeName: shortControlTypeName
            };
            if (this._studioService.addElement(nonVirtWP, cell)) {
                this._studioService.setDirty();
                this._studioService.refreshRoot();
            }
        }
    }

    xmlTextChanged(txt:string) {
        if(this._studioService.getRoot().isDirty === true){
            this.root.__xml = txt;
        }
        else{
            if(this.root.__xml != txt ){
                this._studioService.setDirty();
                this.root.__xml = txt;
                this.xmlChanged = true;
            }
        }
    }

    isCellSelected(cell: ICell) {
        if( this.mode == DesignModes.Design)
            return cell.isSelected === true;

        return false;
    }

    getProperty(control: IElement, name: string){
        if( control.properties )
            return val(control.properties, name);
        return null;
    }

    get showEditorArea() : boolean {
        return this.mode != DesignModes.Xml && 
            this.mode != DesignModes.PersonalizationReport && this.mode != DesignModes.WhereUsed && !!this.layoutManager;
    }

    private initDesign(): void {
        if (this.root.type == 'Page')
            this.elementIconClass = 'fa fa-list-alt';
        else if (this.root.type == 'Webpart')
            this.elementIconClass = 'fa fa-puzzle-piece';
        else
            this.elementIconClass = 'hidden';

        this.mode = this.root.__designMode;
        this.isNew = this.root.isNew === true;        

        if( !this.layoutManager )
            this.layoutManager = new LayoutManager(this.root, this._studioService.getRoot().history);

        this.layoutManager.clearSelection();
        this._cd.detectChanges();
    }

    getWsColor(controlElement: IElement, cell: LayoutCell) {
        if (this.customWorkspaceColor) {
            if (controlElement.map) {
                let boxStyle = 'inset 0 0 5px ';
                _.forEach(controlElement.map, v => {
                    boxStyle += this.customWorkspaceColor;
                });
                return boxStyle;
            }

            if (cell && cell.isStatic === false) {
                return 'inset 0 0 5px ' + this.customWorkspaceColor;
            }
        }
        return null;
    }

    private designChanged(): void {
        this.layoutManager.refresh();

        if( this.root.__designMode == DesignModes.Xml){
            this.reloadXml();
        }
        
        if(this.root.__selectedElement)
            this._studioService.updatePropertyPanel(this.root.__selectedElement);

        this._cd.markForCheck();
    }

    private reloadXml(){
        if(this.root.__xml)
            this.xmlText = of<string>(this.root.__xml);
        else
            this.xmlText = this._portalService.elementToXml(this.root, this._metadataService.getWorkspaceCodeById(this.root.__workspace));
    }

}

