export class uuid {
    static id(pref: string = ""): string {
        if( pref ){
            let v = _.random(0, 0xFFFF).toString(16);
            return pref + "0000".substr(0, 4 - v.length) + v;
        }
        else{
            let v1 = _.random(0, 0xFFFFFFFF).toString(16);
            let v2 = _.random(0, 0xFFFFFFFF).toString(16);
            return  "00000000".substr(0, 8 - v1.length) + v1 + 
                    "00000000".substr(0, 8 - v2.length) + v2;
        }
    }
}

