export class UpdatedInfo {
    timestamp: number;

    constructor(public data: IUpdatedData = new AnyUpdatedData(), public originator?: ChangeOriginators) {
        this.timestamp = new Date().valueOf();
    }

    touch(): void {
        this.timestamp = new Date().valueOf();
    }

    is(t: UpdateDataTypes) {
        if (this.data)
            return t === this.data.__dataType;

        return false;
    }

    DataOf<T extends IUpdatedData>(): T {
        return <T>this.data;
    }
}

