export class TypeHandlerParameter {
    Name: string;
    Type: string;
    Value: string;
}

export class TypeHandler {
    protected __type = "TypeHandler";
    public Name: string;
    public Parameters: TypeHandlerParameter[];

    public getMethodSignature(): string {
        let prms = this.Parameters.map(prm => prm.Name + ':' + TypeHandler.getShortType(prm.Type));
        return `${this.Name}(${prms.join()})`;
    }

    public static getShortType = (fullType: string) => {
        if (fullType) {
            let typeName = fullType.split(',', 1)[0];
            if (typeName)
                return _.last(typeName.split('.'));
        }
        return '';
    }

    public comparer(i: ISelectableItem): boolean {
        if (typeof i.value == "object") {
            let h = <TypeHandler>i.value;
            if (h.Name == this.Name && h.Parameters.length == this.Parameters.length)
                return true;
        }
        return false;
    }

}

export class RevisionedObjectRef {
    protected __type = "RevisionedObjectRef";
    public name: string;
    public revisionOfRecord: boolean;
    public revision: string;
    public cdoTypeName: string;

    constructor(name: string, revision: string, ror: boolean) {
        this.name = name;
        this.revision = revision;
        this.revisionOfRecord = ror;
    }

    public comparer(i: ISelectableItem): boolean {
        if (typeof i.value == "object") {
            let h = <RevisionedObjectRef>i.value;
            if (h.cdoTypeName === this.cdoTypeName) {
                if (h.name === this.name) {
                    if (this.revision || h.revision)
                        return this.revision === h.revision;
                    return h.revisionOfRecord === this.revisionOfRecord;
                }
            }
        }
        return false;
    }
}

