export class LayoutBuilder {

    public static getLayout(rows: number, cols: number): any {

        let layout = {
            __type: 'GridLayout',
            columnNumber: cols,
            rowNumber: rows,
            tableCells: LayoutBuilder.getTableCells(rows, cols)
        };
        return layout;
    }

    public static getTableCells(rows, cols): any[] {
        let tableCells = [];

        for (let r = 0; r < rows; r++) {
            tableCells.push([]);

            for (let c = 0; c < cols; c++) {
                tableCells[r].push(LayoutBuilder.newCell(r,c));
            }
        }

        return tableCells;
    }

    public static newCell(r:number, c:number, hidden: boolean = false){
        return <ICell>{
            col: c, row: r,
            colSpan: 1, rowSpan: 1,
            isEmpty: true,
            id: uuid.id('cell'),
            isHidden: hidden,
            items: {},
            properties: {
                __type: 'GridLayoutCell', 
                column: c, 
                row: r, 
                isStatic: false,
                staticName :  r + '_' + c,
                style: { __type: 'Style' } 
            },
            name: 'cell_' + r + '_' + c,
            type: 'cell', typeName: 'GridLayoutCell'
        }
    }

    public static getDefaultTab(ctl: IElement): any {

        return {
            __arrayType: 'TabDefinition[]',
            __type: 'TabDefinition',
            name: 'TabDefinition0',
            visible: true,
            index: 0,
            layout: LayoutBuilder.getLayout(2, 1)
        };
    }
}

