export function val(o: any, key: string, setValue?: any, oType?: string): any {
    if (o === undefined)
        return undefined;

    let keyLc = key.toLowerCase();
    let fieldKey = Object.keys(o).find(k => k.toLowerCase() == keyLc) || key;
    if (setValue !== undefined) {
        if (oType !== undefined)
            o.__type == oType;

        if (setValue === null)
            delete o[fieldKey];
        else
            o[fieldKey] = setValue;
    }
    return o ? o[fieldKey] : undefined;
}

