export class ContextMenuItem{    
    public checked: boolean;
    public linePos : ContextMenuItemPositions;
    public nameItem : string;
    public key : string;

    constructor(
        public type: ContextMenuItemTypes, 
        public text: string, 
        opt? : any
        ){

        this.key = text && text.toLowerCase();
        
        if(opt){
            if(typeof opt.checked != "undefined")
                this.checked = opt.checked === true;
            if(opt.linePos)
                this.linePos = opt.linePos;
            if(opt.nameItem)
                this.nameItem = opt.nameItem;
            if(opt.key)
                this.key = (<string> opt.key).toLowerCase();
        }
    }
}


export enum ContextMenuItemTypes { caption = "caption", checked = "checked", selected = "selected" }
export enum ContextMenuItemPositions { above="above", below="below" };
