import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';

import { PortalSettingsService, SettingSection } from '../../../common/services/portalSettings.service';
import { PortalService } from '../../../common/services/portal.service';

@Component({
    selector: 'csi-page-wizard',
    styles: [`
        :host{
            min-height: 350px;
            min-width: 500px;
            height: 100%;
            width: 100%;

            display: flex;
            flex-direction: column;            
            flex: 1 1 auto;
        }

        .section-template-selector{
            display: flex;
            flex-direction: row;            
            flex: 1 1 150px;
        }

        .section-page-parameters{
            display: flex;
            flex-direction: column;            
            flex: 0 0 auto;
            padding-left: 10px;
        }

        .section-page-parameters > div {
            padding: 5px;
        }

        form {
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
        }

        .template-type-description {
            min-height: 100px;
            min-width: 300px;
            padding: 10px;
        }

        .radio-group-scrolling{  
            overflow-y: auto;
            flex-basis: 350px;
        }
            
        .radio-group {
            padding: 10px;
            display: inline-flex;
            flex-direction: column;
        }

        .radio-group label {
            flex: 1 1 auto;
        }
            
        .radio-button {
            margin: 5px;
        }            

        span.disabled {
            background-color: #aaaaaa;
            cursor: not-allowed;
        }

        footer {
            border-top: none;
        }

        .section-page-parameters > div #pageNameId{
            width: 208px;
        }

        .section-page-parameters > div select {
            height: 27px;
        }
        
    `],
    templateUrl: 'pageWizard.component.html'
})
export class PageWizardComponent implements OnInit {
    public pageForm: FormGroup;
    public errorMessage: string;
    public templateData: ITemplateDataInfo;

    get dialog(): MatDialogRef<PageWizardComponent> {
        return this._dialog;
    }

    constructor(
        private _fb: FormBuilder,
        private _dialog: MatDialogRef<PageWizardComponent>,
        private _portalService: PortalService,
        private _portalSettings: PortalSettingsService) { }

    ngOnInit(): void {
        this.pageForm = this._fb.group({
            pageName: ['', Validators.required],
            serviceType: '',
            serviceTypes: [],
            template: 'blank',
            eproc: false
        });
        this._portalService.getCamstarServices().subscribe(services => {
            this.pageForm.get('serviceTypes').setValue(services);
        });
        let sections = this._portalSettings.getSections();
        sections.subscribe(sects => {
            let templateSection = { name: "templatesettings" };
            this.templateData = this._portalSettings.getSectionData(<SettingSection>templateSection);
        })
    }

    save(): void {
        if (this.pageForm.valid) {
            let form = this.pageForm.value;
            let template = form.template;
            
            if (template == 'modeling')
                template = this.templateData.ModelingTemplatePage;
            if (template == 'container')
                template = form.eproc ? this.templateData.ContainerWithEprocedureTemplatePage : this.templateData.ContainerTemplatePage;

            let page = {
                name: form.pageName,
                serviceType: form.serviceType,
                template: template,
                isEproc: form.eproc
            }
            this._dialog.close(page);
        }
    }

    cancel(): void {
        this._dialog.close();
    }
}

export interface ITemplateDataInfo {
    ContainerTemplatePage: string;
    ContainerWithEprocedureTemplatePage: string;
    ModelingTemplatePage: string;
    UserTemplate1?: string;
    UserTemplate2?: string;
    UserTemplate3?: string;
}