import { NgModule, Optional, SkipSelf }       from '@angular/core';

import { PanelModule } from '../../panel/panel.module';
import { SharedModule } from '../../shared/shared.module';

import { HierarchyComponent } from './hierarchy.component';
import { RawTextComponent } from './rawText.component';
import { ResubmitRequestComponent } from './resubmitRequest.component';
import { ServiceTraceViewComponent } from './serviceTraceView.component';
import { TraceLogComponent } from './traceLog.component';
import { TraceItemComponent } from './traceItem.component';
import { TraceRawViewComponent } from './traceRawView.component';

@NgModule({
    imports: [
        PanelModule,
        SharedModule,
    ],
    declarations: [
        HierarchyComponent,
        RawTextComponent,
        ResubmitRequestComponent,
        ServiceTraceViewComponent,
        TraceLogComponent,
        TraceItemComponent,        
        TraceRawViewComponent,        
    ],
    exports: [
        HierarchyComponent,
        RawTextComponent,
        ResubmitRequestComponent,        
        ServiceTraceViewComponent, 
        TraceLogComponent,
        TraceRawViewComponent, 
    ],
    entryComponents: [
        RawTextComponent,
        ResubmitRequestComponent,        
        ServiceTraceViewComponent,         
        TraceRawViewComponent, 
    ]
})
export class TraceLogModule {
    constructor( @Optional() @SkipSelf() parentModule: TraceLogModule) {
        if (parentModule) {
            throw new Error(
                'TraceLogModule is already loaded. Import it in the LayoutModule only');
        }
    }
}

