import {Directive, ElementRef,  Input, AfterViewInit, Renderer2, OnDestroy } from '@angular/core';
import { DragService } from '../drag.service';
import {DragInfo, dragModes} from '../drag';

export class DragDataInfo {
    disabled: boolean = false;
    type: string;
    id: string;
    model: any;    
    handleContainer?: HTMLElement;
    hndClass? : string;
    dragAction?: (d: DragInfo) => void;
}

@Directive({
    selector: '[csi-draggable]'
})
export class DraggableDirective implements AfterViewInit, OnDestroy {
    @Input('csi-draggable') dragData: DragDataInfo;

    hnd: HTMLElement;

    constructor(private _el: ElementRef, private _drag: DragService, private _render: Renderer2) {       
    }

    ngAfterViewInit(){
        if( this.dragData.disabled === undefined)
            this.dragData.disabled = false;

        if( !this.dragData.disabled ){
            if(!this.dragData.handleContainer)
                this.dragData.handleContainer = document.body;                            
            let mode = dragModes.dragElement;
            if(this.dragData.type == 'collection-item')
                mode = dragModes.reorder;
            else if(this.dragData.type == 'panel-splitter')
                mode = dragModes.resizePanel;
            
            const dragId = this.dragData.type + "@" + this.dragData.id;
            if(typeof this.dragData.dragAction == "function"){
                this._drag.add(mode, dragId, (d:DragInfo) => this.dragData.dragAction(d),
                    this.dragData.type, this.dragData.model, this.dragData.hndClass);
            }
            else{
                this._drag.add(mode, dragId, (d:DragInfo) => this.dragging(d),
                    this.dragData.type, this.dragData.model, this.dragData.hndClass);
            }

            this._el.nativeElement.setAttribute("dragDropId", dragId);
            this._el.nativeElement.classList.add("drag-element");
        }
    }

    ngOnDestroy(){
        if( !this.dragData.disabled ){
            this._drag.remove(this.dragData.type == 'collection-item' ? dragModes.reorder :  dragModes.dragElement, "");
            this.removeHandle();
        }
    }

    private dragging(d: DragInfo) : void{
        if( !d.endOfDrag ){
            if(! this.hnd ){
                try{
                    this.hnd = this.createHandle();
                }
                catch(e){
                    console.warn("drag handle is not created", d);
                    this.hnd = null;
                }
            }
            if( !this.hnd )
                return;

            let contPos = this.dragData.handleContainer.getBoundingClientRect();
            let offset = {y: -25, x: 5};
            this._render.setStyle(this.hnd, 'top', (d.y + offset.y - contPos.top) + 'px');
            this._render.setStyle(this.hnd, 'left', (d.x + offset.x - contPos.left ) + 'px');
            if(d.ctrlPressed)
                this._render.addClass(this.hnd, "copy");
            else
                this._render.removeClass(this.hnd, "copy");
        }
        else{
            // drops
            this.removeHandle();
        }
    }

    private createHandle() : any{

        let root = this._render.createElement("div");
        this._render.addClass(root, "dragging-handle");

        let imgs = this._el.nativeElement.getElementsByTagName('IMG');
        if( imgs.length ){
            let src = imgs[0].src;
            let img = this._render.createElement('img');            
            this._render.setProperty(img, 'src', src);
            this._render.addClass(img, 'toolbox-image');
            this._render.appendChild(root, img);                    
        }

        let txtImgs = this._el.nativeElement.getElementsByTagName('I');
        if( txtImgs.length){
            let t = (<HTMLElement> txtImgs[0]);
            let txtImg = this._render.createElement('I');
            t.className.split(' ').forEach(c=>this._render.addClass(txtImg, c));            
            this._render.setAttribute(txtImg, "aria-hidden", "true");
            this._render.appendChild(root, txtImg);
        }
        
        let txt = (this.dragData.model && this.dragData.model.name) || this.dragData.id || this.dragData.type;
        this._render.appendChild(root, this._render.createText(txt));
        this._render.appendChild(this.dragData.handleContainer, root);

        return root;
    }       

    private removeHandle(){
        if(this.hnd){
            this._render.removeChild(this.dragData.handleContainer, this.hnd);
            delete this.hnd;
        }
    }    

} 
