    private getPanelType(el : IElement | IUIComponentRef): string {
        let panelType : string;
        let compRef = <IUIComponentRef> el;
        if( compRef.__type === "UIComponentRef" ){
            panelType = compRef.type;
        }
        else{
            let element = <IElement> el;
            let maintType = element.__maint || element.__unbound;
            if(maintType)
                panelType = _.findKey(this._portalElementServiceMap, p => p == maintType);
            else
                panelType = element.type;
        }
        return panelType;
    }

    getSvcService(panelType: string): string {
        return this._portalElementServiceMap[panelType.toLowerCase()];
    }

    private readonly _portalElementServiceMap = {
        "menu": "PortalMenuDefinitionMaint",
        "tab": "PortalTabMaint",
        "action": "ActionMaint",
        "actioncategory": "ActionCategoryMaint",
        "actionrule": "ActionRuleMaint",
        "pagemap": "*pageMap",
        "targetmatrix": "*targetMatrix"
    };
