import { Component, Input, OnChanges } from '@angular/core';
import { Alert } from './alert';

@Component({
    selector: 'csi-alert',
    template: 
    `<div *ngFor="let al of statusAlerts" [class]="al.class" role="alert" title="Click to close" (click)="close(al)">
        <div class="alert-time">{{al.time | date:'mediumTime'}}</div>
        <div>{{al.message}}</div>
     </div>`
})

export class AlertComponent implements OnChanges {
    @Input() alerts: Alert[];

    // Alerts displayed in status area
    statusAlerts: Alert[];

    ngOnChanges(changes) {
        if (changes.alerts) {
            let als = (<Alert[]>changes.alerts.currentValue);
            this.statusAlerts = als.map(sa => sa);
        }
    }

    close(alert): void {
        this.remove(alert);
    }

    remove(alertToRemove: Alert) {
        let i = this.alerts.findIndex(a => a.time == alertToRemove.time);
        if (i != -1) {
            this.alerts.splice(i, 1);
            this.statusAlerts = this.alerts.filter(a => !a.modal);
        }
    }
}