import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { IElement, ValueUpdatedData, PropertyRef, StudioSubjects } from '../../common/objects/common';
import { IPropertyMetadata, IMetadataType, PropertyCategory, PropertyDescriptions } from '../../common/objects/viewModel';
import { IContentPanel, PanelSettings, ReadOnlyLevel } from '../panel';
import { MetadataService } from '../../common/services/metadata.service';
import { LabelService } from '../../common/services/label.service';
import { LocalService } from '../../common/services/local.service';
import { DataAccessors } from '../../common/services/dataAccessors';
import { RecordCommand, PropertyHistoryData } from '../../common/objects/historyLog';

import { ValueProviderService } from '../../common/services/dataValue.service';

import { Subject, Subscription } from 'rxjs';
import _ from 'lodash';
import { StudioStateService } from '../../common/services/studioStateService';

@Component({
    selector: 'csi-property-list',
    templateUrl: 'propertyList.html',
    styleUrls: ['propertyList.less', '../../shared/internalTabs.less'],
    changeDetection: ChangeDetectionStrategy.Default
})

export class PropertyListComponent implements OnInit, IContentPanel {

    visible: boolean;
    isCollapsed: boolean = false;

    selectedTab: ViewTabs = ViewTabs.unset;
    searchFilter: string;
    panel: PanelSettings;
    showInternalTabs: boolean = false;
    get showDescription() : boolean {return this.descriptionSections.length > 0;}
    valueUpdated: Subject<ValueUpdatedData>;
    descriptionName: string;
    descriptionText: string;
    groups: Group[];
    internalTabs: ViewTab[];
    properties: IPropertyMetadata[];
    isRowMode = false;
    isSidePanel = false;
    cdoType = "";
    
    private get propertyDescriptions(): PropertyDescriptions { return this.metadataService.propertyDescriptions ;}
    private propertyPanelSubscription: Subscription;
    private searchInputSubscription: Subscription;
    private nameInputSubscription: Subscription;
    private nameChangedSubscription: Subscription;
    private valueUpdatedSubscription: Subscription;
    private descriptionSubscription: Subscription;
    private nameProperty: IPropertyMetadata;
    private instance: any;
    private customWorkspaceColor: string;
    private favProperties: string[];
    private _propertyType: string;
    private descriptionSections: string[] = [];
    private instanceMetadataType : IMetadataType;
    private onPropertyChangeSubj : Subject<any>;

    constructor(
        public metadataService: MetadataService,
        private _labelService: LabelService,
        private _localService: LocalService,
        private _dataAccessors: DataAccessors,
        private _valueService: ValueProviderService,
        private _studioStateService: StudioStateService,
        private _chRef: ChangeDetectorRef) {

        this.valueUpdated = new Subject<ValueUpdatedData>();        
        this.internalTabs = 
        [
            <ViewTab>{ tab: ViewTabs.favorites, label: this._labelService.getLabel("PS_PROPERTY_TAB_FAVORITES", 'Favorites'), caption: 'Favorites' },
            <ViewTab>{ tab: ViewTabs.sorted, label: '', caption: 'Sorted' },
            <ViewTab>{ tab: ViewTabs.grouped, label: '', caption: 'Grouped' }
        ];        
    }

    ngOnInit() {
        this.valueUpdatedSubscription = this.valueUpdated.subscribe(vc => {
            let property = this.properties.find(p => p.name.toLowerCase() == vc.propertyName.toLowerCase());
            if (property){
                this.setPropertyValue(property, vc.val, vc.valueType);
            }
        });        
    }   

    init(s: PanelSettings, subjects: StudioSubjects): void {

        if(s.parameters && s.parameters.cdoType)
            this.cdoType = s.parameters.cdoType;

        this.isRowMode = s.dataSource === 'propertyCondition' || s.dataSource === 'propertyCondition/initiator';
        if(s.parameters && s.parameters.sidePanel === true )
            this.isSidePanel = true;

        s.actions = [];
        if (!this.isRowMode) {
            s.showSearch = false;
            s.showName = false;
            s.nameInput.hidden = true;

            this.searchInputSubscription = s.searchInput.subscribe(si => {
                this.searchFilter = si.toLowerCase();
                s.searchText = si;                
                this.ping();
            });
            // the event is fired when the name changed by typing in name input of panel
            this.nameInputSubscription = s.nameInput.onValChange.subscribe(nv => this.onNameChanged(nv));
            // the event is fired when the name has been changed in model (for example by undo/redo )
            this.nameChangedSubscription = subjects.nameChanged.subscribe(n => s.nameInput.val = n);
        }
        else {
            s.showSearch = false;
            s.showName = false;
        }

        s.showRefresh = false;
        this.panel = s;

        this.propertyPanelSubscription = subjects.propertyPanel
            .subscribe(data => {

                // Do not refresh if properties are not changed
                if( this.instance )
                    if( this.instance == data)
                        return;

                if( data && data.__type && data.__type == "UIComponentRef" && !this.isSidePanel ){                    
                    return; // ignore
                }

                this.instance = data;
                this.selectedTab = ViewTabs.unset;
                
                this.onInstanceChanged();
            });     
        this.onPropertyChangeSubj = subjects.property;
    }

    destroy(subjects: StudioSubjects) {
        [this.propertyPanelSubscription, this.searchInputSubscription,
        this.nameInputSubscription, this.valueUpdatedSubscription, this.nameChangedSubscription, this.descriptionSubscription]
            .forEach(s => { if (s) s.unsubscribe(); });
    }

    displayDescription(property: PropertyRef = null): void {     
        this.descriptionName = '';
        this.descriptionText = '';        
        if (property) {            
            let name:string = property.displayedName || property.name;
            this.descriptionName = name;
            //find desc text by property name            
            let pLc = property.name.toLowerCase();          
            this.descriptionSections.map(ds => {                     
                let sec = this.propertyDescriptions[ds];
                let key = Object.keys(sec).find(k => k.toLowerCase() == pLc);
                if (key && ds.toLowerCase() == 'generalcontrol') {
                    // populates description if there is no description for control.
                    this.descriptionText = !this.descriptionText.length ? sec[key] : this.descriptionText;
                }                                
                else if (key && ds.toLowerCase() == this._propertyType.toLowerCase()) {
                    this.descriptionText = sec[key];
                }                   
            });            
        }
    }

    private ping(){
        this._chRef.markForCheck();
    }

    private onInstanceChanged() : void {

        let pt: string = null;
        let readonly = false;
        this.customWorkspaceColor = this._studioStateService.getCustomWorkspaceColor();

        if (this.instance) {
            if (this.instance.__unbound && this.isSidePanel) {
                this.visible = false;
                this.properties = [];
                this.groups = [];
                this.descriptionSections = [];
                this.showInternalTabs = false;
                this.setupPanel(this.instance);
                this.ping();
                return;
            }
            else {
                this.visible = true;
                this.showInternalTabs = true;

                if (this.isRowMode)
                    this.showInternalTabs = false;                

                if (this.instance.isReference || this.panel.readOnly === ReadOnlyLevel.full || this.panel.readOnly === ReadOnlyLevel.value) {
                    readonly = true;
                }                
            }

            if (this.instance.typeName)
                pt = this.instance.typeName;
            else if (this.instance.__type)
                pt = this.instance.__type;

            if (!(this.panel.parameters && this.panel.parameters.showDescription === false)) {
                if (this.instance.type === 'Control')
                    this.descriptionSections = ['generalControl'];
                else if (this.instance.type === 'Page' || this.instance.type === 'Webpart')
                    this.descriptionSections = ['pageContent', 'webPartDefinition', 'generalControl'];
                else if (this.instance.type === 'Pageflow' || this.instance.type === 'PageFlowNode' || this.instance.type === 'PageTransition')
                    this.descriptionSections = ['pageflowContent', 'generalControl'];            
                else
                    this.descriptionSections = [];
            }            
            this.displayDescription();
            this._studioStateService.searchPanelText(this.panel, "");

            if (pt) {
                this.favProperties = this._localService.getFavProperties(pt);
                if( pt.startsWith('config.'))
                    this.showInternalTabs = false;

                this._propertyType = pt;
                if (this.internalTabs) {
                    // set initial tab
                    if (this.favProperties && this.favProperties.length > 0 && this.selectedTab == ViewTabs.unset)
                        this.selectedTab = ViewTabs.favorites;
                    else if (this.internalTabs.length > 1 && this.selectedTab == ViewTabs.unset)
                        this.selectedTab = ViewTabs.sorted;
                }

                // build properties                                
                this.instanceMetadataType = this.metadataService.getMetadataType(pt);
                this.properties = this.createProperties(readonly);
               
                this.panel.nameInput.readonly = readonly; 

                this.properties.forEach(p=> this.populateValue(p));
                this.properties.forEach(p=> this.setValueProvider(p) );

                this.groups = this.createGroups(this.properties);
            }
        }
        else {
            this.properties = [];
            this.groups = [];
            this.descriptionSections = [];
            this.showInternalTabs = false;
        }
        this.setupPanel(this.instance);
        this.ping();       
    }

    private createGroups(properties: IPropertyMetadata[]) : Group[]{

        return _.chain(properties)
            .map(x => x.category ? x.category.toLowerCase() : "other")
            .union(["favorites"])
            .uniq()
            .map(cat=> {
                let i = this.metadataService.propertyCategories.findIndex( c=> c.name.toLowerCase()== cat);
                return new Group(this.metadataService.propertyCategories[i], i);
            })
            .sortBy( g=> g.order )
            .value();
    }

    private populateValue(p: IPropertyMetadata ) : void {
        p.favorite = (this.favProperties && this.favProperties.indexOf(p.name.toLowerCase()) != -1);
        let v = this.getPropertyValue(p);
        if (p.accessor) {
            // Call setter to apply dependencies
            this._dataAccessors.callSetter(v, this.instance, this.properties, undefined, p, true);
        }
        p.value = v;
    }

    private createProperties( readonly: boolean) : IPropertyMetadata[] {

        let valueBehavior;
        let nameComponent;
        let properties = this.instance['properties'];
        if(properties){
            valueBehavior = properties.behavior;
            nameComponent = properties.name;
        }

        let props = this.instanceMetadataType.properties.map( mp => {
            let p = _.cloneDeep(mp);
            if (readonly)
                p.readOnly = readonly;

            if (p.editor && p.editor.directive == "modal") 
                p.editor.directive = "text";

            if (!this.isRefType(this.instanceMetadataType)) {
                if (mp.name.toLowerCase() == 'name' ||
                    (mp.displayedName && mp.displayedName.toLowerCase() == 'name') ||
                    (mp.editor && mp.editor.parameters && mp.editor.parameters.find(ep => ep == 'asName'))) {
                    // Save 'name' property for further using
                    this.nameProperty = p;
                    p.hidden = true;
                    if (this.isRowMode)
                        p.hidden = false;
                }
            }

            if(valueBehavior === 4 && p.name == "Display"){
                const n = nameComponent.toLowerCase();
                if(this._displayPagesEnabled.filter(p=> p.toLowerCase() == n).length == 0)
                    p.readOnly = true;
            }

            return p;
        });
        return _.sortBy(props, p => { return (p.order ? p.order : 999).toString() + (p.displayedName || p.name).toLowerCase(); });
    }

    private setValueProvider(p: IPropertyMetadata) : void{
        if( p.editor && p.editor.directive){
            if( p.editor.directive == 'enum'){
                if( !p.editor.provider )
                    p.editor.provider = "enum";                    
                p.editor.directive = 'selector';
            }

            if( p.editor.parameters ){
                let ci = p.editor.parameters.indexOf("#CDOTypeName");
                if( ci != -1){
                    p.editor.parameters[ci] = this.cdoType;
                }
            }

            if(p.editor.directive == 'selector'){
                p.editor.selectableItems = this._valueService.getSelectableItems(p, this.instance);
            }

        }
    }

    private getPropertyValue(p: IPropertyMetadata): any {

        // Create default value
        if (p.defaultValue === undefined) {

            if (p.editor && p.editor.directive == "enum" && p.enumValues) {
                p.defaultValue = p.enumValues[0].value;
            }
            else if (p.originalTypeName.toLowerCase() == "string") {
                // Set empty for string
                p.defaultValue = '';
            }
            else
                p.defaultValue = null;
        }

        if (this.instance) {
            let n = p.name.toLowerCase();

            let wscolor = this.getWorkspaceColor(n);
            if (wscolor)
                p['__wscolor'] = 'inset 0 0 5px ' + wscolor;

            if (this._dataAccessors.hasGetter(p))
                return  this._dataAccessors.callGetter(this.instance, p);            

            let propertyValues = this.getPropertyValues();
            let key = _.findKey(propertyValues, (v, k) => k.toLowerCase() == n);
            if (key && propertyValues[key]!==undefined)
                return propertyValues[key];
        }

        if (p.defaultValue !== undefined) {
            let dv = p.defaultValue;
            if (p.enumValues && _.isString(p.defaultValue)) {
                // convert symbolic name to number
                let enumItem = p.enumValues.find(v => v.name == p.defaultValue);
                if (enumItem)
                    dv = enumItem.value;
                else
                    console.error('enum item ' + p.defaultValue + ' not found', p.enumValues);
            }
            return dv;
        }
        return null;
    }

    private setPropertyValue(p: IPropertyMetadata, v: any, valType: string = 'string') {
        let name = p.name.toLowerCase();
        let touched = false;
        let oldValue: any;

        if (this.instance) {
            var pl = this.getPropertyValues();            
            if (p.accessor) {
                touched = this._dataAccessors.callSetter(v, this.instance, this.properties, valType, p, false);
            }
            else {
                let foundPropKey = _.findKey(pl, (v, k) => k.toLowerCase() == name);
                if (foundPropKey) {
                    oldValue = pl[foundPropKey];
                    pl[foundPropKey] = v;       // Modify existing
                }
                else {
                    pl[p.name] = v;               // Add new value
                }
                touched = true;
            }

            if (touched) {
                p.value = v;

                // find the properties that depends on the current
                let dependentProperties = this.getDependentProperties(p.name);

                // notify dependants
                _.forEach(dependentProperties, pd => {
                    // Reset value and selectableItems
                    pd.value = null;
                    if(pd.editor.directive == "selector" )
                        pd.editor.selectableItems = this._valueService.getSelectableItems(pd, this.instance);
                });

                const prm : [any, any, IPropertyMetadata, any] = [v, oldValue, p, this.instance];
                this._studioStateService.propertyChanged(prm);
                if( this.onInstanceChanged )
                    this.onPropertyChangeSubj.next(prm);
            }
        }
        this.ping();
    }

    getDependentProperties(pn: string): IPropertyMetadata[] {
        pn = pn.toLowerCase();
        return this.properties.filter( p => _.find(p.dependencies, d => d.toLowerCase() == pn)).map(x => x);
    }

    private setupPanel(inst: any) {
        let pp = this.panel;
        if (inst == null || !this.visible) {
            pp.collapsed = true;
            pp.nameInput.hidden = true;
            pp.showName = false;
            pp.showSearch = false;
            return;
        }
        else {
            pp.collapsed = false;
        }
        if (this.isRowMode)
            return;

        // For properties on the top level
        if (inst) {
            pp.showSearch = true;
            if (this.nameProperty) {
                pp.showName = true;
                pp.nameInput.label = inst.typeName || inst.__type;
                pp.nameInput.val = this.getPropertyValue(this.nameProperty);
                pp.nameInput.readonly = this.panel.nameInput.readonly;
                pp.nameInput.hidden = false;

                if (!pp.nameInput.onValChange) {
                    pp.nameInput.onValChange = new Subject<string>();
                    pp.nameInput.onValChange
                        .subscribe(nv => this.onNameChanged(nv));
                }
            }
            else {
                pp.showName = false;
            }
        }
        else {
            pp.showName = false;
            pp.showSearch = false;
        }
    }

    onNameChanged(newName: any) {

        this._studioStateService.isNameValid(newName, this.instance).subscribe(ok => {
            if(ok){
                let oldName = this.nameProperty.value;
                this._studioStateService.renameLayoutRefs(this.instance, oldName, newName);
                this.setPropertyValue(this.nameProperty, newName, null);
                if(this.instance.name)
                    this.instance.name = newName;

                // Register history
                if (this.isElement()) {
                    let oldVal = {}; oldVal[this.nameProperty.name] = oldName;
                    let newVal = {}; newVal[this.nameProperty.name] = newName;
                    let c = new PropertyHistoryData(this.instance, this.nameProperty.name, oldVal, newVal, true);
                    this._studioStateService.writeHistory(RecordCommand.propertyChange, c);
                }
            }
            else{
                // alert error and restore
                this.panel.nameInput.val = this.getPropertyValue(this.nameProperty);
            }
        });
    }

    selectTab(t: ViewTab) {
        this.selectedTab = t.tab;
    }

    setFavorite(pr: IPropertyMetadata) {
        let changed = false;

        if (!this.favProperties)
            this.favProperties = [];

        let name = pr.name.toLowerCase();
        let i = this.favProperties.findIndex(p => p == name);
        if (i != -1) {
            // remove
            this.favProperties.splice(i, 1);
            changed = true;
            pr.favorite = false;
        }
        else {
            if (this.selectedTab != ViewTabs.favorites) {
                // add
                this.favProperties.push(name);
                changed = true;
                pr.favorite = false;
            }
        }

        if (changed) {
            this._localService.setFavProperties(this._propertyType, this.favProperties);
            this.onInstanceChanged();
        }
    }

    private getPropertyValues(): any {
        let element = <IElement>this.instance;
        if (element && element.__unbound)
            return this.instance;
        if (element && element.id !== undefined && element.name !== undefined && element.__type !== 'TabDefinition' && element.__maint === undefined) {
            return this.instance.properties;
        }
        return this.instance;
    }

    private isRefType(t: IMetadataType) {
        if (t._name == "NamedObjectRef" || t._name == "PageMappingParameter") {
            return true;
        }
        if (t.baseTypeNames && t.baseTypeNames.find(v => v == "NamedObjectRef"))
            return true;

        return false;
    }

    private getWorkspaceColor(n: string) {
        if (this.instance.map) {
            if (_.find(this.instance.map, (o, k: string) => k.toLowerCase() == n))
                return this.customWorkspaceColor;
        }

        return undefined;
    }

    private isElement(): boolean {
        return (this.instance && (this.instance._fullTypeName || this.instance.properties));
    }

    private _displayPagesEnabled = ["ModelingVP", "ModelingAuditTrail_VP", "ModelingEsig_vp", "DT_Selection_vp", "DT_References_vp"];
}

export enum ViewTabs { favorites, sorted, grouped, unset };

class ViewTab {
    public tab: ViewTabs;
    public caption: string;
    public label: string;
}

export class Group {
    expanded: boolean = false;
    hidden: boolean = false;

    constructor(public category: PropertyCategory, public order: number) {
        this.hidden = category.name == "favorites";
        this.expanded = category.name == "favorites";
    }
}
