import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';

import { LabelService } from '../../../common/services/label.service';

@Component({
    selector: 'csi-label-search',
    styles: [`
    :host{
        display:flex;
        flex-direction: column;
        flex: 1 1 auto;

        min-height: 550px;
        min-width: 600px;

        height: 100%;
        width: 100%;
    }

    .csi-label-search {
        display:flex;
        flex-direction: column;
        flex: 1 1 auto;
    }
    
    .csi-label-search > main {
        padding: 15px 15px;
    }

    .csi-label-search > main > form {
        display:flex;
        flex-direction: column;
        height: 450px;
    }
    
    .csi-label-search .ls-filter .ls-label {
        min-width: 150px;
    }
    
    .csi-label-search .ls-filter .ls-input {
        display:flex;
        flex-direction: column;
        flex: 1 1 auto;
    }
    .csi-label-search .ls-filter .ls-input > span {
        min-width: 100px;
    }
    
    .csi-label-search .ls-filter .ls-input input {
        flex-grow: 1;
        width: 100%;
    }
    
    .csi-label-search .ls-group {
        display: flex;
        flex: 1 1 auto;
        padding: 3px 0px;
    }
    
    .csi-label-search .ls-labels {
        display:flex;
        flex-direction: column;
        flex: 1 1 auto;
        border: 1px solid black;
        margin: 15px 0px;
        overflow: hidden;
    }

    .ls-labels span {
        white-space: nowrap;
        overflow: hidden;
    }
    
    .ls-label-header {
        display: flex;
        background-color: #DCDCDC;
        font-weight: bold;
        border-bottom: 1px solid black;
        min-height: 17px;
    }

    .csi-label-search .ls-label-results {
        flex-grow: 1;
        overflow: auto;
    }
    
    .csi-label-search .ls-labels .ls-label-results .ls-label {
        display: flex;
        border-bottom: 1px solid black;
    }
    
    .csi-label-search .ls-name-col {
        border-right: 1px solid black;
        width: 180px;
    }
    
    .csi-label-search .ls-default-col {
        border-right: 1px solid black;
        width: 150px;
    }
    
    .csi-label-search .ls-local-col {
        border-right: 1px solid black;
        width: 150px;
    }
    
    .csi-label-search .ls-category-col {
        width: 80px;
    }
    
    .csi-label-search .ls-results .ls-label {
        min-width: 100px;
    }
    
    .csi-label-search .ls-results .ls-group .ls-input {
        flex-grow: 1;
    }
    .csi-label-search .ls-results .ls-group .ls-input input {
        width: 100%;
    }
    
    span.ls-label-alert {
        color: red;
    }
    `],
    templateUrl: 'labelSearch.html'
})

export class LabelSearchComponent {
    context: any;
    categories: any[];
    labels: any[];
    searchForm: FormGroup;
    resultMessage = { isAlert: false, text: '' };

    get dialogRef(): MatDialogRef<LabelSearchComponent> {
        return this._dialogRef;
    }

    constructor(
        private _dialogRef: MatDialogRef<LabelSearchComponent>,
        private _formBuilder: FormBuilder,
        private _labelService: LabelService
    ) { }

    ngOnInit(): void {
        this._labelService.getLabelCategories().subscribe(categories => {
            this.categories = categories;
        });
    }

    initContext(context: any): void {
        this.searchForm = this._formBuilder.group({
            category: '',
            labelSearchName: '',
            labelSearchText: '',
            selectedLabelName: ['', Validators.required],
            selectedDefaultText: '',
            selectedLocalText: ''
        });

        this.context = context;
        let labelName = this.context.value;
        if (labelName != null && labelName.length > 0) {
            this.searchForm.get('labelSearchName').setValue(labelName);
            this.search(context.value);
        }
    }

    search(selectedValue: string = ""): void {
        let category = this.searchForm.get('category').value;
        let searchName = this.searchForm.get('labelSearchName').value;
        let searchText = this.searchForm.get('labelSearchText').value;

        this._labelService.getLabels(category, searchName, searchText)
            .subscribe(labels => {
                this.labels = labels;
                if (category == "" && searchName == "" && searchText == "" && labels.length > 199)
                    this.resultMessage = {
                        isAlert: true,
                        text: "Returned more than 200 rows.  Please use filters to narrow your request."
                    };
                else
                    this.resultMessage = {
                        isAlert: false,
                        text: "Returned: " + labels.length + " rows."
                    };

                if (selectedValue) {
                    let label = labels.find(l => l.name = searchName)
                    if (label)
                        this.selectLabel(label);
                }
            });
    }

    selectLabel(label: any): void {
        this.searchForm.get('selectedLabelName').setValue(label.name);
        this.searchForm.get('selectedDefaultText').setValue(label.defaultValue);
        this.searchForm.get('selectedLocalText').setValue(label.value);
    }

    ok(): void {
        let labelName = this.searchForm.get('selectedLabelName').value;
        this.context.value = labelName;
        this._dialogRef.close(labelName);
    }

    cancel(): void {
        this._dialogRef.close();
    }
}