import { Component, Input, AfterViewInit, ElementRef, OnDestroy, Renderer2 } from '@angular/core';
import { Subject } from 'rxjs';
import { DragInfo, dragModes } from '../shared/drag';
import { DragService } from '../shared/drag.service';
import { uuid } from '../common/objects/common';
import { DragDataInfo } from '../shared/directives/draggable.directive';

@Component({
    selector: 'csi-panel-splitter',
    styleUrls: [`panelSplitter.component.less`],
    template: `
    <div *ngIf="!disabled" class="csi-panel-splitter" 
        [csi-draggable]="dragInfo"
        [class.vertical]="isVertical" [class.horizontal]="!isVertical"></div>`
})

export class PanelSplitterComponent implements AfterViewInit, OnDestroy {
    @Input() direction: string = 'h';
    @Input() panel: HTMLElement;
    @Input() disabled: boolean = false;
    @Input() panelBefore: boolean = false;

    id: string = uuid.id();
    get dragInfo() : DragDataInfo {return <DragDataInfo>{
        type: 'panel-splitter', 
        id: this.id, 
        model: null,
        dragAction : (d)=> {
            this.onDragging(d);
        }
    }};

    private draggingClass = "do-dragging";
    private get isVertical(): boolean { return this.direction == 'v'; }
    private started = false;

    private get sizeStyle(): string { return this.isVertical ? 'width' : 'height'; }

    private splitterBar: HTMLDivElement;

    private resizeEnsure: Subject<number>;

    constructor(private _el: ElementRef, private _drag: DragService, private _render: Renderer2) {
    }

    ngAfterViewInit() {

        if (!this.disabled) {

            this.splitterBar = <HTMLDivElement>(<HTMLDivElement>this._el.nativeElement).firstElementChild;


            this.resizeEnsure = new Subject<number>();
            this.resizeEnsure.subscribe(r => {
                // reached min/max limit  -- set size to the limit                
                this._render.setStyle(this.panel, this.sizeStyle, r + "px");
            });
        }
    }

    ngOnDestroy() {
        if (!this.disabled)
            this._drag.remove(dragModes.resizePanel, this.splitterBar.id);
    }

    private dragStart(d: DragInfo) {

        this.started = true;
        this._render.addClass(this._el.nativeElement, this.draggingClass);
    }

    private dragEnd(ev: DragInfo) {
        this._render.removeClass(this._el.nativeElement, this.draggingClass);
        this.started = false;
    }

    private onDragging(d: DragInfo) {
        if (d.endOfDrag) {
            this.dragEnd(d);
            return;
        }

        if (!this.started) {
            this.dragStart(d);
        }

        if (this.started) {
            this.doResize(this.isVertical ? -d.diffx : -d.diffy);
        }
    }

    private doResize(diffSize: number) {
        let initSize = this.panel.getBoundingClientRect()[this.sizeStyle];
        let size = this.panelBefore ? initSize - diffSize : initSize + diffSize;

        this._render.setStyle(this.panel, "flex", "0 0 auto");
        this._render.setStyle(this.panel, this.sizeStyle, size + "px");

        //check whether the size is changed        
        let rect = this.panel.getBoundingClientRect();

        if (rect[this.sizeStyle] != size) {
            // revert 
            this.resizeEnsure.next(rect[this.sizeStyle]);
        }
    }
}

