import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'componentItemFilter'
})
@Injectable()
export class ComponentItemFilter implements PipeTransform {
    transform(compItems: any[], item: any, type: string): any[] {        
        if(item == undefined)
            return;    
                                
        let items = [];
        for(let key in compItems) {
            let value = compItems[key];
            if(value.name == item.name && value.type == type)
                items.push(compItems[key]);
        }       
                     
        return items;
    }
}

