import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ModalPropertiesContext } from '../properties';
import { StudioSubjects } from '../../../common/objects/common';
import { IMetadataType } from '../../../common/objects/viewModel';
import { ReadOnlyLevel } from '../../panel';

@Component({
    selector: 'csi-modal-object',
    styles: [
        `
        :host{
            min-height: 450px;
            min-width: 400px;
            height: 100%;
            width: 100%;
        }

        .csi-modal-object > main > section{
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
        }
        .csi-modal-object > main > section > .type-selector {
            flex: 0 0 auto;
        }
        .csi-modal-object > main > section > .type-selector > select {
            height: 24px;
            width: 100%;
            cursor: pointer;
        }
        .csi-modal-object > main > section > .content {
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
        }
        `
    ],
    template: `
<div class="csi-modal csi-modal-object" csi-dialog-decor [dialog]="dialog">
    <header csi-closable (onClose)="cancel()">
        <span class="title">{{context.property.name}}</span>
    </header>
    <main>
        <section>            
            <div class="type-selector" [hidden]="!showTypeSelector">
                <select [(ngModel)]="activePropertyTypeName" [disabled]="lockTypeSelector">
                    <option *ngFor="let ptype of propertyTypes" [value]="ptype._name">{{ptype._displayedName || ptype._name}}</option>
                </select>
            </div>
            <div class="content" (click)="touch()">
                <csi-panel [component]="'csi-property-list'" [subjects]="subjects" [readOnly]="readOnly" [parameters]="{showDescription: false}"></csi-panel>
            </div>
        </section>
    </main>
    <footer>        
        <div class="popup-message"><span>{{errorMessage}}</span></div>
        <div class="btns">
            <span class="default-btn" (click)="cancel()">{{'Cancel' | label: 'PS_CANCEL'}}</span>
            <span class="primary-btn" (click)="ok()" *ngIf="readOnly===undefined">{{'Ok' | label: 'PS_OK'}}</span>
        </div>
    </footer>
</div>`
})

export class ModalObjectComponent implements OnInit {
    context: ModalPropertiesContext;

    instance: any;
    subjects: StudioSubjects;

    // base type of object
    basePropertyType: string;

    // types derived from base type
    propertyTypes: IMetadataType[];

    // type of active collection item
    get activePropertyTypeName(): string { return this._activePropertyType._name; }
    set activePropertyTypeName(t: string) { this.onChangePropertyType(t); }

    showTypeSelector: boolean = false;
    lockTypeSelector : boolean = false;

    errorMessage: string;    

    readOnly: ReadOnlyLevel;

    get dialog(): MatDialogRef<ModalObjectComponent> {
        return this._dialog;
    }

    constructor(
        private _dialog: MatDialogRef<ModalObjectComponent>,
        private _chDetection: ChangeDetectorRef) {
        this.subjects = new StudioSubjects();
    }

    initContext(context: any): void {
        this.context = context;
    }

    ngOnInit() {
        this.basePropertyType = this.context.property.originalTypeName;
        if (!this.basePropertyType) {
            this.errorMessage = "Error: Property type is not defined. Editing impossible";
            return;
        }

        this.propertyTypes = this.context.metadataService.getSubTypes(this.basePropertyType);
        this.showTypeSelector = this.propertyTypes ? this.propertyTypes.length > 1 : false;
        if( this.context.property && this.context.property.editor && 
            this.context.property.editor.parameters && this.context.property.editor.parameters.find(p=> p == "noTypeSelector"))
            this.showTypeSelector = false;
            
        if (this.context.value && this.context.value.__type) {
            this.setActiveType(this.context.value.__type);
        }
        else {
            if (this.propertyTypes.length) {
                // select first                
                this._activePropertyType = this.propertyTypes[0];
            }
        }

        this.instance = this.context.value;
        if (this.instance == null && this._activePropertyType) {
            let tn = this._activePropertyType._name;
            if (this._activePropertyType._namespace)
                tn = this._activePropertyType._namespace + '.' + tn;

            this.instance = { __type: tn };
        }
        this.readOnly = this.context.property.readOnly ? ReadOnlyLevel.value : undefined;
        if (this.readOnly == ReadOnlyLevel.value)
            this.showTypeSelector = false;
       
        this.instance.__parent = this.context.instance;
        this.subjects.panel.subscribe(() => this.subjects.propertyPanel.next(this.instance));
    }

    touch() {
        this._chDetection.detectChanges();
    }

    setActiveType(t: string) {
        this._activePropertyType = this.propertyTypes.find(mdType => {
            if (mdType._namespace)
                return (mdType._namespace + '.' + mdType._name) == t;
            else
                return mdType._name == t;
        });
    }

    onChangePropertyType(t: string) {
        this.setActiveType(t);
        if (this._activePropertyType) {
            this.instance.__type = this._activePropertyType._name;
            if(this.instance.typeName)
                this.instance.typeName = this._activePropertyType._name;
            
            this.subjects.propertyPanel.next(this.instance);
        }
    }

    ok() {
        delete this.instance.__parent;
        this._dialog.close(this.instance);
    }

    cancel() {
        delete this.instance.__parent;
        this._dialog.close();
    }

    private _activePropertyType: IMetadataType;
}
