import { Component, Input, OnChanges } from '@angular/core';

import { ConditionExpression, IMetadataEnumValue } from '../../../common/objects/viewModel';
import { MetadataService } from '../../../common/services/metadata.service';

@Component({
    selector: 'csi-expression',
    templateUrl: 'expression.html',
    styleUrls: ['./expression.less']
})

export class ExpressionComponent implements OnChanges {
    @Input() expression: ConditionExpression;

    get enableAndAdd(): boolean { return this.expression.and !== undefined; }
    get enableOrAdd(): boolean { return this.expression.or !== undefined; };

    andOr = ['and', 'or'];
    conditionComparisonEnum: IMetadataEnumValue[];

    constructor(private _metadata: MetadataService) {
        this.conditionComparisonEnum = this._metadata.getMetadataType('ConditionComparisonType').enumValues;
    }

    ngOnChanges(ch) {
        if (ch.expression && ch.expression.currentValue.comparisonType === undefined)
            ch.expression.currentValue.comparisonType = 0;
    }

    addRemoveGroup(op: string): void {
        if (this.expression[op]) {
            delete this.expression[op];
        }
        else {
            if (!this.expression[op])
                this.expression[op] = [];
        }
    }

    addExpression(op: string): void {
        this.expression[op].push(new ConditionExpression);
    }

    deleteExpression(exp: ConditionExpression, expressions: ConditionExpression[]): void {
        let i = expressions.indexOf(exp);
        if (i != -1)
            expressions.splice(i, 1);
    }
}