import { MatDialog } from '@angular/material/dialog';

import { environment } from '../../environments/environment';
import { IUIDesigner, IElement, TopMenuState, ExecuteCommand } from '../common/objects/common';
import { HistoryLog } from '../common/objects/historyLog';
import { OMType} from '../common/objects/viewModel';
import { LabelService } from '../common/services/label.service';
import { LocalService } from '../common/services/local.service';
import { PortalService } from '../common/services/portal.service';

import { InputDialogComponent, InputDialogContext } from '../shared/dialog/input.dialog.component';

import * as _ from 'lodash';
import { StudioStateService } from '../common/services/studioStateService';
import { MetadataService } from '../common/services/metadata.service';
import { PortalSettingsService, SettingSection } from '../common/services/portalSettings.service';

export class ActionManager  {
    private _cmd: TopMenuState;

    get rootElement(): IUIDesigner { return <IUIDesigner>this._studioStateService.activeRootElement; }
    get history(): HistoryLog {
        if (this._studioStateService.activeRootElement)
            return this.rootElement['history'];
        return undefined;
    }

    _settings: any;
    constructor(
        private _dialog: MatDialog,
        private _labelService: LabelService,
        private _localService: LocalService,
        private _studioStateService: StudioStateService,
        private _portalService: PortalService,
        private _metadataService : MetadataService,
        private _portalSettingsService: PortalSettingsService
        )
        {
        this._cmd = new TopMenuState();
        this.portalSettings();
    }

    portalSettings() {
        let sections = this._portalSettingsService.getSections();
        sections.subscribe(sects => {
            let templateSection = { name: "studiosettings" };
            this._settings = this._portalSettingsService.getSectionData(<SettingSection>templateSection);
        });
    }

    updateMenu(cmd: TopMenuState): void {
        this._cmd = cmd;
    }

    //actions
    execute(command: string, element?: IElement, data?: any) {
        this._studioStateService.subjects.execute.next(new ExecuteCommand(command, element, data)); 
    }

    copy() {
        let okLabel = this._labelService.getLabel('PS_ACTION_OK', 'Ok');
        let cancelLabel = this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel');
        let copyTitleLabel = this._labelService.getLabelwithArguments('PS_COPY', 'Copy {0}', [this.rootElement.type]);
        let copyInputLabel = this._labelService.getLabelwithArguments('PS_COPY_OF', 'Copy of {0}', [this.rootElement.name]);
        let inputContext: InputDialogContext = {
            title: copyTitleLabel,
            message: '',
            actions: [
                { action: 'ok', name: okLabel, isPrimary: true },
                { action: 'cancel', name: cancelLabel, isPrimary: false }],
            inputs: [{ name: 'txt', type: 'text', text: '', value: copyInputLabel }]
        };

        let dialog = this._dialog.open(InputDialogComponent, { disableClose: true });

        dialog.componentInstance.initContext(inputContext);
        dialog.afterClosed().subscribe(res => {
            let elementInput = res[0];
            if (elementInput) {
                let newName = elementInput.value;
                this._portalService.copyElement(this.rootElement, newName, this._metadataService.getWorkspaceCodeById(this.rootElement.__workspace))
                    .subscribe(element => {
                        if (element) {                            
                            if (element.type && ['page', 'webpart', 'pageflow'].indexOf(element.type.toLowerCase()) >= 0) {
                                this._studioStateService.addOpenedElement(element);
                                this._localService.addToElementHistory(element);
                                this._studioStateService.addOrUpdateComponentRef((<IUIDesigner>element).componentRef);
                            }
                            this.execute('copy', element, {"name": newName, "message": element['Message']});
                        }
                    });
            }
        });
    }

    generate() {
        this.execute("generate", this._cmd.element, this._cmd.data);
    }

    publish() {
        let DEBUG_MODE = false;
        let titleLabel = this._labelService.getLabel('PS_WARNING_PUBLISH_TITLE', 'Publishing');
        let messageLabel = this._labelService.getLabel('PS_WARNING_PUBLISH_MESSAGE', 'Publishing will replace the current available component with the new one.  Do you want to continue?');
        let okLabel = this._labelService.getLabel('PS_ACTION_OK', 'Ok');
        let cancelLabel = this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel');
        let removeAllText = this._labelService.getLabel('PS_WARNING_PUBLISH_INPUT_TEXT', 'Remove All personalization associated with this component.');
        let replaceSystemText = this._labelService.getLabel('PS_WARNING_PUBLISH_REPLACE_TEXT', "Replace the currently availiable component's System content with the new one.");

        let inp = [{
            name: 'cb1',
            type: 'checkbox',
            text: removeAllText,
            value: false
        }];
        if (DEBUG_MODE) {
            inp.push({
                name: 'cb2',
                type: 'checkbox',
                text: replaceSystemText,
                value: false
            });
        }

        let inputContext: any = {
            title: titleLabel,
            message: messageLabel,
            actions: [
                { action: 'ok', name: okLabel, isPrimary: true },
                { action: 'cancel', name: cancelLabel, isPrimary: false }],
            inputs: inp
        };

        let dialog = this._dialog.open(InputDialogComponent, { disableClose: true });

        dialog.componentInstance.initContext(<InputDialogContext>inputContext);
        dialog.afterClosed().subscribe(res => {
            if (res === "cancel") {
            }
            else {
                this.rootElement['__publishFlags'] = _.map(res, p => <boolean>p['value']);
                this.execute('publish');
            }
        });
    }

    preview(): void {
        let activeComponent = this.rootElement;
        if (!activeComponent.isDirty && activeComponent.name) {
            let url = `${environment.portalUrl}/${activeComponent.name}.aspx?Test=true&CallStackKey=Preview`;
            let win = window.open(url, '_blank');
            win.focus();
        }
    }

    run(): void {
        let activeComponent = this.rootElement;
        if (!activeComponent.isDirty && activeComponent.name) {
            let url = "";
            if (activeComponent.type === "Pageflow")
                url = `${environment.portalUrl}/Main.aspx?redirectToPageflow=${activeComponent.name}`;
            else
                url = `${environment.portalUrl}/Main.aspx?redirectToPage=${activeComponent.name}`;
            let win = window.open(url, '_blank');
            win.focus();
        }
    }
    importComponent(event): void {
        let files = event.srcElement.files;
        if (files.length) {
            let xmlFile = files[0];
            if (xmlFile.type == "text/xml") {
                let reader = new FileReader();
                reader.onloadend = (e: ProgressEvent) => {
                    let xmlContent = (<FileReader>e.target).result;
                    let activeComponent = this.rootElement;
                    if (activeComponent != null) {
                        var dataToImport = {
                            rootElement: this.rootElement,
                            content: xmlContent
                        }
                        this.execute('import', null, dataToImport);
                    }
                    return true;
                }
                reader.readAsArrayBuffer(xmlFile);
                event.srcElement.value = "";
            }
        }
    }
   
    //History
    displayHistoryLog: string;
    undoRedo(cmd: string, shiftKey: boolean): void {
        if (shiftKey) {
            this.displayHistoryLog = this.displayHistoryLog ? '' : cmd;
        }
        else {
            this.displayHistoryLog = '';
            this.execute(cmd);
        }
    }

    togglePanel(panelName: string): void {
        this.execute("togglePanel", null, panelName);
    }

    resetPanels(): void {
        this.execute("resetPanels", null);
    }

    canExecute(command: string): boolean { 
        let canExecute = null;
        switch (command.toLowerCase()) {
            case "batchimport":
            case "batchexport":
                canExecute = true;
                break;
            case "copy":
                canExecute = !this.isStartOrNotAvailable() && !this.isNew();
                break;
            case "delete":
                canExecute = !this.isStartOrNotAvailable() && !this.isNew() && !this.isStatic();
                break;
            case "export":
                canExecute = !this.isStartOrNotAvailable() && (this.isPage() || this.isWebPart() || this.isPageflow());
                break;
            case "extract":
                canExecute = !this.isStartOrNotAvailable() && this.isPage() && (this._studioStateService.activeElement && this._studioStateService.activeElement.type == "Webpart" && this._studioStateService.activeElement.isReference == false);
                break;
            case "generate":
                canExecute = this._cmd && this._cmd.generate && this._cmd.data && this._cmd.data.ItemType == OMType.Service;
                break;
            case "import":
                canExecute = !this.isStartOrNotAvailable() && (this.isPage() || this.isWebPart() || this.isPageflow());
                break;
            case "preview":
                canExecute = !this.isStartOrNotAvailable() && !this.isDirty() && this.rootElement.isViewable; 
                break;
            case "publish":
                canExecute = !this.isStartOrNotAvailable() && (this.isPage() || this.isWebPart()) && !this.isDirty() && !this.isNew() && this.isInDeveloping() && this.isInDeveloping();
                break;
            case "redo":
                canExecute = this.history && this.history.redoCount;
                break;
            case "revert":
                canExecute = this.isRevertable("published") || this.isRevertable("system");
                break;
            case "revert_published":
                canExecute = this.isRevertable("published");
                break;
            case "revert_system":
                canExecute = this.isRevertable("system");
                break;
            case "run":
                canExecute = !this.isStartOrNotAvailable() && !this.isDirty() && this.rootElement.isTestable;
                break;
            case "save":
                canExecute = !this.isStartOrNotAvailable() && (this.isDirty() || this.isAvailableSave());
                break;
            case "saveall":
                canExecute = !this.isStartOrNotAvailable() && (this.isAnyElementDirty() || this.isAvailableSave());
                break;
            case "undo":
                canExecute = this.history && this.history.undoCount;
                break;
            case "reloadwebpart":
                canExecute = !this.isStartOrNotAvailable() && this._studioStateService.activeElement && this._studioStateService.activeElement.type == 'Webpart' && !this._studioStateService.activeElement.isReference && this._studioStateService.activeRootElement.type == 'Page';
                break;
        }
        return canExecute;
    }

    getDisplayState(name: string): boolean {
        let state: boolean = null;
        switch (name.toLowerCase()) {
            case "catalog":
                state = this._localService.isDisplayCatalog();
                break;
            case "toolbox":
                state = this._localService.isDisplayToolbox();
                break;
            case "property":
                state = this._localService.isDisplayProperty();
                break;
            case "traceviewer":
                state = this._localService.isDisplayTraceViewer();
                break;
            default:
                break;
        }
        return state;
    }

    //state helpers
     isAvailableSave():boolean{
        if(this._settings){
            if(this._settings.hasOwnProperty("AlwaysEnableSave")){
                return this._settings["AlwaysEnableSave"];
            }else return false;
        }
        else return false
    }
    private isStartOrNotAvailable(): boolean {
        return !this.rootElement || this.rootElement.type == 'StartPage';
    }
    private isPage(): boolean {
        return !this.isStartOrNotAvailable() && this.rootElement.type == "Page";
    }
    private isWebPart(): boolean {
        return !this.isStartOrNotAvailable() && this.rootElement.type == "Webpart";
    }
    private isPageflow(): boolean {
        return !this.isStartOrNotAvailable() && this.rootElement.type == "Pageflow";
    }
    private isAnyElementDirty(): boolean {
        return _.some(this._studioStateService.openedElements, ['isDirty', true]);
    }
    private isDirty(): boolean {
        return this.rootElement.isDirty;
    }
    private isNew(): boolean {
        return this.rootElement.isNew;
    }
    private isInDeveloping(): boolean {
        return this.rootElement.workspaceIndexes && this.rootElement.workspaceIndexes.indexOf(0) != -1;
    }
    
    public isCustomWorkspace(): boolean {
        return typeof this.rootElement.__workspace != "undefined" && this.rootElement.__workspace > 1;
    }
    private isStatic(): boolean {
        return this.rootElement['isStatic'] === true;
    }
    private isPublished(): boolean {
        return this.rootElement.workspaceIndexes && this.rootElement.workspaceIndexes.filter(w => w > 1).length > 0;
    }
    private isSystem(): boolean {
        return this.rootElement.workspaceIndexes && this.rootElement.workspaceIndexes.filter(w => w == 1).length > 0;
    }
    private isRevertable(t: string ) : boolean{
        let r = !this.isStartOrNotAvailable() && (this.isPage() || this.isWebPart()) && !this.isNew();
        if( t == "system")
            return r && this.isSystem() && (this.isPublished() || this.isInDeveloping());
        else if( t == "published")
            return r && this.isPublished() && this.isInDeveloping();
        return false;
    }
}