import { Component, Input, ChangeDetectionStrategy, HostBinding, OnInit } from '@angular/core'
import { IElement, StudioSubjects, IDesignControl } from '../../../common/objects/common';
import { ControlManager } from './controlManager';

@Component({
    selector: 'csi-control',
    template: '<div></div>',
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class ControlComponentBase implements OnInit, IDesignControl {
    @Input("element") __element: any;
    @Input() subjects: StudioSubjects;
    @HostBinding('class.csi-control-image') simpleControl: boolean = false;
    @HostBinding('class.csi-control-LayoutContainer') layoutContainerControl: boolean = false;

    controlManager: ControlManager;

    get element(): IElement { return this.__element; }
    set element(v: IElement) { this.__element = v; }
    isReference:boolean;
    get labelClass() : string {
        return 'label-' + this._labelPositions[this.controlManager.getControlProperty("labelPosition", 0)] + 
            ((this.controlManager.getControlProperty("required") === true) ? " field-required" : "");
    }

    getLabelClass(defaultPosition: number){
        return 'label-' + this._labelPositions[this.controlManager.getControlProperty("labelPosition", defaultPosition)] + 
            ((this.controlManager.getControlProperty("required") === true) ? " field-required" : "");
    }

    ngOnInit(): void {
        this.controlManager = new ControlManager(this.element);
        this.simpleControl = !this.isLayoutContainer(this.element);
        this.layoutContainerControl = !this.simpleControl;             
    }

    isLayoutContainer(element: IElement): boolean {
        return element.layout || element.typeName == "JQTabContainer";
    }

    private _labelPositions = ["top", "bottom", "left", "right", "hidden"];
}

