import { Pipe, PipeTransform, Injectable } from "@angular/core";

@Pipe({
    name: 'gridWizardFilter'
})
@Injectable()

export class GridWizardFilter implements PipeTransform {
    transform(items: any[], searchText:string) {
        if (items && items.length && searchText && searchText.length){
            return items.filter(x => x.Text.toLowerCase().indexOf(searchText.toLowerCase()) > -1);       
        }
        return items;
    }
}