import { Injectable } from '@angular/core';
import { IResultStatus, ILabel } from '../common/objects/common';
import { Alert, AlertTypes } from './alert';
import { LabelService } from '../common/services/label.service';

import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable } from 'rxjs';
import {map } from 'rxjs/operators';
import { AlertModalComponent } from './alert.modal.component';

@Injectable()
export class AlertService {

    public alerts: Alert[] = [];

    constructor(
        private _dialog: MatDialog,
        private _labelService: LabelService,
        private _snackBar: MatSnackBar) {
    }

    public init() : void {
        this.alerts = [];
    }

    public displayResultStatus(status: IResultStatus) {
        this.addAlert(status.IsSuccess ? AlertTypes.success : AlertTypes.danger, status, false);
    }


    public error(err: string | IResultStatus | ILabel) {
        this.addAlert(AlertTypes.danger, err, false);
    }

    public warning(err: string | IResultStatus | ILabel) {
        this.addAlert(AlertTypes.warning, err, false);
    }

    public success(err: string | IResultStatus | ILabel) {
        this.addAlert(AlertTypes.success, err, false);
    }


    public displayResultStatusModal(status: IResultStatus) {
        this.addAlert(status.IsSuccess ? AlertTypes.success : AlertTypes.danger, status, true);
    }

    public warningModal(err: string | IResultStatus | ILabel, title?: string | ILabel): Observable<boolean> {
        return this.openModalAlert(AlertTypes.warning, err, title).pipe(map(action => action == "primary"));
    }
    public errorModal(err: string | IResultStatus | ILabel, title?: string | ILabel) {
        return this.openModalAlert(AlertTypes.danger, err, title);
    }

    public confirmModal(txt: string | ILabel, title?: string | ILabel): Observable<boolean>  {
        return this.openModalAlert(AlertTypes.choice, txt, title).pipe(map(action => action == "primary"));
    }

    public choiceModal(txt: string | ILabel, title?: string | ILabel, choices?: ILabel[]): Observable<string>  {
        return this.openModalAlert(AlertTypes.choice, txt, title, choices);
    }

    private resolveText(textOrLabelOrStatus: string | IResultStatus | ILabel): string {
        let txt: string;

        if (typeof textOrLabelOrStatus === "string")
            txt = <string>textOrLabelOrStatus;
        else if ((<ILabel>textOrLabelOrStatus).Name) {
            let lbl = <ILabel>textOrLabelOrStatus;
            txt = lbl.arguments ?
                this._labelService.getLabelwithArguments(lbl.Name, lbl.DefaultValue, lbl.arguments) :
                this._labelService.getLabel(lbl.Name, lbl.DefaultValue);
        }
        else {
            let rs = <IResultStatus>textOrLabelOrStatus;
            txt = rs.Message || (rs.ExceptionData && rs.ExceptionData.Description) || "";
        }

        return txt;
    }

    private createAlert(t: AlertTypes, err: string | IResultStatus | ILabel, modal: boolean, title?: string | ILabel): Alert {

        let alert: Alert = new Alert(t, this.resolveText(err));
        alert.modal = modal;
        alert.title = this.getDefaultTitle(t);

        if (title !== undefined) {
            alert.title = this.resolveText(title);
        }

        return alert;
    }

    private addAlert(t: AlertTypes, err: string | IResultStatus | ILabel, modal: boolean): void {

        let newAlerts = this.alerts.map<Alert>(a => a);
        let alert = this.createAlert(t, err, modal);
        if (alert) {
            newAlerts.push(alert);
            let snackBarConfig;

            switch (alert.type) {
                case 1:
                    snackBarConfig = {
                        panelClass: 'snack-bar-warning'
                    };
                    break;
                case 3:
                    snackBarConfig = {
                        panelClass: 'snack-bar-success',
                        duration: 3000
                    };
                    break;
            }

            let ref = this._snackBar.open(alert.message, 'Dismiss', snackBarConfig);
            ref.onAction().subscribe(action => {
            });
        }                    
        this.alerts = newAlerts;
    }

    private openModalAlert(alertType: AlertTypes, txt: string | IResultStatus | ILabel, title?: string | ILabel, choices?: ILabel[]): Observable<string> {
        let modalAlert = this.createAlert(alertType, txt, true, title);

        let modalRef = this._dialog.open(AlertModalComponent, {disableClose: true});
        let am = modalRef.componentInstance;
        am.messageText = modalAlert.message;
        am.messageClass = modalAlert.class;
        am.title = modalAlert.title;
        am.cancelBtnCaption = (alertType == AlertTypes.choice) ? this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel') : '';
        am.primaryBtnCaption = this._labelService.getLabel('PS_ACTION_OK', 'Ok');

        if( !choices ){
            am.cancelBtnCaption = (alertType == AlertTypes.choice) ? this._labelService.getLabel('PS_ACTION_CANCEL', 'Cancel') : '';
            am.primaryBtnCaption = this._labelService.getLabel('PS_ACTION_OK', 'Ok');
        }
        else{
            am.cancelBtnCaption = this._labelService.getLabel(choices[0].Name, choices[0].DefaultValue);
            am.primaryBtnCaption = this._labelService.getLabel(choices[1].Name, choices[1].DefaultValue);
        }
        return modalRef.afterClosed();
    }

    private getDefaultTitle(alertType: AlertTypes): string {
        let t = '';
        switch (alertType) {
            case AlertTypes.success:
                t = 'Success'; break;
            case AlertTypes.danger:
                t = 'Error'; break;
            case AlertTypes.warning:
                t = 'Warning'; break;
            case AlertTypes.choice:
                t = 'Confirm'; break;
            case AlertTypes.info:
                t = 'Info'; break;
        }
        return t;
    }

}

