import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

import { ConditionExpression} from '../../../common/objects/viewModel';
import { ModalPropertiesContext } from '../properties';

import * as _ from 'lodash';

@Component({
    selector: 'csi-condition-editor',
    styles:[
        `
        :host { 
            height: 100%;
            min-height: 600px;
            min-width: 850px;
        }
        .condition-expression {
            overflow: auto;
        }
        `
    ],
    templateUrl: 'editCondition.html'
})

export class EditConditionComponent {
    condExp: ConditionExpression;

    get dialogRef(): MatDialogRef<EditConditionComponent> {
        return this._dialogRef;
    }

    constructor(
        private _dialogRef: MatDialogRef<EditConditionComponent>) {
    }

    initContext(context: ModalPropertiesContext): void {
        this.condExp = context.value ? _.cloneDeep(context.value) : new ConditionExpression();
    }

    expressionValueChanged(editCondition: any): void {
        this.condExp = editCondition;
    }

    ok(): void {        
        this._dialogRef.close(this.condExp);
    }

    cancel(): void {
        this._dialogRef.close(null);
    }

}

