export class HandlerItem {
    protected __type: string = "HandlerItem";
    public name: string = "";
    public parameters: HandlerParameter[] = [];

    public static parse(s: string | HandlerItem) : HandlerItem{
        // parse method and parameters
        let h = new HandlerItem();
        if( !s ){
            h.name = '';
            h.parameters = [];            
        }
        else{
            if( _.isString(s) ){
                let spl = s.split('(');
                h.name = spl[0];
                h.parameters = (spl[1] ? spl[1].replace(')', '').split(',') : [])
                    .filter(sp=> !!sp)
                    .map(ps=> {
                            let spp = ps.split(':');
                            return <HandlerParameter> {name: spp[0], type: spp[1], value: undefined } 
                    });
                }
            else{
                let hs = s as HandlerItem;
                h.parameters = hs.parameters;
                h.name = hs.name;
                // cut the parameters from the name
                let i = h.name.indexOf('(');
                if( i != -1)
                    h.name = h.name.substr(0, i);
            }
        }
        return h;
    }

    public get methodSignature() : string {
        let prms = this.parameters.map(prm => prm.name  +':'+ HandlerItem.getShortType(prm.type));
        return `${this.name}(${prms.join()})`;
    }

    public static getShortType = (fullType: string) => {
        if(fullType ){
            let typeName = fullType.split(',', 1)[0];
            if( typeName )
                return _.last(typeName.split('.'));
        }
        return '';
    }

    public toTypeHandler() : TypeHandler{
        let th = new TypeHandler();
        th.Name = this.name;
        if( this.parameters )
            th.Parameters = this.parameters.map(par=> {
                let hp = new TypeHandlerParameter(); 
                hp.Name = par.name; 
                hp.Type = par.type;
                return hp;
            });

        return th;
    }

    public static fromTypeHandler(th: TypeHandler) : HandlerItem{
        let h = new HandlerItem();
        if( !th )
            return h;

        h.name = th.Name;
        h.parameters = 
            th.Parameters ? 
            th.Parameters.map(p=> { 
                let tp = new HandlerParameter(); 
                tp.name = p.Name; 
                tp.type = p.Type; 
                return tp;
            } ) 
            : [];

        return h;
    }

    public static equals(h1: HandlerItem, h2: HandlerItem) : boolean {
        if( !h1 && !h2 )
            return true;

        else if( (!h1 && h2) || (h1 && !h2) )
            return false;

        else if( h1.name == h2.name && h1.parameters.length == h2.parameters.length)
            return true;

        return false;
    }

}

