import { Component, EventEmitter, Input, OnInit, Output, ChangeDetectionStrategy, ElementRef } from '@angular/core';

import { IElement, IUIDesigner, StudioSubjects, ILabel } from '../common/objects/common';
import { fromEvent } from 'rxjs';

import { AlertService } from '../alert/alert.service';
import { StudioStateService } from '../common/services/studioStateService';

@Component({
    selector: 'csi-layout',
    templateUrl: 'layout.component.html',
    styleUrls:['layout.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LayoutComponent implements OnInit {
    @Input('rootElement') __rootElement: any;
    @Input() subjects: StudioSubjects;
    @Input() showTraceViewer: boolean;

    @Output() onLoadElement = new EventEmitter<IElement>();

    inArea = false;

    get rootElement(): IUIDesigner { return this.__rootElement; }

    constructor(
        private _element: ElementRef,
        private _studioStateService: StudioStateService,
        private _alertService: AlertService) {
    }

    ngOnInit(): void {
        fromEvent<KeyboardEvent>(document, 'keyup')
            .subscribe(ke => {
                if((ke.key == "Delete" || ke.key == "Del") && !(ke.altKey || ke.shiftKey || ke.ctrlKey) ){                    
                    if( this.inArea && this.rootElement && this.rootElement.type !== "StartPage" )
                        this.deleteElement();
                    else{
                        let act = this._studioStateService.activeElement;
                        if(act && act['__type'] === "UIComponentRef" ){
                            this.subjects.deleteElement.next(act);
                        }
                    }
                }
            });

        fromEvent<MouseEvent>(this._element.nativeElement, 'mouseenter').subscribe(m => this.inArea = true);
        fromEvent<MouseEvent>(this._element.nativeElement, 'mouseleave').subscribe(m => this.inArea = false);
    }

    loadElement(element: IElement): void {
        this.onLoadElement.emit(element);
    }

    deleteElement(): void {
        let { name: activeName, type: activeType } = this._studioStateService.getRoot().__selectedElement;
        // Webpart fix
        if( activeType == "Webpart")
            activeType = "Web Part";

        let lblMessage = <ILabel>{
            Name: 'PS_WARNING_DELETE_ELEMENT_MESSAGE',
            DefaultValue: 'You are about to permanently delete the selected {0} {1}.Please make sure that other components have no dependencies on it.Do you want to continue?',
            arguments: [activeName, activeType.toLowerCase()]
        };
        let titleLabel = <ILabel>{
            Name: 'PS_WARNING_DELETE_ELEMENT_TITLE',
            DefaultValue: 'Delete {1}',
            arguments: [activeName, activeType]
        };

        this._alertService.confirmModal(lblMessage, titleLabel)
            .subscribe(ok => { if (ok) this.removeElementFinally(); });
    }

    private removeElementFinally() {
        this._studioStateService.removeElementFromCell();
    }

}
