import { Component, Input,  OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';

import { ModalPropertiesContext } from '../properties';
import { Subject, Subscription } from 'rxjs';
import {map, switchAll} from "rxjs/operators";
import { PortalService } from '../../../common/services/portal.service';
import * as _ from 'lodash';
import { val } from '../../../common/objects/common';


@Component({
    selector: 'csi-target-matrix-rows',
    template: `
        <div class='target-matrix-data'>
            <div class="buttons">
                <button (click)="addRow()">Add Row</button>
                <input type="file" #excelInputFile (change)="inputFileChanged($event)" accept=".xls,.xlsx">
                <button type="button" class="inputFileButton" (click)="inputFileBtnClick($event)">Import from Excel</button>    
            </div>
            <div class="matrix-data-grid">
                <div class="data-rows-container">
                    <div class="headers">
                        <div class="column-caption" *ngFor="let col of columns">{{col.caption}}</div>
                        <div class="column-caption">{{'Target'}}</div>
                    </div>
                    <div class="data-rows">
                        <div class="row" *ngFor="let row of rows; index as rowIndex; even as rowEven; last as rowLast">
                            <div class="cell" *ngFor="let c of row.cells" [class.row-even]="rowEven" [class.row-last]="rowLast">
                                <input type="text" class="input-value" [(ngModel)]="c.value">
                            </div>
                            <div class="cell" [class.row-even]="rowEven" [class.row-last]="rowLast">
                                <input type="text" class="input-value" [(ngModel)]="row.targetValue">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>`,
    styles:[`
        :host {
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }
        .target-matrix-data{
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }
        .target-matrix-data .buttons{
            display: flex;
            flex: 0 0 auto;
        }

        .target-matrix-data .buttons button{
            padding: 2px 4px;
        }
        
        .matrix-data-grid{
            padding: 2px;
            border: 1px solid #cccccc;
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }

        .matrix-data-grid .headers{
            flex: 0 0 auto;
            display: flex;
            background-color: #dddddd;
        }

        .column-caption, .cell {
            flex: 0 0 100px;
            border-right: 1px solid #aaaaaa;
            padding-left: 3px;
            padding-right: 3px;
        }

        .cell .input-value{
            width: 100%;
            border-color: transparent;            
            background-color: transparent;
        }

        .cell.row-even{
            background-color: #eeeeee;
        }

        .cell.row-last{
            border-bottom: 1px solid #aaaaaa;
        }

        .column-caption{
            padding-top: 2px;
            padding-bottom: 2px;
            background-color: #dddddd;
        }
        
        .data-rows-container{
            display: flex;
            flex-direction: column;
            flex: 1 1;
            overflow: auto;
        }
        .data-rows {
            display: flex;
            flex-direction: column;
            flex: 1 1;
        }
        .data-rows .row {
            display: flex;
            flex: 0 0 auto;
            margin: 0;
        }
        input[type=file]{
            display: none;
        }
    `]

})

export class TargetMatrixRowsComponent implements OnInit, OnDestroy {  

    @Input() context: ModalPropertiesContext;

    @ViewChild("excelInputFile")  excelInputFileCtl;

    excelFile: string;
    excelDataReader = new Subject<any>();
    columns : targetMatrixColumn[];
    rows: targetMatrixRow[];
    excelReaderSubs: Subscription;

    constructor(private _portalService: PortalService) {
    }  
      
    ngOnInit(): void {
        this.context.title = this.context.property.displayedName || this.context.property.name;
        this.context.beforeClose = (ok: boolean) => this.onBeforeClose(ok);
        this.rows = this.context.value as targetMatrixRow[] || [];

        if(this.context.instance)
            this.columns = _.map(val(this.context.instance, 'columns'), c=> {
                let cx = new targetMatrixColumn();
                cx.caption = val(c, 'caption');
                cx.name = val(c, 'name');
                return cx;
            });

        this.excelReaderSubs = this.excelDataReader.pipe(
                map(b => this._portalService.targetMatrixFromExcel(b)),
                switchAll()
            ).subscribe(tx=> {                    
                this.rows = tx.rows;
                this.columns = tx.columns;
            });        
    }

    ngOnDestroy(){
        if( this.excelReaderSubs )
            this.excelReaderSubs.unsubscribe();
    }

    inputFileChanged(e : Event) {
        let fileName = "";
        let fileElement = (this.excelInputFileCtl as ElementRef).nativeElement as HTMLInputElement;
        if (fileElement) {
            if (fileElement.files && fileElement.files.length > 0) {
                let file = fileElement.files[0];
                if (file.type == "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet") {
                    fileName = file.name;
                    this.fileReaderObs(file);
                }
            }
        }
        this.excelFile = fileName;
    }

    addRow(){
        let r =  new targetMatrixRow();
        r.cells = this.columns.map(col=> new targetMatrixCell(col.name));

        this.rows.push(r);
    }

    private fileReaderObs(file : File) {
        let reader = new FileReader();
        reader.onload = () => {
            // convert to byte array
            let res : Uint8Array = new Uint8Array((<any>reader).result);
            let data : Uint8Array[] = [];
            for( let i = 0; i<res.length; i++)
                data.push(<any> res[i]);
            this.excelDataReader.next(data);
        }        
        reader.readAsArrayBuffer(file);
    }

    private onBeforeClose(ok: boolean) : boolean {
        if( ok ){
            this.context.value = this.rows;
            val(this.context.instance, 'columns', this.columns);
            val(this.context.instance, 'importedFileName', this.excelFile);
            return true;
        }

        return true;
    }

    inputFileBtnClick(e) {
        let fileElement = (this.excelInputFileCtl as ElementRef).nativeElement as HTMLInputElement;
        if (fileElement) {
            fileElement.focus();
            fileElement.click();
        }
    }

}

class targetMatrixColumn{
    name: string;
    caption: string;
    __type: string = "TargetMatrixColumn";
}

class targetMatrixCell{
    __type: string = "TargetMatrixCell";
    name: string = "";
    value: string = "";

    constructor(n: string = ""){
        this.name = n;
    }

}
class targetMatrixRow{
    cells: targetMatrixCell[];
    targetValue: string = "";   
    __type : string = "TragetMatrixRow";
}
