import { uuid, IElement, LayoutActions } from './common';
import * as _ from 'lodash';

export enum HistoryOperation { undo=1, redo=2 };

export class HistoryLog {

    private _undoStack: LogItem[] = [];

    private undoPointer: number = 0;
    public get undoCount(): number { return this.undoPointer; }
    public get redoCount(): number { return this._undoStack.length - this.undoPointer; }

    constructor() {
        this.undoPointer = 0;
    }

    public recordHistoryItem(cmd: RecordCommand, data: IHistory): void {
        // discard all items next to undoPointer
        this._undoStack.splice(this.undoPointer, this._undoStack.length - this.undoPointer);

        if (cmd == RecordCommand.propertyChange) {
            let lastRecord = _.last(this._undoStack);
            if (lastRecord && _.isFunction(lastRecord.specificData.tryToCombine)) {
                // combine text changes into one transaction
                if (lastRecord.specificData.tryToCombine(data))
                    return;
            }
        }
        this._undoStack.push(new LogItem(cmd, data));
        this.undoPointer = this._undoStack.length;
    }

    public pop(operation: HistoryOperation): LogItem {
        let c: LogItem;
        if (operation == HistoryOperation.undo) {
            c = this._undoStack[this.undoPointer - 1];
            this.undoPointer--;
        }
        else {
            c = this._undoStack[this.undoPointer];
            this.undoPointer++;
        }
        return c;
    }

    public display(operation: string): string[] {
        return this._undoStack
            .filter((d, i) => operation == 'undo' ? (i < this.undoPointer) : (i >= this.undoPointer))
            .map(l => l.toDisplay());
    }
}

export enum HistoryObjectType { Webpart, Control, Property, Layout };

export enum RecordCommand { add, remove, layoutChange, move, propertyChange, replace }

export interface IHistory {
    objectType: HistoryObjectType,
    toDisplay(): string;
    tryToCombine?(data: IHistory): boolean;
}

// add, delete, move - Control, Webpart
export class ElementHistoryData implements IHistory {
    public element: IElement;
    public oldElement: IElement;
    public cellId: string;
    public fromCellId: string;

    constructor(public objectType: HistoryObjectType) {
    }

    toDisplay(): string {
        return `${this.element.name} of ${this.element.typeName}`;
    }

}

export class LayoutHistoryData implements IHistory {

    public column: number;
    public row: number;
    public objectType: HistoryObjectType = HistoryObjectType.Layout;

    constructor(public command: LayoutActions, public layoutBefore: any, public layoutAfter: any, public layoutOwner: IElement) {
    }

    toDisplay(): string {
        let r = LayoutActions[this.command];
        return r;
    }
}

type nv = { [name: string]: any };

export class PropertyHistoryData implements IHistory {
    public objectType: HistoryObjectType = HistoryObjectType.Property;

    constructor(public instance: any, public path: string, public oldValues: nv, public newValues: nv, public isName: boolean) {
    }

    toDisplay(): string {
        return `${this.path} from ${JSON.stringify(this.oldValues)} to ${JSON.stringify(this.newValues)}`;
    }

    tryToCombine(data: IHistory): boolean {
        let d = <PropertyHistoryData>data;
        if (d.path == this.path) {
            this.newValues[this.path] = d.newValues[d.path];
            return true;
        }

        return false;
    }
}

export class LogItem {
    public specificData: IHistory;
    public id: string;

    constructor(public command: RecordCommand, data: IHistory) {
        this.id = uuid.id('log-');
        this.specificData = data;
    }

    toDisplay(): string {
        let r = RecordCommand[this.command]; 
        if (this.specificData)
            r += (' ' + HistoryObjectType[this.specificData.objectType] + ' ' + this.specificData.toDisplay());

        return r;
    }
}

