import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ServerTraceItem, textLine } from './traceLog';

@Component({
    selector: 'csi-trace-raw-view',
    template: `
    <div class="csi-modal">
        <header>
            <span class="title">{{'Raw View' | label: 'PS_SERVER_TRACE_RAWVIEW'}}</span>
            <span class="close-icon" (click)="close('cancel')"><i class="fa fa-times" aria-hidden="true"></i></span>
        </header>
        <main>        
            <csi-raw-text [text]="txtLines"></csi-raw-text>
        </main>
        <footer>
            <div class="btns">
                <span class="default-btn" (click)="close('ok')">{{'OK' | label: 'PS_OK'}}</span>
                <span class="primary-btn" (click)="close('cancel')">{{'Cancel' | label: 'PS_CANCEL'}}</span>
            </div>
        </footer>
    </div>        
    `
})

export class TraceRawViewComponent {

    public set traceItem(tr: ServerTraceItem) { this.getRawText(tr); }

    txtLines: textLine[];

    constructor(private _dialogRef: MatDialogRef<TraceRawViewComponent>) {
        this.txtLines = [];
    }

    close(action: string): void {       
        this._dialogRef.close();
    }

    getRawText(t: ServerTraceItem, level: number = 0) {

        let s = '<' + t.Name;
        t.Attributes.forEach(a => s += (` ${a.Name}='${a.Value}'`));

        let hasValue = (t.Value !== undefined && t.Value !== null);

        let hasChidren = (t.Children && t.Children.length > 0);

        if (hasValue) {
            s += '>';
            // if multiline add \n otherwise the value will be in the same line with open tag
            if (t.Value.indexOf('\n') == -1) {
                s += (t.Value + '</' + t.Name + '>');
                hasValue = false;
            }
        }
        else {
            if (!hasChidren)
                s += '/>';
            else
                s += '>';
        }

        // open <tag>
        this.txtLines.push(<textLine>{ level: level, text: s });
        if (hasValue) {
            t.Value.split('\n').forEach(v => this.txtLines.push(<textLine>{ level: 0, text: v }));
        }

        if (hasChidren) {
            // add child nodes
            t.Children.forEach(c => this.getRawText(c, level + 1));
        }

        if (hasValue || hasChidren) {
            // closed <tag>
            this.txtLines.push(<textLine>{ level: level, text: '</' + t.Name + '>' });
        }

        return s;
    }
}

